import { CollectionViewer, DataSource } from '@angular/cdk/collections';
import { BehaviorSubject, merge, Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { RdsTreeFlattener } from './tree-flattener';
import { RdsTreeControl } from './tree-control';

export class RdsTreeDataSource<T, F, K = F> extends DataSource<F> {
  _flattenedData = new BehaviorSubject<F[]>([]);

  _expandedData = new BehaviorSubject<F[]>([]);

  _data: BehaviorSubject<T[]>;
  get data(): T[] {
    return this._data.value;
  }

  set data(value: T[]) {
    this._data.next(value);
    this._flattenedData.next(this._treeFlattener.flattenNodes(this.data));
    this._treeControl.dataNodes = this._flattenedData.value;
  }

  constructor(
    private _treeControl: RdsTreeControl<F, K>,
    private _treeFlattener: RdsTreeFlattener<T, F, K>,
    initialData: T[] = [],
  ) {
    super();
    this._data = new BehaviorSubject<T[]>(initialData);
  }

  connect(collectionViewer: CollectionViewer): Observable<F[]> {
    const changes = [
      collectionViewer.viewChange,
      this._treeControl.expansionModel.changed.asObservable(),
      this._flattenedData.asObservable(),
    ];

    return merge(...changes).pipe(
      map(() => {
        this._expandedData.next(
          this._treeFlattener.expandFlattenedNodes(
            this._flattenedData.value,
            this._treeControl,
          ),
        );

        return this._expandedData.value;
      }),
    );
  }

  disconnect(): void {
    // no op
  }
}
