import {
  ChangeDetectionStrategy,
  Component,
  ContentChild,
  Input,
  ViewEncapsulation,
} from '@angular/core';
import { RdsCardComponent } from '../card.component';
import { RdsCardSelectableDirective } from '../card-selectable.directive';
import { RdsCardDashboardColor } from '../card.types';
import { RdsCardLabelDirective } from '../card-label.directive';
import { RdsBadgeComponent } from '../../badge';

@Component({
  selector: 'rds-dashboard-card',
  templateUrl: './dashboard-card.component.html',
  styleUrls: ['./dashboard-card.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  exportAs: 'rdsDashboardCard',
  standalone: true,
  hostDirectives: [
    {
      directive: RdsCardSelectableDirective,
      inputs: ['isSelected'],
    },
  ],
  imports: [RdsBadgeComponent],
})
export class RdsDashboardCardComponent extends RdsCardComponent {
  private _label: RdsCardLabelDirective;

  @ContentChild(RdsCardLabelDirective)
  set label(value: RdsCardLabelDirective) {
    if (value !== this._label) {
      this._label = value;

      // we need additional change detection to handle cases where
      // user has *ngIf on the <rds-card-label>
      this.cdr.markForCheck();
    }
  }
  get label(): RdsCardLabelDirective {
    return this._label;
  }

  override get class(): string {
    return `rds-card rds-dashboard-card rds-dashboard-card-${this.color}`;
  }

  @Input()
  color: RdsCardDashboardColor = 'default';
}
