import {
  AfterContentInit,
  ContentChildren,
  DestroyRef,
  Directive,
  HostBinding,
  HostListener,
  inject,
  Input,
  isDevMode,
  OnChanges,
  OnDestroy,
  QueryList,
  SimpleChanges,
} from '@angular/core';
import { merge } from 'rxjs';
import { RdsListItemComponent } from './list-item.component';
import { startWith, switchMap, tap } from 'rxjs/operators';
import { FocusKeyManager } from '@angular/cdk/a11y';
import { DOWN_ARROW, ENTER, SPACE, UP_ARROW } from '@angular/cdk/keycodes';
import { RdsMultiSelectListItemComponent } from './multi-select-list-item.component';
import { RdsAbstractSelectableListItemComponent } from './abstract-selectable-list-item.component';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { TAB_INDEX_DEFAULT, TAB_INDEX_NOT_FOCUSABLE } from '../utils';

@Directive({
  selector: '[rds-list-item-container], [rdsListItemContainer]',
})
export class RdsListItemContainerDirective
  implements OnChanges, OnDestroy, AfterContentInit
{
  private readonly destroyRef = inject(DestroyRef);

  @ContentChildren(RdsListItemComponent, { descendants: true })
  protected _allItems: QueryList<RdsListItemComponent>;
  private _directDescendantItems = new QueryList<RdsListItemComponent>();
  private _keyManager: FocusKeyManager<RdsListItemComponent>;

  @Input()
  role: 'menu' | 'listbox';

  @HostBinding('attr.role')
  protected attrRole: 'menu' | 'listbox' = 'menu';

  @HostBinding('tabIndex')
  protected tabIndex = TAB_INDEX_NOT_FOCUSABLE;

  @HostBinding('attr.aria-multiselectable')
  protected ariaMultiSelectable = false;

  @HostBinding('attr.aria-activedescendant')
  protected ariaActiveDescendant: string | null = null;

  @HostBinding('class')
  protected readonly class = 'rds-list-item-container';

  ngAfterContentInit(): void {
    this._updateDirectDescendants();
    this._keyManager = new FocusKeyManager(this._directDescendantItems)
      .skipPredicate((i) => i.disabled)
      .withWrap()
      .withTypeAhead()
      .withHomeAndEnd();
    this._directDescendantItems.changes
      .pipe(
        startWith(this._directDescendantItems),
        tap(() => {
          this.tabIndex = this._directDescendantItems.filter((i) => !i.disabled)
            ? TAB_INDEX_DEFAULT
            : TAB_INDEX_NOT_FOCUSABLE;
          this.setRoleTagsIfParentNotSetAndChildrenHasSameValue();
          this.checkRoleTags();
        }),
        switchMap((items) =>
          merge(...items.map((item: RdsListItemComponent) => item._focused)),
        ),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe((focusedItem) =>
        this._keyManager.updateActiveItem(focusedItem as RdsListItemComponent),
      );
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['role'] && changes['role'].currentValue) {
      this.attrRole = this.role;
      this.checkRoleTags();
    }
  }

  ngOnDestroy(): void {
    this._directDescendantItems.destroy();
  }

  @HostListener('focus')
  focus(): void {
    if (!this._keyManager.activeItem) {
      this._keyManager.setFirstItemActive();
    }

    if (this._keyManager.activeItem) {
      const firstSelected = this._directDescendantItems
        .toArray()
        .findIndex(
          (i) => (i as RdsAbstractSelectableListItemComponent).checked,
        );

      if (firstSelected) {
        this._directDescendantItems.get(firstSelected)?.focus('keyboard');
      }

      this._keyManager.activeItem.focus('keyboard');
      this.ariaActiveDescendant = this._keyManager.activeItem.getLabel();
    }
  }

  @HostListener('blur')
  blur(): void {
    this._keyManager.activeItem?.blur();
    this.ariaActiveDescendant = null;
  }

  @HostListener('keydown', ['$event'])
  protected handleKeydown(event: KeyboardEvent): void {
    if (event.keyCode === UP_ARROW || event.keyCode === DOWN_ARROW) {
      this._keyManager.setFocusOrigin('keyboard');
    }

    const lastActiveItem = this._keyManager.activeItem;
    this._keyManager.onKeydown(event);

    if (this._keyManager.activeItem !== lastActiveItem) {
      lastActiveItem?.blur();
    }

    if (this._keyManager.activeItem) {
      if (event.keyCode === ENTER || event.keyCode === SPACE) {
        this._keyManager.activeItem.click();
        this._keyManager.activeItem.markAsActive();
      }

      this.ariaActiveDescendant = this._keyManager.activeItem.getLabel();
    }
  }

  @HostListener('keyup', ['$event'])
  protected handleKeyup(event: KeyboardEvent): void {
    if (
      (event.keyCode === ENTER || event.keyCode === SPACE) &&
      this._keyManager.activeItem
    ) {
      this._keyManager.activeItem.markAsInactive();
    }
  }

  private _updateDirectDescendants(): void {
    this._allItems.changes
      .pipe(startWith(this._allItems), takeUntilDestroyed(this.destroyRef))
      .subscribe((items: QueryList<RdsListItemComponent>) => {
        this._directDescendantItems.reset(items.toArray());
        this._directDescendantItems.notifyOnChanges();
      });
  }

  private setRoleTagsIfParentNotSetAndChildrenHasSameValue(): void {
    if (!this.role) {
      const childRoles = this._directDescendantItems.reduce(
        (acc, curr) => {
          if (!acc.includes(curr.role)) {
            acc.push(curr.role);
          }

          return acc;
        },
        [] as ('menuitem' | 'option')[],
      );

      if (childRoles.length === 1) {
        this.attrRole = childRoles[0] === 'menuitem' ? 'menu' : 'listbox';
      }
    }
  }

  private checkRoleTags(): void {
    this.ariaMultiSelectable =
      this._directDescendantItems.filter(
        (i) => !(i instanceof RdsMultiSelectListItemComponent),
      ).length === 0;

    if (isDevMode()) {
      if (this.attrRole === 'menu') {
        const hasOptionsInsideMenu = this._directDescendantItems.some(
          (i) => i.role !== 'menuitem',
        );

        if (hasOptionsInsideMenu) {
          throw new Error(
            `Defining role other than 'menuitem' for 'menu' parent is forbidden.`,
          );
        }
      } else if (this.attrRole === 'listbox') {
        const hasMenuitemInsideListbox = this._directDescendantItems.some(
          (i) => i.role !== 'option',
        );

        if (hasMenuitemInsideListbox) {
          throw new Error(
            `Defining role other than 'option' for 'listbox' parent is forbidden.`,
          );
        }
      }
    }
  }
}
