import { FocusableOption, FocusMonitor, FocusOrigin } from '@angular/cdk/a11y';
import {
  AfterViewInit,
  Attribute,
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  EventEmitter,
  HostBinding,
  HostListener,
  inject,
  Input,
  numberAttribute,
  OnDestroy,
  Output,
} from '@angular/core';
import { TAB_INDEX_DEFAULT } from '../utils';

export interface RdsBreadcrumbItem {
  text: string;
  title?: string;
}

@Component({
  selector: 'rds-breadcrumb-item',
  template: `<span [title]="item.title ?? ''">{{ item.text }}</span>`,
  changeDetection: ChangeDetectionStrategy.OnPush,
  standalone: true,
})
export class RdsBreadcrumbItemComponent<T extends RdsBreadcrumbItem>
  implements AfterViewInit, OnDestroy, FocusableOption
{
  private readonly focusMonitor = inject(FocusMonitor);
  private readonly elementRef = inject(ElementRef);

  @HostBinding('class') class = 'rds-breadcrumb-item rds-focus-indicator';

  @HostBinding('tabIndex')
  @Input({ transform: (v: unknown) => numberAttribute(v, TAB_INDEX_DEFAULT) })
  tabIndex = TAB_INDEX_DEFAULT;

  @Input() item: T;

  @Output() itemClick = new EventEmitter<T>();

  constructor(@Attribute('tabindex') tabIndex?: string) {
    this.tabIndex = numberAttribute(tabIndex, TAB_INDEX_DEFAULT);
  }

  ngAfterViewInit(): void {
    this.focusMonitor.monitor(this.elementRef, false);
  }

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.elementRef);
  }

  @HostListener('click')
  @HostListener('keydown.enter')
  @HostListener('keydown.space')
  onClickItem(): void {
    this.itemClick.emit(this.item);
  }

  focus(origin: FocusOrigin = 'program', options?: FocusOptions): void {
    this.focusMonitor.focusVia(this.elementRef.nativeElement, origin, options);
  }
}
