import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  DestroyRef,
  ElementRef,
  EventEmitter,
  HostBinding,
  inject,
  Input,
  OnDestroy,
  OnInit,
  Output,
  TemplateRef,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { RdsTimelineEventIconMarkerDirective } from './timeline-event-icon-marker.directive';
import { RdsTimelineOppositeDirective } from './timeline-event-opposite.directive';
import { ContentObserver } from '@angular/cdk/observers';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { FocusMonitor } from '@angular/cdk/a11y';

export type RdsTimelineEventStatus =
  | 'default'
  | 'danger'
  | 'disabled'
  | 'success'
  | 'warning';

export type RdsTimelineEvent<T> = T & { status?: RdsTimelineEventStatus };

@Component({
  selector: 'rds-timeline-event',
  template: `
    <div class="rds-timeline-event-content">
      <ng-content />
    </div>
    <div class="rds-timeline-event-separator">
      <div
        #marker
        class="rds-timeline-event{{
          iconMarkerTemplate ? '-icon' : ''
        }}-marker rds-timeline-event{{
          iconMarkerTemplate ? '-icon' : ''
        }}-marker-{{ event.status ?? 'default' }}"
        [tabindex]="event.status === 'disabled' ? -1 : 0"
        [class.active]="isActive"
        (click)="selected.emit()"
        (keydown.enter)="selected.emit()"
        (keydown.space)="selected.emit()"
      >
        <ng-content
          select="rds-icon[rds-timeline-event-icon-marker], rds-icon[rdsTimelineEventIconMarker]"
        />
      </div>
      <div class="rds-timeline-event-connector"></div>
    </div>
    <div class="rds-timeline-event-opposite">
      @if (oppositeTemplate) {
        <ng-content
          select="rds-timeline-opposite, [rds-timeline-opposite], [rdsTimelineOpposite]"
        />
      }
    </div>
  `,
  styleUrls: ['./timeline-event.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class RdsTimelineEventComponent<T>
  implements OnInit, AfterViewInit, OnDestroy
{
  private readonly elementRef = inject(ElementRef);
  private readonly contentObserver = inject(ContentObserver);
  private readonly focusMonitor = inject(FocusMonitor);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly destroyRef = inject(DestroyRef);

  @HostBinding('class') private readonly class = 'rds-timeline-event';

  @Input({ required: true }) event: RdsTimelineEvent<T>;

  @Output() selected = new EventEmitter<void>();

  @ContentChild(RdsTimelineEventIconMarkerDirective)
  protected iconMarkerTemplate: TemplateRef<RdsTimelineEventIconMarkerDirective>;
  @ContentChild(RdsTimelineOppositeDirective)
  protected oppositeTemplate: TemplateRef<RdsTimelineOppositeDirective>;

  @ViewChild('marker', { static: true }) protected marker: HTMLElement;

  set isActive(value: boolean) {
    if (this._isActive !== value) {
      this._isActive = value;
      this.cdr.markForCheck();
    }
  }

  get isActive(): boolean {
    return this._isActive;
  }

  private _isActive = false;

  ngOnInit(): void {
    this.contentObserver
      .observe(this.elementRef)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this.cdr.markForCheck());
  }

  ngAfterViewInit(): void {
    this.focusMonitor.monitor(this.marker);
  }

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.marker);
  }
}
