import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { RdsFileItem } from './file-item.class';
import {
  FILTER_FILE_SIZE_DESCRIPTION_INTL_TOKEN,
  FILTER_FILE_TYPE_DESCRIPTION_INTL_TOKEN,
  FILTER_QUEUE_LIMIT_DESCRIPTION_INTL_TOKEN,
} from './upload.filters';

export type RdsUploadIntlMessageFactory = (fileItem: RdsFileItem) => string;
export type RdsUploadIntlToken = keyof typeof RdsUploadIntlService.translations;

@Injectable({ providedIn: 'root' })
export class RdsUploadIntlService {
  static readonly translations = {
    EMPTY: '',
    chooseFilesButtonLabel: 'Upload',
    dropZoneChooseFiles: 'Upload',
    dropZoneOrDropFiles: 'or drop files here',
    uploadListAriaLabel: 'File upload queue',
    startUploadButtonLabel: 'Upload',
    retryButtonAriaLabel: 'Retry',
    cancelButtonAriaLabel: 'Cancel',
    uploadFailed: 'Upload failed.',
    [FILTER_QUEUE_LIMIT_DESCRIPTION_INTL_TOKEN]: '',
    [FILTER_FILE_SIZE_DESCRIPTION_INTL_TOKEN]: 'File exceeds size limit.',
    [FILTER_FILE_TYPE_DESCRIPTION_INTL_TOKEN]: 'Invalid file type.',
  };

  private readonly _change$ = new BehaviorSubject(
    RdsUploadIntlService.translations,
  );
  change$ = this._change$.asObservable();

  setTranslation(key: RdsUploadIntlToken, value: string): void {
    RdsUploadIntlService.translations[key] = value;
    this._change$.next({ ...RdsUploadIntlService.translations });
  }

  setFileItemUploadSuccessMessageFactory(
    fn: RdsUploadIntlMessageFactory,
  ): void {
    this.fileItemUploadSuccessMessageFactory = fn;
  }

  setFileItemUploadErrorMessageFactory(fn: RdsUploadIntlMessageFactory): void {
    this.fileItemUploadErrorMessageFactory = fn;
  }

  setFileItemFilterFailedMessageFactory(fn: RdsUploadIntlMessageFactory): void {
    this.fileItemFilterFailedMessageFactory = fn;
  }

  setFileItemProgressMessageFactory(
    fn: (progress: number | null) => string,
  ): void {
    this.fileItemProgressMessageFactory = fn;
  }

  getFileItemUploadSuccessMessage(fileItem: RdsFileItem): string {
    return this.fileItemUploadSuccessMessageFactory(fileItem);
  }

  getFileItemUploadErrorMessage(fileItem: RdsFileItem): string {
    return this.fileItemUploadErrorMessageFactory(fileItem);
  }

  getFileItemFilterFailedMessage(fileItem: RdsFileItem): string {
    return this.fileItemFilterFailedMessageFactory(fileItem);
  }

  getFileItemProgressMessage(progress: number | null): string {
    return this.fileItemProgressMessageFactory(progress);
  }

  private fileItemUploadSuccessMessageFactory: RdsUploadIntlMessageFactory = (
    fileItem,
  ): string => {
    return fileItem.file.name + ' upload completed successfully';
  };

  private fileItemUploadErrorMessageFactory: RdsUploadIntlMessageFactory = (
    fileItem,
  ): string => {
    return (
      fileItem.file.name +
      ' error: ' +
      RdsUploadIntlService.translations[fileItem.errorIntlToken]
    );
  };

  private fileItemFilterFailedMessageFactory: RdsUploadIntlMessageFactory = (
    fileItem,
  ) => {
    return (
      fileItem.file.name +
      ' error: ' +
      RdsUploadIntlService.translations[fileItem.errorIntlToken]
    );
  };

  private fileItemProgressMessageFactory = (
    progress: number | null,
  ): string => {
    return progress == null ? '' : `${progress}% Uploaded`;
  };
}
