import {
  booleanAttribute,
  Directive,
  EventEmitter,
  HostBinding,
  inject,
  InjectionToken,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
} from '@angular/core';
import { Observable, ReplaySubject, Subject } from 'rxjs';

export type SortDirection = 'asc' | 'desc' | '';

export interface RdsSortable {
  id: string;
  start: SortDirection;
  disableClear: boolean;
}

export interface Sort {
  active: string;
  direction: SortDirection;
}

export interface RdsSortDefaultOptions {
  disableClear?: boolean;
}

export const RDS_SORT_DEFAULT_OPTIONS =
  new InjectionToken<RdsSortDefaultOptions>('RDS_SORT_DEFAULT_OPTIONS');

@Directive({
  selector: '[rdsSort], [rds-sort]',
  exportAs: 'rdsSort',
})
export class RdsSortDirective implements OnChanges, OnDestroy, OnInit {
  private readonly defaultOptions = inject(RDS_SORT_DEFAULT_OPTIONS, {
    optional: true,
  });

  readonly _stateChanges = new Subject<void>();
  sortables = new Map<string, RdsSortable>();

  @HostBinding('class') class = 'rds-sort';

  @Input('rdsSortActive') active: string | null = null;

  @Input({ alias: 'rdsSortDisabled', transform: booleanAttribute }) disabled =
    false;

  @Input('rdsSortStart') start: SortDirection = 'asc';

  @Input('rdsSortDirection')
  get direction(): SortDirection {
    return this._direction;
  }

  set direction(direction: SortDirection) {
    this._direction = direction;
  }

  private _direction: SortDirection = '';

  @Input({ alias: 'rdsSortDisableClear', transform: booleanAttribute })
  disableClear = false;

  get initialized(): Observable<void> {
    return this._initialized.asObservable();
  }

  private _initialized = new ReplaySubject<void>(1);

  @Output()
  readonly rdsSortChange: EventEmitter<Sort> = new EventEmitter<Sort>();

  register(sortable: RdsSortable): void {
    if (!sortable.id) {
      throw Error(`RdsSortHeader must be provided with a unique id.`);
    }

    if (this.sortables.has(sortable.id)) {
      throw Error(
        `Cannot have two RdsSortables with the same id (${sortable.id}).`,
      );
    }

    this.sortables.set(sortable.id, sortable);
  }

  deregister(sortable: RdsSortable): void {
    this.sortables.delete(sortable.id);
  }

  sort(sortable: RdsSortable): void {
    if (this.active != sortable.id) {
      this.active = sortable.id;
      this.direction = sortable.start ? sortable.start : this.start;
    } else {
      this.direction = this.getNextSortDirection(sortable);
    }

    this.rdsSortChange.emit({ active: this.active, direction: this.direction });
  }

  getNextSortDirection(sortable: RdsSortable): SortDirection {
    if (!sortable) {
      return '';
    }

    const disableClear =
      sortable?.disableClear ??
      this.disableClear ??
      !!this.defaultOptions?.disableClear;
    const sortDirectionCycle = this._getSortDirectionCycle(
      sortable.start,
      disableClear,
    );

    let nextDirectionIndex = sortDirectionCycle.indexOf(this.direction) + 1;

    if (nextDirectionIndex >= sortDirectionCycle.length) {
      nextDirectionIndex = 0;
    }

    return sortDirectionCycle[nextDirectionIndex];
  }

  ngOnInit(): void {
    this._initialized.next();
  }

  ngOnChanges(): void {
    this._stateChanges.next();
  }

  ngOnDestroy(): void {
    this._stateChanges.complete();
    this._initialized.complete();
  }

  private _getSortDirectionCycle(
    start: SortDirection = this.start,
    disableClear: boolean,
  ): SortDirection[] {
    const sortOrder: SortDirection[] = ['asc', 'desc'];

    if (start === 'desc') {
      sortOrder.reverse();
    }

    if (!disableClear) {
      sortOrder.push('');
    }

    return sortOrder;
  }
}
