import {
  AfterContentInit,
  AfterViewInit,
  booleanAttribute,
  Component,
  ContentChildren,
  DestroyRef,
  ElementRef,
  EventEmitter,
  HostBinding,
  HostListener,
  inject,
  Input,
  OnDestroy,
  Output,
  QueryList,
} from '@angular/core';
import { FocusableOption, FocusMonitor, FocusOrigin } from '@angular/cdk/a11y';
import { RdsIconComponent } from '../icons';
import { throwInvalidSizeError } from '../core/errors';
import { ContentObserver } from '@angular/cdk/observers';
import { BooleanInput } from '@angular/cdk/coercion';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

export type RdsButtonSize = 's' | 'm' | 'l';

@Component({
  template: '',
})
export class RdsButtonBaseComponent
  implements AfterContentInit, AfterViewInit, OnDestroy, FocusableOption
{
  protected readonly elementRef =
    inject<ElementRef<HTMLButtonElement>>(ElementRef);
  protected readonly focusMonitor = inject(FocusMonitor);
  protected readonly destroyRef = inject(DestroyRef);
  protected readonly contentObserver = inject(ContentObserver);

  protected buttonHostAttributesToClassMapper: { [key: string]: string } = {};
  protected _defaultSize: RdsButtonSize = 'm';

  protected _size = this._defaultSize;
  protected _hasIcon = false;
  protected _hasFirstIcon = false;
  protected _hasLastIcon = false;
  protected _hasOnlyIcon = false;
  protected _pressed = false;

  @Input()
  get size(): RdsButtonSize {
    return this._size;
  }

  set size(size: RdsButtonSize) {
    if (size !== 's' && size !== 'm' && size !== 'l') {
      throwInvalidSizeError(this, size);
    }

    this._size = size;
  }

  @HostBinding('class.rds-button-active')
  @Input()
  set pressed(isPressed: BooleanInput) {
    const value = this.pressed;
    this._pressed = booleanAttribute(isPressed);

    if (this.toggle && value !== this._pressed) {
      this.pressedChange.next(this._pressed);
    }
  }

  get pressed(): boolean {
    return this.toggle && this._pressed;
  }

  @HostBinding('attr.aria-pressed')
  get attrToggle(): boolean | null {
    return this.toggle ? this.pressed : null;
  }

  @Input({ transform: booleanAttribute })
  toggle = false;

  get disabled(): boolean {
    return !!this.elementRef.nativeElement.getAttribute('disabled');
  }

  @HostBinding('class') get buttonClasses(): string[] {
    const classesMappedFromHostAttributes = Object.entries(
      this.buttonHostAttributesToClassMapper,
    )
      .filter(([hostAttribute]) => this.hostElement.hasAttribute(hostAttribute))
      .map(([, buttonClass]) => buttonClass);

    const additionalClasses = [];

    if (this._hasFirstIcon) {
      additionalClasses.push('rds-button--with-left-icon');
    }

    if (this._hasLastIcon) {
      additionalClasses.push('rds-button--with-right-icon');
    }

    if (this._hasOnlyIcon) {
      additionalClasses.push('rds-button--with-only-icon');
    }

    return [
      'rds-button',
      `rds-button-${this.size}`,
      ...classesMappedFromHostAttributes,
      ...additionalClasses,
    ];
  }

  @HostBinding('class.rds-button--with-icon') get hasIcon(): boolean {
    return this._hasIcon;
  }

  @ContentChildren(RdsIconComponent) icons: QueryList<RdsIconComponent>;

  @Output()
  pressedChange = new EventEmitter<boolean>();

  ngAfterContentInit(): void {
    this.checkIcons();
    this.icons.changes.subscribe(() => this.checkIcons());
  }

  ngAfterViewInit(): void {
    this.focusMonitor.monitor(this.elementRef, true);
    this.contentObserver
      .observe(this.elementRef)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this.onContentChange());
  }

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.elementRef);
  }

  @HostListener('click')
  changeToggle(): void {
    if (this.toggle && !this.disabled) {
      this.pressed = !this.pressed;
    }
  }

  focus(origin: FocusOrigin = 'program', options?: FocusOptions): void {
    this.focusMonitor.focusVia(this.hostElement, origin, options);
  }

  get hostElement(): HTMLButtonElement {
    return this.elementRef.nativeElement as HTMLButtonElement;
  }

  protected checkIcons(): void {
    this._hasIcon = this.icons.length > 0;

    if (this.hasIcon) {
      const btnContent = this.elementRef.nativeElement;
      const firstChild = this._getChild('first');
      const lastChild = this._getChild('last');

      if (firstChild && firstChild.nodeName) {
        this._hasFirstIcon = firstChild.nodeName.toUpperCase() === 'RDS-ICON';
      } else {
        this._hasFirstIcon = false;
      }

      if (lastChild && lastChild.nodeName) {
        this._hasLastIcon =
          btnContent.lastChild?.nodeName.toUpperCase() === 'RDS-ICON';
      } else {
        this._hasLastIcon = false;
      }

      if (firstChild && firstChild === lastChild) {
        this._hasOnlyIcon = true;
        this._hasLastIcon = false;
        this._hasFirstIcon = false;
      } else {
        this._hasOnlyIcon = false;
      }
    } else {
      this._hasLastIcon = false;
      this._hasLastIcon = false;
      this._hasOnlyIcon = false;
    }
  }

  protected _getButtonClassName(): string {
    return this.constructor.name;
  }

  protected onContentChange(): void {
    this.checkIcons();
  }

  private _getChild(firstOrLast: 'first' | 'last'): ChildNode | null {
    let currentChild =
      firstOrLast === 'first'
        ? this.elementRef.nativeElement.firstChild
        : this.elementRef.nativeElement.lastChild;

    while (
      !currentChild ||
      currentChild.nodeName.toUpperCase() === '#COMMENT'
    ) {
      if (!currentChild) {
        return null;
      }

      currentChild =
        firstOrLast === 'first'
          ? currentChild.nextSibling
          : currentChild.previousSibling;
    }

    return currentChild;
  }
}
