import { BooleanInput, NumberInput } from '@angular/cdk/coercion';
import {
  AfterContentInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  EventEmitter,
  HostBinding,
  HostListener,
  inject,
  Input,
  numberAttribute,
  OnDestroy,
  Output,
  QueryList,
  ViewEncapsulation,
} from '@angular/core';
import { ControlValueAccessor, NgControl } from '@angular/forms';
import { merge, noop, Observable } from 'rxjs';
import { startWith, switchMap } from 'rxjs/operators';
import { RdsChipComponent } from '../chip/chip.component';
import { RdsAvatarComponent } from '../../avatar';
import {
  RdsChipCompareFunction,
  RdsChipEvent,
  RdsChipSelectEvent,
  RdsChipsSize,
} from '../chip.types';
import { RDS_CHIP_LIST } from '../chip.tokens';
import { RdsChipSetComponent } from '../chip-set';
import { TAB } from '@angular/cdk/keycodes';
import { RdsChipSelectableComponent } from '../chip-selectable/chip-selectable.component';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { TAB_INDEX_NOT_FOCUSABLE } from '../../utils';

@Component({
  selector: 'rds-chip-list',
  template: `
    <ng-content
      select="rds-chip-selectable, rds-chip, button[rds-chip], button[rdsChip]"
    />
  `,
  styleUrls: ['../chips.common.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [
    {
      provide: RDS_CHIP_LIST,
      useExisting: RdsChipListComponent,
    },
  ],
})
export class RdsChipListComponent<T>
  extends RdsChipSetComponent<T>
  implements AfterContentInit, OnDestroy, ControlValueAccessor
{
  private readonly ngControl = inject(NgControl, {
    self: true,
    optional: true,
  });
  private readonly cdr = inject(ChangeDetectorRef);

  private _value: T;
  private _multiple = false;
  private _selectable = true;
  private _pendingInitialValue: any;

  @ContentChildren(RdsChipSelectableComponent, { descendants: true })
  selectableChips: QueryList<RdsChipSelectableComponent<T>>;

  @Input()
  override set tabIndex(value: NumberInput) {
    super.tabIndex = numberAttribute(value);
  }

  override get tabIndex(): number {
    return super.tabIndex;
  }

  @HostBinding('tabIndex')
  get tabindex(): number {
    return this.empty ? TAB_INDEX_NOT_FOCUSABLE : this.tabIndex;
  }

  override get empty(): boolean {
    return this.selectableChips.length === 0;
  }

  @Output()
  readonly valueChange = new EventEmitter<any>();

  @ContentChildren(RdsAvatarComponent, { descendants: true })
  avatars: QueryList<RdsAvatarComponent>;

  private onChange: (_: any) => void = noop;
  private onTouched = noop;

  @HostBinding('attr.aria-orientation')
  orientation = 'horizontal';

  @HostBinding('class') get hostClasses(): string[] {
    const classes = [
      'rds-chips',
      `rds-chips--size-${this.size}`,
      `rds-chips--type-${this.type}`,
    ];

    if (this.disabled) {
      classes.push('rds-chips--disabled');
    }

    return classes;
  }

  @Input()
  @HostBinding('attr.aria-multiselectable')
  set multiple(value: BooleanInput) {
    const oldValue = this._multiple;
    this._multiple = booleanAttribute(value);
    this.syncListProperties(oldValue !== this._multiple);
  }

  get multiple(): boolean {
    return this._multiple;
  }

  @Input()
  set selectable(value: BooleanInput) {
    this._selectable = booleanAttribute(value);
    this.syncListProperties();
  }

  get selectable(): boolean {
    return this._selectable;
  }

  @Input()
  get size(): RdsChipsSize {
    return this._size;
  }

  set size(size: RdsChipsSize) {
    this._size = size;
  }

  protected override _size: RdsChipsSize = 'm';

  @Input()
  compareFn: RdsChipCompareFunction<T> = (valueOne: T, valueTwo: T): boolean =>
    valueOne === valueTwo;

  @Input()
  set value(value: T) {
    this.writeValue(value);
    this._value = value;
  }

  get value(): T {
    return this._value;
  }

  @Input()
  override set disabled(v: BooleanInput) {
    super.disabled = v;
  }

  override get disabled(): boolean {
    return this.ngControl ? !!this.ngControl.disabled : this._disabled;
  }

  get selected():
    | RdsChipSelectableComponent<T>
    | RdsChipSelectableComponent<T>[] {
    const selectedChips = this.selectableChips
      .toArray()
      .filter((chip) => chip.selected);

    return this.multiple ? selectedChips : selectedChips[0];
  }

  get chipSelectionChanges(): Observable<RdsChipSelectEvent<T>> {
    return this.getChipSelectableStream<
      RdsChipSelectEvent<T>,
      RdsChipSelectableComponent<T>
    >((chip) => chip.selectedChange);
  }

  get chipBlurChanges(): Observable<RdsChipEvent<T>> {
    return this.getChipSelectableStream((chip) => chip.blur$);
  }

  override get focused(): boolean {
    return this.hasFocusedChip();
  }

  constructor() {
    super();

    if (this.ngControl) {
      // Note: we provide the value accessor through here, instead of
      // the `providers` to avoid running into a circular import.
      this.ngControl.valueAccessor = this;
    }

    this.role = 'listbox';
  }

  override ngAfterContentInit(): void {
    super.ngAfterContentInit();

    if (this._pendingInitialValue !== undefined) {
      Promise.resolve().then(() => {
        this.setSelectionByValue(this._pendingInitialValue, false);
        this._pendingInitialValue = undefined;
      });
    }

    this.selectableChips.changes
      .pipe(
        startWith(this.selectableChips),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe(() => {
        const newRole =
          this.selectableChips.length > 0 ? 'listbox' : 'presentation';

        if (newRole !== this.role) {
          this.role = newRole;
        }

        // Update listbox selectable/multiple properties on chips
        this.syncListProperties();
      });

    this.chipBlurChanges
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this.blur());

    this.chipSelectionChanges
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((event) => {
        if (!this.multiple) {
          this.selectableChips.forEach((chip) => {
            if (chip !== event.source) {
              chip.setSelectedState(false, false, false);
            }
          });
        }

        if (event.isUserInput) {
          this.propagateChanges();
        }
      });

    this.checkAndSetAvatarSizes();
    this.avatars.changes
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this.checkAndSetAvatarSizes());
  }

  /**
   * Focuses the first selected chip in this chip listbox, or the first non-disabled chip when there
   * are no selected chips.
   */
  @HostListener('focus')
  override focus(): void {
    if (this.disabled) {
      return;
    }

    const firstSelectedChip = this.getFirstSelectedChip();

    if (firstSelectedChip && !firstSelectedChip.disabled) {
      firstSelectedChip.focus();
    } else if (this.selectableChips.length > 0) {
      this.keyManager.setFirstItemActive();
    } else {
      this.elementRef.nativeElement.focus();
    }
  }

  @HostListener('blur')
  blur(): void {
    if (!this.disabled) {
      // Wait to see if focus moves to an individual chip.
      setTimeout(() => {
        if (!this.focused) {
          this.markAsTouched();
        }
      });
    }
  }

  // ControlValueAccessor Start
  writeValue(value: any): void {
    if (this.selectableChips) {
      this.setSelectionByValue(value, false);
    } else if (value != null) {
      this._pendingInitialValue = value;
    }
  }

  registerOnChange(fn: (_: any) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  // ControlValueAccessor End

  protected getChipSelectableStream<
    E,
    C extends RdsChipSelectableComponent<T> = RdsChipSelectableComponent<T>,
  >(mappingFunction: (chip: C) => Observable<E>): Observable<E> {
    return this.selectableChips.changes.pipe(
      startWith(null),
      switchMap(() =>
        merge(...(this.selectableChips as QueryList<C>).map(mappingFunction)),
      ),
    );
  }

  protected override hasFocusedChip(): boolean {
    return (
      this.selectableChips &&
      this.selectableChips.some((chip) => chip.hasFocus())
    );
  }

  @HostListener('keydown', ['$event'])
  private onKeydown(e: KeyboardEvent): void {
    if (e.keyCode === TAB) {
      super.allowFocusEscape();
    }
  }

  private getFirstSelectedChip(): RdsChipComponent<T> | undefined {
    if (Array.isArray(this.selected)) {
      return this.selected.length ? this.selected[0] : undefined;
    } else {
      return this.selected;
    }
  }

  private markAsTouched(): void {
    this.onTouched();
    this.cdr.markForCheck();
  }

  private syncListProperties(clearSelection = false): void {
    if (this.selectableChips) {
      // Defer setting the value in order to avoid the "Expression
      // has changed after it was checked" errors from Angular.
      Promise.resolve().then(() => {
        this.selectableChips.forEach((chip) => {
          chip.chipListSelectable = this.selectable;
          chip.chipListMulti = this.multiple;

          if (clearSelection) {
            chip.deselect();
          }

          chip.cdr.markForCheck();
        });
      });
    }
  }

  private clearSelection(skip?: RdsChipComponent<T>): void {
    this.selectableChips.forEach((chip) => {
      if (chip !== skip) {
        chip.deselect();
      }
    });
  }

  private propagateChanges(fallbackValue?: any): void {
    let valueToEmit: any;

    if (this.multiple) {
      valueToEmit = (this.selected as RdsChipComponent<T>[]).map(
        (chip) => chip.value,
      );
    } else {
      valueToEmit = this.selected
        ? (this.selected as RdsChipComponent<T>).value
        : fallbackValue || null;
    }

    this.value = valueToEmit;
    this.onChange(valueToEmit);
    this.valueChange.emit(valueToEmit);
  }

  private setSelectionByValue(value: any, isUserInput = true): void {
    this.clearSelection();

    if (this.multiple && value) {
      if (!Array.isArray(value)) {
        throw new Error(
          'A multiselect chip-list requires the given value to be an array',
        );
      }

      value.forEach((currentValue) =>
        this.selectValue(currentValue, isUserInput),
      );
    } else {
      this.selectValue(value, isUserInput);
    }
  }

  private selectValue(
    value: any,
    isUserInput: boolean,
  ): RdsChipComponent<T> | undefined {
    const correspondingChip = this.selectableChips.find((chip) => {
      try {
        return chip.value != null && this.compareFn(chip.value as T, value);
      } catch (error) {
        return false;
      }
    });

    if (correspondingChip) {
      isUserInput
        ? correspondingChip.selectViaInteraction()
        : correspondingChip.select();
    }

    return correspondingChip;
  }

  private checkAndSetAvatarSizes(): void {
    this.avatars
      .filter((a) => a.size !== this.size)
      .forEach((a) => (a.size = this.size));
  }
}
