import { Observable, Subject } from 'rxjs';
import { RdsToastRef } from './rds-toast.ref';
import { RdsToastIndividualConfig } from './rds-toast.config';

export class RdsToastPackage<T, A> {
  private readonly _onTap = new Subject<void>();
  private readonly _onAction = new Subject<A>();

  constructor(
    public readonly toastId: number,
    public readonly config: RdsToastIndividualConfig,
    public readonly message: string | null | undefined,
    public readonly title: string | undefined,
    public readonly toastRef: RdsToastRef<T>,
  ) {
    this.toastRef.afterClosed().subscribe(() => {
      this._onAction.complete();
      this._onTap.complete();
    });
  }

  triggerTap(): void {
    this._onTap.next();

    if (this.config.tapToDismiss) {
      this._onTap.complete();
    }
  }

  onTap(): Observable<void> {
    return this._onTap.asObservable();
  }

  triggerAction(action: A): void {
    this._onAction.next(action);
  }

  onAction(): Observable<A> {
    return this._onAction.asObservable();
  }
}
