import { Injectable, Optional, Provider, SkipSelf } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable({ providedIn: 'root' })
export class RdsPaginatorIntl {
  readonly changes: Subject<void> = new Subject<void>();

  nextPageLabel = 'Next page';
  previousPageLabel = 'Previous page';
  firstPageLabel = 'First page';
  lastPageLabel = 'Last page';
  ofPagesLabel = 'of';

  getRangeLabel = (page: number, pageSize: number, length: number): string => {
    if (length == 0 || pageSize == 0) {
      return `0 of ${length}`;
    }

    length = Math.max(length, 0);

    const startIndex = page * pageSize;

    const endIndex =
      startIndex < length
        ? Math.min(startIndex + pageSize, length)
        : startIndex + pageSize;

    return `${startIndex + 1}  ${endIndex} of ${length}`;
  };
}

export function RDS_PAGINATOR_INTL_PROVIDER_FACTORY(
  parentIntl: RdsPaginatorIntl,
): RdsPaginatorIntl {
  return parentIntl || new RdsPaginatorIntl();
}

export const RDS_PAGINATOR_INTL_PROVIDER: Provider = {
  provide: RdsPaginatorIntl,
  deps: [[new Optional(), new SkipSelf(), RdsPaginatorIntl]],
  useFactory: RDS_PAGINATOR_INTL_PROVIDER_FACTORY,
};
