import {
  AfterContentChecked,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  DestroyRef,
  ElementRef,
  inject,
  Input,
  OnInit,
  TrackByFunction,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { AsyncPipe, NgTemplateOutlet } from '@angular/common';
import { RdsListItemModule, RdsListItemSize } from '../list-item';
import { RdsIconComponent } from '../icons';
import { RdsAutocompleteIntl } from './autocomplete-intl.service';
import { RdsAutocompleteHighlightDirective } from './autocomplete-highlight.directive';
import { ContentObserver } from '@angular/cdk/observers';
import { RdsAbstractAutocompleteComponent } from './abstract-autocomplete.component';
import {
  RDS_AUTOCOMPLETE_CONFIG,
  RDS_AUTOCOMPLETE_DEFAULT_CONFIG,
} from './autocomplete.config';
import { RdsAutocompleteOptionDirective } from './autocomplete-option.directive';

export type RdsAutocompleteType = 'single' | 'multi';

@Component({
  selector: 'rds-autocomplete-container',
  template: `
    <div class="rds-autocomplete-container" #autocompleteContainer>
      @if (autocomplete.loading | async) {
        @if (autocomplete.loadingTemplate) {
          <ng-container
            [ngTemplateOutlet]="autocomplete.loadingTemplate"
            [ngTemplateOutletContext]="{
              $implicit: autocomplete,
              autocomplete
            }"
          />
        } @else {
          <button
            class="rds-autocomplete-loading"
            rds-list-item
            [size]="size"
            disabled
          >
            {{ autocomplete.loadingText || loadingText }}
          </button>
        }
      } @else {
        @for (option of selectedOptions; track trackBy($index, option)) {
          <ng-container
            [ngTemplateOutlet]="
              autocomplete.optionTemplate || defaultOptionTemplate
            "
            [ngTemplateOutletContext]="{
              option,
              keyboardFocusedItem,
              $implicit: autocomplete,
              autocomplete
            }"
          />
        }
        @if (
          selectedOptions.length > 0 &&
          (availableOptions.length > 0 ||
            shouldShowCreateNewOption() ||
            (options.length === 0 && !shouldShowCreateNewOption()))
        ) {
          <div class="rds-autocomplete-separator"></div>
        }
        @if (shouldShowCreateNewOption()) {
          @if (autocomplete.newItemTemplate) {
            <ng-container
              [ngTemplateOutlet]="autocomplete.newItemTemplate"
              [ngTemplateOutletContext]="{
                $implicit: autocomplete,
                autocomplete,
                keyboardFocusedItem
              }"
            />
          } @else {
            <button
              [rdsAutocompleteOption]="autocomplete"
              [keyboardFocusedItem]="keyboardFocusedItem"
              #option="rdsAutocompleteOption"
              class="rds-autocomplete-option-new-item"
              rds-list-item
              [size]="size"
              (click)="autocomplete.selectNewItem()"
            >
              <span class="rds-autocomplete-text">{{ newItemText }}</span>
              {{ autocomplete.getSearchQuery() }}
            </button>
          }
        }
        @for (option of availableOptions; track trackBy($index, option)) {
          <ng-container
            [ngTemplateOutlet]="
              autocomplete.optionTemplate || defaultOptionTemplate
            "
            [ngTemplateOutletContext]="{
              option,
              keyboardFocusedItem,
              $implicit: autocomplete,
              autocomplete
            }"
          />
        }
        <ng-template #defaultOptionTemplate let-o="option">
          @if (type === 'single') {
            <button
              [rdsAutocompleteOption]="autocomplete"
              [keyboardFocusedItem]="keyboardFocusedItem"
              #option="rdsAutocompleteOption"
              rds-single-select-list-item
              [size]="size"
              [checked]="autocomplete.isSelected(o)"
              (click)="autocomplete.toggle(o)"
            >
              <span
                [innerHTML]="autocomplete.getDisplayProperty(o)"
                [rdsAutocomplateHighlight]="autocomplete.getSearchQuery()"
                [rdsAutocomplateHighlightDisabled]="
                  autocomplete.highlightQuery === false
                "
                [rdsAutocomplateHighlightInverse]="
                  autocomplete.highlightQuery === 'inverse'
                "
              ></span>
            </button>
          }
          @if (type === 'multi') {
            <button
              [rdsAutocompleteOption]="autocomplete"
              [keyboardFocusedItem]="keyboardFocusedItem"
              #option="rdsAutocompleteOption"
              rds-multi-select-list-item
              [size]="size"
              [checked]="autocomplete.isSelected(o)"
              (click)="autocomplete.toggle(o)"
            >
              <span
                [innerHTML]="autocomplete.getDisplayProperty(o)"
                [rdsAutocomplateHighlight]="autocomplete.getSearchQuery()"
                [rdsAutocomplateHighlightDisabled]="
                  autocomplete.highlightQuery === false
                "
                [rdsAutocomplateHighlightInverse]="
                  autocomplete.highlightQuery === 'inverse'
                "
              ></span>
            </button>
          }
        </ng-template>
      }
      @if (
        (autocomplete.loading | async) === false &&
        options.length === 0 &&
        !shouldShowCreateNewOption()
      ) {
        @if (autocomplete.noDataTemplate) {
          <ng-container
            [ngTemplateOutlet]="autocomplete.noDataTemplate"
            [ngTemplateOutletContext]="{
              $implicit: autocomplete,
              autocomplete
            }"
          />
        } @else {
          <button
            class="rds-autocomplete-option-no-data"
            rds-list-item
            [size]="size"
            disabled
          >
            <rds-icon namespace="outlined" icon="warning_circle" />
            {{ autocomplete.noDataText || noDataText }}
          </button>
        }
      }
    </div>
  `,
  styleUrls: ['./autocomplete-container.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  standalone: true,
  imports: [
    AsyncPipe,
    RdsListItemModule,
    NgTemplateOutlet,
    RdsIconComponent,
    RdsAutocompleteHighlightDirective,
    RdsAutocompleteOptionDirective,
  ],
})
export class RdsAutocompleteContainerComponent<
    T,
    ArrayOfFlatType extends T | T[],
  >
  implements AfterContentChecked, OnInit
{
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly intl = inject(RdsAutocompleteIntl);
  private readonly observer = inject(ContentObserver);
  private readonly destroyRef = inject(DestroyRef);
  private readonly elementRef = inject(ElementRef);
  protected readonly config = {
    ...RDS_AUTOCOMPLETE_DEFAULT_CONFIG,
    ...inject(RDS_AUTOCOMPLETE_CONFIG, { optional: true }),
  };

  autocomplete: RdsAbstractAutocompleteComponent<T, ArrayOfFlatType>;
  @Input() size: RdsListItemSize = 's';
  @Input({ required: true }) type: RdsAutocompleteType;

  @Input()
  set options(options: T[]) {
    this._options = options;
    this.selectedOptions = options.filter((o) =>
      this.autocomplete.isSelected(o),
    );
    this.availableOptions = options.filter(
      (o) => !this.autocomplete.isSelected(o),
    );
    this.lastSelectedValue =
      this.autocomplete.values.length > 0
        ? this.autocomplete.values[this.autocomplete.values.length - 1]
        : null;
  }

  get options(): T[] {
    return this._options;
  }

  protected _options: T[] = [];
  protected selectedOptions: T[] = [];
  protected availableOptions: T[] = [];
  protected lastSelectedValue: T | null = null;
  protected loadingText = RdsAutocompleteIntl.translations.loadingText;
  protected noDataText = RdsAutocompleteIntl.translations.noDataText;
  protected newItemText = RdsAutocompleteIntl.translations.newItemText;
  protected keyboardFocusedItem: RdsAutocompleteOptionDirective<
    T,
    ArrayOfFlatType
  > | null;
  private autocompleteOptions = new Map<
    HTMLElement,
    RdsAutocompleteOptionDirective<T, ArrayOfFlatType>
  >();
  private optionElements: RdsAutocompleteOptionDirective<T, ArrayOfFlatType>[] =
    [];

  @ViewChild('autocompleteContainer')
  protected autocompleteContainer: ElementRef<HTMLElement>;

  protected trackBy: TrackByFunction<T> = (_i: number, a: T) =>
    this.autocomplete.getUniqProperty(a);

  ngOnInit(): void {
    this.intl.change$
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((t) => {
        this.loadingText = this.autocomplete.loadingText || t.loadingText;
        this.noDataText = this.autocomplete.noDataText || t.noDataText;
        this.newItemText = this.autocomplete.newItemText || t.newItemText;
        this.cdr.markForCheck();
      });
    this.observer
      .observe(this.elementRef)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this.autocomplete.updatePosition());
  }

  _addOption(
    option: HTMLElement,
    component: RdsAutocompleteOptionDirective<T, ArrayOfFlatType>,
  ): void {
    this.autocompleteOptions.set(option, component);
  }

  _deleteOption(option: HTMLElement): void {
    this.autocompleteOptions.delete(option);
  }

  ngAfterContentChecked(): void {
    this.optionElements = [];

    for (
      let i = 0;
      i < this.autocompleteContainer?.nativeElement.children.length;
      i++
    ) {
      const record = this.autocompleteOptions.get(
        this.autocompleteContainer.nativeElement.children.item(
          i,
        ) as HTMLElement,
      );

      if (record) {
        this.optionElements.push(record);
      }
    }
  }

  onKeydown(event: KeyboardEvent): void {
    if (['Enter', 'ArrowUp', 'ArrowDown'].includes(event.key)) {
      event.preventDefault();
      event.stopPropagation();

      if (event.key === 'Enter') {
        this.onEnter();
      } else if (event.key === 'ArrowUp') {
        this.onUpArrow();
      } else if (event.key === 'ArrowDown') {
        this.onDownArrow();
      }
    }
  }

  protected shouldShowCreateNewOption(): boolean {
    if (
      this.autocomplete.isInputFilled() &&
      typeof this.autocomplete.newItem === 'function'
    ) {
      const newItem = this.autocomplete.getNewItem() as T;

      return !this.getSelectedFirstOptions().some((o) =>
        this.autocomplete.compareItem(o, newItem),
      );
    } else {
      return false;
    }
  }

  protected getSelectedFirstOptions(): T[] {
    return [...this.selectedOptions, ...this.availableOptions];
  }

  private onDownArrow(): void {
    if (!this.keyboardFocusedItem) {
      this.selectFirstItem();
    } else {
      const index = this.optionElements.findIndex(
        (e) => e === this.keyboardFocusedItem,
      );
      const nextIndex = (index + 1) % this.optionElements.length;

      this.keyboardFocusedItem = this.optionElements[nextIndex];
    }

    this.scrollToSelectedItem();
    this.cdr.markForCheck();
  }

  private onUpArrow(): void {
    if (!this.keyboardFocusedItem) {
      this.selectFirstItem();
    } else {
      const index = this.optionElements.findIndex(
        (e) => e === this.keyboardFocusedItem,
      );
      const nextIndex =
        index - 1 < 0 ? this.optionElements.length - 1 : index - 1;

      this.keyboardFocusedItem = this.optionElements[nextIndex];
    }

    this.scrollToSelectedItem();
    this.cdr.markForCheck();
  }

  private onEnter(): void {
    if (this.optionElements) {
      const index = this.optionElements.findIndex(
        (v) => v === this.keyboardFocusedItem,
      );

      if (index >= 0) {
        for (const [element, option] of this.autocompleteOptions) {
          if (this.optionElements[index] === option) {
            element.dispatchEvent(new MouseEvent('click'));
            break;
          }
        }
      }
    }
  }

  private scrollToSelectedItem(): void {
    let optionElement: HTMLElement | undefined;

    for (const [element, option] of this.autocompleteOptions) {
      if (this.keyboardFocusedItem === option) {
        optionElement = element;
        break;
      }
    }

    if (this.keyboardFocusedItem) {
      const itemHeight = optionElement?.offsetHeight || 0;
      const scrollContainerHeight =
        this.autocompleteContainer.nativeElement.scrollHeight;
      const panelHeight = Math.min(
        scrollContainerHeight,
        this.config.optionsContainerMaxHeight,
      );
      const divider = 2;
      const maxScroll = scrollContainerHeight - panelHeight;
      const scrollBuffer = panelHeight / divider;
      const optionOffsetFormScrollTop =
        this.optionElements.findIndex((el) => el === this.keyboardFocusedItem) *
        itemHeight;
      const halfOptionHeight = itemHeight / divider;

      const optimalScrollPosition =
        optionOffsetFormScrollTop - scrollBuffer + halfOptionHeight;
      this.autocompleteContainer.nativeElement.scrollTop = Math.min(
        Math.max(0, optimalScrollPosition),
        maxScroll,
      );
    }
  }

  private selectFirstItem(): void {
    if (this.optionElements[0]) {
      this.keyboardFocusedItem = this.optionElements[0];
    }
  }
}
