import {
  animate,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import {
  Component,
  DestroyRef,
  HostBinding,
  HostListener,
  inject,
  NgZone,
  OnDestroy,
} from '@angular/core';
import { RdsToastPackage } from './rds-toast.package';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

@Component({
  selector: '[rds-toast-component]',
  templateUrl: './rds-toast.component.html',
  animations: [
    trigger('flyInOut', [
      state('inactive', style({ opacity: 0 })),
      state('active', style({ opacity: 1 })),
      state('removed', style({ opacity: 0 })),
      transition(
        'inactive => active',
        animate('{{ easeTime }}ms {{ easing }}'),
      ),
      transition('active => removed', animate('{{ easeTime }}ms {{ easing }}')),
    ]),
  ],
  preserveWhitespaces: false,
})
export class RdsToastComponent implements OnDestroy {
  public readonly rdsToastPackage =
    inject<RdsToastPackage<RdsToastComponent, void>>(RdsToastPackage);
  protected readonly destroyRef = inject(DestroyRef);
  protected readonly ngZone = inject(NgZone, { optional: true });

  message = this.rdsToastPackage.message || null;
  title = this.rdsToastPackage.title || null;
  options = this.rdsToastPackage.config;
  originalTimeout = this.rdsToastPackage.config.timeOut;

  private timeout: number;
  private hideTime: number;

  @HostBinding('class') toastClasses = '';
  @HostBinding('@flyInOut')
  state = {
    value: 'inactive',
    params: {
      easeTime: this.rdsToastPackage.config.easeTime,
      easing: 'ease-in',
    },
  };

  @HostBinding('style.display')
  get displayStyle(): string | undefined {
    if (this.state.value === 'inactive') {
      return 'none';
    }

    return undefined;
  }

  @HostListener('click')
  tapToast(): void {
    if (this.state.value === 'removed') {
      return;
    }

    this.rdsToastPackage.triggerTap();

    if (this.options.tapToDismiss) {
      this.remove();
    }
  }

  @HostListener('mouseenter')
  stickAround(): void {
    if (this.state.value === 'removed') {
      return;
    }

    clearTimeout(this.timeout);
    this.options.timeOut = 0;
    this.hideTime = 0;
  }

  @HostListener('mouseleave')
  delayedHideToast(): void {
    if (
      this.options.disableTimeOut === true ||
      this.options.disableTimeOut === 'extendedTimeOut' ||
      this.options.extendedTimeOut === 0 ||
      this.state.value === 'removed'
    ) {
      return;
    }

    this.outsideTimeout(() => this.remove(), this.options.extendedTimeOut);
    this.options.timeOut = this.options.extendedTimeOut;
    this.hideTime = new Date().getTime() + (this.options.timeOut || 0);
  }

  constructor() {
    this.toastClasses = `rds-toast-${this.rdsToastPackage.config.type} ${this.rdsToastPackage.config.toastClass}`;

    if (this.rdsToastPackage.config.tapToDismiss) {
      this.toastClasses += ' rds-toast-dismiss-click';
    }

    if (this.rdsToastPackage.config.closeButton) {
      this.toastClasses += ' rds-toast-with-close-icon';
    }

    if (this.title) {
      this.toastClasses += ' rds-toast-with-title';
    }

    this.rdsToastPackage.toastRef
      .afterActivate()
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this.activateToast());
    this.rdsToastPackage.toastRef
      .manualClosed()
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this.remove());
  }

  ngOnDestroy(): void {
    clearTimeout(this.timeout);
  }

  activateToast(): void {
    this.state = { ...this.state, value: 'active' };

    if (
      !(
        this.options.disableTimeOut === true ||
        this.options.disableTimeOut === 'timeOut'
      ) &&
      this.options.timeOut
    ) {
      this.outsideTimeout(() => this.remove(), this.options.timeOut);
      this.hideTime = new Date().getTime() + this.options.timeOut;
    }
  }

  remove(): void {
    if (this.state.value === 'removed') {
      return;
    }

    clearTimeout(this.timeout);
    this.state = { ...this.state, value: 'removed' };
    this.outsideTimeout(
      () => this.rdsToastPackage.toastRef.close(),
      +this.rdsToastPackage.config.easeTime,
    );
  }

  outsideTimeout<T>(func: () => T, timeout: number): void {
    if (this.ngZone) {
      this.ngZone.runOutsideAngular(() => {
        this.timeout = window.setTimeout(
          () => this.runInsideAngular(func),
          timeout,
        );
      });
    } else {
      this.timeout = window.setTimeout(() => func(), timeout);
    }
  }

  private runInsideAngular<T>(func: () => T): void {
    if (this.ngZone) {
      this.ngZone.run(() => func());
    } else {
      func();
    }
  }
}
