import { RdsTreeControl } from './data-source/tree-control';
import { SelectionModel } from '@angular/cdk/collections';
import { RdsFlatTreeNode } from './tree.types';

interface SelectionModelOptions<T> {
  // _multiple?: boolean is omitted on purpose as it has to be set to true anyway
  initiallySelectedValues?: T[];
  _emitChanges?: boolean;
  compareWith?: (o1: T, o2: T) => boolean;
}

export class RdsTreeSelectionModel<
  T extends { level: number } = RdsFlatTreeNode,
  K = T,
> extends SelectionModel<T> {
  constructor(
    private treeControl: RdsTreeControl<T, K>,
    getLevel?: ((node: T) => number) | null,
    selectionModelOptions?: SelectionModelOptions<T>,
  ) {
    super(
      true,
      selectionModelOptions?.initiallySelectedValues,
      selectionModelOptions?._emitChanges,
      selectionModelOptions?.compareWith,
    );

    if (getLevel) {
      this.getLevel = getLevel;
    }
  }

  descendantsAllSelected(node: T): boolean {
    const descendants = this.treeControl.getDescendants(node);

    return (
      descendants.length > 0 &&
      descendants.every((child) => {
        return this.isSelected(child);
      })
    );
  }

  /**
   * @returns false when all or none selected, otherwise true
   */
  descendantsPartiallySelected(node: T): boolean {
    return (
      this.descendantsAnySelected(node) && !this.descendantsAllSelected(node)
    );
  }

  /**
   * @returns true when at least one descendant is selected, otherwise false
   */
  descendantsAnySelected(node: T): boolean {
    const descendants = this.treeControl.getDescendants(node);

    return descendants.some((child) => this.isSelected(child));
  }

  itemSelectionToggle(node: T): void {
    this.toggle(node);
    const descendants = this.treeControl.getDescendants(node);
    this.isSelected(node)
      ? this.select(...descendants)
      : this.deselect(...descendants);

    // Force update for the parent
    descendants.forEach((child) => this.isSelected(child));
    this.checkAllParentsSelection(node);
  }

  leafItemSelectionToggle(node: T): void {
    this.toggle(node);
    this.checkAllParentsSelection(node);
  }

  checkAllParentsSelection(node: T): void {
    let parent = this.getParentNode(node);

    while (parent) {
      this.checkRootNodeSelection(parent);
      parent = this.getParentNode(parent);
    }
  }

  checkRootNodeSelection(node: T): void {
    const nodeSelected = this.isSelected(node);
    const descendants = this.treeControl.getDescendants(node);
    const descAllSelected =
      descendants.length > 0 &&
      descendants.every((child) => {
        return this.isSelected(child);
      });

    if (nodeSelected && !descAllSelected) {
      this.deselect(node);
    } else if (!nodeSelected && descAllSelected) {
      this.select(node);
    }
  }

  getLevel = (node: T): number => node.level;

  getParentNode(node: T): T | null {
    const currentLevel = this.getLevel(node);

    if (currentLevel < 1) {
      return null;
    }

    const startIndex = this.treeControl.dataNodes.indexOf(node) - 1;

    for (let i = startIndex; i >= 0; i--) {
      const currentNode = this.treeControl.dataNodes[i];

      if (this.getLevel(currentNode) < currentLevel) {
        return currentNode;
      }
    }

    return null;
  }
}
