import {
  AfterViewInit,
  booleanAttribute,
  ChangeDetectorRef,
  DestroyRef,
  Directive,
  ElementRef,
  EventEmitter,
  HostBinding,
  inject,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild,
} from '@angular/core';
import { BehaviorSubject, EMPTY, noop, Observable, of, skip } from 'rxjs';
import { RdsSearchIntl } from './search.intl';
import { ControlValueAccessor } from '@angular/forms';
import {
  FocusableOption,
  FocusMonitor,
  FocusOptions,
  FocusOrigin,
} from '@angular/cdk/a11y';
import {
  RdsSearchEvent,
  RdsSearchEventTrigger,
  RdsSearchSize,
} from './search.types';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

@Directive()
export abstract class RdsAbstractSearchComponent
  implements
    OnInit,
    OnChanges,
    OnDestroy,
    AfterViewInit,
    ControlValueAccessor,
    FocusableOption
{
  protected readonly destroyRef = inject(DestroyRef);
  protected readonly intl = inject(RdsSearchIntl);
  protected readonly cdr = inject(ChangeDetectorRef);
  protected readonly elementRef = inject(ElementRef);
  protected readonly focusMonitor = inject(FocusMonitor);

  @Input() placeholder: string;
  @Input() buttonText: string;
  @Input() emptyValue: string;
  @Input() ariaClearLabel: string;
  @Input({ transform: booleanAttribute }) disabled = false;
  @Input({ transform: booleanAttribute }) includeButton = false;
  @Input({ transform: booleanAttribute }) autoSearch = false;

  @Input()
  size: RdsSearchSize = 'l';

  get value(): string {
    return this._value;
  }

  protected _value: string;
  protected readonly _lastEmitted = new BehaviorSubject<RdsSearchEvent>({
    query: '',
    trigger: 'input',
  });

  @Output()
  search = this._lastEmitted.pipe(skip(1));

  @Output()
  clear = new EventEmitter<void>();

  @ViewChild('searchInput') searchInput: ElementRef<HTMLInputElement>;

  @HostBinding('attr.role') role = 'search';
  @Input()
  @HostBinding('attr.aria-label')
  ariaInputLabel: string;

  _onChange: (value: string) => void = noop;
  _onTouched: () => void = noop;

  ngOnInit(): void {
    this.setIntlValues();
    this.setNewValue(this.intl.emptyValue).subscribe();
    this.intl.changes
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => {
        this.setIntlValues();
        this.cdr.markForCheck();
      });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (
      changes['placeholder'] ||
      changes['buttonText'] ||
      changes['emptyValue'] ||
      changes['ariaClearLabel'] ||
      changes['ariaInputLabel']
    ) {
      this.setIntlValues();
    }
  }

  ngAfterViewInit(): void {
    this.focusMonitor
      .monitor(this.elementRef.nativeElement)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this._onTouched());
  }

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.elementRef.nativeElement);
  }

  registerOnChange(fn: (value: string) => void): void {
    this._onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this._onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    if (this.disabled !== isDisabled) {
      this.disabled = isDisabled;
      this.cdr.markForCheck();
    }
  }

  writeValue(value: string): void {
    this.setNewValue(value).subscribe(() => this.emitValue('form'));
  }

  onInput(event: Event): void {
    this.setNewValue((event.target as HTMLInputElement).value).subscribe(() =>
      this.emitValue('input', event),
    );
  }

  focus(origin: FocusOrigin = 'program', options?: FocusOptions): void {
    this.focusMonitor.focusVia(this.elementRef.nativeElement, origin, options);
  }

  isDirtyValue(): boolean {
    return this._value !== this.intl.emptyValue;
  }

  resetInput(event: Event): void {
    if (!this.disabled && this.isDirtyValue()) {
      this.setNewValue(this.intl.emptyValue).subscribe();
      this.emitValue('reset', event);
      this.clear.emit();
      this.searchInput.nativeElement.focus();
    }
  }

  emitValue(trigger: RdsSearchEventTrigger, event?: Event): void {
    if (this._lastEmitted.getValue().query !== this._value) {
      this._lastEmitted.next({ query: this._value, trigger, event });
      this._onChange(this._value);
    }
  }

  protected setIntlValues(): void {
    this.placeholder =
      this.placeholder && this.placeholder !== ''
        ? this.placeholder
        : this.intl.searchInputPlaceholder;
    this.buttonText =
      this.buttonText && this.buttonText !== ''
        ? this.buttonText
        : this.intl.searchButtonText;
    this.emptyValue = this.emptyValue || this.intl.emptyValue;
    this.ariaInputLabel =
      this.ariaInputLabel && this.ariaInputLabel !== ''
        ? this.ariaInputLabel
        : this.intl.ariaInputLabel;
    this.ariaClearLabel =
      this.ariaClearLabel && this.ariaClearLabel !== ''
        ? this.ariaClearLabel
        : this.intl.ariaClearLabel;
  }

  private setNewValue(value: string): Observable<void> {
    if (
      value !== null &&
      value !== undefined &&
      this._value !== String(value)
    ) {
      this._value = String(value);
      this.cdr.markForCheck();

      if (this.autoSearch) {
        return of(void 0);
      }
    }

    return EMPTY;
  }
}
