import {
  booleanAttribute,
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  inject,
  Input,
  numberAttribute,
  OnChanges,
  Output,
} from '@angular/core';
import { RdsPaginatorIntl } from '../paginator-intl';

export interface RdsPageChangeEvent {
  previousPageIndex: number;
  pageIndex: number;
}

@Component({
  selector: 'rds-paginator-basic',
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: './paginator-basic.component.html',
  styleUrls: ['./paginator-basic.component.scss'],
})
export class RdsPaginatorBasicComponent implements OnChanges {
  protected readonly intl = inject(RdsPaginatorIntl);

  @Input({ transform: booleanAttribute }) disabled = false;
  @Input({ transform: booleanAttribute }) showButtons = true;
  @Input({ transform: numberAttribute }) collectionSize: number;
  @Input({ transform: numberAttribute }) maxSize: number;
  @Input({ transform: numberAttribute }) page = 1;
  @Input({ transform: numberAttribute }) pageSize: number;

  @Output() pageChange = new EventEmitter<RdsPageChangeEvent>();

  protected pageCount = 0;
  protected pages: number[] = [];

  hasPrevious(): boolean {
    return this.page > 1;
  }

  hasNext(): boolean {
    return this.page < this.pageCount;
  }

  selectFirstPage(): void {
    this._updatePages(1);
  }

  selectPreviousPage(): void {
    if (!this.isPreviousDisabled()) {
      this._updatePages(this.page - 1);
    }
  }

  selectPage(pageNumber: number): void {
    this._updatePages(pageNumber);
  }

  selectNextPage(): void {
    if (!this.isNextDisabled()) {
      this._updatePages(this.page + 1);
    }
  }

  selectLastPage(): void {
    this._updatePages(this.pageCount);
  }

  ngOnChanges(): void {
    this._updatePages(this.page);
  }

  protected isNextDisabled(): boolean {
    return !this.hasNext() || this.disabled;
  }

  protected isPreviousDisabled(): boolean {
    return !this.hasPrevious() || this.disabled;
  }

  protected isEllipsis(pageNumber: number): boolean {
    return pageNumber === -1;
  }

  private _applyEllipses(start: number, end: number): void {
    if (start > 0) {
      if (start > 2) {
        this.pages.unshift(-1);
      } else if (start === 2) {
        this.pages.unshift(2);
      }

      this.pages.unshift(1);
    }

    if (end < this.pageCount) {
      if (end < this.pageCount - 2) {
        this.pages.push(-1);
      } else if (end === this.pageCount - 2) {
        this.pages.push(this.pageCount - 1);
      }

      this.pages.push(this.pageCount);
    }
  }

  private _setPageInRange(newPageNo: number): void {
    const prevPageNo = this.page;
    this.page = this._getValueInRange(newPageNo, this.pageCount, 1);

    if (this.page !== prevPageNo && this._isNumber(this.collectionSize)) {
      this.pageChange.emit({
        previousPageIndex: prevPageNo - 1,
        pageIndex: this.page - 1,
      });
    }
  }

  private _updatePages(newPage: number): void {
    this.pageCount = Math.ceil(this.collectionSize / this.pageSize);

    if (!this._isNumber(this.pageCount)) {
      this.pageCount = 0;
    }

    this.pages = [];

    for (let i = 1; i <= this.pageCount; i++) {
      this.pages.push(i);
    }

    this._setPageInRange(newPage);

    if (this.maxSize > 0 && this.pageCount > this.maxSize) {
      const [start, end] = this._applyRotation();
      this.pages = this.pages.slice(start, end);
      this._applyEllipses(start, end);
    }
  }

  private _getValueInRange(value: number, max: number, min = 0): number {
    return Math.max(Math.min(value, max), min);
  }

  private _applyRotation(): [number, number] {
    let start = 0;
    let end = this.pageCount;
    const leftOffset = Math.floor(this.maxSize / 2);
    const rightOffset = this.maxSize % 2 === 0 ? leftOffset - 1 : leftOffset;

    if (this.page <= leftOffset) {
      end = this.maxSize;
    } else if (this.pageCount - this.page < leftOffset) {
      start = this.pageCount - this.maxSize;
    } else {
      start = this.page - leftOffset - 1;
      end = this.page + rightOffset;
    }

    return [start, end];
  }

  private _isNumber<T>(value: T): boolean {
    return !isNaN(this._toInteger(value));
  }

  private _toInteger<T>(value: T): number {
    return parseInt(`${value}`, 10);
  }
}
