import {
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  HostBinding,
  inject,
  OnDestroy,
} from '@angular/core';
import { FocusableOption, FocusMonitor, FocusOrigin } from '@angular/cdk/a11y';
import { NativeElement } from '../sidenav.types';
import { filter } from 'rxjs/operators';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

@Component({
  selector: 'a[rds-sidenav-menu-header], a[rdsSidenavMenuHeader]',
  templateUrl: './sidenav-menu-header.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsSidenavMenuHeaderComponent
  implements OnDestroy, FocusableOption, NativeElement
{
  private readonly focusMonitor = inject(FocusMonitor);

  readonly nativeElement =
    inject<ElementRef<HTMLElement>>(ElementRef).nativeElement;
  readonly disabled = false;
  readonly _focused = this.focusMonitor.monitor(this.nativeElement, false).pipe(
    filter((v) => !!v),
    takeUntilDestroyed(),
  );

  @HostBinding('class')
  readonly classes = 'rds-sidenav-menu-header';

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.nativeElement);
  }

  focus(origin?: FocusOrigin, options?: FocusOptions): void {
    if (origin) {
      this.focusMonitor.focusVia(this.nativeElement, origin, options);
    } else {
      this.nativeElement.focus(options);
    }
  }

  getLabel(): string {
    const clone = this.nativeElement.cloneNode(true) as HTMLElement;
    const icons = clone.querySelectorAll('rds-icon');

    for (let i = 0; i < icons.length; i++) {
      icons[i].remove();
    }

    return clone.textContent?.trim() || '';
  }
}
