import {
  ChangeDetectionStrategy,
  Component,
  ViewEncapsulation,
} from '@angular/core';
import {
  RdsDatepickerBaseDirective,
  RdsDatepickerControl,
} from './datepicker-base';
import { RDS_SINGLE_DATE_SELECTION_MODEL_PROVIDER } from './date-selection.model';

@Component({
  selector: 'rds-datepicker',
  template: '',
  exportAs: 'rdsDatepicker',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  providers: [
    RDS_SINGLE_DATE_SELECTION_MODEL_PROVIDER,
    {
      provide: RdsDatepickerBaseDirective,
      useExisting: RdsDatepickerComponent,
    },
  ],
})
export class RdsDatepickerComponent<D> extends RdsDatepickerBaseDirective<
  RdsDatepickerControl<D>,
  D | null,
  D
> {}
