import {
  booleanAttribute,
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  EventEmitter,
  HostBinding,
  inject,
  Input,
  NgZone,
  OnChanges,
  OnDestroy,
  Output,
  SimpleChanges,
  ViewEncapsulation,
} from '@angular/core';
import { take } from 'rxjs/operators';
import { RdsCalendarView } from './calendar.types';

export type RdsCalendarCellCssClasses =
  | string
  | string[]
  | Set<string>
  | { [key: string]: unknown };

export type RdsCalendarCellClassFunction<D> = (
  date: D,
  view: RdsCalendarView,
) => RdsCalendarCellCssClasses;

/* eslint-disable-next-line @typescript-eslint/no-explicit-any */
export class RdsCalendarCell<D = any> {
  constructor(
    public value: number,
    public displayValue: string,
    public ariaLabel: string,
    public enabled: boolean,
    public cssClasses: RdsCalendarCellCssClasses = {},
    public rawValue: D,
    public compareValue = value,
  ) {}
}

export interface RdsCalendarUserEvent<D> {
  value: D;
  event: Event;
}

@Component({
  selector: '[rdsCalendarBody], [rds-calendar-body]',
  templateUrl: './calendar-body.component.html',
  styleUrls: ['./calendar-body.component.scss'],
  exportAs: 'rdsCalendarBody',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsCalendarBodyComponent<D> implements OnChanges, OnDestroy {
  private readonly elementRef = inject(ElementRef);
  private readonly ngZone = inject(NgZone);

  private _skipNextFocus: boolean | null = null;

  @HostBinding('class') class = 'rds-calendar-body';
  @HostBinding('attr.role') role = 'grid';
  @HostBinding('attr.aria-readonly') ariaReadonly = 'true';

  @Input() label: string | null = null;
  @Input() rows: RdsCalendarCell<D>[][] | null = null;
  @Input() todayValue: number | null = null;
  @Input() startValue: number | null = null;
  @Input() endValue: number | null = null;
  @Input() labelMinRequiredCells: number | null = null;
  @Input() numCols = 7;
  @Input() activeCell: RdsCalendarCell<D> | null = null;
  @Input({ transform: booleanAttribute }) isRange = false;
  @Input() cellAspectRatio = 1;
  @Input() comparisonStart: number | null = null;
  @Input() comparisonEnd: number | null = null;
  @Input() previewStart: number | null = null;
  @Input() previewEnd: number | null = null;

  @Output() readonly selectedValueChange = new EventEmitter<
    RdsCalendarUserEvent<RdsCalendarCell<D>>
  >();

  @Output() previewChange = new EventEmitter<
    RdsCalendarUserEvent<RdsCalendarCell<D> | null>
  >();

  _firstRowOffset: number | null = null;
  _cellPadding: string | null = null;
  _cellWidth: string | null = null;

  constructor() {
    this.ngZone.runOutsideAngular(() => {
      const element = this.elementRef.nativeElement;
      element.addEventListener('mouseenter', this._enterHandler, true);
      element.addEventListener('focus', this._enterHandler, true);
      element.addEventListener('mouseleave', this._leaveHandler, true);
      element.addEventListener('blur', this._leaveHandler, true);
    });
  }

  _cellClicked(cell: RdsCalendarCell<D>, event: MouseEvent): void {
    if (cell.enabled) {
      this.selectedValueChange.emit({ value: cell, event });
    }
  }

  _isSelected(value: number): boolean {
    return this.startValue === value || this.endValue === value;
  }

  ngOnChanges(changes: SimpleChanges): void {
    const columnChanges = changes['numCols'];
    const { rows, numCols } = this;

    if (changes['rows'] || columnChanges) {
      this._firstRowOffset =
        rows && rows.length && rows[0].length ? numCols - rows[0].length : 0;
    }

    if (changes['cellAspectRatio'] || columnChanges || !this._cellPadding) {
      this._cellPadding = `${(50 * this.cellAspectRatio) / numCols}%`;
    }

    if (columnChanges || !this._cellWidth) {
      this._cellWidth = `${100 / numCols}%`;
    }
  }

  ngOnDestroy(): void {
    const element = this.elementRef.nativeElement;
    element.removeEventListener('mouseenter', this._enterHandler, true);
    element.removeEventListener('focus', this._enterHandler, true);
    element.removeEventListener('mouseleave', this._leaveHandler, true);
    element.removeEventListener('blur', this._leaveHandler, true);
  }

  _isActiveCell(item: RdsCalendarCell<D>): boolean {
    return this.activeCell === item;
  }

  _focusActiveCell(movePreview = true): void {
    this.ngZone.runOutsideAngular(() => {
      this.ngZone.onStable.pipe(take(1)).subscribe(() => {
        const activeCell: HTMLElement | null =
          this.elementRef.nativeElement.querySelector(
            '.rds-calendar-body-active',
          );

        if (activeCell) {
          if (!movePreview) {
            this._skipNextFocus = true;
          }

          activeCell.focus();
        }
      });
    });
  }

  _isRangeStart(value: number): boolean {
    return isStart(value, this.startValue, this.endValue);
  }

  _isRangeEnd(value: number): boolean {
    return isEnd(value, this.startValue, this.endValue);
  }

  _isInRange(value: number): boolean {
    return isInRange(value, this.startValue, this.endValue, this.isRange);
  }

  _isComparisonStart(value: number): boolean {
    return isStart(value, this.comparisonStart, this.comparisonEnd);
  }

  _isComparisonBridgeStart(
    value: number,
    rowIndex: number,
    colIndex: number,
  ): boolean {
    if (
      !this._isComparisonStart(value) ||
      this._isRangeStart(value) ||
      !this._isInRange(value)
    ) {
      return false;
    }

    let previousCell: RdsCalendarCell<D> | null = this.rows
      ? this.rows[rowIndex][colIndex - 1]
      : null;

    if (!previousCell && this.rows) {
      const previousRow = this.rows[rowIndex - 1];
      previousCell = previousRow && previousRow[previousRow.length - 1];
    }

    return previousCell ? !this._isRangeEnd(previousCell.compareValue) : false;
  }

  _isComparisonBridgeEnd(
    value: number,
    rowIndex: number,
    colIndex: number,
  ): boolean {
    if (
      !this._isComparisonEnd(value) ||
      this._isRangeEnd(value) ||
      !this._isInRange(value)
    ) {
      return false;
    }

    let nextCell: RdsCalendarCell<D> | null = this.rows
      ? this.rows[rowIndex][colIndex + 1]
      : null;

    if (!nextCell && this.rows) {
      const nextRow = this.rows[rowIndex + 1];
      nextCell = nextRow && nextRow[0];
    }

    return nextCell ? !this._isRangeStart(nextCell.compareValue) : false;
  }

  _isComparisonEnd(value: number): boolean {
    return isEnd(value, this.comparisonStart, this.comparisonEnd);
  }

  _isInComparisonRange(value: number): boolean {
    return isInRange(
      value,
      this.comparisonStart,
      this.comparisonEnd,
      this.isRange,
    );
  }

  _isComparisonIdentical(value: number): boolean {
    return (
      this.comparisonStart === this.comparisonEnd &&
      value === this.comparisonStart
    );
  }

  _isPreviewStart(value: number): boolean {
    return isStart(value, this.previewStart, this.previewEnd);
  }

  _isPreviewEnd(value: number): boolean {
    return isEnd(value, this.previewStart, this.previewEnd);
  }

  _isInPreview(value: number): boolean {
    return isInRange(value, this.previewStart, this.previewEnd, this.isRange);
  }

  private _enterHandler = (event: Event): void => {
    if (this._skipNextFocus && event.type === 'focus') {
      this._skipNextFocus = false;

      return;
    }

    if (event.target && this.isRange) {
      const cell = this._getCellFromElement(event.target as HTMLElement);

      if (cell) {
        this.ngZone.run(() =>
          this.previewChange.emit({ value: cell.enabled ? cell : null, event }),
        );
      }
    }
  };

  private _leaveHandler = (event: Event): void => {
    if (this.previewEnd !== null && this.isRange) {
      if (event.target && isTableCell(event.target as HTMLElement)) {
        this.ngZone.run(() => this.previewChange.emit({ value: null, event }));
      }
    }
  };

  private _getCellFromElement(element: HTMLElement): RdsCalendarCell<D> | null {
    let cell: HTMLElement | undefined;

    if (isTableCell(element)) {
      cell = element;
    } else if (isTableCell(element.parentNode as Node)) {
      cell = element.parentNode as HTMLElement;
    }

    if (cell) {
      const row = cell.getAttribute('data-rds-row');
      const col = cell.getAttribute('data-rds-col');

      if (row && col && this.rows) {
        return this.rows[parseInt(row)][parseInt(col)];
      }
    }

    return null;
  }
}

function isTableCell(node: Node): node is HTMLTableCellElement {
  return node.nodeName === 'TD';
}

function isStart(
  value: number,
  start: number | null,
  end: number | null,
): boolean {
  return end !== null && start !== end && value < end && value === start;
}

function isEnd(
  value: number,
  start: number | null,
  end: number | null,
): boolean {
  return start !== null && start !== end && value >= start && value === end;
}

function isInRange(
  value: number,
  start: number | null,
  end: number | null,
  rangeEnabled: boolean,
): boolean {
  return (
    rangeEnabled &&
    start !== null &&
    end !== null &&
    start !== end &&
    value >= start &&
    value <= end
  );
}
