import { BooleanInput } from '@angular/cdk/coercion';
import { CdkTreeNodeToggle } from '@angular/cdk/tree';
import {
  booleanAttribute,
  Directive,
  ElementRef,
  HostListener,
  inject,
  Input,
} from '@angular/core';

@Directive({
  selector: '[rdsTreeNodeToggle], [rds-tree-node-toggle]',
  providers: [
    { provide: CdkTreeNodeToggle, useExisting: RdsTreeNodeToggleDirective },
  ],
})
export class RdsTreeNodeToggleDirective<T> extends CdkTreeNodeToggle<T> {
  private readonly hostTag = inject(ElementRef).nativeElement.tagName;

  @Input({ alias: 'rdsTreeNodeToggleRecursive', transform: booleanAttribute })
  override recursive = false;

  @Input({ transform: booleanAttribute }) disabled = false;

  @HostListener('keydown.enter', ['$event'])
  onInteraction(event: KeyboardEvent): void {
    // CdkTreeNodeToggle listens to (click) and button's enter clicks are handled
    // using that event, that's why we omit it here - interaction would be doubled
    if (this.hostTag !== 'BUTTON') {
      this._toggle(event);
    }
  }

  override _toggle(event: Event): void {
    if (!this.disabled) {
      super._toggle(event);
    }
  }
}
