import { Directive, HostBinding, Input } from '@angular/core';

let nextUniqueId = 0;

@Directive({
  selector: 'rds-control-error',
})
export class RdsErrorDirective {
  private _id = `rds-control-error-${nextUniqueId++}`;

  @HostBinding('attr.role') readonly hostRole = 'alert';

  @Input()
  @HostBinding('attr.id')
  get id(): string {
    return this._id;
  }
  set id(newId: string) {
    this._id = newId;
  }
}
