import {
  AfterViewInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  HostBinding,
  inject,
  Input,
  OnDestroy,
  Output,
  ViewEncapsulation,
} from '@angular/core';
import { RdsToggleChangedEvent } from './model/toggle-changed-event';
import { FocusableOption, FocusMonitor, FocusOrigin } from '@angular/cdk/a11y';
import { TAB_INDEX_NOT_FOCUSABLE } from '../utils';

@Component({
  selector: 'rds-toggle-button',
  template: `
    <button
      class="rds-toggle__button"
      [class.rds-toggle__button--checked]="checked"
      type="button"
      [attr.disabled]="disabled ? 'disabled' : null"
      [attr.tabindex]="disabled ? -1 : tabindex"
      [attr.aria-pressed]="checked"
      [attr.aria-label]="ariaLabel"
      [attr.aria-labelledby]="ariaLabelledby"
      (click)="onButtonClick()"
    >
      <ng-content />
    </button>
    @if (showSeparator) {
      <div class="rds-toggle__separator"></div>
    }
  `,
  styleUrls: ['./toggle-button.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class RdsToggleButtonComponent<T>
  implements AfterViewInit, OnDestroy, FocusableOption
{
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly elementRef = inject<ElementRef<HTMLElement>>(ElementRef);
  private readonly focusMonitor = inject(FocusMonitor);

  private _showSeparator = false;
  tabindex = 0;

  @HostBinding('tabIndex') readonly hostTabindex = TAB_INDEX_NOT_FOCUSABLE;

  @Input('aria-label') ariaLabel: string;
  @Input('aria-labelledby') ariaLabelledby: string;

  @Input() value: T;

  get showSeparator(): boolean {
    return this._showSeparator;
  }

  set showSeparator(value: boolean) {
    this._showSeparator = booleanAttribute(value);
    this.cdr.markForCheck();
  }

  @Input({ transform: booleanAttribute }) checked = false;
  @Input({ transform: booleanAttribute }) disabled = false;

  @Output() readonly changed = new EventEmitter<RdsToggleChangedEvent<T>>();

  ngAfterViewInit(): void {
    this.focusMonitor.monitor(this.elementRef, true);
  }

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.elementRef);
  }

  focus(origin: FocusOrigin = 'program', options?: FocusOptions): void {
    this.focusMonitor.focusVia(this.elementRef.nativeElement, origin, options);
  }

  onButtonClick(): void {
    this.changed.emit({
      buttonToggle: this,
      checked: !this.checked,
    });
  }
}
