import { AnimationEvent } from '@angular/animations';
import {
  ConfigurableFocusTrap,
  ConfigurableFocusTrapFactory,
  FocusMonitor,
  FocusOrigin,
  InteractivityChecker,
} from '@angular/cdk/a11y';
import { BooleanInput } from '@angular/cdk/coercion';
import { ESCAPE, hasModifierKey } from '@angular/cdk/keycodes';
import { ContentObserver } from '@angular/cdk/observers';
import { DOCUMENT, isPlatformBrowser } from '@angular/common';
import {
  AfterContentInit,
  AfterViewInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  DestroyRef,
  ElementRef,
  EventEmitter,
  HostBinding,
  HostListener,
  inject,
  Input,
  NgZone,
  OnDestroy,
  Output,
  PLATFORM_ID,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { BehaviorSubject, filter, fromEvent, Observable, Subject } from 'rxjs';
import { RdsDrawerActionsDirective } from './drawer-actions.directive';
import { rdsDrawerAnimations } from './drawer-animations';
import {
  AutoFocusTarget,
  RdsDrawerMode,
  RdsDrawerPosition,
  RdsDrawerSize,
} from './drawer-types';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { TAB_INDEX_NOT_FOCUSABLE } from '../utils';
import { RdsIconComponent } from '../icons';
import { RdsButtonModule } from '../button';

@Component({
  selector: 'rds-drawer',
  templateUrl: './drawer.component.html',
  styleUrls: ['drawer.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  animations: [rdsDrawerAnimations.expandDrawer],
  standalone: true,
  imports: [RdsIconComponent, RdsButtonModule],
})
export class RdsDrawerComponent
  implements AfterViewInit, AfterContentInit, OnDestroy
{
  private readonly destroyRef = inject(DestroyRef);
  private readonly elementRef = inject<ElementRef<HTMLElement>>(ElementRef);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly contentObserver = inject(ContentObserver);
  private readonly ngZone = inject(NgZone);
  private readonly focusTrapFactory = inject(ConfigurableFocusTrapFactory);
  private readonly focusMonitor = inject(FocusMonitor);
  private readonly document = inject(DOCUMENT);
  private readonly interactivityChecker = inject(InteractivityChecker);
  private readonly platformId = inject<Record<string, unknown>>(PLATFORM_ID);

  readonly _modeChanged = new Subject<void>();
  readonly _animationStarted = new Subject<AnimationEvent>();
  readonly _animationEnd = new Subject<AnimationEvent>();

  _animationState: 'open-instant' | 'open' | 'void' = 'void';
  _animating: 'expanding' | 'collapsing' | null = null;

  private readonly _isOpened$ = new BehaviorSubject(false);

  private _focusTrap: ConfigurableFocusTrap;
  private _elementFocusedBeforeDrawerWasOpened: HTMLElement | null = null;

  @Input() size: RdsDrawerSize = 'm';

  @Input() closeBtnAriaLabel = 'Close';

  @Input()
  get mode(): RdsDrawerMode {
    return this._mode;
  }

  set mode(value: RdsDrawerMode) {
    this._mode = value;
    this.updateFocusTrapState();
    this._modeChanged.next();
  }

  private _mode: RdsDrawerMode = 'overlay';

  @Input()
  get position(): RdsDrawerPosition {
    return this._position;
  }

  set position(value: RdsDrawerPosition) {
    this._position = value === 'end' ? 'end' : 'start';
    this.positionChanged.emit(this._position);
  }

  private _position: RdsDrawerPosition = 'start';

  @Input({ transform: booleanAttribute }) closable = true;

  @Input()
  set expanded(value: BooleanInput) {
    this.toggle(booleanAttribute(value));
  }

  get expanded(): boolean {
    return this.isOpened;
  }

  private _openedVia: FocusOrigin | null;

  @Input({ transform: booleanAttribute }) disableClose = false;

  @Input()
  set autoFocus(value: AutoFocusTarget | string | BooleanInput) {
    if (value === 'true' || value === 'false' || value == null) {
      value = booleanAttribute(value);
    }

    this._autoFocus = value;
  }

  get autoFocus(): AutoFocusTarget | string | boolean {
    const value = this._autoFocus;

    if (value == null) {
      if (this.mode === 'side') {
        return 'dialog';
      } else {
        return 'first-tabbable';
      }
    }

    return value;
  }

  private _autoFocus: AutoFocusTarget | string | boolean | undefined;

  @Output() readonly closeDrawer = new EventEmitter<void>();

  @Output() readonly openDrawer = new EventEmitter<void>();

  @Output() readonly positionChanged = new EventEmitter<RdsDrawerPosition>();

  @ContentChild(RdsDrawerActionsDirective) actions: RdsDrawerActionsDirective;

  @ViewChild('body') body: ElementRef = { nativeElement: undefined };

  @HostBinding('class') get class(): string[] {
    const classes = ['rds-drawer', `rds-drawer-${this.size}`];

    if (this.mode === 'overlay') {
      classes.push('rds-drawer-overlay');
    }

    if (this.position === 'end') {
      classes.push('rds-drawer-end');
    } else {
      classes.push('rds-drawer-start');
    }

    return classes;
  }

  @HostBinding('tabIndex') tabIndex = TAB_INDEX_NOT_FOCUSABLE;

  @HostBinding('@transform')
  get transform(): string {
    return this._animationState;
  }

  get isOpened(): boolean {
    return this._isOpened$.getValue();
  }

  get isOpened$(): Observable<boolean> {
    return this._isOpened$.asObservable();
  }

  @HostBinding('class.rds-drawer-scrollable')
  scrollable = false;

  constructor() {
    this.ngZone.runOutsideAngular(() => {
      if (isPlatformBrowser(this.platformId)) {
        fromEvent<KeyboardEvent>(window, 'keydown')
          .pipe(
            filter(
              (event) =>
                event.keyCode === ESCAPE &&
                !this.disableClose &&
                !hasModifierKey(event),
            ),
            takeUntilDestroyed(),
          )
          .subscribe((event) =>
            this.ngZone.run(() => {
              this.close();
              event.stopPropagation();
              event.preventDefault();
            }),
          );
      }
    });
  }

  ngAfterViewInit(): void {
    this._focusTrap = this.focusTrapFactory.create(
      this.elementRef.nativeElement,
    );
    this.updateFocusTrapState();
  }

  ngAfterContentInit(): void {
    this.contentObserver
      .observe(this.elementRef.nativeElement)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => {
        this.scrollable = this.isScrollable();
        this.cdr.markForCheck();
      });
  }

  ngOnDestroy(): void {
    if (this._focusTrap) {
      this._focusTrap.destroy();
    }

    this._animationStarted.complete();
    this._animationEnd.complete();
    this._modeChanged.complete();
  }

  @HostListener('@transform.start', ['$event'])
  transformStart(event: AnimationEvent): void {
    if (event.toState === 'void') {
      this._animating = 'collapsing';
    } else if (event.toState === 'open') {
      this._animating = 'expanding';
    }

    this._animationStarted.next(event);
  }

  @HostListener('@transform.done', ['$event'])
  transformEnd(event: AnimationEvent): void {
    this._animating = null;
    this._animationEnd.next(event);

    // animation.done changes opened value
    const isOpened =
      event.toState === 'open' || event.toState === 'open-instant';

    if (isOpened) {
      if (this.document) {
        this._elementFocusedBeforeDrawerWasOpened = this.document
          .activeElement as HTMLElement;
      }

      this.takeFocus();
    } else if (this.isFocusWithinDrawer()) {
      this.restoreFocus(this._openedVia || 'program');
    }

    this._isOpened$.next(isOpened);

    if (event.fromState !== event.toState) {
      if (isOpened) {
        this.openDrawer.emit();
      } else {
        this.closeDrawer.emit();
      }
    }

    this.scrollable = this.isScrollable();
  }

  toggle(
    expanded = !this._isOpened$.getValue(),
    openedVia?: FocusOrigin,
  ): void {
    if (expanded && openedVia) {
      this._openedVia = openedVia;
    }

    if (this.isOpened !== expanded) {
      if (expanded) {
        this.open();
      } else {
        this.close();
      }
    }
  }

  open(): void {
    this._animationState = 'open';
  }

  close(): void {
    this._animationState = 'void';
  }

  private forceFocus(element: HTMLElement, options?: FocusOptions): void {
    if (!this.interactivityChecker.isFocusable(element)) {
      element.tabIndex = TAB_INDEX_NOT_FOCUSABLE;
      this.ngZone.runOutsideAngular(() => {
        const callback = (): void => {
          element.removeEventListener('blur', callback);
          element.removeEventListener('mousedown', callback);
          element.removeAttribute('tabindex');
        };

        element.addEventListener('blur', callback);
        element.addEventListener('mousedown', callback);
      });
    }

    element.focus(options);
  }

  private focusByCssSelector(selector: string, options?: FocusOptions): void {
    const elementToFocus =
      this.elementRef.nativeElement.querySelector<HTMLElement>(selector);

    if (elementToFocus) {
      this.forceFocus(elementToFocus, options);
    }
  }

  private takeFocus(): void {
    if (!this._focusTrap) {
      return;
    }

    const element = this.elementRef.nativeElement;

    switch (this.autoFocus) {
      case false:
      case 'dialog':
        return;
      case true:
      case 'first-tabbable':
        this._focusTrap.focusInitialElementWhenReady().then((hasMovedFocus) => {
          if (
            !hasMovedFocus &&
            typeof this.elementRef.nativeElement.focus === 'function'
          ) {
            element.focus();
          }
        });
        break;
      case 'first-heading':
        this.focusByCssSelector(
          'rds-drawer-header, [rds-drawer-header], [rdsDrawerHeader]',
        );
        break;
      default:
        this.focusByCssSelector(this.autoFocus);
        break;
    }
  }

  private restoreFocus(focusOrigin: Exclude<FocusOrigin, null>): void {
    if (this.autoFocus === 'dialog') {
      return;
    } else if (this._elementFocusedBeforeDrawerWasOpened) {
      this.focusMonitor.focusVia(
        this._elementFocusedBeforeDrawerWasOpened,
        focusOrigin,
      );
    } else {
      this.elementRef.nativeElement.blur();
    }

    this._elementFocusedBeforeDrawerWasOpened = null;
  }

  private isFocusWithinDrawer(): boolean {
    const activeEl = this.document.activeElement;

    return !!activeEl && this.elementRef.nativeElement.contains(activeEl);
  }

  private updateFocusTrapState(): void {
    if (this._focusTrap) {
      this._focusTrap.enabled = this.expanded && this.mode !== 'side';
    }
  }

  private isScrollable(): boolean {
    return (
      this.body.nativeElement.scrollHeight >
      this.body.nativeElement.clientHeight
    );
  }
}
