import {
  CDK_TREE_NODE_OUTLET_NODE,
  CdkTreeNodeOutlet,
} from '@angular/cdk/tree';
import { Directive, inject, ViewContainerRef } from '@angular/core';

@Directive({
  selector: '[rdsTreeNodeOutlet], [rds-tree-node-outlet]',
  providers: [
    {
      provide: CdkTreeNodeOutlet,
      useExisting: RdsTreeNodeOutletDirective,
    },
  ],
})
export class RdsTreeNodeOutletDirective implements CdkTreeNodeOutlet {
  public readonly viewContainer = inject(ViewContainerRef);
  public readonly _node = inject(CDK_TREE_NODE_OUTLET_NODE, { optional: true });
}
