import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  DestroyRef,
  HostBinding,
  inject,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  SimpleChanges,
  ViewEncapsulation,
} from '@angular/core';
import { RdsAnchorMenuSectionData } from '../anchor-menu.types';
import { filter, takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

@Component({
  selector: 'a[rdsAnchorMenuItem], a[rds-anchor-menu-item]',
  templateUrl: './anchor-menu-item.component.html',
  styleUrls: ['./anchor-menu-item.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class RdsAnchorMenuItemComponent
  implements OnInit, OnChanges, OnDestroy
{
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly destroyRef = inject(DestroyRef);

  @Input('rdsAnchorMenuItem')
  item: RdsAnchorMenuSectionData;

  @HostBinding('class')
  protected readonly classes = 'rds-anchor-menu-item';

  @HostBinding('class.rds-anchor-menu-item-active')
  protected isActive = false;

  private readonly killItemSubscription$ = new Subject<void>();

  ngOnInit(): void {
    this.subscribeItem();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['item'] && !changes['item'].isFirstChange()) {
      this.killItemSubscription$.next();
      this.subscribeItem();
    }
  }

  ngOnDestroy(): void {
    this.killItemSubscription$.complete();
  }

  private subscribeItem(): void {
    this.item?.isActive$
      .pipe(
        filter((v) => v !== this.isActive),
        takeUntil(this.killItemSubscription$),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe((v) => {
        this.isActive = v;
        this.cdr.markForCheck();
      });
  }
}
