import { ComponentRef } from '@angular/core';
import {
  RdsBasePortalHost,
  RdsComponentPortal,
} from '../portal/rds-component-portal';

export class RdsToastOverlayRef<T> {
  constructor(private readonly _portalHost: RdsBasePortalHost<T>) {}

  attach(portal: RdsComponentPortal<T>, newestOnTop = true): ComponentRef<T> {
    return this._portalHost.attach(portal, newestOnTop);
  }

  detach(): void {
    return this._portalHost.detach();
  }
}
