import {
  Directive,
  ElementRef,
  HostBinding,
  inject,
  Input,
} from '@angular/core';
import { RdsMinimalBadgeColor } from '../badge';

@Directive({
  selector: 'rds-card-label, [rds-card-label], [rdsCardLabel]',
  standalone: true,
})
export class RdsCardLabelDirective {
  private readonly nativeElement =
    inject<ElementRef<HTMLElement>>(ElementRef).nativeElement;

  @HostBinding('class') readonly class = 'rds-card-label';

  @Input()
  color: RdsMinimalBadgeColor = 'blue';

  getTextContent(): string {
    return this.nativeElement.innerText;
  }
}
