import {
  AfterViewInit,
  DestroyRef,
  Directive,
  ElementRef,
  forwardRef,
  HostBinding,
  HostListener,
  inject,
  InjectionToken,
  Input,
  OnDestroy,
  Provider,
  Renderer2,
} from '@angular/core';
import { NG_VALIDATORS, NG_VALUE_ACCESSOR, Validators } from '@angular/forms';
import { merge, Subject } from 'rxjs';
import {
  DateFilterFn,
  RdsDatepickerInputBaseDirective,
} from './datepicker-input.base';
import { RdsDatepickerControl, RdsDatepickerPanel } from './datepicker-base';
import { DateSelectionModelChange } from './date-selection.model';
import { RDS_FORM_FIELD, RdsFormFieldComponent } from '../form-field';
import { takeUntil } from 'rxjs/operators';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

export const RDS_DATEPICKER_VALUE_ACCESSOR: Provider = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => RdsDatepickerInputDirective),
  multi: true,
};

export const RDS_DATEPICKER_VALIDATORS: Provider = {
  provide: NG_VALIDATORS,
  useExisting: forwardRef(() => RdsDatepickerInputDirective),
  multi: true,
};

export const RDS_INPUT_VALUE_ACCESSOR = new InjectionToken<{ value: unknown }>(
  'RDS_INPUT_VALUE_ACCESSOR',
);

@Directive({
  selector: 'input[rdsDatepicker], input[rds-datepicker]',
  providers: [
    RDS_DATEPICKER_VALUE_ACCESSOR,
    RDS_DATEPICKER_VALIDATORS,
    {
      provide: RDS_INPUT_VALUE_ACCESSOR,
      useExisting: RdsDatepickerInputDirective,
    },
  ],
  exportAs: 'rdsDatepickerInput',
})
export class RdsDatepickerInputDirective<D>
  extends RdsDatepickerInputBaseDirective<D | null, D>
  implements RdsDatepickerControl<D | null>, AfterViewInit, OnDestroy
{
  protected readonly renderer = inject(Renderer2);
  protected readonly destroyRef = inject(DestroyRef);
  protected readonly formField = inject<RdsFormFieldComponent<D>>(
    RDS_FORM_FIELD,
    { optional: true },
  ) as RdsFormFieldComponent<D>;

  private _destroyDatepickerSubscriptions$ = new Subject<void>();

  @HostBinding('class') class = 'rds-datepicker-input';

  @HostBinding('attr.aria-haspopup') get ariaHasPopup(): string | null {
    return this._datepicker ? 'dialog' : null;
  }

  @HostBinding('attr.aria-owns') get ariaOwns(): string | null {
    return (this._datepicker?.opened && this._datepicker.id) || null;
  }

  @HostBinding('attr.min') get attrMin(): string | null {
    return this.min ? this.dateAdapter.toIso8601(this.min) : null;
  }

  @HostBinding('attr.max') get attrMax(): string | null {
    return this.max ? this.dateAdapter.toIso8601(this.max) : null;
  }

  @HostBinding('attr.data-rds-calendar') get attrDataRdsCalendar():
    | string
    | null {
    return this._datepicker ? this._datepicker.id : null;
  }

  @HostBinding('disabled') get attrDisabled(): boolean | null {
    return this.disabled ? true : null;
  }

  @Input()
  set rdsDatepicker(
    datepicker: RdsDatepickerPanel<RdsDatepickerControl<D>, D | null, D>,
  ) {
    this._destroyDatepickerSubscriptions$.next();

    if (datepicker) {
      this._datepicker = datepicker;
      this._datepicker.closedStream
        .pipe(
          takeUntil(this._destroyDatepickerSubscriptions$),
          takeUntilDestroyed(this.destroyRef),
        )
        .subscribe(() => this._onTouched());
      this._registerModel(datepicker.registerInput(this));
      merge(this._datepicker.stateChanges, this.dateAdapter.localeChanges)
        .pipe(
          takeUntil(this._destroyDatepickerSubscriptions$),
          takeUntilDestroyed(this.destroyRef),
        )
        .subscribe(() => {
          this._setDefaultPlaceholder();
          this._formatValue(this.value);
        });
    }
  }

  _datepicker?: RdsDatepickerPanel<RdsDatepickerControl<D>, D | null, D>;

  @Input()
  get min(): D | null {
    return this._min;
  }

  set min(value: D | null) {
    const validValue = this.dateAdapter.getValidDateOrNull(
      this.dateAdapter.deserialize(value),
    );

    if (!this.dateAdapter.sameDate(validValue, this._min)) {
      this._min = validValue;
      this._validatorOnChange();
    }
  }

  private _min: D | null = null;

  @Input()
  get max(): D | null {
    return this._max;
  }

  set max(value: D | null) {
    const validValue = this.dateAdapter.getValidDateOrNull(
      this.dateAdapter.deserialize(value),
    );

    if (!this.dateAdapter.sameDate(validValue, this._max)) {
      this._max = validValue;
      this._validatorOnChange();
    }
  }

  private _max: D | null = null;

  @Input('rdsDatepickerFilter')
  get dateFilter(): DateFilterFn<D | null> {
    return this._dateFilter as DateFilterFn<D | null>;
  }

  set dateFilter(value: DateFilterFn<D | null>) {
    const wasMatchingValue = this._matchesFilter(this.value);
    this._dateFilter = value;

    if (this._matchesFilter(this.value) !== wasMatchingValue) {
      this._validatorOnChange();
    }
  }

  private _dateFilter?: DateFilterFn<D | null>;

  protected _validator = Validators.compose(super._getValidators());

  @HostListener('input', ['$event'])
  onInput($event: Event): void {
    this._onInput(($event.target as HTMLInputElement).value);
  }

  @HostListener('change')
  override _onChange(): void {
    super._onChange();
  }

  @HostListener('blur')
  override _onBlur(): void {
    super._onBlur();
  }

  @HostListener('keydown', ['$event'])
  override _onKeydown($event: KeyboardEvent): void {
    super._onKeydown($event);
  }

  getConnectedOverlayOrigin(): ElementRef {
    return this.formField
      ? this.formField.getConnectedOverlayOrigin()
      : this.elementRef;
  }

  getStartValue(): D | null {
    return this.value;
  }

  override ngAfterViewInit(): void {
    super.ngAfterViewInit();
    this._setDefaultPlaceholder();
  }

  override ngOnDestroy(): void {
    super.ngOnDestroy();
    this._destroyDatepickerSubscriptions$.complete();
  }

  protected override _formatValue(value: D | null): void {
    this.elementRef.nativeElement.value = value
      ? this.dateAdapter.format(
          value,
          this.dateFormats.display.dateInput,
          this._datepicker?.availableViews,
        )
      : '';
  }

  protected _openPopup(): void {
    if (this._datepicker) {
      this._datepicker.open();
    }
  }

  protected _getValueFromModel(modelValue: D | null): D | null {
    return modelValue;
  }

  protected _assignValueToModel(value: D | null): void {
    if (this._model) {
      this._model.updateSelection(value, this);
    }
  }

  _getMinDate(): D {
    return this._min as D;
  }

  _getMaxDate(): D {
    return this._max as D;
  }

  protected _getDateFilter(): DateFilterFn<D | null> {
    return this._dateFilter as DateFilterFn<D | null>;
  }

  protected _shouldHandleChangeEvent(
    event: DateSelectionModelChange<D>,
  ): boolean {
    return event.source !== this;
  }

  protected _setDefaultPlaceholder(): void {
    if (this.elementRef && this.elementRef.nativeElement) {
      this.renderer.setAttribute(
        this.elementRef.nativeElement,
        'date-placeholder',
        this._calculateDefaultPlaceholder(),
      );
    }
  }

  protected _calculateDefaultPlaceholder(): string {
    return this.dateAdapter.placeholderFormat(
      this.dateFormats.display.dateInput,
      this._datepicker?.availableViews,
    );
  }
}
