import { AfterViewChecked, Component, HostBinding, Input } from '@angular/core';
import { Subject } from 'rxjs';
import { RdsListItemComponent } from '../../list-item';
import {
  RDS_SINGLE_SELECT_LIST_ITEM_TEMPLATE,
  RdsSingleSelectListItemComponent,
} from '../../list-item';
import { AbstractFocusableOption } from './abstract-focusable-option';
import { RdsSelectOptionSelectionChangedEvent } from './select-option-selection-changed.event';

let nextId = 0;

@Component({
  template: RDS_SINGLE_SELECT_LIST_ITEM_TEMPLATE,
  selector: 'rds-select-option',
  providers: [
    {
      provide: RdsListItemComponent,
      useExisting: RdsSingleSelectOptionComponent,
    },
    {
      provide: AbstractFocusableOption,
      useExisting: RdsSingleSelectOptionComponent,
    },
  ],
})
export class RdsSingleSelectOptionComponent<T>
  extends RdsSingleSelectListItemComponent
  implements AfterViewChecked, AbstractFocusableOption
{
  private _currentViewValue = '';

  @HostBinding('class') hostClass = 'rds-select__option';

  @Input()
  @HostBinding('attr.id')
  id = `rds-option-${nextId++}`;

  @Input() value: T;

  readonly selectionChange$ = new Subject<
    RdsSelectOptionSelectionChangedEvent<T>
  >();

  get viewValue(): string {
    return (this.elementRef.nativeElement.textContent || '').trim();
  }

  ngAfterViewChecked(): void {
    if (this.checked) {
      const viewValue = this.viewValue;

      if (viewValue !== this._currentViewValue) {
        this._currentViewValue = viewValue;
      }
    }
  }

  rect(): DOMRect {
    return this.elementRef.nativeElement.getBoundingClientRect();
  }

  isVisible(): boolean {
    return !this.elementRef.nativeElement.classList.contains('hide');
  }

  toggleVisibility(value: boolean | null): void {
    if (value === true) {
      this.show();
    } else if (value === false) {
      this.hide();
    } else {
      if (this.isVisible()) {
        this.hide();
      } else if (!this.isVisible()) {
        this.show();
      }
    }
  }

  hide(): void {
    this.renderer.addClass(this.elementRef.nativeElement, 'hide');
  }

  show(): void {
    this.renderer.removeClass(this.elementRef.nativeElement, 'hide');
  }

  override toggle(): void {
    this.handleClick();
  }

  handleClick(): void {
    if (!this.disabled && !this.checked) {
      this.checked = true;
      this.selectionChange$.next(
        new RdsSelectOptionSelectionChangedEvent(this, true),
      );
    }
  }

  setActiveStyles(): void {
    this.focus('keyboard');
  }

  setInactiveStyles(): void {
    if (this._focused) {
      this.blur();
      this.cdr.markForCheck();
    }
  }
}
