import {
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  DestroyRef,
  EventEmitter,
  HostBinding,
  inject,
  Input,
  OnInit,
  Output,
} from '@angular/core';
import { RdsButtonSize } from '../../button';
import { RdsUploadSize } from '../upload.types';
import { RdsFileItem } from '../file-item.class';
import { RdsUploadIntlService } from '../rds-upload-intl.service';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

let rdsUploadItemNextId = 0;

@Component({
  selector: 'li[rds-upload-list-item], li[rdsUploadListItem]',
  templateUrl: './upload-list-item.component.html',
  styleUrls: ['./upload-list-item.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsUploadListItemComponent implements OnInit {
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly destroyRef = inject(DestroyRef);
  readonly intl = inject(RdsUploadIntlService);

  private _size: RdsUploadSize;
  private stateClass = '';

  actionButtonSize: RdsButtonSize;
  ariaLiveContent = '';

  @Input() fileItem: RdsFileItem;

  @Input({ transform: booleanAttribute }) disabled = false;

  @Input()
  set size(v: RdsUploadSize) {
    this._size = v;
    this.actionButtonSize = v === 'l' ? 'm' : 's';
  }

  get size(): RdsUploadSize {
    return this._size;
  }

  @Output() upload = new EventEmitter<RdsFileItem>();
  @Output() remove = new EventEmitter<RdsFileItem>();

  @HostBinding('class')
  private get classes(): string[] {
    return [this.stateClass, `rds-upload-item-size-${this.size}`];
  }

  @HostBinding('attr.aria-labelledby')
  nameId = `rds-upload-item-${rdsUploadItemNextId++}-name`;

  ngOnInit(): void {
    this.fileItem.state$
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((state) => {
        this.stateClass = `rds-upload-item-${state}`;
        this.updateAriaLiveRegion();
        this.cdr.markForCheck();
      });
  }

  private updateAriaLiveRegion(): void {
    if (this.fileItem.hasFailedFilters) {
      // we need to delay the update in case of immediately known errors, like incorrect file type,
      // because otherwise screen readers will miss the difference and won't announce anything
      setTimeout(() => {
        this.ariaLiveContent = this.intl.getFileItemFilterFailedMessage(
          this.fileItem,
        );
        this.cdr.markForCheck();
      });

      return;
    }

    if (this.fileItem.state === 'success') {
      this.ariaLiveContent = this.intl.getFileItemUploadSuccessMessage(
        this.fileItem,
      );
    }

    if (this.fileItem.state === 'error') {
      this.ariaLiveContent = this.intl.getFileItemUploadErrorMessage(
        this.fileItem,
      );
    }
  }
}
