import { BooleanInput } from '@angular/cdk/coercion';
import { hasModifierKey, TAB } from '@angular/cdk/keycodes';
import {
  AfterContentInit,
  AfterViewInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  EventEmitter,
  HostBinding,
  HostListener,
  inject,
  Input,
  isDevMode,
  OnDestroy,
  Output,
  QueryList,
  ViewEncapsulation,
} from '@angular/core';
import { merge, noop, Observable } from 'rxjs';
import { RdsInputChipComponent } from '../input-chip/input-chip.component';
import { RdsChipSetComponent } from '../../chips/chip-set';
import { RdsChipInputDirective } from '../chip-input';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { TAB_INDEX_NOT_FOCUSABLE } from '../../utils';
import { RdsChipEditedEvent } from '../../chips/chip.types';

export class RdsChipGridChange<T> {
  constructor(
    public readonly source: RdsChipGridComponent<T>,
    public readonly value: T[],
  ) {}
}

@Component({
  selector: 'rds-chip-grid',
  template: '<ng-content />',
  styleUrls: ['../../chips/chips.common.scss', './chip-grid.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsChipGridComponent<T>
  extends RdsChipSetComponent<T>
  implements AfterContentInit, AfterViewInit, OnDestroy
{
  private readonly cdr = inject(ChangeDetectorRef);

  private _ariaDescribedbyIds: string[] = [];
  private _placeholder: string;

  private chipInput: RdsChipInputDirective<T>;

  protected _value: T[] = [];

  override _defaultRole = 'grid';
  readonly stateChanges = new EventEmitter<void>();

  get id(): string {
    return this.chipInput.id;
  }

  override get empty(): boolean {
    return (
      (!this.chipInput || this.chipInput.empty) &&
      (!this.chips || this.chips.length === 0)
    );
  }

  override get focused(): boolean {
    return this.chipInput?.focused || this.hasFocusedChip();
  }

  get chipEditChanges(): Observable<RdsChipEditedEvent<T>> {
    return this.getChipStream<RdsChipEditedEvent<T>, RdsInputChipComponent<T>>(
      (chip) => chip.edited,
    );
  }

  @HostBinding('class')
  get classes(): Record<string, boolean> {
    return {
      'rds-chips': true,
      'rds-chips--size-s': true,
      'rds-chip-list-disabled': this.disabled,
      'rds-chip-list-invalid': this.errorState,
    };
  }

  @HostBinding('attr.aria-invalid')
  errorState = false;

  @HostBinding('tabIndex')
  get dynamicTabIndex(): number {
    return this.chips && this.chips.length === 0
      ? TAB_INDEX_NOT_FOCUSABLE
      : this.tabIndex;
  }

  @HostBinding('attr.aria-disabled')
  get ariaDisabled(): string {
    return this.disabled.toString();
  }

  @Input()
  override set disabled(value: BooleanInput) {
    this._disabled = booleanAttribute(value);
    this.syncChipsState();
  }

  override get disabled(): boolean {
    return this._disabled;
  }

  @Input()
  set placeholder(value: string) {
    this._placeholder = value;
    this.stateChanges.next();
  }

  get placeholder(): string {
    return this.chipInput ? this.chipInput.placeholder : this._placeholder;
  }

  @Input()
  set value(value: T[]) {
    this._value = value;
  }

  get value(): T[] {
    return this._value;
  }

  @Output()
  readonly gridChange = new EventEmitter<RdsChipGridChange<T>>();

  @Output()
  readonly valueChange = new EventEmitter<T[]>();

  @ContentChildren(RdsInputChipComponent, { descendants: true })
  override chips: QueryList<RdsInputChipComponent<T>>;

  _onTouched = noop;
  _onChange: (value: T[]) => void = noop;

  override ngAfterContentInit(): void {
    super.ngAfterContentInit();
    merge(this.chipEditChanges, this.chips.changes)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => {
        this.propagateChanges();
        this.stateChanges.next();
      });

    merge(this.chipFocusChanges, this.chips.changes)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this.stateChanges.next());
  }

  override ngAfterViewInit(): void {
    super.ngAfterViewInit();

    if (!this.chipInput && isDevMode()) {
      throw Error(
        'rds-chip-grid must be used in combination with rdsChipInputFor.',
      );
    }
  }

  override ngOnDestroy(): void {
    super.ngOnDestroy();
    this.stateChanges.complete();
  }

  @HostListener('focus')
  override focus(): void {
    if (this.disabled || this.chipInput.focused) {
      return;
    }

    if (!this.chips.length || this.chips.first.disabled) {
      Promise.resolve().then(() => this.chipInput.focus());
    } else if (this.chips.length) {
      this.keyManager.setFirstItemActive();
    }

    this.stateChanges.next();
  }

  @HostListener('blur')
  blur(): void {
    if (!this.disabled) {
      setTimeout(() => {
        if (!this.focused) {
          this.markAsTouched();
        }
      });
    }
  }

  override handleKeydown(event: KeyboardEvent): void {
    if (event.keyCode === TAB) {
      if (
        this.chipInput.focused &&
        hasModifierKey(event, 'shiftKey') &&
        this.chips.length &&
        !this.chips.last.disabled
      ) {
        event.preventDefault();

        if (this.keyManager.activeItem) {
          this.keyManager.setActiveItem(this.keyManager.activeItem);
        } else {
          this.focusLastChip();
        }
      } else {
        this.allowFocusEscape();
      }
    } else if (!this.chipInput.focused) {
      super.handleKeydown(event);
    }

    this.stateChanges.next();
  }

  registerInput(inputElement: RdsChipInputDirective<T>): void {
    this.chipInput = inputElement;
    this.chipInput.setDescribedByIds(this._ariaDescribedbyIds);
  }

  onContainerClick(event: MouseEvent): void {
    if (!this.disabled && !this.originatesFromChip(event)) {
      this.focus();
    }
  }

  setDescribedByIds(ids: string[]): void {
    // We must keep this up to date to handle the case where ids are set
    // before the chip input is registered.
    this._ariaDescribedbyIds = ids;
    this.chipInput?.setDescribedByIds(ids);
  }

  setDisabledState(isDisabled: boolean): void {
    if (isDisabled !== this.disabled) {
      this.disabled = isDisabled;
      this.stateChanges.next();
    }
  }

  focusLastChip(): void {
    if (this.chips.length) {
      this.chips.last.focus();
    }
  }

  protected override allowFocusEscape(): void {
    if (!this.chipInput.focused) {
      super.allowFocusEscape();
    }
  }

  private propagateChanges(): void {
    const valueToEmit = this.chips.length
      ? this.chips.toArray().map((chip) => chip.value as T)
      : [];
    this._value = valueToEmit;
    this.gridChange.emit(new RdsChipGridChange(this, valueToEmit));
    this.valueChange.emit(valueToEmit);
    this._onChange(valueToEmit);
    this.cdr.markForCheck();
  }

  private markAsTouched(): void {
    this._onTouched();
    this.cdr.markForCheck();
    this.stateChanges.next();
  }
}
