import {
  booleanAttribute,
  Directive,
  DoCheck,
  ElementRef,
  HostBinding,
  HostListener,
  inject,
  Input,
  numberAttribute,
  OnChanges,
  OnDestroy,
} from '@angular/core';
import { Validators } from '@angular/forms';
import { getSupportedInputTypes } from '@angular/cdk/platform';
import { BooleanInput } from '@angular/cdk/coercion';
import { RdsAbstractFormFieldControl } from './abstract-form-field-control';
import { RdsControlType } from './control.type';
import { updateErrorState } from '../utils';
import { RDS_INPUT_VALUE_ACCESSOR } from './input-value-accessor';

const INPUT_INVALID_TYPES = [
  'button',
  'checkbox',
  'file',
  'hidden',
  'image',
  'radio',
  'range',
  'reset',
  'submit',
];

let nextUniqueId = 0;

@Directive({
  selector: ` input[rds-input], input[rdsInput], textarea[rds-input], textarea[rdsInput]`,
  providers: [
    { provide: RdsAbstractFormFieldControl, useExisting: RdsInputDirective },
  ],
})
export class RdsInputDirective
  extends RdsAbstractFormFieldControl<string>
  implements OnChanges, OnDestroy, DoCheck
{
  private readonly elementRef =
    inject<ElementRef<HTMLInputElement | HTMLTextAreaElement>>(ElementRef);
  private readonly inputValueAccessor = inject(RDS_INPUT_VALUE_ACCESSOR, {
    optional: true,
    self: true,
  });

  override prefixed = false;
  override suffixed = false;
  protected _uid = `rds-input-${nextUniqueId++}`;

  private _inputValueAccessor: { value: string } =
    this.inputValueAccessor || this.elementRef.nativeElement;
  private _previousNativeValue = this.value;
  private _type = 'text';
  private _placeholder = '';
  private _readonly = false;
  private _ariaDescribedby: string;

  private _neverEmptyInputTypes = [
    'date',
    'datetime',
    'datetime-local',
    'month',
    'time',
    'week',
  ].filter((t) => getSupportedInputTypes().has(t));

  readonly _isTextarea =
    this.elementRef.nativeElement.nodeName.toLowerCase() === 'textarea';

  @HostBinding('class') readonly hostClass = 'rds-form-field__control';
  @HostBinding('attr.autocomplete') readonly hostAutocomplete = 'off';

  @HostBinding('attr.aria-describedby')
  get hostAriaDescribedBy(): string {
    return this._ariaDescribedby;
  }

  set hostAriaDescribedBy(newValue: string) {
    this._ariaDescribedby = newValue;
  }

  @HostBinding('attr.aria-invalid')
  get errorState(): boolean {
    return this._errorState;
  }

  @HostBinding('class.rds-form-field__control--suffixed')
  get hostClassSuffixed(): boolean {
    return this.suffixed;
  }

  @HostBinding('class.rds-form-field__control--prefixed')
  get hostClassPrefixed(): boolean {
    return this.prefixed;
  }

  @Input()
  @HostBinding('id')
  override get id(): string {
    return this._id;
  }

  override set id(value: string) {
    this._id = value || this._uid;
  }
  protected override _id = this._uid;

  @HostBinding('attr.disabled')
  get attrDisabled(): boolean | null {
    if (this.ngControl && this.ngControl.disabled !== null) {
      return this.ngControl.disabled ? this.ngControl.disabled : null;
    }

    return this._disabled ? this._disabled : null;
  }

  @Input()
  override set disabled(value: BooleanInput) {
    this._disabled = booleanAttribute(value);

    if (this._focused) {
      this._focused = false;
      this.stateChanges.next();
    }
  }

  override get disabled(): boolean {
    return this.attrDisabled || false;
  }

  @HostBinding('attr.required')
  get attrRequired(): boolean | null {
    return (
      this._required ??
      this.ngControl?.control?.hasValidator(Validators.required) ??
      null
    );
  }
  override get required(): boolean {
    return this.attrRequired ?? false;
  }

  @Input()
  override set required(value: BooleanInput) {
    this._required = booleanAttribute(value);
  }

  get readonly(): boolean | null {
    return this._readonly ? true : null;
  }

  @Input()
  @HostBinding('attr.readonly')
  set readonly(value: BooleanInput) {
    this._readonly = booleanAttribute(value);
  }

  @Input()
  get type(): string {
    return this._type;
  }

  set type(newValue: string) {
    this._type = newValue || 'text';
    this.validateType();

    // When using Angular inputs, developers are no longer able to set the properties on the native
    // input element. To ensure that bindings for `type` work, we need to sync the setter
    // with the native property. Textarea elements don't support the type property or attribute.
    if (!this._isTextarea && getSupportedInputTypes().has(this._type)) {
      (this.elementRef.nativeElement as HTMLInputElement).type = this._type;
    }
  }

  @Input()
  get placeholder(): string {
    return this._placeholder;
  }

  set placeholder(value: string) {
    this._placeholder = value || '';
  }

  @Input()
  override get value(): string {
    return this._inputValueAccessor.value;
  }

  override set value(newValue: string) {
    this._inputValueAccessor.value = newValue;

    if (newValue !== this._inputValueAccessor.value) {
      this.stateChanges.next();
    }
  }

  get empty(): boolean {
    return (
      !this.isNeverEmpty() &&
      !this.elementRef.nativeElement.value &&
      !this.isNativelyBadInput()
    );
  }

  get shouldLabelFloat(): boolean {
    return this._focused || !this.empty;
  }

  ngOnChanges(): void {
    this.stateChanges.next();
  }

  ngOnDestroy(): void {
    this.stateChanges.complete();
  }

  ngDoCheck(): void {
    this.showHidePlaceholder();

    if (this.ngControl) {
      // We need to re-evaluate this on every change detection cycle, because there are some
      // error triggers that we can't subscribe to (e.g. parent form submissions). This means
      // that whatever logic is in here has to be super lean or we risk destroying the performance.
      const oldState = this._errorState;
      this._errorState = updateErrorState(
        this.errorState,
        this.parentFormGroup,
        this.parentForm,
        this.ngControl.control,
      );

      if (oldState !== this._errorState) {
        this.stateChanges.next();
      }
    }

    // We need to dirty-check the native element's value, because there are some cases where
    // we won't be notified when it changes (e.g. the consumer isn't using forms or they're
    // updating the value using `emitEvent: false`).
    this.dirtyCheckNativeValue();
    this.dirtyDisabledValue();
  }

  @HostListener('focus', ['true'])
  @HostListener('blur', ['false'])
  focusChanged(isFocused: boolean): void {
    if (isFocused !== this.focused && (!this.readonly || !isFocused)) {
      this._focused = isFocused;
      this.stateChanges.next();
    }
  }

  @HostListener('input')
  onInput(): void {
    // This is a noop function and is used to let Angular know whenever the value changes.
    // Angular will run a new change detection each time the `input` event has been dispatched.
    // It's necessary that Angular recognizes the value change, because when floatingLabel
    // is set to false and Angular forms aren't used, the placeholder won't recognize the
    // value changes and will not disappear.
    // Listening to the input event wouldn't be necessary when the input is using the
    // FormsModule or ReactiveFormsModule, because Angular forms also listens to input events.
  }

  setDescribedByIds(ids: string[]): void {
    this.hostAriaDescribedBy = ids.join(' ');
  }

  onContainerClick(): void {
    if (!this._focused) {
      this.focus();
    }
  }

  get controlType(): RdsControlType {
    if (this.elementRef?.nativeElement?.tagName.toUpperCase() === 'TEXTAREA') {
      return 'textarea-field';
    }

    return this.elementRef?.nativeElement?.classList.contains(
      'rds-datepicker-input',
    )
      ? 'date-picker'
      : 'text-field';
  }

  getMaxlengthAttr(): number {
    return numberAttribute(
      this.elementRef.nativeElement.getAttribute('maxlength'),
      0,
    );
  }

  private dirtyCheckNativeValue(): void {
    const newValue = this.elementRef.nativeElement.value;

    if (this._previousNativeValue !== newValue) {
      this._previousNativeValue = newValue;
      this.stateChanges.next();
    }
  }

  private dirtyDisabledValue(): void {
    if (this._disabled !== this.disabled) {
      this._disabled = this.disabled;
      this.stateChanges.next();
    }
  }

  private validateType(): void {
    if (INPUT_INVALID_TYPES.indexOf(this._type) > -1) {
      throw new Error(`Unsupported type of rds input <${this._type}>`);
    }
  }

  private isNeverEmpty(): boolean {
    return this._neverEmptyInputTypes.indexOf(this._type) > -1;
  }

  private isNativelyBadInput(): boolean {
    const validity = (this.elementRef.nativeElement as HTMLInputElement)
      .validity;

    return validity && validity.badInput;
  }

  private focus(options?: FocusOptions): void {
    this.elementRef.nativeElement.focus(options);
  }

  private showHidePlaceholder(): void {
    const element = this.elementRef.nativeElement;
    const datePlaceholder = element.getAttribute('date-placeholder') ?? '';
    const placeholder = this.placeholder ? this.placeholder : datePlaceholder;
    const isSearch = element.type === 'search';
    const contentInfix = this.getContentInfixParentElement(element);
    const isFloatingLabel = contentInfix?.classList.contains(
      'rds-form-field__content-infix--floating-label',
    );
    const isInputWithChips = element.classList.contains('rds-chip-input');
    const inputWithChipsHasPlaceholder = !element.classList.contains(
      'rds-chip-input-no-placeholder',
    );

    if (element.value) {
      return;
    }

    if (isSearch || (isInputWithChips && inputWithChipsHasPlaceholder)) {
      element.placeholder = placeholder;

      return;
    }

    element.placeholder = !isFloatingLabel || this._focused ? placeholder : '';
  }

  private getContentInfixParentElement(
    target: HTMLElement,
  ): HTMLElement | null {
    let parent = target.parentElement;

    while (
      parent &&
      !parent.classList.contains('rds-form-field__content-infix')
    ) {
      parent = parent.parentElement;
    }

    return parent;
  }
}
