import {
  A,
  DOWN_ARROW,
  ENTER,
  hasModifierKey,
  LEFT_ARROW,
  RIGHT_ARROW,
  SPACE,
  UP_ARROW,
} from '@angular/cdk/keycodes';
import {
  AfterContentInit,
  booleanAttribute,
  AfterViewInit,
  ChangeDetectionStrategy,
  Component,
  ContentChildren,
  DoCheck,
  inject,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  QueryList,
  ViewChildren,
  ViewEncapsulation,
} from '@angular/core';
import { ControlValueAccessor } from '@angular/forms';
import { merge } from 'rxjs';
import {
  debounceTime,
  filter,
  map,
  startWith,
  switchMap,
  takeUntil,
  tap,
} from 'rxjs/operators';
import { RdsAbstractFormFieldControl } from '../abstract-form-field-control';
import { RdsFormFieldSize } from '../form-field.component';
import { SelectAnimations } from './select.animations';
import { RdsAbstractSelectComponent } from './abstract-select.component';
import { SelectionModel } from '@angular/cdk/collections';
import { RdsMultiSelectOptionComponent } from './multi-select-option.component';
import { RdsSelectAllOptionComponent } from './select-all-option.component';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { BooleanInput } from '@angular/cdk/coercion';
import { RdsChipComponent } from '../../chips';
import { FocusKeyManager } from '@angular/cdk/a11y';
import { RdsChipActionDirective } from '../../chips/chip-action';
import { Directionality } from '@angular/cdk/bidi';
import { RdsControlType } from '../control.type';

@Component({
  selector: 'rds-multi-select',
  template: `
    <ng-container aria-hidden="true" (click)="toggle()">
      @if (shouldShowValue()) {
        <div class="rds-form-field__select-value" #wrapperElement>
          @if (withChips) {
            @if (triggerValues.length && triggerValues[0]) {
              <rds-chip-list size="s">
                @for (value of triggerValues; track value; let i = $index) {
                  <rds-chip
                    closable
                    (closeChip)="removeValue(i)"
                    #valueContainer
                    [disabled]="disabled"
                    [tabindex]="
                      !focusedChip || focusedChip === valueContainer ? 0 : -1
                    "
                  >
                    {{ value }}
                  </rds-chip>
                }
              </rds-chip-list>
            }
          } @else {
            <span
              class="rds-form-field__select-trigger-value"
              rds-trigger-values
              multiple
              #rdsTriggerValues="rdsTriggerValues"
              [triggerValues]="triggerValues"
              [wrapperElement]="wrapperElement"
            ></span>
            @if (((rdsTriggerValues.hiddenValues | async) || 0) > 0) {
              <span
                class="rds-form-field__select-hidden-trigger-values-placeholder"
              >
                <rds-badge
                  type="minimal"
                  size="s"
                  [color]="disabled ? 'black' : 'blue'"
                  [label]="'+' + (rdsTriggerValues.hiddenValues | async)"
                />
              </span>
            }
          }
          @if (withChips && triggerValues.length && triggerValues[0]) {
            <div class="rds-form-field__select-x-wrapper">
              <button
                rds-icon-button
                size="m"
                [disabled]="disabled"
                (click)="clearSelectedOptions($event)"
              >
                <rds-icon namespace="filled" icon="close" />
              </button>
            </div>
          }
        </div>
      } @else {
        @if (shouldShowPlaceholder()) {
          <div class="rds-form-field__select-value">
            <div
              class="rds-form-field__select-trigger-value rds-form-field__select-trigger-value-placeholder"
            >
              {{ placeholder }}
            </div>
          </div>
        }
      }
      <div class="rds-form-field__select-arrow-wrapper">
        <div
          class="rds-form-field__select-arrow"
          [class.rds-form-field__select-arrow--rotate]="panelOpen"
        ></div>
      </div>
    </ng-container>

    <ng-template
      cdk-connected-overlay
      cdkConnectedOverlayLockPosition
      cdkConnectedOverlayHasBackdrop
      cdkConnectedOverlayPanelClass="test"
      cdkConnectedOverlayBackdropClass="cdk-overlay-transparent-backdrop"
      [cdkConnectedOverlayScrollStrategy]="scrollStrategy"
      [cdkConnectedOverlayOrigin]="origin"
      [cdkConnectedOverlayOpen]="panelOpen"
      [cdkConnectedOverlayPositions]="positions"
      [cdkConnectedOverlayMinWidth]="minWidth"
      (backdropClick)="close(); onBlur()"
      (attach)="onAttached()"
      (detach)="close()"
    >
      <div class="rds-select__panel-wrapper" [@transformPanelWrap]="null">
        <div
          #panel
          class="rds-select__panel"
          aria-multiselectable="true"
          [attr.id]="id + '-panel'"
          [@transformPanel]="'showing'"
          (@transformPanel.done)="panelDoneAnimatingStream.next($event.toState)"
          [style.transformOrigin]="transformOrigin"
          (keydown)="handleKeydown($event)"
        >
          <ng-content select="rds-select-search-input" />
          <ng-content select="rds-select-all-option" />
          <ng-content select="rds-multi-select-option" />
        </div>
      </div>
    </ng-template>
  `,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  animations: SelectAnimations,
  providers: [
    {
      provide: RdsAbstractFormFieldControl,
      useExisting: RdsMultiSelectComponent,
    },
  ],
})
export class RdsMultiSelectComponent<T>
  extends RdsAbstractSelectComponent<
    T,
    RdsMultiSelectOptionComponent<T>,
    T[],
    RdsMultiSelectOptionComponent<T>[]
  >
  implements
    AfterContentInit,
    AfterViewInit,
    OnChanges,
    OnDestroy,
    OnInit,
    DoCheck,
    ControlValueAccessor
{
  private readonly dir = inject(Directionality, { optional: true });

  protected override readonly _name = 'rds-multi-select';

  private allOptionsSelected = false;
  private _withChips = false;

  override get selected(): RdsMultiSelectOptionComponent<T>[] {
    return this.selectionModel.selected;
  }

  override get triggerValues(): string[] {
    if (this.empty) {
      return [''];
    }

    return this.selectionModel.selected.map((option) => option.viewValue);
  }

  override get controlType(): RdsControlType {
    if (this.withChips) {
      return 'select-with-chips';
    } else {
      return 'select';
    }
  }

  @Input()
  set withChips(value: BooleanInput) {
    const newValue = booleanAttribute(value);

    if (this._withChips !== newValue) {
      this._withChips = newValue;
      this.stateChanges.emit();
    }
  }

  get withChips(): boolean {
    return this._withChips;
  }

  @ContentChildren(RdsSelectAllOptionComponent)
  selectAllElements: QueryList<RdsSelectAllOptionComponent<T>>;
  @ViewChildren(RdsChipComponent) chips: QueryList<RdsChipComponent<T>>;

  protected focusedChip: RdsChipComponent<T> | null = null;
  private keyManagerChips: FocusKeyManager<RdsChipActionDirective>;
  private readonly chipActions = new QueryList<RdsChipActionDirective>();

  override ngOnInit(): void {
    super.ngOnInit();
    this.selectionModel = new SelectionModel<RdsMultiSelectOptionComponent<T>>(
      true,
    );
  }

  override ngAfterContentInit(): void {
    super.ngAfterContentInit();

    this.selectionModel.changed
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => {
        // check it
        this.calculateAllOptionsSelected();
        this.refreshSelectAllCheck();
        this.cdr.markForCheck();
      });

    this.calculateAllOptionsSelected();

    this.selectAllElements.changes
      .pipe(
        startWith(this.selectAllElements),
        tap(() => this.selectAllDestroy$.next()),
        filter(() => this.selectAllElements.length > 0),
        switchMap(() =>
          merge(
            ...this.selectAllElements.map((all) => all.selectAllChanged),
          ).pipe(
            takeUntil(this.selectAllDestroy$),
            takeUntilDestroyed(this.destroyRef),
          ),
        ),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe(() => {
        this.toggleAllOptions();
      });
  }

  ngAfterViewInit(): void {
    this.setUpFocusManagement();
  }

  override close(): void {
    super.close();

    if (this._panelOpen) {
      this.selectAllElements.forEach((all) => all.setInactiveStyles());
    }
  }

  toggleAllOptions(selectAll = !this.allOptionsSelected): void {
    if (this.allOptionsSelected !== selectAll) {
      this.selectionModel.clear();
      this.allOptionsSelected = selectAll;
      this.selectionModel.select(
        ...this.options.filter((item) =>
          this.allOptionsSelected
            ? item.checked || !item.disabled
            : item.checked && item.disabled,
        ),
      );
      this.sortValues();
      this.propagateChanges();
    }
  }

  selectAllOptions(): void {
    this.toggleAllOptions(true);
  }

  deselectAllOptions(): void {
    this.toggleAllOptions(false);
  }

  protected clearSelectedOptions(event: Event): void {
    event.preventDefault();
    event.stopPropagation();
    this.selectionModel.clear();
    this.propagateChanges();
  }

  protected override handleClosedKeydown(event: KeyboardEvent): void {
    if (this.withChips) {
      return;
    }

    const keyCode = event.keyCode;
    const isArrowKey =
      keyCode === DOWN_ARROW ||
      keyCode === UP_ARROW ||
      keyCode === LEFT_ARROW ||
      keyCode === RIGHT_ARROW;
    const isOpenKey = keyCode === ENTER || keyCode === SPACE;
    const manager = this.keyManager;

    // Open the select on ALT + arrow key to match the native <select>
    if (
      (!manager.isTyping() && isOpenKey && !hasModifierKey(event)) ||
      isArrowKey
    ) {
      event.preventDefault(); // prevents the page from scrolling down when pressing space
      this.open();
    }
  }

  protected override handleOpenKeydown(event: KeyboardEvent): void {
    const manager = this.keyManager;
    const keyCode = event.keyCode;
    const isArrowKey = keyCode === DOWN_ARROW || keyCode === UP_ARROW;
    const isTyping = manager.isTyping();

    if (isArrowKey && event.altKey) {
      // Close the select on ALT + arrow key to match the native <select>
      event.preventDefault();
      this.close();
      // Don't do anything in this case if the user is typing,
      // because the typing sequence can include the space key.
    } else if (
      !isTyping &&
      (keyCode === ENTER || keyCode === SPACE) &&
      manager.activeItem &&
      !hasModifierKey(event)
    ) {
      event.preventDefault();
      manager.activeItem.handleClick();
    } else if (!isTyping && keyCode === A && event.ctrlKey) {
      event.preventDefault();
      this.toggleAllOptions();
      this.selectAllElements.forEach((option) => option.markForCheck());
    } else {
      const previouslyFocusedIndex = manager.activeItemIndex;

      manager.onKeydown(event);

      if (
        isArrowKey &&
        event.shiftKey &&
        manager.activeItem &&
        manager.activeItemIndex !== previouslyFocusedIndex
      ) {
        manager.activeItem.handleClick();
      }
    }
  }

  protected override setSelectionByValue(value: T[]): void {
    if (value) {
      this.selectionModel.clear();
      value.forEach((currentValue) => this.selectValue(currentValue));
      this.sortValues();
    }

    this.cdr.markForCheck();
  }

  protected override resetOptions(): void {
    super.resetOptions();

    this.optionSelectionChanges
      .pipe(
        takeUntil(this.options.changes),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe((event) => this.onSelect(event.source));
    // Listen to changes in the internal state of the options and react accordingly.
    // Handles cases like the labels of the selected options changing.
    merge(...this.options.map((option) => option.checkedChanged))
      .pipe(
        // should emit one time if states changes for many options
        debounceTime(1),
        takeUntil(this.options.changes),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe(() => {
        this.calculateAllOptionsSelected();
        this.refreshSelectAllCheck();
        this.selectAllElements.forEach((option) => option.markForCheck());
        this.stateChanges.next();
      });

    this.calculateAllOptionsSelected();
    this.refreshSelectAllCheck();
  }

  protected override onSelect(option: RdsMultiSelectOptionComponent<T>): void {
    super.onSelect(option);
    this.sortValues();
    this.stateChanges.next();
  }

  private sortValues(): void {
    const options = this.options.toArray();

    this.selectionModel.sort((a, b) => {
      return this.sortComparator
        ? this.sortComparator(a, b, options)
        : options.indexOf(a) - options.indexOf(b);
    });
    this.stateChanges.next();
  }

  protected getValueToEmit(): T[] {
    return this.selected.map((s) => s.value);
  }

  private setUpFocusManagement(): void {
    this.chips.changes
      .pipe(startWith(this.chips), takeUntilDestroyed(this.destroyRef))
      .subscribe((chips: QueryList<RdsChipComponent<T>>) => {
        const actions: RdsChipActionDirective[] = [];
        chips.forEach((chip) =>
          chip.getActions().forEach((action) => actions.push(action)),
        );
        this.chipActions.reset(actions);
        this.chipActions.notifyOnChanges();
      });

    this.keyManagerChips = new FocusKeyManager(this.chipActions)
      .withVerticalOrientation()
      .withHorizontalOrientation(this.dir ? this.dir.value : 'ltr')
      .withHomeAndEnd()
      .skipPredicate((action) => action.disabled);

    this.chips.changes
      .pipe(
        startWith(null),
        switchMap(() => merge(...this.chips.map((c) => c.focus$))),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe(({ source: chip }) => {
        const action = chip.getSourceAction(document.activeElement as Element);

        if (action) {
          this.keyManagerChips.updateActiveItem(action);
        }
      });
    this.chips.changes
      .pipe(
        startWith(null),
        switchMap(() =>
          merge(
            ...this.chips.map((c) =>
              c.focus$.pipe(map((e) => ({ focused: true, source: e.source }))),
            ),
            ...this.chips.map((c) =>
              c.blur$.pipe(map((e) => ({ focused: false, source: e.source }))),
            ),
          ),
        ),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe((chip) => {
        if (chip.focused) {
          this.focusedChip = chip.source;
        } else if (this.focusedChip === chip.source) {
          this.focusedChip = null;
        }

        this.cdr.markForCheck();
      });

    this.dir?.change
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((direction) =>
        this.keyManagerChips.withHorizontalOrientation(direction),
      );
  }

  private refreshSelectAllCheck(): void {
    this.selectAllElements.forEach(
      (all) => (all.checked = this.allOptionsSelected),
    );
    this.cdr.markForCheck();
  }

  private calculateAllOptionsSelected(): void {
    this.allOptionsSelected = this.options
      .toArray()
      .filter((item) => !item.disabled)
      .every((item) => item.checked);
    this.cdr.markForCheck();
  }

  protected override _setSize(size: RdsFormFieldSize): void {
    super._setSize(size);
    this.selectAllElements?.forEach((element) => (element.size = size));
  }
}
