import {
  ApplicationRef,
  ComponentFactoryResolver,
  ComponentRef,
  EmbeddedViewRef,
} from '@angular/core';
import { RdsBasePortalHost, RdsComponentPortal } from './rds-component-portal';

export class RdsDomPortalHost<T> extends RdsBasePortalHost<T> {
  constructor(
    private readonly _hostDomElement: Element,
    private readonly _componentFactoryResolver: ComponentFactoryResolver,
    private readonly _applicationRef: ApplicationRef,
  ) {
    super();
  }

  attachComponentPortal(
    portal: RdsComponentPortal<T>,
    newestOnTop: boolean,
  ): ComponentRef<T> {
    const componentFactory =
      this._componentFactoryResolver.resolveComponentFactory(portal.component);
    const componentRef = componentFactory.create(portal.injector);
    this._applicationRef.attachView(componentRef.hostView);

    this.setDisposeFn(() => {
      this._applicationRef.detachView(componentRef.hostView);
      componentRef.destroy();
    });

    if (newestOnTop) {
      const newChild = this._getComponentRootNode(componentRef);
      const refChild = this._hostDomElement.firstChild;
      this._hostDomElement.insertBefore(newChild, refChild);
    } else {
      this._hostDomElement.appendChild(
        this._getComponentRootNode(componentRef),
      );
    }

    return componentRef;
  }

  private _getComponentRootNode(componentRef: ComponentRef<T>): HTMLElement {
    return (componentRef.hostView as EmbeddedViewRef<T>)
      .rootNodes[0] as HTMLElement;
  }
}
