import { BooleanInput } from '@angular/cdk/coercion';
import {
  AfterContentInit,
  Attribute,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  DestroyRef,
  Directive,
  forwardRef,
  HostBinding,
  HostListener,
  inject,
  Input,
  numberAttribute,
  OnChanges,
  OnDestroy,
  SimpleChanges,
  ViewEncapsulation,
} from '@angular/core';
import { merge, of, Subscription } from 'rxjs';
import { RdsDatepickerIntl } from './datepicker.intl';
import { RdsDatepickerControl, RdsDatepickerPanel } from './datepicker-base';
import { ExtractDateTypeFromSelection } from './date-selection.model';
import { RdsFormFieldComponent, RdsFormFieldSize } from '../form-field';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { TAB_INDEX_DEFAULT } from '../utils';

@Directive({
  selector: '[rdsDatepickerToggleIcon], [rds-datepicker-toggle-icon]',
})
export class RdsDatepickerToggleIconDirective {}

@Component({
  selector: 'rds-datepicker-toggle',
  templateUrl: './datepicker-toggle.component.html',
  styleUrls: ['./datepicker-toggle.component.scss'],
  exportAs: 'rdsDatepickerToggle',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsDatepickerToggleComponent<
    S,
    D = ExtractDateTypeFromSelection<S>,
  >
  implements AfterContentInit, OnChanges, OnDestroy
{
  public readonly intl = inject(RdsDatepickerIntl);
  private readonly destroyRef = inject(DestroyRef);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly formField = inject<RdsFormFieldComponent<D>>(
    forwardRef(() => RdsFormFieldComponent),
    { optional: true },
  );

  private _stateChanges = Subscription.EMPTY;

  @HostBinding('class') get classes(): string[] {
    const classes = ['rds-datepicker-toggle'];

    if (this.forDatepicker && this.forDatepicker.opened) {
      classes.push('rds-datepicker-toggle-active');
    }

    return classes;
  }

  @HostBinding('attr.data-rds-calendar') get dataRdsCalendar(): string | null {
    return this.forDatepicker ? this.forDatepicker.id : null;
  }

  @HostBinding('attr.tabindex') _tabIndex = null;

  @Input() forDatepicker?: RdsDatepickerPanel<RdsDatepickerControl<D>, S, D>;
  @Input({ transform: numberAttribute }) tabIndex: number | null;
  @Input('aria-label') ariaLabel = '';

  @Input()
  set disabled(value: BooleanInput) {
    const newValue = value === undefined ? undefined : booleanAttribute(value);

    if (newValue !== this._disabled) {
      this._disabled = newValue;
      this.cdr.markForCheck();
    }
  }

  get disabled(): boolean {
    if (this._disabled === undefined && this.forDatepicker) {
      return this.forDatepicker.disabled;
    }

    return this._disabled || false;
  }

  private _disabled: boolean | undefined;

  @ContentChild(RdsDatepickerToggleIconDirective)
  _customIcon: RdsDatepickerToggleIconDirective | null = null;

  get _buttonSize(): RdsFormFieldSize {
    if (this.formField) {
      return this.formField?.size !== 'l' ? this.formField?.size : 'm';
    } else {
      return 's';
    }
  }

  @HostListener('click', ['$event'])
  _onClick($event: Event): void {
    this._open($event);
  }

  constructor(@Attribute('tabindex') tabIndex?: string) {
    const parsedTabIndex = numberAttribute(tabIndex, TAB_INDEX_DEFAULT);
    this.tabIndex =
      parsedTabIndex || parsedTabIndex === 0 ? parsedTabIndex : null;
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['datepicker']) {
      this._watchStateChanges();
    }
  }

  ngOnDestroy(): void {
    this._stateChanges.unsubscribe();
  }

  ngAfterContentInit(): void {
    this._watchStateChanges();

    if (this.formField) {
      this.formField.control.stateChanges
        .pipe(takeUntilDestroyed(this.destroyRef))
        .subscribe(() => this.cdr.markForCheck());
    }
  }

  _open(event: Event): void {
    if (this.forDatepicker && !this.disabled) {
      this.forDatepicker.open();
      event.stopPropagation();
    }
  }

  private _watchStateChanges(): void {
    const datepickerStateChanged = this.forDatepicker
      ? this.forDatepicker.stateChanges.asObservable()
      : of(null);
    const inputStateChanged =
      this.forDatepicker && this.forDatepicker.datepickerInput
        ? this.forDatepicker.datepickerInput.stateChanges
        : of(null);
    const datepickerToggled = this.forDatepicker
      ? merge(this.forDatepicker.openedStream, this.forDatepicker.closedStream)
      : of(null);

    this._stateChanges.unsubscribe();
    this._stateChanges = merge(
      this.intl.changes,
      datepickerStateChanged,
      inputStateChanged,
      datepickerToggled,
    ).subscribe(() => this.cdr.markForCheck());
  }
}
