import { CdkTree } from '@angular/cdk/tree';
import {
  ChangeDetectionStrategy,
  Component,
  HostBinding,
  Input,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { RdsTreeNodeOutletDirective } from './tree-node-outlet.directive';
import { RdsTreeControl } from './data-source/tree-control';

export type RdsTreeNodeSize = 's' | 'm';

@Component({
  selector: 'rds-tree',
  exportAs: 'rdsTree',
  template: ` <ng-container rdsTreeNodeOutlet /> `,
  styleUrls: ['tree.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.Default,
  providers: [{ provide: CdkTree, useExisting: RdsTreeComponent }],
})
export class RdsTreeComponent<T> extends CdkTree<T> {
  @HostBinding('class') class = 'rds-tree';
  @HostBinding('attr.role') role = 'tree';
  @ViewChild(RdsTreeNodeOutletDirective, { static: true })
  override _nodeOutlet: RdsTreeNodeOutletDirective;
  @Input()
  override treeControl: RdsTreeControl<T>;
  @Input()
  size: RdsTreeNodeSize = 's';
}
