import { Directive, ElementRef, HostBinding, inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { TAB_INDEX_NOT_FOCUSABLE } from '../utils';

@Directive({
  selector: 'span[rdsChipEditInput], span[rds-chip-edit-input]',
})
export class RdsChipEditInputDirective {
  private readonly elementRef = inject(ElementRef);
  private readonly document = inject(DOCUMENT);

  @HostBinding('class')
  private readonly classes = 'rds-chip-edit-input';

  @HostBinding('attr.role')
  private readonly role = 'textbox';

  @HostBinding('tabIndex')
  private readonly tabindex = TAB_INDEX_NOT_FOCUSABLE;

  @HostBinding('attr.contenteditable')
  private readonly contenteditable = true;

  initialize(initialValue: string): void {
    this.getNativeElement().focus();
    this.setValue(initialValue);
  }

  getNativeElement(): HTMLElement {
    return this.elementRef.nativeElement;
  }

  setValue(value: string): void {
    this.getNativeElement().textContent = value;
    this.moveCursorToEndOfInput();
  }

  getValue(): string {
    return this.getNativeElement().textContent || '';
  }

  private moveCursorToEndOfInput(): void {
    const range = this.document.createRange();
    range.selectNodeContents(this.getNativeElement());
    range.collapse(false);
    const sel = window.getSelection();

    if (sel) {
      sel.removeAllRanges();
      sel.addRange(range);
    }
  }
}
