import {
  ControlValueAccessor,
  FormControl,
  FormControlDirective,
  FormControlName,
  FormGroupDirective,
  NG_VALUE_ACCESSOR,
  NgControl,
  NgForm,
  NgModel,
} from '@angular/forms';
import {
  AfterContentInit,
  booleanAttribute,
  ChangeDetectorRef,
  ContentChildren,
  DestroyRef,
  Directive,
  DoCheck,
  EventEmitter,
  forwardRef,
  HostBinding,
  inject,
  InjectionToken,
  Injector,
  Input,
  OnInit,
  Output,
  QueryList,
} from '@angular/core';
import {
  RdsRadioButtonComponent,
  RdsRadioButtonPosition,
} from './radio-button.component';
import { RdsRadioChangedEvent } from './radio-button-changed-event';
import { tap } from 'rxjs/operators';
import { updateErrorState } from '../utils';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

/**
 * Provider Expression that allows rds-radio-group to register as a ControlValueAccessor. This
 * allows it to support [(ngModel)] and ngControl.
 */
// eslint-disable-next-line @typescript-eslint/no-explicit-any
export const RDS_RADIO_GROUP = new InjectionToken<RdsRadioGroupDirective<any>>(
  'RDS_RADIO_GROUP',
);

/**
 * Injection token that can be used to inject instances of `RdsRadioGroupDirective`. It serves as
 * alternative token to the actual `RdsRadioGroupDirective` class which could cause unnecessary
 * retention of the class and its component metadata.
 */
export const RDS_RADIO_GROUP_CONTROL_VALUE_ACCESSOR = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => RdsRadioGroupDirective),
  multi: true,
};

let nextUniqueId = 0;

@Directive({
  selector: 'rds-radio-group',
  providers: [
    RDS_RADIO_GROUP_CONTROL_VALUE_ACCESSOR,
    { provide: RDS_RADIO_GROUP, useExisting: RdsRadioGroupDirective },
  ],
})
export class RdsRadioGroupDirective<T>
  implements OnInit, AfterContentInit, ControlValueAccessor, DoCheck
{
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly destroyRef = inject(DestroyRef);
  private readonly injector = inject(Injector);
  readonly parentForm = inject(NgForm, { optional: true });
  readonly parentFormGroup = inject(FormGroupDirective, { optional: true });

  private _value: T | null;
  private _name = `rds-radio-group-${nextUniqueId++}`;
  private _selected: RdsRadioButtonComponent<T> | null = null;
  private _isInitialized = false;
  private _labelPosition: RdsRadioButtonPosition = 'after';
  private _errorState = false;

  control!: FormControl;

  @ContentChildren(forwardRef(() => RdsRadioButtonComponent), {
    descendants: true,
  })
  _radios: QueryList<RdsRadioButtonComponent<T>>;

  @HostBinding('class') readonly hostClass = 'rds-radio-group';

  @Input()
  get name(): string {
    return this._name;
  }
  set name(value: string) {
    this._name = value;
    this.updateRadioButtonNames();
  }

  @Input()
  get labelPosition(): RdsRadioButtonPosition {
    return this._labelPosition;
  }
  set labelPosition(v: RdsRadioButtonPosition) {
    const oldPosition = this._labelPosition;
    this._labelPosition = v === 'before' ? 'before' : 'after';

    if (oldPosition !== this._labelPosition) {
      this._radios?.forEach((r) => (r.labelPosition = this.labelPosition));
      this.markRadiosForCheck();
    }
  }

  @Input()
  get value(): T | null {
    return this._value;
  }
  set value(newValue: T | null) {
    if (this._value !== newValue) {
      this._value = newValue;

      this.updateSelectedRadioFromValue();
      this.checkSelectedRadioButton();
    }
  }

  @Input()
  get selected(): RdsRadioButtonComponent<T> | null {
    return this._selected;
  }
  set selected(selected: RdsRadioButtonComponent<T> | null) {
    this._selected = selected;
    this.value = selected ? selected.value : null;
    this.checkSelectedRadioButton();
  }

  @Input({ transform: booleanAttribute }) disabled = false;
  @Input({ transform: booleanAttribute }) required = false;

  @Output() buttonChanged = new EventEmitter<RdsRadioChangedEvent<T>>();

  // eslint-disable-next-line @typescript-eslint/no-empty-function
  _controlValueAccessorChangeFn: (value: T) => void = () => {};

  // eslint-disable-next-line @typescript-eslint/no-empty-function
  onTouched: () => void = () => {};

  ngOnInit(): void {
    const injectedControl = this.injector.get(NgControl);

    switch (injectedControl.constructor) {
      case NgModel: {
        const { control, update } = injectedControl as NgModel;

        this.control = control;

        this.control.valueChanges
          .pipe(
            tap((value: T) => update.emit(value)),
            takeUntilDestroyed(this.destroyRef),
          )
          .subscribe();
        break;
      }

      case FormControlName: {
        this.control = this.injector
          .get(FormGroupDirective)
          .getControl(injectedControl as FormControlName);
        break;
      }

      default: {
        this.control = (injectedControl as FormControlDirective)
          .form as FormControl;
        break;
      }
    }
  }

  ngAfterContentInit(): void {
    // Mark this component as initialized in AfterContentInit because the initial value can
    // possibly be set by NgModel on RdsRadioGroupDirective, and it is possible that the OnInit of the
    // NgModel occurs *after* the OnInit of the RdsRadioGroupDirective.
    this._isInitialized = true;
    this._radios?.forEach((r) => (r.labelPosition = this.labelPosition));
    this.markRadiosForCheck();
  }

  ngDoCheck(): void {
    if (this.control) {
      // We need to re-evaluate this on every change detection cycle, because there are some
      // error triggers that we can't subscribe to (e.g. parent form submissions). This means
      // that whatever logic is in here has to be super lean or we risk destroying the performance.
      this._errorState = updateErrorState(
        this._errorState,
        this.parentFormGroup,
        this.parentForm,
        this.control,
      );
    }
  }

  checkSelectedRadioButton(): void {
    if (this._selected && !this._selected.checked) {
      this._selected.checked = true;
    }
  }

  touch(): void {
    if (this.onTouched) {
      this.onTouched();
    }
  }

  private updateRadioButtonNames(): void {
    if (this._radios) {
      this._radios.forEach((radio) => {
        radio.name = this.name;
        radio.markForCheck();
      });
    }
  }

  private updateSelectedRadioFromValue(): void {
    const isAlreadySelected =
      this._selected !== null && this._selected.value === this._value;

    if (this._radios && !isAlreadySelected) {
      this._selected = null;
      this._radios.forEach((radio) => {
        radio.checked = this.value === radio.value;

        if (radio.checked) {
          this._selected = radio;
        }
      });
    }
  }

  emitChangeEvent(): void {
    if (this._isInitialized) {
      this.buttonChanged.emit(
        new RdsRadioChangedEvent<T>(this._selected, this._value),
      );
    }
  }

  markRadiosForCheck(): void {
    if (this._radios) {
      this._radios.forEach((radio) => radio.markForCheck());
    }
  }

  writeValue(value: T): void {
    this.value = value;
    this.cdr.markForCheck();
  }

  registerOnChange(fn: (value: T) => void): void {
    this._controlValueAccessorChangeFn = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    if (this.disabled !== isDisabled) {
      this.disabled = isDisabled;
      this.cdr.markForCheck();
    }
  }
}
