import {
  AfterViewInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  DestroyRef,
  ElementRef,
  inject,
  Input,
  ViewEncapsulation,
} from '@angular/core';
import { merge } from 'rxjs';
import { RdsFooterCustomContentDirective } from './footer-custom-content.directive';
import { RdsFooterDisclaimerDirective } from './footer-disclaimer.directive';
import { RdsFooterNavigationDirective } from './footer-navigation.directive';
import { RdsFooterSitemapDirective } from './footer-sitemap.directive';
import { RdsFooterIntl } from './footer.intl';
import { ContentObserver } from '@angular/cdk/observers';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

@Component({
  selector: 'rds-footer, footer[rds-footer], footer[rdsFooter]',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsFooterComponent implements AfterViewInit {
  protected readonly intl = inject(RdsFooterIntl);
  private readonly destroyRef = inject(DestroyRef);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly elementRef = inject(ElementRef);
  private readonly contentObserver = inject(ContentObserver);

  @ContentChild(RdsFooterSitemapDirective, { static: false })
  _sitemap: RdsFooterSitemapDirective;
  @ContentChild(RdsFooterCustomContentDirective, { static: false })
  _customContent: RdsFooterCustomContentDirective;
  @ContentChild(RdsFooterNavigationDirective, { static: false })
  _navigation: RdsFooterNavigationDirective;
  @ContentChild(RdsFooterDisclaimerDirective, { static: false })
  _disclaimer: RdsFooterDisclaimerDirective;

  @Input({ transform: booleanAttribute }) neutral = false;

  ngAfterViewInit(): void {
    merge(this.intl.changes, this.contentObserver.observe(this.elementRef))
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this.cdr.markForCheck());
  }
}
