import { ValidationErrors } from '@angular/forms';
import { RdsAbstractAutocompleteComponent } from './abstract-autocomplete.component';

const INPUT_FILLED_ERROR_KEY = 'rdsInputFilled';

export class RdsAutocompleteValidators {
  static inputFilled<T, ArrayOfFlatType extends T | T[]>(
    autocomplete: RdsAbstractAutocompleteComponent<T, ArrayOfFlatType>,
  ): ValidationErrors | null {
    return autocomplete && autocomplete.isInputFilled()
      ? {
          [INPUT_FILLED_ERROR_KEY]: {
            filledValue: autocomplete.getInputFilledValue(),
          },
        }
      : null;
  }
}
