import {
  AfterViewInit,
  ChangeDetectorRef,
  DestroyRef,
  Directive,
  ElementRef,
  HostBinding,
  inject,
  Input,
  NgZone,
  OnInit,
} from '@angular/core';
import { RdsDialogRef } from './model';
import { RdsDialogService } from './dialog.service';
import { getClosestDialog } from './get-closet-dialog';
import { debounceTime } from 'rxjs';
import { ContentObserver } from '@angular/cdk/observers';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

let nextId = 0;

@Directive({
  selector: '[rds-dialog-header], [rdsDialogHeader]',
})
export class RdsDialogHeaderDirective<T, R> implements AfterViewInit, OnInit {
  private readonly dialogService = inject(RdsDialogService);
  private readonly destroyRef = inject(DestroyRef);
  private readonly elementRef = inject(ElementRef);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly ngZone = inject(NgZone);
  private readonly contentObserver = inject(ContentObserver);
  private dialogRef = inject<RdsDialogRef<T, R>>(RdsDialogRef, {
    optional: true,
  });

  private _id = `rds-dialog-header-${nextId++}`;

  @HostBinding('style.width')
  private _width = '100%';

  @HostBinding('class') readonly hostClass = 'rds-dialog__header';

  @Input()
  @HostBinding('id')
  get id(): string {
    return this._id;
  }

  set id(newValue: string) {
    this._id = newValue;
  }

  ngOnInit(): void {
    if (!this.dialogRef) {
      const dialogRef = getClosestDialog(
        this.elementRef,
        this.dialogService.openDialogs,
      );

      if (dialogRef) {
        this.dialogRef = dialogRef;
      }
    }

    if (this.dialogRef) {
      Promise.resolve().then(() => {
        if (this.dialogRef && !this.dialogRef.dialogContainer.ariaLabelledBy) {
          this.dialogRef.dialogContainer.ariaLabelledBy = this.id;
        }
      });
    }
  }

  ngAfterViewInit(): void {
    this.ngZone.runOutsideAngular(() => this._refreshWidth());
    this.contentObserver
      .observe(this.elementRef)
      .pipe(debounceTime(50), takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this._refreshWidth());
  }

  private _refreshWidth(): void {
    const contentElement = this.elementRef.nativeElement;
    const parentElement = contentElement.parentElement as HTMLElement;

    const iconPlaceholder = parentElement.querySelector(
      '.rds-dialog__icon-placeholder',
    );

    const iconsWidth = iconPlaceholder?.clientWidth || 0;

    Promise.resolve().then(() => {
      const newWidth = `calc(100% - ${iconsWidth}px)`;

      if (newWidth !== this._width) {
        this.ngZone.run(() => {
          this._width = newWidth;
          this.cdr.markForCheck();
        });
      }
    });
  }
}
