import { ENTER, hasModifierKey } from '@angular/cdk/keycodes';
import {
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  EventEmitter,
  HostBinding,
  Input,
  Output,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';

@Component({
  selector: 'rds-header-searchbar',
  template: `
    <button
      rds-icon-button
      size="m"
      class="rds-header-search-search-icon"
      (click)="emitValue()"
    >
      <rds-icon namespace="outlined" icon="search" />
    </button>
    <input
      #input
      [ngModel]="searchValue"
      (ngModelChange)="onInputChange($event)"
      (keydown)="onKeyPress($event)"
      class="rds-header-search-input"
      [placeholder]="getPlaceholder()"
    />
    <button
      rds-icon-button
      (click)="onCloseSearch()"
      class="rds-header-search-close-icon"
    >
      <rds-icon namespace="outlined" icon="close" />
    </button>
  `,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsHeaderSearchbarComponent {
  private readonly _defaultPlaceholder =
    'Search for the products, page, document';

  @HostBinding('class') class = 'rds-header-search';
  @Input() placeholder: string;
  @Input() searchValue = '';
  @Output() searchChange = new EventEmitter<string>();
  @Output() closeSearch = new EventEmitter<void>();
  @Output() search = new EventEmitter<string>();
  @ViewChild('input', { static: true }) searchInput: ElementRef;

  onInputChange(newValue: string): void {
    this.searchValue = newValue;
    this.searchChange.next(this.searchValue);
  }

  onCloseSearch(): void {
    this.closeSearch.emit();
  }

  onKeyPress(event: KeyboardEvent): void {
    if (event.keyCode === ENTER && !hasModifierKey(event)) {
      this.emitValue();
    }
  }

  emitValue(): void {
    this.search.emit(this.searchValue);
  }

  focusSearch(): void {
    this.searchInput.nativeElement.focus();
  }

  getPlaceholder(): string {
    return this.placeholder || this._defaultPlaceholder;
  }
}
