import { InjectionToken } from '@angular/core';

export interface RdsAutocompleteConfig {
  debounce: number;
  minSearchChars: number;
  optionsContainerMaxHeight: number;
  optionsContainerMinWidth: number;
  multiMinimumInputWidth: number;
  multiMinimumInputWidthOffset: number;
}

export const RDS_AUTOCOMPLETE_DEFAULT_CONFIG =
  Object.freeze<RdsAutocompleteConfig>({
    debounce: 1000,
    minSearchChars: 3,
    optionsContainerMaxHeight: 190,
    optionsContainerMinWidth: 170,
    multiMinimumInputWidth: 50,
    multiMinimumInputWidthOffset: 10,
  });

export const RDS_AUTOCOMPLETE_CONFIG =
  new InjectionToken<RdsAutocompleteConfig>('RDS_AUTOCOMPLETE_CONFIG');
