import {
  AfterViewInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  DestroyRef,
  ElementRef,
  EventEmitter,
  HostBinding,
  inject,
  Input,
  NgZone,
  OnDestroy,
  OnInit,
  Output,
  Renderer2,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { rdsHeaderSearchAnimations } from './header-search-animations';
import { fromEvent } from 'rxjs';
import { tap } from 'rxjs/operators';
import { RdsHeaderSearchbarComponent } from './header-searchbar.component';
import { RdsIconButtonComponent } from '../button';
import { FocusMonitor } from '@angular/cdk/a11y';
import { ENTER, hasModifierKey, SPACE } from '@angular/cdk/keycodes';
import { ContentObserver } from '@angular/cdk/observers';
import { AnimationEvent } from '@angular/animations';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

export type RdsHeaderExpandedMode = 'expanded' | 'collapsed';
export type RdsHeaderSearchPosition = 'none' | 'start' | 'end' | 'animated';

@Component({
  selector: 'rds-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
  animations: [
    rdsHeaderSearchAnimations.headerCollapse,
    rdsHeaderSearchAnimations.searchExpansion,
  ],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsHeaderComponent implements AfterViewInit, OnInit, OnDestroy {
  private readonly destroyRef = inject(DestroyRef);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly renderer = inject(Renderer2);
  private readonly elementRef = inject(ElementRef);
  private readonly ngZone = inject(NgZone);
  private readonly focusMonitor = inject(FocusMonitor);
  private readonly contentObserver = inject(ContentObserver);

  searchMode = false;

  @Input() scrollTarget:
    | HTMLElement
    | ElementRef<HTMLElement>
    | string
    | undefined
    | null;
  @Input() searchbarPlaceholder: string;
  @Input() searchValue = '';

  @Output() search = new EventEmitter<string>();
  @Output() searchChange = new EventEmitter<string>();
  @Output() logoTriggered = new EventEmitter<MouseEvent | KeyboardEvent>();

  @HostBinding('class') class = 'rds-header';
  @ViewChild(RdsHeaderSearchbarComponent, { static: false })
  searchbar: RdsHeaderSearchbarComponent;
  @ViewChild('searchIcon', { static: false })
  searchIcon: RdsIconButtonComponent;
  @ViewChild('logoElement', { static: true }) logoElement: ElementRef;

  @Input()
  get searchPosition(): RdsHeaderSearchPosition {
    return this._searchPosition;
  }

  set searchPosition(value: RdsHeaderSearchPosition) {
    this._searchPosition = value;
  }

  @Input({ transform: booleanAttribute })
  @HostBinding('class.rds-header-minimal-margins')
  minimalMargins = false;

  private _searchPosition: RdsHeaderSearchPosition = 'none';

  ngOnInit(): void {
    this.ngZone.runOutsideAngular(() => {
      const scrollTarget = this.getScrollTarget();
      fromEvent(scrollTarget, 'scroll')
        .pipe(takeUntilDestroyed(this.destroyRef))
        .subscribe(() => {
          const scrollTop =
            scrollTarget instanceof HTMLElement
              ? scrollTarget.scrollTop
              : scrollTarget.scrollY;

          if (
            scrollTop === 0 &&
            this.elementRef.nativeElement.classList.contains('rds-header-fixed')
          ) {
            this.ngZone.run(() =>
              this.renderer.removeClass(
                this.elementRef.nativeElement,
                'rds-header-fixed',
              ),
            );
          } else if (
            scrollTop !== 0 &&
            !this.elementRef.nativeElement.classList.contains(
              'rds-header-fixed',
            )
          ) {
            this.ngZone.run(() =>
              this.renderer.addClass(
                this.elementRef.nativeElement,
                'rds-header-fixed',
              ),
            );
          }
        });
    });
  }

  ngAfterViewInit(): void {
    this.focusMonitor.monitor(this.logoElement.nativeElement, true);
    this.contentObserver
      .observe(this.elementRef.nativeElement)
      .pipe(
        tap(() => this.cdr.markForCheck()),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe();
  }

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.logoElement.nativeElement);
  }

  startSearching(): void {
    this.searchMode = true;
  }

  endSearching(): void {
    this.searchMode = false;
  }

  getExpandedState(): RdsHeaderExpandedMode {
    return this.searchMode ? 'expanded' : 'collapsed';
  }

  getCollapsedState(): RdsHeaderExpandedMode {
    return this.searchMode ? 'collapsed' : 'expanded';
  }

  onSearch(value: string): void {
    this.search.emit(value);
  }

  emitSearch(event: Event): void {
    if (
      event instanceof KeyboardEvent &&
      event.keyCode === ENTER &&
      !hasModifierKey(event)
    ) {
      this.search.emit(this.searchValue);
    }
  }

  onLogoClick(event: MouseEvent | KeyboardEvent): void {
    if (
      (event instanceof KeyboardEvent &&
        !hasModifierKey(event, 'shiftKey', 'altKey', 'ctrlKey') &&
        (event.keyCode === SPACE || event.keyCode === ENTER)) ||
      event instanceof MouseEvent
    ) {
      if (event instanceof KeyboardEvent && event.keyCode === SPACE) {
        event.stopPropagation();
        event.preventDefault();
      }

      this.logoTriggered.next(event);
    }
  }

  focusSearchInput(state: AnimationEvent): void {
    if (this.searchbar && state.fromState !== 'void') {
      if (this.getExpandedState() === 'expanded') {
        Promise.resolve().then(() => this.searchbar.focusSearch());
      } else if (this.getExpandedState() === 'collapsed') {
        Promise.resolve().then(() => this.searchIcon?.focus());
      }
    }
  }

  private getScrollTarget(): HTMLElement | Window {
    if (this.scrollTarget instanceof ElementRef) {
      return this.scrollTarget.nativeElement;
    } else if (this.scrollTarget instanceof HTMLElement) {
      return this.scrollTarget;
    } else if (typeof this.scrollTarget === 'string') {
      return document.querySelector(
        this.scrollTarget,
      ) as unknown as HTMLElement;
    } else if (this.scrollTarget === undefined || this.scrollTarget === null) {
      return window;
    } else {
      throw new Error(
        'ScrollTarget for RdsHeader is provided in a wrong format, pass ElementRef, selector or null.',
      );
    }
  }
}
