import { SafeResourceUrl } from '@angular/platform-browser';

export class IconLoaderErrors {
  static noHttpProviderError(): Error {
    return Error(
      'Could not find HttpClient provider for use with Rds Icons. ' +
        'Please provide the HttpClient by `provideHttpClient()` method in the providers for' +
        ' standalone application or include the HttpClientModule from @angular/common/http in your ' +
        'app imports.',
    );
  }

  static iconUrlFailedToSanitize(url: SafeResourceUrl): Error {
    return Error(
      `The URL provided to RdsIconRegistry was not trusted as a resource URL ` +
        `via Angular's DomSanitizer. Attempted URL was "${url}".`,
    );
  }

  static invalidNamespaceOrIconNameError(
    namespace: string,
    iconName: string,
  ): Error {
    return Error(
      `Cannot find icon when namespace or iconName is invalid. Attempted namespace was <${namespace}> and iconName was <${iconName}>`,
    );
  }

  static svgTagNotFound(): Error {
    return Error('<svg> tag not found in svg string');
  }
}
