import { Directive, ElementRef, inject, Renderer2 } from '@angular/core';

@Directive({
  selector: `rds-upload-error, [rds-upload-error], [rdsUploadError]`,
})
export class RdsUploadErrorDirective {
  private readonly nativeElement = inject(ElementRef<HTMLElement>)
    .nativeElement;
  private readonly renderer = inject(Renderer2);

  setId(id: string): void {
    this.renderer.setAttribute(this.nativeElement, 'id', id);
  }
}
