import { FocusableOption, FocusOrigin } from '@angular/cdk/a11y';
import {
  booleanAttribute,
  ChangeDetectionStrategy,
  Component,
  ContentChild,
  ElementRef,
  HostBinding,
  HostListener,
  inject,
  Input,
  Renderer2,
  ViewEncapsulation,
} from '@angular/core';
import { Subject } from 'rxjs';
import { RdsIconComponent } from '../icons';
import { RDS_LIST_ITEM_TEMPLATE } from './list-item.templates';
import { TAB_INDEX_NOT_FOCUSABLE } from '../utils';

export type RdsListItemSize = 's' | 'm' | 'l';

@Component({
  template: RDS_LIST_ITEM_TEMPLATE,
  selector: 'button[rds-list-item], button[rdsListItem]',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class RdsListItemComponent implements FocusableOption {
  protected readonly elementRef =
    inject<ElementRef<HTMLButtonElement>>(ElementRef);
  protected readonly renderer = inject(Renderer2);

  @ContentChild(RdsIconComponent) protected icon: RdsIconComponent;

  readonly _focused = new Subject<RdsListItemComponent>();

  @Input() size: RdsListItemSize = 'm';

  @Input()
  @HostBinding('attr.role')
  role: 'menuitem' | 'option' = 'menuitem';

  @HostBinding('tabIndex')
  protected readonly tabIndex = TAB_INDEX_NOT_FOCUSABLE;

  @HostBinding('class')
  protected get cssClasses(): string[] {
    return [
      'rds-list-item',
      `rds-list-item--${this.size}`,
      'rds-focus-indicator',
    ];
  }

  @HostBinding('class.rds-list-item--disabled')
  @Input({ transform: booleanAttribute })
  disabled = false;

  @HostBinding('attr.aria-disabled')
  protected get attrAriaDisabled(): string {
    return this.disabled.toString();
  }

  @HostBinding('attr.disabled')
  protected get attrDisabled(): 'disabled' | null {
    return this.disabled ? 'disabled' : null;
  }

  @HostListener('focus')
  focus(origin?: FocusOrigin): void {
    if (origin === 'keyboard') {
      this.renderer.addClass(this.elementRef.nativeElement, 'cdk-focused');
      this.renderer.addClass(
        this.elementRef.nativeElement,
        'cdk-keyboard-focused',
      );
    }

    this._focused.next(this);
  }

  @HostListener('blur')
  blur(): void {
    this.renderer.removeClass(this.elementRef.nativeElement, 'cdk-focused');
    this.renderer.removeClass(
      this.elementRef.nativeElement,
      'cdk-keyboard-focused',
    );
    this.markAsInactive();
  }

  click(): void {
    this.elementRef.nativeElement.click();
  }

  getLabel(): string {
    const clone = this.elementRef.nativeElement.cloneNode(true) as HTMLElement;
    this.removeNodes(clone.querySelectorAll('rds-icon'));
    this.removeNodes(clone.querySelectorAll('rds-avatar'));
    this.removeNodes(clone.querySelectorAll('rds-pseudo-checkbox'));

    return clone.textContent?.trim() || '';
  }

  markAsActive(): void {
    this.renderer.addClass(
      this.elementRef.nativeElement,
      'rds-list-item-active',
    );
  }

  markAsInactive(): void {
    this.renderer.removeClass(
      this.elementRef.nativeElement,
      'rds-list-item-active',
    );
  }

  private removeNodes(nodes: NodeListOf<Element>): void {
    for (let i = 0; i < nodes.length; i++) {
      nodes[i].remove();
    }
  }
}
