import {
  FocusableOption,
  FocusMonitor,
  FocusOptions,
  FocusOrigin,
} from '@angular/cdk/a11y';
import {
  AfterContentChecked,
  AfterContentInit,
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  forwardRef,
  HostBinding,
  HostListener,
  inject,
  Input,
  OnChanges,
  OnDestroy,
  SimpleChanges,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { throwInvalidSizeError } from '../core/errors';
import { RdsDropdownToggleDirective } from './dropdown-toggle.directive';

export type RdsDropdownToggleButtonSize = 's' | 'm' | 'l';
declare type RdsDropdownToggleButtonIcon = 'caret_up' | 'caret_down';

@Component({
  selector: `
    button[rdsDropdownToggleButton], button[rds-dropdown-toggle-button],
    button[rdsDropdownToggleBorderlessButton], button[rds-dropdown-toggle-borderless-button],
    button[rdsDropdownTogglePrimaryButton], button[rds-dropdown-toggle-primary-button],
    button[rdsDropdownToggleSecondaryButton], button[rds-dropdown-toggle-secondary-button],
    button[rdsDropdownToggleTextButton], button[rds-dropdown-toggle-text-button],
    button[rdsDropdownToggleWarningButton], button[rds-dropdown-toggle-warning-button],
    button[rdsDropdownToggleIconButton], button[rds-dropdown-toggle-icon-button],
  `,
  template: `
    <span class="rds-dropdown-toggle-button__content">
      @if (!showIcon) {
        <ng-content select="rds-icon" />
      }
      @if (showIcon) {
        <ng-content />
      }
    </span>
    @if (showIcon) {
      <rds-icon
        class="rds-dropdown-toggle-button__icon"
        namespace="filled"
        [icon]="icon"
      />
    }
  `,
  styleUrls: ['./dropdown-toggle-button.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: RdsDropdownToggleDirective,
      useExisting: forwardRef(() => RdsDropdownToggleButtonComponent),
    },
  ],
})
export class RdsDropdownToggleButtonComponent
  extends RdsDropdownToggleDirective
  implements
    OnChanges,
    AfterContentInit,
    AfterViewInit,
    AfterContentChecked,
    OnDestroy,
    FocusableOption
{
  protected readonly focusMonitor = inject(FocusMonitor);
  protected readonly cdr = inject(ChangeDetectorRef);

  protected icon: RdsDropdownToggleButtonIcon = 'caret_down';
  protected showIcon = !this._isIconButton();
  private _size: RdsDropdownToggleButtonSize = this._getDefaultSize();

  @ViewChild('imageWrapper', { static: true }) imageWrapper: ElementRef;

  @Input()
  get size(): RdsDropdownToggleButtonSize {
    return this._size;
  }

  set size(size: RdsDropdownToggleButtonSize) {
    if (size !== 's' && size !== 'm' && size !== 'l') {
      throwInvalidSizeError(this, size);
    }

    this._size = size;
  }

  @HostBinding('class')
  override class = '';

  @HostBinding('attr.disabled')
  get isDisabled(): 'disabled' | null {
    return this.disabled ? 'disabled' : null;
  }

  @Input()
  @HostBinding('attr.aria-expanded')
  override get isOpen(): boolean {
    return this._isOpen;
  }

  override set isOpen(isOpen: boolean) {
    this._isOpen = isOpen;
    this.icon = this.isOpen ? 'caret_up' : 'caret_down';
    this.class = this._getCssClass();
    this.cdr.markForCheck();
  }

  @HostListener('keydown.enter', ['$event'])
  override onEnter($event: KeyboardEvent): void {
    this.toggle.next($event);
  }

  override ngOnChanges(changes: SimpleChanges): void {
    super.ngOnChanges(changes);
    this.showIcon = !this._isIconButton();
    this.class = this._getCssClass();
  }

  override ngAfterContentInit(): void {
    super.ngAfterContentInit();
    this.class = this._getCssClass();
  }

  ngAfterViewInit(): void {
    super.ngAfterContentInit();
    this.focusMonitor.monitor(this.elementRef, true);
    this.class = this._getCssClass();
  }

  ngAfterContentChecked(): void {
    super.ngAfterContentInit();
    this.class = this._getCssClass();
  }

  override ngOnDestroy(): void {
    super.ngOnDestroy();
    this.focusMonitor.stopMonitoring(this.elementRef);
  }

  focus(origin: FocusOrigin = 'program', options?: FocusOptions): void {
    this.focusMonitor.focusVia(this.hostElement, origin, options);
  }

  private _isDefault(): boolean {
    return (
      this.hostElement.hasAttribute('rdsDropdownToggleButton') ||
      this.hostElement.hasAttribute('rds-dropdown-toggle-button')
    );
  }
  private _isBorderless(): boolean {
    return (
      this.hostElement.hasAttribute('rdsDropdownToggleBorderlessButton') ||
      this.hostElement.hasAttribute('rds-dropdown-toggle-borderless-button')
    );
  }
  private _isPrimary(): boolean {
    return (
      this.hostElement.hasAttribute('rdsDropdownTogglePrimaryButton') ||
      this.hostElement.hasAttribute('rds-dropdown-toggle-primary-button')
    );
  }
  private _isSecondary(): boolean {
    return (
      this.hostElement.hasAttribute('rdsDropdownToggleSecondaryButton') ||
      this.hostElement.hasAttribute('rds-dropdown-toggle-secondary-button')
    );
  }
  private _isText(): boolean {
    return (
      this.hostElement.hasAttribute('rdsDropdownToggleTextButton') ||
      this.hostElement.hasAttribute('rds-dropdown-toggle-text-button')
    );
  }
  private _isWarning(): boolean {
    return (
      this.hostElement.hasAttribute('rdsDropdownToggleWarningButton') ||
      this.hostElement.hasAttribute('rds-dropdown-toggle-warning-button')
    );
  }
  private _isIconButton(): boolean {
    return (
      this.hostElement.hasAttribute('rdsDropdownToggleIconButton') ||
      this.hostElement.hasAttribute('rds-dropdown-toggle-icon-button')
    );
  }

  private _getCssClass(): string {
    const classes = [
      'rds-dropdown-toggle-button',
      `rds-dropdown-toggle-button--${this.size}`,
    ];

    if (this._isSecondary()) {
      classes.push('rds-dropdown-toggle-button--secondary');
    }

    if (this._isIconButton()) {
      classes.push('rds-dropdown-toggle-button--icon');
    }

    if (this._isPrimary()) {
      classes.push('rds-dropdown-toggle-button--primary');
    }

    if (this._isBorderless()) {
      classes.push('rds-dropdown-toggle-button--borderless');
    }

    if (this._isText()) {
      classes.push('rds-dropdown-toggle-button--text');
    }

    if (this._isWarning()) {
      classes.push('rds-dropdown-toggle-button--warning');
    }

    if (this._isDefault()) {
      classes.push('rds-dropdown-toggle-button--default');
    }

    if (this.isOpen) {
      classes.push('rds-dropdown-toggle-button--open');
    }

    return classes.join(' ');
  }

  get hostElement(): HTMLButtonElement {
    return this.elementRef.nativeElement as HTMLButtonElement;
  }

  private _getDefaultSize(): RdsDropdownToggleButtonSize {
    return 'm';
  }
}
