import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Output,
  ViewEncapsulation,
} from '@angular/core';
import { RdsAbstractSelectableListItemComponent } from '../../list-item';
import { AbstractFocusableOption } from './abstract-focusable-option';
import { RdsSelectAllChangedEvent } from './select-all-changed.event';

let uniqueId = 0;

@Component({
  selector: 'rds-select-all-option',
  template: `
    <rds-pseudo-checkbox noSpacing [checked]="checked" [disabled]="disabled" />
    <span>Select all</span>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: AbstractFocusableOption,
      useExisting: RdsSelectAllOptionComponent,
    },
  ],
})
export class RdsSelectAllOptionComponent<T>
  extends RdsAbstractSelectableListItemComponent
  implements AbstractFocusableOption
{
  id = `rds-select__select-all-${uniqueId++}`;

  @Output()
  readonly selectAllChanged = new EventEmitter<RdsSelectAllChangedEvent<T>>();

  rect(): DOMRect {
    return this.elementRef.nativeElement.getBoundingClientRect();
  }

  isVisible(): boolean {
    return true;
  }

  get hostElement(): HTMLElement {
    return this.elementRef.nativeElement;
  }

  setActiveStyles(): void {
    this.focus('keyboard');
  }

  setInactiveStyles(): void {
    this.blur();
  }

  protected override getSelectTypeClass(): string {
    return 'rds-select__option--select-all';
  }

  override toggle(): void {
    super.toggle();

    this.selectAllChanged.emit(
      new RdsSelectAllChangedEvent(this, this.checked),
    );
    this.cdr.markForCheck();
  }

  handleClick(): void {
    this.toggle();
  }

  markForCheck(): void {
    this.cdr.markForCheck();
  }
}
