import { BooleanInput } from '@angular/cdk/coercion';
import { booleanAttribute, EventEmitter, inject } from '@angular/core';
import {
  FormGroupDirective,
  NgControl,
  NgForm,
  Validators,
} from '@angular/forms';
import { RdsControlType } from './control.type';

export abstract class RdsAbstractFormFieldControl<T> {
  public readonly ngControl = inject(NgControl, { optional: true, self: true });
  public readonly parentForm = inject(NgForm, { optional: true });
  public readonly parentFormGroup = inject(FormGroupDirective, {
    optional: true,
  });

  prefixed: boolean;
  suffixed: boolean;

  readonly stateChanges = new EventEmitter<void>();

  protected _value: T;
  protected _id: string;
  protected _disabled = false;
  protected _focused = false;
  protected _errorState = false;
  protected _required: boolean | undefined;

  abstract get errorState(): boolean;
  abstract get controlType(): RdsControlType;
  abstract get empty(): boolean;
  abstract get shouldLabelFloat(): boolean;
  abstract setDescribedByIds(ids: string[]): void;
  abstract onContainerClick(event: MouseEvent): void;

  get focused(): boolean {
    return this._focused;
  }

  get value(): T {
    return this._value;
  }

  set value(newId: T) {
    this._value = newId;
  }

  get disabled(): boolean {
    return this._disabled;
  }

  set disabled(isDisabled: BooleanInput) {
    this._disabled = booleanAttribute(isDisabled);
  }

  get required(): boolean {
    return (
      this._required ??
      this.ngControl?.control?.hasValidator(Validators.required) ??
      false
    );
  }

  set required(isRequired: BooleanInput) {
    this._required = booleanAttribute(isRequired);
  }

  get id(): string {
    return this._id;
  }

  set id(newId: string) {
    this._id = newId;
  }
}
