import { FocusableOption, FocusMonitor } from '@angular/cdk/a11y';
import {
  AfterViewInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  DestroyRef,
  ElementRef,
  EventEmitter,
  HostBinding,
  HostListener,
  inject,
  Input,
  NgZone,
  OnDestroy,
  Output,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { ContentObserver } from '@angular/cdk/observers';
import { take } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { RdsChipActionDirective } from '../chip-action';
import { BACKSPACE, DELETE, SPACE } from '@angular/cdk/keycodes';
import { RdsChipEvent, RdsChipsSize, RdsChipType } from '../chip.types';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

let nextId = 0;

@Component({
  selector: 'rds-chip, button[rds-chip], button[rdsChip]',
  templateUrl: './chip.component.html',
  styleUrls: ['../chips.common.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsChipComponent<T>
  implements FocusableOption, AfterViewInit, OnDestroy
{
  protected readonly elementRef = inject<ElementRef<HTMLElement>>(ElementRef);
  protected readonly focusMonitor = inject(FocusMonitor);
  protected readonly contentObserver = inject(ContentObserver);
  protected readonly ngZone = inject(NgZone);
  protected readonly destroyRef = inject(DestroyRef);
  public readonly cdr = inject(ChangeDetectorRef);

  private _value: T;
  private hasFocusInternal = false;
  private _pendingFocus = false;
  private _ariaLabelCloseButton?: string;
  private _initialized = false;

  readonly focus$ = new Subject<RdsChipEvent<T>>();
  readonly blur$ = new Subject<RdsChipEvent<T>>();

  @HostBinding('class')
  get hostClasses(): Record<string, boolean> {
    return {
      'rds-chips__chip': true,
      'rds-chips__chip--input': this.closable,
      [`rds-chips--size-${this.size}`]: true,
      [`rds-chips--type-${this.type}`]: true,
    };
  }

  @HostBinding('attr.id')
  readonly id = `rds-chip--${nextId++}`;

  @Input()
  set ariaLabelCloseButton(v: string | undefined) {
    this._ariaLabelCloseButton = v;
  }

  get ariaLabelCloseButton(): string {
    return this._ariaLabelCloseButton || 'close' + this.textContent;
  }

  @Input()
  set size(value: RdsChipsSize) {
    if (this._size !== value) {
      this._size = value;

      if (this._initialized) {
        this.cdr.detectChanges();
      }
    }
  }

  get size(): RdsChipsSize {
    return this._size;
  }

  private _size: RdsChipsSize;

  @Input()
  type: RdsChipType = 'blue';

  @Input()
  @HostBinding('attr.role')
  role: string | null = null;

  @HostBinding('attr.aria-disabled')
  @HostBinding('disabled')
  @Input({ transform: booleanAttribute })
  disabled = false;

  @Input({ transform: booleanAttribute }) closable = false;

  @Input()
  set value(value: T) {
    const oldValue = this._value;
    this._value = value;

    if (this._value !== oldValue) {
      this.cdr.markForCheck();
    }
  }

  get value(): T | string {
    return this._value !== undefined ? this._value : this.innerText;
  }

  get innerText(): string {
    return this.elementRef.nativeElement.innerText;
  }

  @Output()
  readonly closeChip = new EventEmitter<void>();

  @Output() readonly destroyed = new EventEmitter<RdsChipEvent<T>>();

  @ViewChild('primary', { read: RdsChipActionDirective })
  primaryAction?: RdsChipActionDirective;

  @ViewChild('closeButton', { read: RdsChipActionDirective })
  closeAction: RdsChipActionDirective;

  get textContent(): string | null {
    return this.elementRef.nativeElement.textContent;
  }

  constructor() {
    this.monitorFocus();
  }

  ngAfterViewInit(): void {
    this.contentObserver
      .observe(this.elementRef)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this.cdr.markForCheck());

    if (this._pendingFocus) {
      this._pendingFocus = false;
      this.focus();
    }

    this._initialized = true;
  }

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.elementRef);
    this.destroyed.emit({ source: this });
    this.destroyed.complete();
  }

  @HostListener('click', ['$event'])
  @HostListener('keydown', ['$event'])
  handleKeydown(event: PointerEvent | KeyboardEvent): void {
    if (
      this.disabled ||
      // let action chips do their own thing
      (event.target as Element)?.tagName.toLowerCase() === 'button'
    ) {
      return;
    }

    if ('keyCode' in event && event.keyCode !== SPACE) {
      if (event.keyCode === BACKSPACE || event.keyCode === DELETE) {
        event.preventDefault();
        this.closeChip.emit();
      }

      return;
    }

    event.preventDefault();
    this.handlePrimaryActionInteraction();
  }

  focus(): void {
    if (!this.disabled) {
      // If `focus` is called before `ngAfterViewInit`, we won't have access to the primary action.
      // This can happen if the consumer tries to focus a chip immediately after it is added.
      // Queue the method to be called again on init.
      if (this.primaryAction) {
        this.primaryAction.focus();
      } else {
        this._pendingFocus = true;
      }
    }
  }

  onClose(e: Event): void {
    if (!this.disabled) {
      e.stopPropagation();
      this.closeChip.emit();
    }
  }

  hasFocus(): boolean {
    return this.hasFocusInternal;
  }

  /** Handles interactions with the primary action of the chip. */
  handlePrimaryActionInteraction(): void {
    // Empty here, but is overwritten in child classes.
  }

  getSourceAction(target: Node): RdsChipActionDirective | undefined {
    return this.getActions().find((action) => {
      const element = action.nativeElement;

      return element === target || element.contains(target);
    });
  }

  getActions(): RdsChipActionDirective[] {
    const result: RdsChipActionDirective[] = [];

    if (this.primaryAction) {
      result.push(this.primaryAction);
    }

    if (this.closeAction) {
      result.push(this.closeAction);
    }

    return result;
  }

  private monitorFocus(): void {
    this.focusMonitor.monitor(this.elementRef, true).subscribe((origin) => {
      const hasFocus = origin !== null;

      if (hasFocus !== this.hasFocusInternal) {
        this.hasFocusInternal = hasFocus;

        if (hasFocus) {
          this.focus$.next({ source: this });
        } else {
          // When animations are enabled, Angular may end up removing the chip from the DOM a little
          // earlier than usual, causing it to be blurred and throwing off the logic in the chip list
          // that moves focus not the next item. To work around the issue, we defer marking the chip
          // as not focused until the next time the zone stabilizes.
          this.ngZone.onStable
            .pipe(take(1))
            .subscribe(() =>
              this.ngZone.run(() => this.blur$.next({ source: this })),
            );
        }
      }
    });
  }
}
