import { FocusMonitor, FocusOrigin } from '@angular/cdk/a11y';
import { Direction, Directionality } from '@angular/cdk/bidi';
import { NumberInput } from '@angular/cdk/coercion';
import {
  DOWN_ARROW,
  END,
  hasModifierKey,
  HOME,
  LEFT_ARROW,
  PAGE_DOWN,
  PAGE_UP,
  RIGHT_ARROW,
  UP_ARROW,
} from '@angular/cdk/keycodes';
import {
  AfterViewInit,
  Attribute,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  DestroyRef,
  ElementRef,
  EventEmitter,
  forwardRef,
  HostBinding,
  HostListener,
  inject,
  Input,
  NgZone,
  numberAttribute,
  OnDestroy,
  Output,
  Provider,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { ANIMATION_MODULE_TYPE } from '@angular/platform-browser/animations';
import { normalizePassiveListenerOptions } from '@angular/cdk/platform';
import { DOCUMENT } from '@angular/common';
import { noop, timer } from 'rxjs';
import { Point } from '@angular/cdk/drag-drop';
import { delay, tap } from 'rxjs/operators';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { TAB_INDEX_DEFAULT, TAB_INDEX_NOT_FOCUSABLE } from '../utils';

const activeEventOptions = normalizePassiveListenerOptions({ passive: false });

const MIN_AUTO_TICK_SEPARATION = 30;
const DISABLED_THUMB_GAP = 7;
const MIN_VALUE_NONACTIVE_THUMB_GAP = 7;
const MIN_VALUE_ACTIVE_THUMB_GAP = 10;

export const RDS_SLIDER_VALUE_ACCESSOR: Provider = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => RdsSliderComponent),
  multi: true,
};

export class RdsSliderChange {
  source: RdsSliderComponent;
  value: number | null;
}

const ANIMATION_TIME = 400;

@Component({
  selector: 'rds-slider',
  exportAs: 'rdsSlider',
  providers: [RDS_SLIDER_VALUE_ACCESSOR],
  templateUrl: './slider.component.html',
  styleUrls: ['./slider.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsSliderComponent
  implements ControlValueAccessor, OnDestroy, AfterViewInit
{
  private readonly elementRef = inject(ElementRef);
  private readonly destroyRef = inject(DestroyRef);
  private readonly focusMonitor = inject(FocusMonitor);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly dir = inject(Directionality, { optional: true });
  private readonly ngZone = inject(NgZone);
  private readonly document = inject(DOCUMENT);
  private readonly animationMode = inject(ANIMATION_MODULE_TYPE, {
    optional: true,
  });

  @HostBinding('class') class = 'rds-slider rds-focus-indicator';
  @HostBinding('attr.role') role = 'slider';

  @HostBinding('attr.aria-disabled')
  @HostBinding('class.rds-slider-disabled')
  @Input({ transform: booleanAttribute })
  disabled = false;

  @HostBinding('tabIndex')
  @Input()
  set tabIndex(value: NumberInput) {
    this._tabIndex = numberAttribute(value, TAB_INDEX_DEFAULT);
  }

  get tabIndex(): number {
    return this.disabled ? TAB_INDEX_NOT_FOCUSABLE : this._tabIndex;
  }

  private _tabIndex = TAB_INDEX_DEFAULT;

  @Input({ transform: booleanAttribute }) invert = false;

  @HostBinding('attr.aria-valuemax')
  @Input()
  set max(v: NumberInput) {
    this._max = numberAttribute(v, this._max);
    this._percent = this._calculatePercentage(this._value);
  }

  get max(): number {
    return this._max;
  }

  private _max = 100;

  @HostBinding('attr.aria-valuemin')
  @Input()
  set min(v: NumberInput) {
    this._min = numberAttribute(v, this._min);
    this._percent = this._calculatePercentage(this._value);
  }

  get min(): number {
    return this._min;
  }

  private _min = 0;

  @Input()
  set step(v: NumberInput) {
    this._step = numberAttribute(v, this._step);
    const step = this._step.toString().split('.').pop();

    if (this._step % 1 !== 0 && step) {
      this._roundToDecimal = step.length;
    }
  }

  get step(): number {
    return this._step;
  }

  private _step = 1;

  @HostBinding('class.rds-slider-thumb-label-showing')
  @Input({ transform: booleanAttribute })
  thumbLabel = false;

  get tickInterval(): 'auto' | number {
    return this._tickInterval;
  }

  @HostBinding('class.rds-slider-has-ticks')
  @Input()
  set tickInterval(value: 'auto' | number) {
    if (value === 'auto') {
      this._tickInterval = 'auto';
    } else if (typeof value === 'number' || typeof value === 'string') {
      this._tickInterval = numberAttribute(value, this._tickInterval as number);
    } else {
      this._tickInterval = 0;
    }
  }

  private _tickInterval: 'auto' | number = 0;

  get value(): number {
    if (this._value === null) {
      this.value = this.min;
    }

    return this._value as number;
  }

  @HostBinding('attr.aria-valuenow')
  @Input()
  set value(v: NumberInput) {
    if (v !== this._value) {
      let value = numberAttribute(v, 0);

      if (this._roundToDecimal && value !== this.min && value !== this.max) {
        value = parseFloat(value.toFixed(this._roundToDecimal));
      }

      if (value !== this._value) {
        this._value = value;
        this._percent = this._calculatePercentage(this._value);
        this.cdr.markForCheck();
      }
    }
  }

  private _value: number | null = null;

  @HostBinding('attr.aria-valuetext')
  get valueTextAttr(): string {
    return this.valueText === null || this.valueText === undefined
      ? String(this.displayValue)
      : this.valueText;
  }

  @Input() displayWith: (value: number) => string | number;

  @Input() valueText: string;

  @HostBinding('attr.aria-orientation')
  get ariaOrientationAttr(): string {
    return this.vertical ? 'vertical' : 'horizontal';
  }

  @HostBinding('class.rds-slider-horizontal')
  get rdsSliderHorizontal(): boolean {
    return !this.vertical;
  }

  @HostBinding('class.rds-slider-vertical')
  @Input({ transform: booleanAttribute })
  vertical = false;

  @HostBinding('class.rds-slider-axis-inverted')
  get rdsSliderAxisInvertedClass(): boolean {
    return this._shouldInvertAxis();
  }

  @HostBinding('class.rds-slider-invert-mouse-coords')
  get rdsSliderInvertMouseCoords(): boolean {
    return this._shouldInvertMouseCoords();
  }

  @HostBinding('class.rds-slider-hide-last-tick')
  get hideLastTick(): boolean {
    return (
      this.disabled ||
      (this._isMinValue() &&
        this._getThumbGap() > 0 &&
        this._shouldInvertAxis())
    );
  }

  @HostBinding('class._rds-animation-noopable')
  get noAnimation(): boolean {
    return this.animationMode === 'NoopAnimations';
  }

  onTouched: () => void = noop;

  get percent(): number {
    return this._clamp(this._percent);
  }

  private _percent = 0;

  @HostBinding('class.rds-slider-sliding')
  _isSliding: 'keyboard' | 'pointer' | null = null;

  _isActive = false;

  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() readonly change: EventEmitter<RdsSliderChange> =
    new EventEmitter<RdsSliderChange>();

  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() readonly input: EventEmitter<RdsSliderChange> =
    new EventEmitter<RdsSliderChange>();

  @Output() readonly valueChange: EventEmitter<number | null> =
    new EventEmitter<number | null>();

  private _tickIntervalPercent = 0;

  private _sliderDimensions: ClientRect | null = null;

  private _controlValueAccessorChangeFn: (value: number) => void = noop;

  private _roundToDecimal: number;

  private _valueOnSlideStart: number | null;

  @ViewChild('sliderWrapper') private _sliderWrapper: ElementRef;

  private _lastPointerEvent: MouseEvent | TouchEvent | null;

  private _touchId: number | undefined;

  get displayValue(): string | number {
    if (this.displayWith) {
      return this.displayWith(this.value);
    }

    if (this._roundToDecimal && this.value && this.value % 1 !== 0) {
      return this.value.toFixed(this._roundToDecimal);
    }

    return this.value || 0;
  }

  focus(options?: FocusOptions): void {
    this._focusHostElement(options);
  }

  blur(): void {
    this._blurHostElement();
  }

  _shouldInvertAxis(): boolean {
    return this.vertical ? !this.invert : this.invert;
  }

  _isMinValue(): boolean {
    return this.percent === 0;
  }

  _getThumbGap(): number {
    if (this.disabled) {
      return DISABLED_THUMB_GAP;
    }

    if (this._isMinValue() && !this.thumbLabel) {
      return this._isActive
        ? MIN_VALUE_ACTIVE_THUMB_GAP
        : MIN_VALUE_NONACTIVE_THUMB_GAP;
    }

    return 0;
  }

  _getTrackFillStyles(): Partial<CSSStyleDeclaration> {
    const percent = this.percent;
    const style: Partial<CSSStyleDeclaration> = {};

    if (this.vertical) {
      style.height = `${
        (this._getSliderDimensions()?.height || 0) * percent
      }px`;

      if (this._shouldInvertMouseCoords()) {
        style.bottom = '0';
      } else {
        style.top = '0';
      }
    } else {
      style.width = `${(this._getSliderDimensions()?.width || 0) * percent}px`;

      if (this._shouldInvertMouseCoords()) {
        style.right = '0';
      } else {
        style.left = '0';
      }
    }

    return {
      ...style,
      display: percent === 0 ? 'none' : '',
    };
  }

  _getTicksContainerStyles(): Partial<CSSStyleDeclaration> {
    const axis = this.vertical ? 'Y' : 'X';
    const sign = !this.vertical && this._getDirection() == 'rtl' ? '' : '-';
    const offset = (this._tickIntervalPercent / 2) * 100;

    return {
      transform: `translate${axis}(${sign}${offset}%)`,
    };
  }

  _getTicksStyles(): Partial<CSSStyleDeclaration> {
    const tickSize = this._tickIntervalPercent * 100;
    const backgroundSize = this.vertical
      ? `3px ${tickSize}%`
      : `${tickSize}% 3px`;
    const axis = this.vertical ? 'Y' : 'X';
    const sign = !this.vertical && this._getDirection() == 'rtl' ? '-' : '';
    const rotate =
      !this.vertical && this._getDirection() == 'rtl' ? ' rotate(180deg)' : '';
    const styles: { [key: string]: string } = {
      backgroundSize: backgroundSize,
      transform: `translateZ(0) translate${axis}(${sign}${
        tickSize / 2
      }%)${rotate}`,
    };

    if (this._isMinValue() && this._getThumbGap()) {
      const shouldInvertAxis = this._shouldInvertAxis();
      let side: string;

      if (this.vertical) {
        side = shouldInvertAxis ? 'Bottom' : 'Top';
      } else {
        side = shouldInvertAxis ? 'Right' : 'Left';
      }

      styles[`padding${side}`] = `${this._getThumbGap()}px`;
    }

    return styles;
  }

  _getThumbContainerStyles(): Partial<CSSStyleDeclaration> {
    const shouldInvertAxis = this._shouldInvertAxis();
    const axis = this.vertical ? 'Y' : 'X';
    const invertOffset =
      this._getDirection() == 'rtl' && !this.vertical
        ? !shouldInvertAxis
        : shouldInvertAxis;
    const offset = (invertOffset ? this.percent : 1 - this.percent) * 100;

    return {
      transform: `translate${axis}(-${offset}%)`,
    };
  }

  _shouldInvertMouseCoords(): boolean {
    const shouldInvertAxis = this._shouldInvertAxis();

    return this._getDirection() == 'rtl' && !this.vertical
      ? !shouldInvertAxis
      : shouldInvertAxis;
  }

  private _getDirection(): Direction {
    return this.dir && this.dir.value == 'rtl' ? 'rtl' : 'ltr';
  }

  constructor(@Attribute('tabindex') tabIndex?: string) {
    this.tabIndex = numberAttribute(tabIndex, TAB_INDEX_DEFAULT);

    this.ngZone.runOutsideAngular(() => {
      const element = this.elementRef.nativeElement;
      element.addEventListener(
        'mousedown',
        this._pointerDown,
        activeEventOptions,
      );
      element.addEventListener(
        'touchstart',
        this._pointerDown,
        activeEventOptions,
      );
    });
  }

  ngAfterViewInit(): void {
    this.focusMonitor
      .monitor(this.elementRef, true)
      .subscribe((origin: FocusOrigin) => {
        this._isActive = !!origin && origin !== 'keyboard';
        this.cdr.detectChanges();
      });

    if (this.dir) {
      this.dir.change
        .pipe(takeUntilDestroyed(this.destroyRef))
        .subscribe(() => this.cdr.markForCheck());
    }

    timer(1)
      .pipe(
        tap(() => this.cdr.markForCheck()),
        delay(ANIMATION_TIME - 1),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe(() => this.cdr.markForCheck());
  }

  ngOnDestroy(): void {
    const element = this.elementRef.nativeElement;
    element.removeEventListener(
      'mousedown',
      this._pointerDown,
      activeEventOptions,
    );
    element.removeEventListener(
      'touchstart',
      this._pointerDown,
      activeEventOptions,
    );
    this._lastPointerEvent = null;
    this._removeGlobalEvents();
    this.focusMonitor.stopMonitoring(this.elementRef);
  }

  @HostListener('mouseenter')
  _onMouseenter(): void {
    if (this.disabled) {
      return;
    }

    this._sliderDimensions = this._getSliderDimensions();
    this._updateTickIntervalPercent();
  }

  @HostListener('focus')
  _onFocus(): void {
    this._sliderDimensions = this._getSliderDimensions();
    this._updateTickIntervalPercent();
  }

  @HostListener('blur')
  _onBlur(): void {
    this.onTouched();
  }

  @HostListener('selectstart', ['$event'])
  _onSelectStart($event: Event): void {
    $event.preventDefault();
  }

  @HostListener('keydown', ['$event'])
  _onKeydown(event: KeyboardEvent): void {
    if (
      this.disabled ||
      hasModifierKey(event) ||
      (this._isSliding && this._isSliding !== 'keyboard')
    ) {
      return;
    }

    const oldValue = this.value;

    switch (event.keyCode) {
      case PAGE_UP:
        this._increment(10);
        break;
      case PAGE_DOWN:
        this._increment(-10);
        break;
      case END:
        this.value = this.max;
        break;
      case HOME:
        this.value = this.min;
        break;
      case LEFT_ARROW:
        this._increment(this._getDirection() == 'rtl' ? 1 : -1);
        break;
      case UP_ARROW:
        this._increment(1);
        break;
      case RIGHT_ARROW:
        this._increment(this._getDirection() == 'rtl' ? -1 : 1);
        break;
      case DOWN_ARROW:
        this._increment(-1);
        break;
      default:
        return;
    }

    if (oldValue != this.value) {
      this._emitInputEvent();
      this._emitChangeEvent();
    }

    this._isSliding = 'keyboard';
    event.preventDefault();
  }

  @HostListener('keyup')
  _onKeyup(): void {
    if (this._isSliding === 'keyboard') {
      this._isSliding = null;
    }
  }

  private _pointerDown = (event: TouchEvent | MouseEvent): void => {
    if (
      this.disabled ||
      this._isSliding ||
      (!isTouchEvent(event) && event.button !== 0)
    ) {
      return;
    }

    this.ngZone.run(() => {
      this._touchId = isTouchEvent(event)
        ? getTouchIdForSlider(event, this.elementRef.nativeElement)
        : undefined;
      const pointerPosition = getPointerPositionOnPage(event, this._touchId);

      if (pointerPosition) {
        const oldValue = this.value;
        this._isSliding = 'pointer';
        this._lastPointerEvent = event;
        event.preventDefault();
        this._focusHostElement();
        this._onMouseenter();
        this._bindGlobalEvents(event);
        this._focusHostElement();
        this._updateValueFromPosition(pointerPosition);
        this._valueOnSlideStart = oldValue;

        if (oldValue != this.value) {
          this._emitInputEvent();
        }
      }
    });
  };

  private _pointerMove = (event: TouchEvent | MouseEvent): void => {
    if (this._isSliding === 'pointer') {
      const pointerPosition = getPointerPositionOnPage(event, this._touchId);

      if (pointerPosition) {
        event.preventDefault();
        const oldValue = this.value;
        this._lastPointerEvent = event;
        this._updateValueFromPosition(pointerPosition);

        if (oldValue != this.value) {
          this._emitInputEvent();
        }
      }
    }
  };

  private _pointerUp = (event: TouchEvent | MouseEvent): void => {
    if (this._isSliding === 'pointer') {
      if (
        !isTouchEvent(event) ||
        typeof this._touchId !== 'number' ||
        findMatchingTouch(event.changedTouches, this._touchId)
      ) {
        event.preventDefault();
        this._removeGlobalEvents();
        this._isSliding = null;
        this._touchId = undefined;

        if (this._valueOnSlideStart != this.value && !this.disabled) {
          this._emitChangeEvent();
        }

        this._valueOnSlideStart = this._lastPointerEvent = null;
      }
    }
  };

  private _windowBlur = (): void => {
    if (this._lastPointerEvent) {
      this._pointerUp(this._lastPointerEvent);
    }
  };

  private _getWindow(): Window {
    return this.document.defaultView || window;
  }

  private _bindGlobalEvents(triggerEvent: TouchEvent | MouseEvent): void {
    const document = this.document;
    const isTouch = isTouchEvent(triggerEvent);
    const moveEventName = isTouch ? 'touchmove' : 'mousemove';
    const endEventName = isTouch ? 'touchend' : 'mouseup';
    document.addEventListener(
      moveEventName,
      this._pointerMove,
      activeEventOptions,
    );
    document.addEventListener(
      endEventName,
      this._pointerUp,
      activeEventOptions,
    );

    if (isTouch) {
      document.addEventListener(
        'touchcancel',
        this._pointerUp,
        activeEventOptions,
      );
    }

    const window = this._getWindow();

    if (typeof window !== 'undefined' && window) {
      window.addEventListener('blur', this._windowBlur);
    }
  }

  private _removeGlobalEvents(): void {
    const document = this.document;
    document.removeEventListener(
      'mousemove',
      this._pointerMove,
      activeEventOptions,
    );
    document.removeEventListener(
      'mouseup',
      this._pointerUp,
      activeEventOptions,
    );
    document.removeEventListener(
      'touchmove',
      this._pointerMove,
      activeEventOptions,
    );
    document.removeEventListener(
      'touchend',
      this._pointerUp,
      activeEventOptions,
    );
    document.removeEventListener(
      'touchcancel',
      this._pointerUp,
      activeEventOptions,
    );

    const window = this._getWindow();

    if (typeof window !== 'undefined' && window) {
      window.removeEventListener('blur', this._windowBlur);
    }
  }

  private _increment(numSteps: number): void {
    this.value = this._clamp(
      (this.value || 0) + this.step * numSteps,
      this.min,
      this.max,
    );
  }

  private _updateValueFromPosition(pos: { x: number; y: number }): void {
    if (!this._sliderDimensions) {
      return;
    }

    const offset = this.vertical
      ? this._sliderDimensions.top
      : this._sliderDimensions.left;
    const size = this.vertical
      ? this._sliderDimensions.height
      : this._sliderDimensions.width;
    const posComponent = this.vertical ? pos.y : pos.x;

    let percent = this._clamp((posComponent - offset) / size);

    if (this._shouldInvertMouseCoords()) {
      percent = 1 - percent;
    }

    if (percent === 0) {
      this.value = this.min;
    } else if (percent === 1) {
      this.value = this.max;
    } else {
      const exactValue = this._calculateValue(percent);
      const closestValue =
        Math.round((exactValue - this.min) / this.step) * this.step + this.min;
      this.value = this._clamp(closestValue, this.min, this.max);
    }
  }

  private _emitChangeEvent(): void {
    this._controlValueAccessorChangeFn(this.value);
    this.valueChange.emit(this.value);
    this.change.emit(this._createChangeEvent());
  }

  private _emitInputEvent(): void {
    this.input.emit(this._createChangeEvent());
  }

  private _updateTickIntervalPercent(): void {
    if (!this.tickInterval || !this._sliderDimensions) {
      return;
    }

    if (this.tickInterval == 'auto') {
      const trackSize = this.vertical
        ? this._sliderDimensions.height
        : this._sliderDimensions.width;
      const pixelsPerStep = (trackSize * this.step) / (this.max - this.min);
      const stepsPerTick = Math.ceil(MIN_AUTO_TICK_SEPARATION / pixelsPerStep);
      const pixelsPerTick = stepsPerTick * this.step;
      this._tickIntervalPercent = pixelsPerTick / trackSize;
    } else {
      this._tickIntervalPercent =
        (this.tickInterval * this.step) / (this.max - this.min);
    }
  }

  private _createChangeEvent(value = this.value): RdsSliderChange {
    const event = new RdsSliderChange();

    event.source = this;
    event.value = value;

    return event;
  }

  private _calculatePercentage(value: number | null): number {
    return ((value || 0) - this.min) / (this.max - this.min);
  }

  private _calculateValue(percentage: number): number {
    return this.min + percentage * (this.max - this.min);
  }

  private _clamp(value: number, min = 0, max = 1): number {
    return Math.max(min, Math.min(value, max));
  }

  private _getSliderDimensions(): ClientRect {
    return this._sliderWrapper
      ? this._sliderWrapper.nativeElement.getBoundingClientRect()
      : null;
  }

  private _focusHostElement(options?: FocusOptions): void {
    this.elementRef.nativeElement.focus(options);
  }

  private _blurHostElement(): void {
    this.elementRef.nativeElement.blur();
  }

  writeValue(value: number): void {
    this.value = value;
  }

  registerOnChange(fn: (value: number) => void): void {
    this._controlValueAccessorChangeFn = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    if (this.disabled !== isDisabled) {
      this.disabled = isDisabled;
      this.cdr.markForCheck();
    }
  }
}

function isTouchEvent(event: MouseEvent | TouchEvent): event is TouchEvent {
  return event.type[0] === 't';
}

function getPointerPositionOnPage(
  event: MouseEvent | TouchEvent,
  id: number | undefined,
): Point | undefined {
  let point: { clientX: number; clientY: number } | undefined;

  if (isTouchEvent(event)) {
    if (typeof id === 'number') {
      point =
        findMatchingTouch(event.touches, id) ||
        findMatchingTouch(event.changedTouches, id);
    } else {
      point = event.touches[0] || event.changedTouches[0];
    }
  } else {
    point = event;
  }

  return point ? { x: point.clientX, y: point.clientY } : undefined;
}

function findMatchingTouch(touches: TouchList, id: number): Touch | undefined {
  for (let i = 0; i < touches.length; i++) {
    if (touches[i].identifier === id) {
      return touches[i];
    }
  }

  return undefined;
}

function getTouchIdForSlider(
  event: TouchEvent,
  sliderHost: HTMLElement,
): number | undefined {
  for (let i = 0; i < event.touches.length; i++) {
    const target = event.touches[i].target as HTMLElement;

    if (sliderHost === target || sliderHost.contains(target)) {
      return event.touches[i].identifier;
    }
  }

  return undefined;
}
