import {
  AfterContentInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  ElementRef,
  HostBinding,
  inject,
  Input,
  isDevMode,
  OnChanges,
  OnDestroy,
  SimpleChanges,
  ViewEncapsulation,
} from '@angular/core';
import { DateAdapter } from './adapter';
import { Validators } from '@angular/forms';
import { merge, Subscription } from 'rxjs';
import { FocusOrigin } from '@angular/cdk/a11y';
import { BooleanInput } from '@angular/cdk/coercion';
import { createMissingDateImplError } from './datepicker-errors';
import { DateFilterFn, dateInputsHaveChanged } from './datepicker-input.base';
import { DateRange, RdsDateSelectionModel } from './date-selection.model';
import {
  RDS_DATE_RANGE_INPUT_PARENT,
  RdsDateRangeInputParent,
  RdsEndDateDirective,
  RdsStartDateDirective,
} from './date-range-input.parts';
import { RdsDatepickerControl, RdsDatepickerPanel } from './datepicker-base';
import { RDS_FORM_FIELD, RdsFormFieldComponent } from '../form-field';
import { RdsAbstractFormFieldControl } from '../form-field';
import { RdsControlType } from '../form-field/control.type';
import { RdsDateRangePickerInput } from './date-range-picker.component';

let nextUniqueId = 0;

@Component({
  selector: 'rds-date-range-input',
  templateUrl: './date-range-input.component.html',
  styleUrls: ['./date-range-input.component.scss'],
  exportAs: 'rdsDateRangeInput',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: RdsAbstractFormFieldControl,
      useExisting: RdsDateRangeInputComponent,
    },
    {
      provide: RDS_DATE_RANGE_INPUT_PARENT,
      useExisting: RdsDateRangeInputComponent,
    },
  ],
})
export class RdsDateRangeInputComponent<D>
  extends RdsAbstractFormFieldControl<DateRange<D>>
  implements
    RdsDatepickerControl<D>,
    RdsDateRangeInputParent<D>,
    RdsDateRangePickerInput<D>,
    AfterContentInit,
    OnChanges,
    OnDestroy
{
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly elementRef = inject<ElementRef<HTMLElement>>(ElementRef);
  private readonly dateAdapter = inject(DateAdapter, {
    optional: true,
  }) as DateAdapter<D>;
  private readonly formField = inject(RDS_FORM_FIELD, {
    optional: true,
  }) as RdsFormFieldComponent<DateRange<D>>;

  private _closedSubscription = Subscription.EMPTY;
  protected override _id = `rds-date-range-input-${nextUniqueId++}`;

  @HostBinding('class') get classes(): string[] {
    const classes = ['rds-date-range-input'];

    if (this._shouldHidePlaceholders()) {
      classes.push('rds-date-range-input-hide-placeholders');
    }

    if (this.required) {
      classes.push('rds-date-range-input-required');
    }

    if (this.disabled) {
      classes.push('rds-date-range-input-disabled');
    }

    return classes;
  }

  @HostBinding('attr.id') idAttr = null;
  @HostBinding('attr.role') role = 'group';

  @HostBinding('attr.aria-labelledby') get _ariaLabelledby(): string {
    return this._getAriaLabelledby();
  }

  @HostBinding('attr.data-rds-calendar') get dataRdsCalendar(): string | null {
    return this.rangePicker ? this.rangePicker.id : null;
  }

  override get value(): DateRange<D> {
    return this._model?.selection as DateRange<D>;
  }

  controlType: RdsControlType = 'date-range';

  private _dateFilter: DateFilterFn<D> | null = null;
  private _min: D | null = null;
  private _max: D | null = null;
  private _rangePicker: RdsDatepickerPanel<
    RdsDatepickerControl<D>,
    DateRange<D>,
    D
  > | null = null;

  get shouldLabelFloat(): boolean {
    return this.focused || !this.empty;
  }

  get placeholder(): string {
    const start = this._startInput?._getPlaceholder() || '';
    const end = this._endInput?._getPlaceholder() || '';

    return start || end ? `${start} ${this.separator} ${end}` : '';
  }

  @Input()
  get rangePicker(): RdsDatepickerPanel<
    RdsDatepickerControl<D>,
    DateRange<D>,
    D
  > {
    return this._rangePicker as RdsDatepickerPanel<
      RdsDatepickerControl<D>,
      DateRange<D>,
      D
    >;
  }

  set rangePicker(
    rangePicker: RdsDatepickerPanel<RdsDatepickerControl<D>, DateRange<D>, D>,
  ) {
    if (rangePicker) {
      this._model = rangePicker.registerInput(this);
      this._rangePicker = rangePicker;
      this._closedSubscription.unsubscribe();
      this._closedSubscription = rangePicker.closedStream.subscribe(() => {
        this._startInput?._onTouched();
        this._endInput?._onTouched();
      });

      if (this._model) {
        this._registerModel(this._model);
      }
    }
  }

  @HostBinding('attr.required')
  get attrRequired(): boolean | null {
    if (this.isBoolean(this._required) && this._required) {
      this.stateChanges.next();

      return this._required;
    } else if (this.ngControl?.control) {
      this.stateChanges.next();

      return this.ngControl?.control?.hasValidator(Validators.required);
    } else if (
      this._startInput &&
      this._endInput &&
      this.isBoolean(this._startInput.attrRequired) &&
      this.isBoolean(this._endInput.attrRequired) &&
      (this._startInput.attrRequired || this._endInput.attrRequired)
    ) {
      this.stateChanges.next();

      return this._startInput.attrRequired || this._endInput.attrRequired;
    } else {
      return null;
    }
  }

  @Input()
  override set required(value: BooleanInput) {
    const isRequired = booleanAttribute(value);

    if (isRequired !== this._required) {
      this._required = isRequired;
      this.stateChanges.next();
    }
  }

  override get required(): boolean {
    return this.attrRequired ?? false;
  }

  @Input()
  get dateFilter(): DateFilterFn<D> {
    return this._dateFilter as DateFilterFn<D>;
  }

  set dateFilter(value: DateFilterFn<D>) {
    const start = this._startInput;
    const end = this._endInput;
    const wasMatchingStart = start && start._matchesFilter(start.value);
    const wasMatchingEnd =
      end && end._matchesFilter((start as RdsStartDateDirective<D>).value);
    this._dateFilter = value;

    if (start && start._matchesFilter(start.value) !== wasMatchingStart) {
      start._validatorOnChange();
    }

    if (end && end._matchesFilter(end.value) !== wasMatchingEnd) {
      end._validatorOnChange();
    }
  }

  @Input()
  get min(): D | null {
    return this._min;
  }

  set min(value: D | null) {
    const validValue = this.dateAdapter.getValidDateOrNull(
      this.dateAdapter.deserialize(value),
    );

    if (!this.dateAdapter.sameDate(validValue, this._min)) {
      this._min = validValue;
      this._revalidate();
    }
  }

  @Input()
  get max(): D | null {
    return this._max;
  }

  set max(value: D | null) {
    const validValue = this.dateAdapter.getValidDateOrNull(
      this.dateAdapter.deserialize(value),
    );

    if (!this.dateAdapter.sameDate(validValue, this._max)) {
      this._max = validValue;
      this._revalidate();
    }
  }

  @Input()
  override get disabled(): boolean {
    return this._startInput && this._endInput
      ? this._startInput.disabled && this._endInput.disabled
      : this._groupDisabled;
  }

  override set disabled(value: BooleanInput) {
    const newValue = booleanAttribute(value);

    if (newValue !== this._groupDisabled) {
      this._groupDisabled = newValue;
      this.stateChanges.next();
    }
  }

  _groupDisabled = false;

  get errorState(): boolean {
    if (this._startInput && this._endInput) {
      return this._startInput.errorState || this._endInput.errorState;
    }

    return false;
  }

  get empty(): boolean {
    const startEmpty = this._startInput ? this._startInput.isEmpty() : false;
    const endEmpty = this._endInput ? this._endInput.isEmpty() : false;

    return startEmpty && endEmpty;
  }

  @HostBinding('attr.aria-describedby')
  _ariaDescribedBy: string | null = null;
  private _model?: RdsDateSelectionModel<DateRange<D>>;

  @Input() separator = '';
  @Input() comparisonStart: D | null = null;
  @Input() comparisonEnd: D | null = null;

  @ContentChild(RdsStartDateDirective) _startInput: RdsStartDateDirective<D>;
  @ContentChild(RdsEndDateDirective) _endInput: RdsEndDateDirective<D>;

  constructor() {
    super();

    if (!this.dateAdapter && isDevMode()) {
      throw createMissingDateImplError('DateAdapter');
    }
  }

  setDescribedByIds(ids: string[]): void {
    this._ariaDescribedBy = ids.length ? ids.join(' ') : null;
  }

  onContainerClick(): void {
    if (!this.focused && !this.disabled) {
      if (!this._model || !this._model.selection?.start) {
        this._startInput?.focus();
      } else {
        this._endInput?.focus();
      }
    }
  }

  ngAfterContentInit(): void {
    if (!this._startInput) {
      throw Error('rds-date-range-input must contain a rdsStartDate input');
    }

    if (!this._endInput) {
      throw Error('rds-date-range-input must contain a rdsEndDate input');
    }

    if (this._model) {
      this._registerModel(this._model);
    }

    merge(this._startInput.stateChanges, this._endInput.stateChanges).subscribe(
      () => {
        this.stateChanges.next(undefined);
      },
    );
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (this.dateAdapter && dateInputsHaveChanged(changes, this.dateAdapter)) {
      this.stateChanges.next(undefined);
    }
  }

  ngOnDestroy(): void {
    this._closedSubscription.unsubscribe();
    this.stateChanges.complete();
  }

  getStartValue(): D | null {
    return this.value ? this.value.start : null;
  }

  getConnectedOverlayOrigin(): ElementRef {
    return this.formField
      ? this.formField.getConnectedOverlayOrigin()
      : this.elementRef;
  }

  _getInputMirrorValue(): string {
    return this._startInput ? this._startInput.getMirrorValue() : '';
  }

  _shouldHidePlaceholders(): boolean {
    if (this.formField?.labelPosition !== 'floating') {
      return false;
    }

    return this._startInput
      ? !this.focused && this._startInput.isEmpty()
      : !this.focused;
  }

  _handleChildValueChange(): void {
    this.stateChanges.next(undefined);
    this.cdr.markForCheck();
  }

  _openDatepicker(): void {
    if (this._rangePicker) {
      this._rangePicker.open();
    }
  }

  _shouldHideSeparator(): boolean {
    return !this.focused && this.empty;
  }

  _getAriaLabelledby(): string {
    return this.formField?.labelId as string;
  }

  _updateFocus(origin: FocusOrigin): void {
    this._focused = origin !== null;
    this.stateChanges.next();
  }

  private _revalidate(): void {
    if (this._startInput) {
      this._startInput._validatorOnChange();
    }

    if (this._endInput) {
      this._endInput._validatorOnChange();
    }
  }

  private _registerModel(model: RdsDateSelectionModel<DateRange<D>>): void {
    if (this._startInput) {
      this._startInput._registerModel(model);
    }

    if (this._endInput) {
      this._endInput._registerModel(model);
    }
  }

  private isBoolean(value: boolean | undefined | null): boolean {
    return value === true || value === false;
  }
}
