import {
  Component,
  ElementRef,
  ErrorHandler,
  HostBinding,
  inject,
  Input,
  OnChanges,
  OnDestroy,
  SimpleChanges,
  ViewEncapsulation,
} from '@angular/core';
import { RdsIconLoader } from './icon-loader';
import { Subscription } from 'rxjs';
import { take } from 'rxjs/operators';
import { RdsIconName, RdsIconNamespace } from './icon-types';

@Component({
  selector: 'rds-icon',
  template: '',
  encapsulation: ViewEncapsulation.None,
  standalone: true,
})
export class RdsIconComponent implements OnChanges, OnDestroy {
  private readonly elementRef = inject(ElementRef);
  private readonly iconLoader = inject(RdsIconLoader);
  private readonly errorHandler = inject(ErrorHandler);

  @Input() namespace: RdsIconNamespace = 'filled';
  @Input() icon: RdsIconName | null = null;

  @HostBinding('class') hostClass = 'rds-icon';
  @HostBinding('attr.aria-hidden') hostAriaHidden = 'true';

  private currentIconFetch = Subscription.EMPTY;

  ngOnChanges(changes: SimpleChanges): void {
    const namespaceChanges = changes['namespace'];
    const iconChanges = changes['icon'];

    // Update the inline SVG if the icon or namespace inputs changed, to avoid unnecessary DOM operations.
    if (iconChanges || namespaceChanges) {
      this.currentIconFetch.unsubscribe();

      if (this.icon) {
        this.currentIconFetch = this.iconLoader
          .getSvgIcon(this.icon, this.namespace)
          .pipe(take(1))
          .subscribe({
            next: (svg) => this._setSvgIcon(svg),
            error: (err: Error) => {
              const errorMessage = `Error retrieving icon ${this.namespace}:${this.icon}! ${err.message}`;
              this.errorHandler.handleError(new Error(errorMessage));
            },
          });
      } else if (iconChanges.previousValue) {
        this._removeSvgIcon();
      }
    }
  }

  ngOnDestroy(): void {
    this.currentIconFetch.unsubscribe();
  }

  private _setSvgIcon(svgElement: SVGElement): void {
    this._removeSvgIcon();
    this.elementRef.nativeElement.appendChild(svgElement);
  }

  private _removeSvgIcon(): void {
    const layoutElement: HTMLElement = this.elementRef.nativeElement;
    let childCount = layoutElement.childNodes.length;

    while (childCount--) {
      const child = layoutElement.childNodes[childCount];

      if (child.nodeType !== 1 || child.nodeName.toLowerCase() === 'svg') {
        layoutElement.removeChild(child);
      }
    }
  }
}
