import { BehaviorSubject } from 'rxjs';
import { RdsFileItemState, RdsUploadFilter } from './upload.types';
import { RdsUploadIntlToken } from './rds-upload-intl.service';

let id = 0;

export class RdsFileItem {
  private _errorIntlToken: RdsUploadIntlToken = 'EMPTY';
  private readonly _progress$ = new BehaviorSubject<number | null>(0);
  private readonly _state$ = new BehaviorSubject<RdsFileItemState>('ready');

  get errorIntlToken(): RdsUploadIntlToken {
    return this._errorIntlToken;
  }

  readonly progress$ = this._progress$.asObservable();
  readonly state$ = this._state$.asObservable();
  readonly id = id++;
  readonly hasFailedFilters = !!this.failedFilters.length;

  get state(): RdsFileItemState {
    return this._state$.value;
  }

  private set state(v: RdsFileItemState) {
    if (v !== this.state) {
      this._state$.next(v);
    }
  }

  get isUploading(): boolean {
    return this.state === 'uploading';
  }

  constructor(
    public readonly file: File,
    public readonly failedFilters: RdsUploadFilter[],
  ) {
    if (this.hasFailedFilters) {
      this._errorIntlToken = this.failedFilters[0].descriptionIntlToken;
      this.state = 'error';
    }
  }

  onBeforeUpload(): void {
    this._errorIntlToken = 'EMPTY';
    this.state = 'uploading';
    this._progress$.next(0);
  }

  onProgress(progress: number | null): void {
    this._progress$.next(progress);
  }

  onSuccess(): void {
    this.state = 'success';
    this._progress$.next(100);
  }

  onError(): void {
    this._errorIntlToken = 'uploadFailed';
    this.state = 'error';
  }

  onAbort(): void {
    this.state = 'aborted';
  }
}
