import {
  booleanAttribute,
  ChangeDetectorRef,
  ComponentRef,
  DestroyRef,
  Directive,
  DoCheck,
  ElementRef,
  EventEmitter,
  HostBinding,
  inject,
  Input,
  isDevMode,
  numberAttribute,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges,
  TemplateRef,
  ViewChild,
} from '@angular/core';
import {
  CdkOverlayOrigin,
  ConnectedPosition,
  Overlay,
  OverlayRef,
  PositionStrategy,
  ScrollStrategyOptions,
} from '@angular/cdk/overlay';
import { ComponentPortal } from '@angular/cdk/portal';
import { RdsAutocompleteContainerComponent } from './autocomplete-container.component';
import {
  BehaviorSubject,
  distinctUntilChanged,
  merge,
  noop,
  Observable,
  skip,
  Subject,
} from 'rxjs';
import { debounceTime, filter, tap } from 'rxjs/operators';
import { hasModifierKey } from '@angular/cdk/keycodes';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import {
  RDS_AUTOCOMPLETE_CONFIG,
  RDS_AUTOCOMPLETE_DEFAULT_CONFIG,
} from './autocomplete.config';
import { RdsAutocompleteIntl } from './autocomplete-intl.service';
import { BooleanInput, NumberInput } from '@angular/cdk/coercion';
import { RDS_FORM_FIELD, RdsAbstractFormFieldControl } from '../form-field';
import { RdsControlType } from '../form-field/control.type';
import { updateErrorState } from '../utils';
import { AbstractControl, ControlValueAccessor } from '@angular/forms';
import {
  RdsAutocompleteNewItemTemplateContext,
  RdsAutocompleteOptionTemplateContext,
  RdsAutocompleteTemplateContext,
} from './autocomplete.types';
import { RdsAutocompleteOptionDirective } from './autocomplete-option.directive';

@Directive()
export abstract class RdsAbstractAutocompleteComponent<
    T,
    ArrayOfFlatType extends T | T[],
  >
  extends RdsAbstractFormFieldControl<ArrayOfFlatType>
  implements OnInit, ControlValueAccessor, DoCheck, OnChanges, OnDestroy
{
  protected readonly cdr = inject(ChangeDetectorRef);
  protected readonly overlay = inject(Overlay);
  protected readonly destroyRef = inject(DestroyRef);
  protected readonly config = {
    ...RDS_AUTOCOMPLETE_DEFAULT_CONFIG,
    ...inject(RDS_AUTOCOMPLETE_CONFIG, { optional: true }),
  };
  protected readonly intl = inject(RdsAutocompleteIntl);
  protected readonly formField = inject(RDS_FORM_FIELD);
  protected readonly scrollStrategyOptions = inject(ScrollStrategyOptions);

  @Input({ required: true }) options:
    | T[]
    | ((query: string) => Observable<T[]>);
  @Input() displayProperty: keyof T | ((value: T) => string);
  @Input() uniqProperty: keyof T | ((value: T) => string | number);
  @Input() compareItem: (i1: T, i2: T) => boolean = (i1: T, i2: T) =>
    this.getUniqProperty(i1) === this.getUniqProperty(i2);
  @Input() filterBy: (item: T, query: string) => boolean;
  @Input() highlightQuery: boolean | 'inverse' = false;
  @Input() newItem: (query: string, fetchedCollection: T[]) => T;
  @Input({ transform: booleanAttribute }) selectNewItemOnBlur = false;
  @Input({ transform: booleanAttribute }) selectLastRemainingOnBlur = false;
  @Input() skipSearchValue: (query: string) => boolean;
  @Input() placeholder: string;
  @Input() loadingText: string;
  @Input() noDataText: string;
  @Input() newItemText: string;
  @Input() optionTemplate: TemplateRef<
    RdsAutocompleteOptionTemplateContext<T, ArrayOfFlatType>
  >;
  @Input() noDataTemplate: TemplateRef<
    RdsAutocompleteTemplateContext<T, ArrayOfFlatType>
  >;
  @Input() loadingTemplate: TemplateRef<
    RdsAutocompleteTemplateContext<T, ArrayOfFlatType>
  >;
  @Input() newItemTemplate: TemplateRef<
    RdsAutocompleteNewItemTemplateContext<T, ArrayOfFlatType>
  >;

  @HostBinding('attr.disabled')
  get attrDisabled(): boolean | null {
    if (this.ngControl && this.ngControl.disabled !== null) {
      return this.ngControl.disabled ? this.ngControl.disabled : null;
    }

    return this._disabled ? this._disabled : null;
  }

  @Input()
  override set disabled(disabled: BooleanInput) {
    this._disabled = booleanAttribute(disabled);

    if (this._focused) {
      this._focused = false;
      this.stateChanges.next();
    }
  }

  override get disabled(): boolean {
    return this.attrDisabled || false;
  }

  @Input()
  set debounce(debounce: NumberInput) {
    this._debounce = numberAttribute(debounce, this.config.debounce);
  }

  get debounce(): number {
    return this._debounce;
  }

  private _debounce = this.config.debounce;

  @Input()
  set minSearchChars(minSearchChars: NumberInput) {
    this._minSearchChars = numberAttribute(
      minSearchChars,
      this.config.minSearchChars,
    );
  }

  get minSearchChars(): number {
    return this._minSearchChars;
  }

  private _minSearchChars = this.config.minSearchChars;
  @HostBinding('class') readonly hostClass = 'w-100';

  readonly controlType: RdsControlType = 'autocomplete';
  protected typing$ = new BehaviorSubject(false);

  @Output() openChanged = new EventEmitter<boolean>();
  @Output() shown = new EventEmitter<void>();
  @Output() hidden = new EventEmitter<void>();
  @Output() typing = this.typing$.pipe(distinctUntilChanged());
  @Output() loading = new BehaviorSubject(false);

  @ViewChild('inputEl', { static: true })
  protected inputEl: ElementRef<HTMLInputElement>;
  @ViewChild('inputContainer', { static: true })
  protected inputContainer: ElementRef<HTMLDivElement>;

  abstract get values(): T[];

  get empty(): boolean {
    return (
      this.values.length === 0 &&
      !this.inputEl.nativeElement.value &&
      !this.isNativelyBadInput()
    );
  }

  get shouldLabelFloat(): boolean {
    return this._focused || !this.empty;
  }

  get errorState(): boolean {
    return this._errorState;
  }

  protected get inputAriaDescribedBy(): string {
    return this._inputAriaDescribedby;
  }

  protected set inputAriaDescribedBy(newValue: string) {
    this._inputAriaDescribedby = newValue;
  }

  _trigger: ElementRef<HTMLDivElement>;
  _origin: CdkOverlayOrigin;

  private _inputAriaDescribedby: string;

  onChange: (value: ArrayOfFlatType) => void = noop;
  onTouched = noop;

  protected override _value: ArrayOfFlatType;
  protected _newValues: T[] = [];
  protected _open = false;
  private _overlayRef: OverlayRef | null = null;
  private _portal: ComponentPortal<
    RdsAutocompleteContainerComponent<T, ArrayOfFlatType>
  >;
  protected _dropdownInstance: ComponentRef<
    RdsAutocompleteContainerComponent<T, ArrayOfFlatType>
  >;
  protected _options: T[] = [];
  protected _onFocusInputValue = '';
  protected _search$ = new BehaviorSubject<string>('');
  private _lastValues: T[] = [];
  private readonly _closed$ = new Subject<void>();

  constructor() {
    super();

    if (this.ngControl) {
      // Note: we provide the value accessor through here, instead of
      // the `providers` to avoid running into a circular import.
      this.ngControl.valueAccessor = this;
    }

    merge(this.intl.change$, this.stateChanges)
      .pipe(takeUntilDestroyed())
      .subscribe(() => this.cdr.markForCheck());
    this.typing$
      .pipe(takeUntilDestroyed())
      .subscribe(() => this.onValueChange());
  }

  ngOnInit(): void {
    this.updateSearch();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (this.disabled) {
      this.hide();
    }

    if (
      this._dropdownInstance &&
      (changes['displayProperty'] ||
        changes['loadingText'] ||
        changes['noDataText'])
    ) {
      this.assignInputsToDropdown();
    }

    if (changes['debounce']) {
      this.updateSearch();
    }
  }

  ngDoCheck(): void {
    if (this.ngControl) {
      const oldState = this._errorState;
      this._errorState =
        !this.typing$.getValue() &&
        !this._open &&
        updateErrorState(
          this.errorState,
          this.parentFormGroup,
          this.parentForm,
          this.ngControl.control,
        );

      if (oldState !== this._errorState) {
        this.stateChanges.next();
      }
    }
  }

  ngOnDestroy(): void {
    if (this._overlayRef) {
      this._overlayRef.detach();
    }

    this._search$.complete();
    this._closed$.next();
    this._closed$.complete();
  }

  protected focusChanged(isFocused: boolean): void {
    this.typing$.next(isFocused);

    if (
      isFocused !== this.focused &&
      (!this.inputEl.nativeElement.readOnly || !isFocused)
    ) {
      this._focused = isFocused;
      this.stateChanges.next();
      this.inputEl.nativeElement.selectionStart =
        this.inputEl.nativeElement.value.length;
    }

    if (!isFocused) {
      this.onTouched();

      if (
        (!Array.isArray(this.value) && !this.value) ||
        Array.isArray(this.value)
      ) {
        this.onValueChange(
          this._onFocusInputValue !== this.inputEl.nativeElement.value,
        );
      }
    } else {
      this._onFocusInputValue = this.inputEl.nativeElement.value;
      this.show();
    }
  }

  show(): void {
    if (
      !this.disabled &&
      !this._open &&
      this.inputEl.nativeElement.value.length >= this.minSearchChars
    ) {
      this._open = true;

      if (this._options.length === 0 && this.minSearchChars === 0) {
        this.searchFn(this._search$.getValue());
      }

      if (!this._overlayRef) {
        this._overlayRef = this.overlay.create({
          hasBackdrop: true,
          backdropClass: 'transparent',
          positionStrategy: this.getPositionStrategy(),
          scrollStrategy: this.scrollStrategyOptions.block(),
          minWidth: Math.max(
            this.config.optionsContainerMinWidth,
            this._trigger.nativeElement.getBoundingClientRect().width,
          ),
        });
        this._overlayRef
          .backdropClick()
          .pipe(takeUntilDestroyed(this.destroyRef))
          .subscribe(() => this.addNewItemOnBlurAndHide());
        this._portal = new ComponentPortal<
          RdsAutocompleteContainerComponent<T, ArrayOfFlatType>
        >(RdsAutocompleteContainerComponent, null);
      }

      this._dropdownInstance = this._overlayRef.attach(this._portal);
      this.assignInputsToDropdown();
      this.openChanged.emit(true);
      this.shown.emit();
      this.inputEl.nativeElement.focus();
    }
  }

  hide(): void {
    if (this._open) {
      this._open = false;

      if (this._overlayRef) {
        this._overlayRef.detach();
      }

      this.onTouched();
      this._closed$.next();
      this.openChanged.emit(false);
      this.hidden.emit();
      this.cdr.markForCheck();
    }
  }

  hasValue(): boolean {
    return this.values.length > 0;
  }

  isSelected(option: T): boolean {
    return this.values.some((o) => this.compareItem(option, o));
  }

  abstract deselect(option?: T): void;

  clear(): void {
    if (!this.disabled) {
      const beforeValue = this.inputEl.nativeElement.value;
      this.setValueForClear();
      this.inputEl.nativeElement.value = '';
      this.onValueChange(beforeValue !== '');
      this._newValues = [];
      this._options = [];
      this.inputEl.nativeElement.focus();
      this.updateDropdownOptions();
    }
  }

  protected abstract setValueForClear(): void;

  abstract select(option: T): void;

  selectNewItem(): void {
    if (typeof this.newItem === 'function') {
      const newItem = this.getNewItem();

      if (newItem) {
        this._options = this._options.filter(
          (o) => !this._newValues.some((newOpt) => this.compareItem(o, newOpt)),
        );
        this._newValues = [
          ...this._newValues.filter((v) => !this.compareItem(v, newItem)),
          newItem,
        ];
        this._options = [...this._options, ...this._newValues];
        this.select(newItem);
      }
    } else if (isDevMode()) {
      throw new Error(
        `Adding new item requires defined newItem as function for autocomplete.`,
      );
    }
  }

  getNewItem(): T | null {
    return typeof this.newItem === 'function'
      ? this.newItem(this.inputEl.nativeElement.value, this._options)
      : null;
  }

  registerOnChange(fn: (value: ArrayOfFlatType) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
    this.cdr.markForCheck();
  }

  writeValue(v: ArrayOfFlatType): void {
    this.value = v;
  }

  abstract isInputFilled(): boolean;

  getInputFilledValue(): string {
    return this.inputEl.nativeElement.value;
  }

  getDisplayProperty(v: T | null): string {
    if (!v) {
      return '';
    } else if (this.displayProperty) {
      if (typeof this.displayProperty === 'function') {
        return this.displayProperty(v);
      } else {
        return String(v[this.displayProperty]);
      }
    } else {
      return String(v);
    }
  }

  getUniqProperty(v: T | null): string | number {
    if (!v) {
      return '';
    } else if (this.uniqProperty) {
      if (typeof this.uniqProperty === 'function') {
        return this.uniqProperty(v);
      } else {
        return String(v[this.uniqProperty]);
      }
    } else {
      return this.getDisplayProperty(v);
    }
  }

  onContainerClick(): void {
    if (!this._focused) {
      this.focus();

      if (
        Array.isArray(this.options) &&
        this.debounce === 0 &&
        this.minSearchChars === 0
      ) {
        this.show();
      }
    }
  }

  setDescribedByIds(ids: string[]): void {
    this.inputAriaDescribedBy = ids.join(' ');
  }

  getSearchQuery(): string {
    return this._search$.getValue();
  }

  updatePosition(): void {
    this._overlayRef?.updatePosition();
  }

  clearValue(event?: Event): void {
    if (event) {
      event.stopPropagation();
    }

    this.clear();
  }

  toggle(option: T): void {
    if (this.isSelected(option)) {
      this.deselect(option);
    } else {
      this.select(option);
    }
  }

  override get required(): boolean {
    if (
      this.ngControl &&
      this.ngControl.control &&
      this.ngControl.control.validator
    ) {
      const validators = this.ngControl.control.validator(
        {} as AbstractControl,
      );

      if (validators && validators['required']) {
        return true;
      }
    }

    return super.required;
  }

  _addOption(
    option: HTMLElement,
    component: RdsAutocompleteOptionDirective<T, ArrayOfFlatType>,
  ): void {
    this._dropdownInstance?.instance._addOption(option, component);
  }

  _deleteOption(option: HTMLElement): void {
    this._dropdownInstance?.instance._deleteOption(option);
  }

  protected onValueChange(force = false): void {
    if (
      force ||
      this.hasAtLeastOneDifferentItem(this.values, this._lastValues)
    ) {
      this._lastValues = [...this.values];
      this.onChange(this.value);
    }
  }

  protected addNewItemOnBlurAndHide(): void {
    this.addNewItemOnBlur();
    this.hide();
  }

  protected addNewItemOnBlur(query = this.inputEl.nativeElement.value): void {
    if (this.selectLastRemainingOnBlur && this._options.length === 1) {
      this.select(this._options[0]);
    } else if (this.selectNewItemOnBlur && typeof this.newItem === 'function') {
      const newVal = this.newItem(query, this._options);

      if (newVal && this._options.every((o) => !this.compareItem(o, newVal))) {
        this._options = this._options.filter(
          (o) => !this._newValues.some((newOpt) => this.compareItem(o, newOpt)),
        );
        this._newValues = [
          ...this._newValues.filter((v) => !this.compareItem(v, newVal)),
          newVal,
        ];
        this._options = [...this._options, ...this._newValues];
        this.updateDropdownOptions();
        this.select(newVal);
      }
    }

    this.onTouched();
    this.typing$.next(false);
  }

  protected getPlaceholder(): string {
    if (
      !this._focused &&
      this.formField.labelPosition === 'floating' &&
      this.values.length === 0
    ) {
      return '';
    } else if (typeof this.placeholder === 'string') {
      return this.placeholder;
    } else {
      return RdsAutocompleteIntl.translations.placeholder;
    }
  }

  protected search(query: string = this.inputEl.nativeElement.value): void {
    if (
      typeof this.skipSearchValue === 'function' &&
      this.skipSearchValue(query)
    ) {
      return;
    }

    this._search$.next(query);
  }

  protected onInputKeydown(event: KeyboardEvent): void {
    if (!hasModifierKey(event, 'altKey', 'shiftKey', 'ctrlKey', 'metaKey')) {
      if (!this._open && event.key === 'Enter') {
        this.show();
      } else if (this._open && event.key === 'Escape') {
        this.hide();
      }

      this._dropdownInstance?.instance.onKeydown(event);
    }
  }

  private _filterBy(item: T, query: string): boolean {
    return typeof this.filterBy === 'function'
      ? this.filterBy(item, query)
      : this.getDisplayProperty(item).includes(query);
  }

  private searchFn(query: string): void {
    if (this.inputEl.nativeElement.value.length >= this.minSearchChars) {
      if (this._focused) {
        this.show();
      }

      this.loading.next(true);

      if (typeof this.options === 'function') {
        this.options(query)
          .pipe(takeUntilDestroyed(this.destroyRef))
          .subscribe((options) => {
            this._options = [
              ...options,
              ...this.getNewNotQueriedSelectedValues(options),
            ];
            this.updateDropdownOptions();
          });
      } else {
        this._options = [
          ...this.options,
          ...this.getNewNotQueriedSelectedValues(this.options),
        ].filter((v) => this._filterBy(v, query));
        this.updateDropdownOptions();
        this.loading.next(false);
      }
    } else {
      this.loading.next(false);
      this.hide();
    }
  }

  protected getNewNotQueriedSelectedValues(options: T[]): T[] {
    return this.values
      .filter((o) =>
        this._newValues.some((newOpt) => this.compareItem(o, newOpt)),
      )
      .filter((o) => !options.some((q) => this.compareItem(q, o)));
  }

  protected updateDropdownOptions(): void {
    if (this._dropdownInstance) {
      this._dropdownInstance.instance.options = this._options;
      this._dropdownInstance.changeDetectorRef.markForCheck();
      this.loading.next(false);
    }
  }

  private getPositionStrategy(): PositionStrategy {
    return this.overlay
      .position()
      .flexibleConnectedTo(this._origin.elementRef)
      .withPositions([this.getPositions()]);
  }

  private getPositions(): ConnectedPosition {
    return {
      originX: 'start',
      originY: 'bottom',
      overlayX: 'start',
      overlayY: 'top',
      offsetX: 0,
      offsetY: 0,
    };
  }

  protected assignInputsToDropdown(): void {
    this._dropdownInstance.instance.autocomplete = this;
    this._dropdownInstance.instance.size = this.formField.size;
    this._dropdownInstance.instance.options = this._options;
    this._dropdownInstance.changeDetectorRef.markForCheck();
  }

  private updateSearch(): void {
    if (this._search$) {
      this._search$.complete();
    }

    const searchQuery = Array.isArray(this.value)
      ? ''
      : this.getDisplayProperty(this.values[0]);

    this._search$ = new BehaviorSubject<string>(searchQuery);
    this._search$
      .pipe(
        skip(1),
        distinctUntilChanged(),
        filter((query) => this.inputEl.nativeElement.value === query),
        tap(() => {
          if (this._focused) {
            this.show();
          }

          if (
            this._dropdownInstance &&
            this.debounce !== 0 &&
            this.inputEl.nativeElement.value.length >= this.minSearchChars
          ) {
            this.loading.next(true);
          }

          if (
            this.debounce !== 0 &&
            this.inputEl.nativeElement.value.length < this.minSearchChars &&
            this.loading.getValue()
          ) {
            this.loading.next(false);
          }
        }),
        debounceTime(this.debounce),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe((q) => this.searchFn(q));
  }

  private isNativelyBadInput(): boolean {
    return (
      this.inputEl.nativeElement.validity &&
      this.inputEl.nativeElement.validity.badInput
    );
  }

  protected hasAtLeastOneDifferentItem(arrayA: T[], arrayB: T[]): boolean {
    if (arrayA.length === 0 && arrayB.length === 0) {
      return false;
    } else if (arrayA.length !== arrayB.length) {
      return true;
    }

    return (
      arrayA.some((o) => !arrayB.some((op) => this.compareItem(o, op))) ||
      arrayB.some((o) => !arrayA.some((op) => this.compareItem(o, op)))
    );
  }

  protected focus(options?: FocusOptions): void {
    this.inputEl.nativeElement.focus(options);
  }
}
