import {
  ChangeDetectorRef,
  Directive,
  HostBinding,
  inject,
} from '@angular/core';

@Directive({
  selector: 'rds-carousel-item',
})
export class RdsCarouselItemDirective {
  private readonly cdr = inject(ChangeDetectorRef);

  @HostBinding('class.rds-carousel__item') private readonly class = true;
  @HostBinding('class.rds-carousel__item--visible') private _shown = false;

  _hide(): void {
    if (this._shown) {
      this._shown = false;
      this.cdr.markForCheck();
    }
  }

  _show(): void {
    if (!this._shown) {
      this._shown = true;
      this.cdr.markForCheck();
    }
  }

  isShown(): boolean {
    return this._shown;
  }
}
