import { InjectionToken } from '@angular/core';
import { CdkAccordion } from '@angular/cdk/accordion';
import { RdsExpansionPanelHeaderComponent } from './expansion-panel-header.component';

export type RdsAccordionTogglePosition = 'before' | 'after';

export type RdsAccordionSize = 's' | 'm';

export interface RdsAccordionBase extends CdkAccordion {
  togglePosition: RdsAccordionTogglePosition;
  _handleHeaderKeydown: (event: KeyboardEvent) => void;
  _handleHeaderFocus: (header: RdsExpansionPanelHeaderComponent) => void;
}

export const RDS_ACCORDION = new InjectionToken<RdsAccordionBase>(
  'RDS_ACCORDION',
);
