import {
  AfterContentInit,
  AfterViewInit,
  ChangeDetectionStrategy,
  Component,
  DestroyRef,
  ElementRef,
  EventEmitter,
  HostBinding,
  HostListener,
  inject,
  Input,
  OnDestroy,
  Output,
  ViewContainerRef,
} from '@angular/core';
import { RdsSidenavMenuComponent } from '../sidenav-menu/sidenav-menu.component';
import { RdsSidenavService } from '../sidenav.service';
import { delay, filter, map, tap } from 'rxjs/operators';
import {
  asapScheduler,
  EMPTY,
  fromEvent,
  merge,
  Observable,
  Subject,
  Subscription,
} from 'rxjs';
import {
  FocusableOption,
  FocusMonitor,
  FocusOrigin,
  isFakeMousedownFromScreenReader,
  isFakeTouchstartFromScreenReader,
} from '@angular/cdk/a11y';
import { TemplatePortal } from '@angular/cdk/portal';
import {
  FlexibleConnectedPositionStrategy,
  HorizontalConnectionPos,
  Overlay,
  OverlayConfig,
  OverlayRef,
  ScrollStrategy,
  VerticalConnectionPos,
} from '@angular/cdk/overlay';
import { Direction, Directionality } from '@angular/cdk/bidi';
import { ENTER, LEFT_ARROW, RIGHT_ARROW, SPACE } from '@angular/cdk/keycodes';
import { normalizePassiveListenerOptions } from '@angular/cdk/platform';
import { rdsSidenavExpansionAnimations } from '../sidenav-menu/sidenav-menu-expansion.animation';
import { NativeElement, RdsSidenavMenuPanel } from '../sidenav.types';
import { throwRdsMenuMissingError } from '../../menu/menu-error';
import { isHoverSupported } from '../../utils';
import { SidenavItem } from '../sidenav-item';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

declare const ngDevMode: boolean;

const passiveEventListenerOptions = normalizePassiveListenerOptions({
  passive: true,
}) as AddEventListenerOptions;

@Component({
  selector:
    'button[rds-sidenav-menu-trigger-for], button[rdsSidenavMenuTriggerFor]',
  templateUrl: './sidenav-menu-trigger.component.html',
  styleUrls: ['./sidenav-menu-trigger.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  animations: [rdsSidenavExpansionAnimations.indicatorRotate],
  providers: [
    { provide: SidenavItem, useExisting: RdsSidenavMenuTriggerComponent },
  ],
})
export class RdsSidenavMenuTriggerComponent
  implements
    AfterContentInit,
    AfterViewInit,
    OnDestroy,
    FocusableOption,
    NativeElement
{
  private readonly sidenavService = inject(RdsSidenavService);
  private readonly destroyRef = inject(DestroyRef);
  private readonly overlay = inject(Overlay);
  private readonly element = inject<ElementRef<HTMLElement>>(ElementRef);
  private readonly viewContainerRef = inject(ViewContainerRef);
  private readonly _dir = inject(Directionality, {
    optional: true,
  }) as Directionality;
  private readonly focusMonitor = inject(FocusMonitor);

  @Output() readonly menuOpened = new EventEmitter<void>();
  @Output() readonly menuClosed = new EventEmitter<void>();

  @HostBinding('attr.role') role = 'menuitem';
  @HostBinding('attr.aria-haspopup') _ariaHaspopup = true;
  @HostBinding('class') class = 'rds-sidenav-menu-trigger';
  @HostBinding('class.rds-sidenav-menu-hovered') isHovered = false;
  _openedBy: Exclude<FocusOrigin, 'program' | null> | 'hover' | undefined =
    undefined;

  private _menu: RdsSidenavMenuPanel;
  private _portal: TemplatePortal;
  private _overlayRef: OverlayRef | null = null;
  private _menuOpen = false;
  private _closingActionsSubscription = Subscription.EMPTY;
  private _menuCloseSubscription = Subscription.EMPTY;
  private readonly scrollStrategy: () => ScrollStrategy = () =>
    this.overlay.scrollStrategies.reposition();

  readonly _focused = new Subject<RdsSidenavMenuTriggerComponent>();
  readonly isMinimal$ = this.sidenavService.isMinimal$;
  readonly nativeElement = this.element.nativeElement;

  private _handleTouchStart = (event: TouchEvent): void => {
    if (!isFakeTouchstartFromScreenReader(event)) {
      this._openedBy = 'touch';
    }
  };

  @HostBinding('attr.aria-expanded')
  get _ariaExpanded(): boolean | null {
    return this.menuOpen || null;
  }

  @HostBinding('attr.aria-controls')
  get _ariaControl(): string | undefined | null {
    return this.menuOpen ? this.menu.panelId : null;
  }

  @Input('rdsSidenavMenuTriggerFor')
  get menu(): RdsSidenavMenuPanel {
    return this._menu;
  }

  set menu(menu: RdsSidenavMenuPanel) {
    if (menu === this._menu) {
      return;
    }

    this._menu = menu as RdsSidenavMenuPanel;
    this._menuCloseSubscription.unsubscribe();

    if (menu) {
      this._menuCloseSubscription = menu.closed.subscribe(() => {
        this._destroyMenu();
      });
    }
  }

  constructor() {
    fromEvent(
      this.element.nativeElement,
      'touchstart',
      passiveEventListenerOptions,
    )
      .pipe(takeUntilDestroyed())
      .subscribe((e: Event) => this._handleTouchStart(e as TouchEvent));
  }

  ngAfterContentInit(): void {
    this._checkMenu();

    if (isHoverSupported()) {
      this._handleHover();
    }

    this.menu.title = this.element.nativeElement.textContent ?? '';
  }

  ngAfterViewInit(): void {
    this.focusMonitor
      .monitor(this.element, false)
      .pipe(
        filter((v) => !!v),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe(() => this._focused.next(this));

    if (!this.sidenavService.isMinimal && this.menu?.openOnInit) {
      // set hover to omit focusing first element
      this._openedBy = 'hover';
      this.openMenu();
    }
  }

  ngOnDestroy(): void {
    if (this._overlayRef) {
      this._overlayRef.dispose();
      this._overlayRef = null;
    }

    if (this.focusMonitor) {
      this.focusMonitor.stopMonitoring(this.element);
    }

    this._menuCloseSubscription.unsubscribe();
    this._closingActionsSubscription.unsubscribe();
    this._focused.complete();
  }

  get menuOpen(): boolean {
    return this._menuOpen;
  }

  get dir(): Direction {
    return this._dir && this._dir.value === 'rtl' ? 'rtl' : 'ltr';
  }

  toggleMenu(): void {
    return this._menuOpen ? this.closeMenu() : this.openMenu();
  }

  openMenu(withBackdrop?: boolean): void {
    if (this._menuOpen) {
      return;
    }

    this._checkMenu();

    if (this.sidenavService.isMinimal) {
      const overlayRef = this._createOverlay();
      const overlayConfig = overlayRef.getConfig();

      this._setPosition(
        overlayConfig.positionStrategy as FlexibleConnectedPositionStrategy,
      );
      overlayConfig.hasBackdrop =
        withBackdrop == undefined ? true : withBackdrop;

      overlayRef.attach(this._getPortal());
    }

    this._closingActionsSubscription = this._menuClosingActions().subscribe(
      () => this.closeMenu(),
    );
    this._initMenu();

    if (this.menu instanceof RdsSidenavMenuComponent) {
      this.menu._startAnimation();
    }
  }

  closeMenu(): void {
    this.menu.closed.emit();
    this.menu.isOpened = false;
  }

  focus(origin?: FocusOrigin | 'hover', options?: FocusOptions): void {
    if (origin && origin === 'hover') {
      return;
    }

    if (this.focusMonitor && origin) {
      this.focusMonitor.focusVia(this.element, origin, options);
    } else {
      this.element.nativeElement.focus(options);
    }

    this._focused.next(this);
  }

  private _destroyMenu(): void {
    if (!this.menuOpen) {
      return;
    }

    const menu = this.menu;
    this._closingActionsSubscription.unsubscribe();

    this._overlayRef?.detach();

    // without setTimeout doesn't work for expandable menu
    setTimeout(() => {
      this.focus(this._openedBy);
      this._openedBy = undefined;
    });

    if (menu instanceof RdsSidenavMenuComponent) {
      menu._resetAnimation();
    }

    this._setIsMenuOpen(false);
  }

  private _initMenu(): void {
    this.menu.parentMenu = undefined;
    this.menu.direction = this.dir;

    if (this._openedBy !== 'hover') {
      this.menu.focusFirstItem(this._openedBy || 'program');
    }

    this._setIsMenuOpen(true);
  }

  private _setIsMenuOpen(isOpen: boolean): void {
    this._menuOpen = isOpen;
    this.menu.isOpened = isOpen;
    this._menuOpen ? this.menuOpened.emit() : this.menuClosed.emit();
  }

  private _checkMenu(): void {
    if (ngDevMode && !this.menu) {
      throwRdsMenuMissingError();
    }
  }

  private _createOverlay(): OverlayRef {
    const config = this._getOverlayConfig();

    if (
      !this._overlayRef ||
      this._overlayRef.getConfig().backdropClass !== config.backdropClass ||
      this._overlayRef.getConfig().panelClass !== config.panelClass ||
      this._overlayRef.getConfig().direction !== config.direction
    ) {
      if (this._overlayRef) {
        this._overlayRef.dispose();
      }

      this._subscribeToPositions(
        config.positionStrategy as FlexibleConnectedPositionStrategy,
      );
      this._overlayRef = this.overlay.create(config);
      this._overlayRef.keydownEvents().subscribe();
    }

    return this._overlayRef;
  }

  private _getOverlayConfig(): OverlayConfig {
    return new OverlayConfig({
      positionStrategy: this.overlay
        .position()
        .flexibleConnectedTo(this.element)
        .withFlexibleDimensions(false)
        .withGrowAfterOpen()
        .withTransformOriginOn('.rds-menu-panel'),
      backdropClass:
        this.menu.backdropClass || 'cdk-overlay-transparent-backdrop',
      panelClass: this.menu.overlayPanelClass,
      scrollStrategy: this.scrollStrategy(),
      direction: this._dir,
    });
  }

  private _subscribeToPositions(
    position: FlexibleConnectedPositionStrategy,
  ): void {
    if (this.menu.setPositionClasses) {
      position.positionChanges.subscribe((change) => {
        const posX =
          change.connectionPair.overlayX === 'start' ? 'after' : 'before';
        const posY =
          change.connectionPair.overlayY === 'top' ? 'below' : 'above';

        if (this.menu.setPositionClasses) {
          this.menu.setPositionClasses(posX, posY);
        }
      });
    }
  }

  private _setPosition(
    positionStrategy: FlexibleConnectedPositionStrategy,
  ): void {
    const originX: HorizontalConnectionPos = 'start';
    const [overlayY, overlayFallbackY]: VerticalConnectionPos[] = [
      'top',
      'bottom',
    ];
    const overlayX = originX;
    const originY = overlayY === 'top' ? 'bottom' : 'top';
    const originFallbackY = overlayFallbackY === 'top' ? 'bottom' : 'top';
    const offsetX = 61;
    const offsetYWhenBelow = -115;
    const offsetYWhenAbove = 65;

    positionStrategy.withPositions([
      {
        originX,
        originY,
        overlayX,
        overlayY,
        offsetX,
        offsetY: offsetYWhenBelow,
      },
      {
        originX,
        originY: originFallbackY,
        overlayX,
        overlayY: overlayFallbackY,
        offsetX,
        offsetY: offsetYWhenAbove,
      },
    ]);
  }

  private _menuClosingActions(): Observable<void | MouseEvent> {
    const backdrop = this._overlayRef
      ? this._overlayRef.backdropClick()
      : EMPTY;
    const detachments = this._overlayRef
      ? this._overlayRef.detachments()
      : EMPTY;

    return merge(backdrop, detachments);
  }

  @HostListener('mousedown', ['$event'])
  _handleMousedown(event: MouseEvent): void {
    if (!isFakeMousedownFromScreenReader(event)) {
      this._openedBy = event.button === 0 ? 'mouse' : undefined;
    }
  }

  @HostListener('keydown', ['$event'])
  _handleKeydown(event: KeyboardEvent): void {
    const keyCode = event.keyCode;
    const openedByArrow =
      (keyCode === RIGHT_ARROW && this.dir === 'ltr') ||
      (keyCode === LEFT_ARROW && this.dir === 'rtl');

    if (keyCode === ENTER) {
      // handled by _handleClick
      return;
    }

    if (keyCode === SPACE || openedByArrow) {
      this._openedBy = 'keyboard';
      this.openMenu();
    }
  }

  @HostListener('click', ['$event'])
  _handleClick({ keyCode }: { keyCode?: number }): void {
    if (keyCode === ENTER) {
      this._openedBy = 'keyboard';
    }

    this.toggleMenu();
  }

  private hovered(): Observable<boolean> {
    let isPanelHovered = false;

    return merge(
      fromEvent(this.element.nativeElement, 'mouseenter').pipe(map(() => true)),
      this.menu.hovered.pipe(tap((v) => (isPanelHovered = v))),
      fromEvent(this.element.nativeElement, 'mouseleave').pipe(
        delay(0),
        filter(() => !isPanelHovered),
        map(() => false),
      ),
    );
  }

  private _handleHover(): void {
    this.hovered()
      .pipe(
        filter(() => this.sidenavService.isMinimal),
        delay(0, asapScheduler),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe((isHovered) => {
        this.isHovered = isHovered;

        if (isHovered) {
          this._openedBy = 'hover';
          this.openMenu(false);
        } else {
          this.closeMenu();
        }
      });
  }

  private _getPortal(): TemplatePortal {
    if (!this._portal || this._portal.templateRef !== this.menu.templateRef) {
      this._portal = new TemplatePortal(
        this.menu.templateRef,
        this.viewContainerRef,
      );
    }

    return this._portal;
  }
}
