import { FocusableOption } from '@angular/cdk/a11y';
import { ENTER, hasModifierKey, SPACE } from '@angular/cdk/keycodes';
import {
  AfterViewInit,
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  HostBinding,
  HostListener,
  OnDestroy,
  OnInit,
  Output,
  ViewEncapsulation,
} from '@angular/core';
import { RdsLinkComponent } from '../link';

@Component({
  selector: 'rds-header-link',
  template: `<ng-content />`,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsHeaderLinkComponent
  extends RdsLinkComponent
  implements OnInit, AfterViewInit, FocusableOption, OnDestroy
{
  @HostBinding('class') class = 'rds-header-link';

  @Output()
  toggle = new EventEmitter<boolean>();

  protected override _standalone = true;

  ngOnInit(): void {
    this.type = 'secondary';
    this.size = 's';
  }

  @HostListener('keydown', ['$event'])
  onKeydown(event: KeyboardEvent): void {
    const code = event.keyCode;

    if ((code === ENTER || code === SPACE) && !hasModifierKey(event)) {
      this.toggle.next(!this.active);

      event.preventDefault();
    }
  }

  @HostListener('click')
  onClick(): void {
    if (!this.disabled) {
      this.toggle.next(!this.active);
    }
  }
}
