import { FocusMonitor } from '@angular/cdk/a11y';
import { NumberInput } from '@angular/cdk/coercion';
import {
  AfterContentInit,
  AfterViewInit,
  Attribute,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  DestroyRef,
  DoCheck,
  ElementRef,
  EventEmitter,
  forwardRef,
  HostBinding,
  HostListener,
  inject,
  Input,
  numberAttribute,
  OnDestroy,
  OnInit,
  Output,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import {
  AbstractControl,
  ControlContainer,
  ControlValueAccessor,
  FormGroupDirective,
  NG_VALUE_ACCESSOR,
  NgForm,
} from '@angular/forms';
import { noop } from 'rxjs';
import {
  animate,
  AnimationTriggerMetadata,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import {
  TAB_INDEX_DEFAULT,
  TAB_INDEX_NOT_FOCUSABLE,
  updateErrorState,
} from '../utils';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

let nextUniqueId = 0;

export class RdsSwitchChange {
  constructor(
    public readonly source: RdsSwitchComponent,
    public readonly checked: boolean,
  ) {}
}

export const rdsSwitchAnimations: {
  readonly transitionMessages: AnimationTriggerMetadata;
} = {
  /** Animation that transitions the form field's error and hint messages. */
  transitionMessages: trigger('transitionMessages', [
    state('enter', style({ opacity: 1, transform: 'translateY(0%)' })),
    transition('void => enter', [
      style({ opacity: 0, transform: 'translateY(-100%)' }),
      animate('200ms cubic-bezier(0.55, 0, 0.55, 0.2)'),
    ]),
  ]),
};

export type RdsSwitchPosition = 'before' | 'after';

export type RdsSwitchSize = 's' | 'm' | 'l';

@Component({
  selector: 'rds-switch',
  templateUrl: './switch.component.html',
  styleUrls: ['./switch.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  animations: [rdsSwitchAnimations.transitionMessages],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => RdsSwitchComponent),
      multi: true,
    },
  ],
})
export class RdsSwitchComponent
  implements
    ControlValueAccessor,
    OnInit,
    DoCheck,
    AfterContentInit,
    AfterViewInit,
    OnDestroy
{
  private readonly destroyRef = inject(DestroyRef);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly elementRef = inject<ElementRef<HTMLElement>>(ElementRef);
  private readonly focusMonitor = inject(FocusMonitor);
  private readonly parentForm = inject(NgForm, { optional: true });
  private readonly parentFormGroup = inject(FormGroupDirective, {
    optional: true,
  });
  private readonly controlContainer = inject(ControlContainer, {
    optional: true,
    host: true,
    skipSelf: true,
  });

  private _uniqueId = `rds-switch-${++nextUniqueId}`;
  private _errorState = false;
  private _tabIndex = TAB_INDEX_DEFAULT;
  private _control: AbstractControl | null | undefined;
  subscriptAnimationState: '' | 'enter' = '';

  @HostBinding('class') readonly hostClass = 'rds-switch';

  @Input() formControlName: string;

  @Input() name: string;

  @Input() size: RdsSwitchSize = 'l';

  @Input()
  @HostBinding('attr.id')
  id: string = this._uniqueId;

  @Input() labelPosition: RdsSwitchPosition = 'after';

  @HostBinding('attr.aria-labelledby')
  @Input('aria-label')
  ariaLabel: string | null = null;

  @HostBinding('attr.aria-label')
  @Input('aria-labelledby')
  ariaLabelledby: string | null = null;

  @Input('aria-describedby') ariaDescribedby: string;

  @Input({ transform: booleanAttribute }) required = false;

  @HostBinding('class.rds-checked')
  @Input({ transform: booleanAttribute })
  checked = false;

  @HostBinding('class.rds-switch--invalid')
  get errorState(): boolean {
    return this._errorState;
  }

  @HostBinding('class.rds-disabled')
  @Input({ transform: booleanAttribute })
  disabled = false;

  @Output() readonly buttonChanged: EventEmitter<RdsSwitchChange> =
    new EventEmitter<RdsSwitchChange>();

  @Output() readonly toggleChange: EventEmitter<void> =
    new EventEmitter<void>();

  get inputId(): string {
    return `${this.id || this._uniqueId}-input`;
  }

  @ViewChild('input') _inputElement: ElementRef<HTMLInputElement>;

  @HostListener('focus')
  onFocus(): void {
    this._inputElement.nativeElement.focus();
  }

  @HostBinding('tabIndex')
  @Input()
  set tabIndex(value: NumberInput) {
    this._tabIndex = numberAttribute(value, TAB_INDEX_DEFAULT);
  }

  get tabIndex(): number {
    return this.disabled ? TAB_INDEX_NOT_FOCUSABLE : this._tabIndex;
  }

  private _onChange: (value: boolean) => void = noop;
  private _onTouched: () => void = noop;

  constructor(@Attribute('tabindex') tabIndex?: string) {
    this._tabIndex = numberAttribute(tabIndex, TAB_INDEX_DEFAULT);
  }

  ngOnInit(): void {
    if (this.controlContainer) {
      if (this.formControlName) {
        this._control = this.controlContainer.control?.get(
          this.formControlName,
        );
      } else {
        // Missing FormControlName directive from host element of the component
      }
    } else {
      // Can't find parent FormGroup directive
    }
  }

  ngDoCheck(): void {
    if (this._control) {
      // We need to re-evaluate this on every change detection cycle, because there are some
      // error triggers that we can't subscribe to (e.g. parent form submissions). This means
      // that whatever logic is in here has to be super lean or we risk destroying the performance.
      this._errorState = updateErrorState(
        this.errorState,
        this.parentFormGroup,
        this.parentForm,
        this._control,
      );
    }
  }

  ngAfterContentInit(): void {
    const control = this._control;

    if (control && control.valueChanges) {
      control.valueChanges
        .pipe(takeUntilDestroyed(this.destroyRef))
        .subscribe(() => {
          control?.markAsDirty();
          control?.markAsTouched();
          this.cdr.markForCheck();
        });
    }

    const parent = this.parentFormGroup || this.parentForm;

    if (parent) {
      parent.ngSubmit
        .pipe(takeUntilDestroyed(this.destroyRef))
        .subscribe(() => {
          this.cdr.markForCheck();
        });
    }
  }

  ngAfterViewInit(): void {
    this.focusMonitor
      .monitor(this.elementRef, true)
      .subscribe((focusOrigin) => {
        if (focusOrigin === 'keyboard' || focusOrigin === 'program') {
          this._inputElement.nativeElement.focus();
        } else if (!focusOrigin) {
          Promise.resolve().then(() => this._onTouched());
        }
      });

    this.subscriptAnimationState = 'enter'; // Avoid animations on load.
    this.cdr.detectChanges();
  }

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.elementRef);
  }

  get isErrorMessageVisible(): boolean {
    return this.errorState;
  }

  _onChangeEvent(event: Event): void {
    event.stopPropagation();
    this.toggleChange.emit();

    if (this.disabled) {
      this._inputElement.nativeElement.checked = this.checked;

      return;
    }

    if (this.checked !== this._inputElement.nativeElement.checked) {
      this.checked = this._inputElement.nativeElement.checked;
      this.cdr.markForCheck();
    }

    this._emitChangeEvent();
  }

  _onInputClick(event: Event): void {
    event.stopPropagation();
  }

  writeValue(value: boolean): void {
    if (this.checked !== value) {
      this.checked = value;
      this.cdr.markForCheck();
    }
  }

  registerOnChange(fn: (value: boolean) => void): void {
    this._onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this._onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    if (this.disabled !== isDisabled) {
      this.disabled = isDisabled;
      this.cdr.markForCheck();
    }
  }

  focus(options?: FocusOptions): void {
    this._inputElement.nativeElement.focus(options);
  }

  toggle(): void {
    this.checked = !this.checked;
    this._onChange(this.checked);
  }

  _onLabelTextChange(): void {
    this.cdr.detectChanges();
  }

  private _emitChangeEvent(): void {
    this._onChange(this.checked);
    this.buttonChanged.emit(new RdsSwitchChange(this, this.checked));
  }
}
