import { CdkStep, STEPPER_GLOBAL_OPTIONS } from '@angular/cdk/stepper';
import {
  AfterContentInit,
  ChangeDetectionStrategy,
  Component,
  ContentChild,
  forwardRef,
  inject,
  OnDestroy,
  ViewContainerRef,
  ViewEncapsulation,
} from '@angular/core';
import { TemplatePortal } from '@angular/cdk/portal';
import { map, startWith, switchMap } from 'rxjs/operators';
import { UntypedFormControl, FormGroupDirective, NgForm } from '@angular/forms';
import { Subscription } from 'rxjs';
import { RdsStepLabelDirective } from './step-label.directive';
import { RdsStepContentDirective } from './step-content.directive';
import { RdsStepperComponent } from './stepper.component';
import { ErrorStateMatcher } from '../core';

@Component({
  selector: 'rds-step',
  template: ` <ng-template>
    <ng-content />
    <ng-template [cdkPortalOutlet]="_portal" />
  </ng-template>`,
  providers: [
    { provide: ErrorStateMatcher, useExisting: RdsStepComponent },
    { provide: CdkStep, useExisting: RdsStepComponent },
  ],
  encapsulation: ViewEncapsulation.None,
  exportAs: 'rdsStep',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsStepComponent
  extends CdkStep
  implements AfterContentInit, OnDestroy
{
  private readonly errorStateMatcher = inject(ErrorStateMatcher, {
    skipSelf: true,
  });
  private readonly viewContainerRef = inject(ViewContainerRef, {
    skipSelf: true,
  });

  private _isSelected = Subscription.EMPTY;

  @ContentChild(RdsStepLabelDirective)
  override stepLabel: RdsStepLabelDirective;

  @ContentChild(RdsStepContentDirective, { static: false })
  _lazyContent: RdsStepContentDirective | undefined;

  _portal: TemplatePortal | undefined;

  constructor() {
    super(
      inject(forwardRef(() => RdsStepperComponent)),
      inject(STEPPER_GLOBAL_OPTIONS, { optional: true }) || undefined,
    );
  }

  ngAfterContentInit(): void {
    this._isSelected = this._stepper.steps.changes
      .pipe(
        switchMap(() => {
          return this._stepper.selectionChange.pipe(
            map((event) => event.selectedStep === this),
            startWith(this._stepper.selected === this),
          );
        }),
      )
      .subscribe((isSelected) => {
        if (isSelected && this._lazyContent && !this._portal) {
          this._portal = new TemplatePortal(
            this._lazyContent._template,
            this.viewContainerRef,
          );
        }
      });
  }

  ngOnDestroy(): void {
    this._isSelected.unsubscribe();
  }

  /** Custom error state matcher that additionally checks for validity of interacted form. */
  isErrorState(
    control: UntypedFormControl | null,
    form: FormGroupDirective | NgForm | null,
  ): boolean {
    const originalErrorState = this.errorStateMatcher.isErrorState(
      control,
      form,
    );

    // Custom error state checks for the validity of form that is not submitted or touched
    // since user can trigger a form change by calling for another step without directly
    // interacting with the current form.
    const customErrorState = !!(control && control.invalid && this.interacted);

    return originalErrorState || customErrorState;
  }
}
