import {
  AfterViewInit,
  ChangeDetectionStrategy,
  Component,
  HostBinding,
  OnChanges,
  ViewEncapsulation,
} from '@angular/core';
import { RdsListItemComponent } from './list-item.component';
import { RDS_SINGLE_SELECT_LIST_ITEM_TEMPLATE } from './list-item.templates';
import { RdsAbstractSelectableListItemComponent } from './abstract-selectable-list-item.component';

@Component({
  template: RDS_SINGLE_SELECT_LIST_ITEM_TEMPLATE,
  selector:
    'button[rds-single-select-list-item], button[rdsSingleSelectListItem]',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: RdsListItemComponent,
      useExisting: RdsSingleSelectListItemComponent,
    },
  ],
})
export class RdsSingleSelectListItemComponent
  extends RdsAbstractSelectableListItemComponent
  implements AfterViewInit, OnChanges
{
  @HostBinding('class.rds-list-item--checked')
  @HostBinding('attr.aria-selected')
  protected get checkedBindings(): boolean {
    return this.checked;
  }

  protected override getSelectTypeClass(): string {
    return 'rds-single-select-list-item';
  }
}
