import { FocusMonitor, FocusOrigin } from '@angular/cdk/a11y';
import {
  AfterViewInit,
  ChangeDetectionStrategy,
  Component,
  HostBinding,
  HostListener,
  inject,
  OnDestroy,
  ViewEncapsulation,
} from '@angular/core';
import { Subject } from 'rxjs';
import {
  RdsListItemComponent,
  RdsNestableListItemComponent,
} from '../list-item';
import { RDS_NESTABLE_LIST_ITEM_TEMPLATE } from '../list-item';
import { RDS_MENU_PANEL, RdsMenuPanel } from './menu-panel';

@Component({
  selector: '[rds-menu-item], [rdsMenuItem]',
  exportAs: 'rdsMenuItem',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  template: RDS_NESTABLE_LIST_ITEM_TEMPLATE,
})
export class RdsMenuItemComponent
  extends RdsNestableListItemComponent
  implements AfterViewInit, OnDestroy
{
  public readonly _parentMenu = inject<RdsMenuPanel<RdsMenuItemComponent>>(
    RDS_MENU_PANEL,
    { optional: true },
  );
  protected readonly focusMonitor = inject(FocusMonitor);

  @HostBinding('class.rds-menu-item') rdsMenuItemClass = true;
  readonly _hovered = new Subject<RdsListItemComponent>();

  ngAfterViewInit(): void {
    this.focusMonitor.monitor(this.elementRef, false);
  }

  ngOnDestroy(): void {
    this._hovered.complete();
    this._focused.complete();
    this.focusMonitor.stopMonitoring(this.elementRef);
  }

  _getHostElement(): HTMLElement {
    return this.elementRef.nativeElement;
  }

  @HostListener('mouseenter')
  protected handleMouseEnter(): void {
    this._hovered.next(this);
  }

  override focus(origin?: FocusOrigin, options?: FocusOptions): void {
    if (this.focusMonitor && origin) {
      this.focusMonitor.focusVia(
        this.elementRef.nativeElement,
        origin,
        options,
      );
    } else {
      super.focus();
    }

    this._focused.next(this);
  }

  @HostListener('blur')
  override blur(): void {
    this.markAsInactive();
  }
}
