import {
  AfterContentInit,
  booleanAttribute,
  ContentChildren,
  Directive,
  HostBinding,
  Input,
  OnDestroy,
  QueryList,
  SimpleChanges,
} from '@angular/core';
import { FocusKeyManager } from '@angular/cdk/a11y';
import { startWith } from 'rxjs/operators';
import {
  RDS_ACCORDION,
  RdsAccordionBase,
  RdsAccordionSize,
  RdsAccordionTogglePosition,
} from './accordion-base';
import { RdsExpansionPanelHeaderComponent } from './expansion-panel-header.component';
import { Subject } from 'rxjs';
import { CdkAccordion } from '@angular/cdk/accordion';

let nextId = 0;

@Directive({
  selector: 'rds-accordion',
  exportAs: 'rdsAccordion',
  providers: [
    {
      provide: RDS_ACCORDION,
      useExisting: RdsAccordionDirective,
    },
  ],
})
export class RdsAccordionDirective
  extends CdkAccordion
  implements RdsAccordionBase, AfterContentInit, OnDestroy
{
  override readonly _stateChanges = new Subject<SimpleChanges>();
  override readonly _openCloseAllActions = new Subject<boolean>();
  override readonly id = `rds-accordion-${nextId++}`;
  private _ownHeaders = new QueryList<RdsExpansionPanelHeaderComponent>();
  private _keyManager: FocusKeyManager<RdsExpansionPanelHeaderComponent>;

  @ContentChildren(RdsExpansionPanelHeaderComponent, { descendants: true })
  _headers: QueryList<RdsExpansionPanelHeaderComponent>;

  @Input()
  size: RdsAccordionSize = 'm';

  @HostBinding('class') get class(): string[] {
    return ['rds-accordion', `rds-accordion-size-${this.size}`];
  }

  @Input({ transform: booleanAttribute })
  @HostBinding('class.rds-accordion-multi')
  override multi: boolean;

  @Input() togglePosition: RdsAccordionTogglePosition = 'after';

  ngAfterContentInit(): void {
    this._headers.changes
      .pipe(startWith(this._headers))
      .subscribe((headers: QueryList<RdsExpansionPanelHeaderComponent>) => {
        this._ownHeaders.reset(
          headers.filter((header) => header.panel.accordion === this),
        );
        this._ownHeaders.notifyOnChanges();
      });

    this._keyManager = new FocusKeyManager(this._ownHeaders)
      .withWrap()
      .withHomeAndEnd();
  }

  override ngOnDestroy(): void {
    super.ngOnDestroy();
    this._ownHeaders.destroy();
  }

  _handleHeaderKeydown(event: KeyboardEvent): void {
    this._keyManager.onKeydown(event);
  }

  _handleHeaderFocus(header: RdsExpansionPanelHeaderComponent): void {
    this._keyManager.updateActiveItem(header);
  }
}
