import { BooleanInput } from '@angular/cdk/coercion';
import {
  AfterContentInit,
  booleanAttribute,
  Component,
  ContentChild,
  DestroyRef,
  ElementRef,
  EventEmitter,
  HostBinding,
  inject,
  Input,
  NgZone,
  OnDestroy,
  Output,
  Renderer2,
  ViewEncapsulation,
} from '@angular/core';
import { BehaviorSubject, filter, merge } from 'rxjs';
import {
  RdsDrawerBackdrop,
  RdsDrawerContainerContentMargins,
} from './drawer-types';
import { RdsDrawerComponent } from './drawer.component';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

@Component({
  selector: 'rds-drawer-container',
  templateUrl: './drawer-container.component.html',
  styleUrls: ['./drawer-container.component.scss'],
  encapsulation: ViewEncapsulation.None,
  standalone: true,
})
export class RdsDrawerContainerComponent
  implements AfterContentInit, OnDestroy
{
  private readonly ngZone = inject(NgZone);
  private readonly elementRef = inject(ElementRef);
  private readonly destroyRef = inject(DestroyRef);
  private readonly renderer = inject(Renderer2);

  @HostBinding('class') readonly class = [
    'rds-drawer-container',
    'rds-drawer-overlay',
  ];
  @ContentChild(RdsDrawerComponent, { descendants: true })
  private drawer: RdsDrawerComponent;
  @ContentChild(RdsDrawerComponent, { descendants: true, read: ElementRef })
  private drawerElement: ElementRef;

  @Output() readonly backdropClick = new EventEmitter<void>();

  private readonly _contentMargin$ =
    new BehaviorSubject<RdsDrawerContainerContentMargins>({
      left: 0,
      right: 0,
    });
  readonly contentMargins$ = this._contentMargin$.asObservable();

  private start: RdsDrawerComponent | null;
  private end: RdsDrawerComponent | null;

  @Input()
  set backdrop(value: RdsDrawerBackdrop | BooleanInput) {
    if (value !== 'transparent') {
      value = booleanAttribute(value);
    }

    this._backdrop = value;
  }

  get backdrop(): RdsDrawerBackdrop {
    if (this._backdrop == null) {
      return !this.start || !this.end;
    }

    return this._backdrop;
  }

  private _backdrop: RdsDrawerBackdrop = false;

  ngAfterContentInit(): void {
    this.watchDrawerToggle();
    merge(this.drawer.positionChanged, this.drawer._modeChanged)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this.updateContentMargins());
  }

  ngOnDestroy(): void {
    this._contentMargin$.complete();
  }

  /** for internal purposes only */
  _onBackdropClicked(): void {
    this.drawer.toggle(false);
    this.backdropClick.emit();
  }

  isShowingBackdrop(): boolean {
    return this.isDrawerOpen() && !!this.backdrop;
  }

  updateContentMargins(): void {
    let left = 0;
    let right = 0;

    if (
      this.drawer &&
      this.drawer._animating === 'expanding' &&
      this.drawer.position == 'start'
    ) {
      if (this.drawer.mode == 'side') {
        left += this.getDrawerWidth();
      } else if (this.drawer.mode == 'push') {
        const width = this.getDrawerWidth();
        left += width;
        right -= width;
      }
    }

    if (
      this.drawer &&
      this.drawer._animating === 'expanding' &&
      this.drawer.position == 'end'
    ) {
      if (this.drawer.mode == 'side') {
        right += this.getDrawerWidth();
      } else if (this.drawer.mode == 'push') {
        const width = this.getDrawerWidth();
        right += width;
        left -= width;
      }
    }

    this.ngZone.run(() => this._contentMargin$.next({ left, right }));
  }

  private isDrawerOpen(): boolean {
    return (
      this.drawer != null &&
      (this.drawer._animating !== null || this.drawer.expanded)
    );
  }

  private watchDrawerToggle(): void {
    this.drawer._animationStarted
      .pipe(
        filter((event) => event.fromState !== event.toState),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe((event) => {
        if (event.toState !== 'open-instant') {
          this.renderer.addClass(
            this.elementRef.nativeElement,
            'rds-drawer-transition',
          );
        }

        this.updateContentMargins();
      });
  }

  private getDrawerWidth(): number {
    return this.drawerElement?.nativeElement?.offsetWidth ?? 0;
  }
}
