import { Pipe, PipeTransform } from '@angular/core';

const THRESHOLD = 1024;

@Pipe({ name: 'rdsFileSize' })
export class RdsFileSizePipe implements PipeTransform {
  transform(sizeInBytes: number, decimalPrecision = 1): string {
    if (sizeInBytes < THRESHOLD) {
      return sizeInBytes + 'B';
    }

    const units = ['kB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
    let unitIndex = -1;
    const r = Math.pow(10, decimalPrecision);

    do {
      sizeInBytes /= THRESHOLD;
      ++unitIndex;
    } while (
      Math.round(sizeInBytes * r) / r >= THRESHOLD &&
      unitIndex < units.length - 1
    );

    return sizeInBytes.toFixed(decimalPrecision) + units[unitIndex];
  }
}
