import { AnimationEvent } from '@angular/animations';
import { CdkAccordionItem } from '@angular/cdk/accordion';
import { UniqueSelectionDispatcher } from '@angular/cdk/collections';
import { TemplatePortal } from '@angular/cdk/portal';
import { DOCUMENT } from '@angular/common';
import {
  AfterContentInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  ElementRef,
  EventEmitter,
  HostBinding,
  inject,
  Input,
  OnChanges,
  OnDestroy,
  Output,
  SimpleChanges,
  ViewChild,
  ViewContainerRef,
  ViewEncapsulation,
} from '@angular/core';
import { Subject } from 'rxjs';
import { distinctUntilChanged, filter, startWith, take } from 'rxjs/operators';
import {
  RDS_ACCORDION,
  RdsAccordionBase,
  RdsAccordionTogglePosition,
} from './accordion-base';
import { rdsExpansionAnimations } from './expansion.animations';
import { RdsExpansionPanelContentDirective } from './expansion-panel-content.directive';
import { FocusableOption, FocusMonitor, FocusOrigin } from '@angular/cdk/a11y';
import { BooleanInput } from '@angular/cdk/coercion';

export type RdsExpansionPanelState = 'expanded' | 'collapsed';

let uniqueId = 0;

@Component({
  styleUrls: ['./expansion-panel.component.scss'],
  selector: 'rds-expansion-panel',
  exportAs: 'rdsExpansionPanel',
  template: ` <ng-content select="rds-expansion-panel-header" />
    <div
      class="rds-expansion-panel-content"
      role="region"
      [@bodyExpansion]="_getExpandedState()"
      (@bodyExpansion.done)="_bodyAnimationDone.next($event)"
      [attr.aria-labelledby]="_headerId"
      [id]="id"
      #body
    >
      <div class="rds-expansion-panel-body">
        <ng-content />
        <ng-template [cdkPortalOutlet]="_portal" />
      </div>
    </div>`,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  animations: [rdsExpansionAnimations.bodyExpansion],
  providers: [{ provide: RDS_ACCORDION, useValue: undefined }],
})
export class RdsExpansionPanelComponent
  extends CdkAccordionItem
  implements AfterContentInit, FocusableOption, OnChanges, OnDestroy
{
  override readonly accordion = inject(RDS_ACCORDION, {
    optional: true,
    skipSelf: true,
  }) as RdsAccordionBase;
  private readonly viewContainerRef = inject(ViewContainerRef);
  private readonly document = inject(DOCUMENT);
  private readonly focusMonitor = inject(FocusMonitor);
  private readonly elementRef = inject(ElementRef);
  private _togglePosition: RdsAccordionTogglePosition;

  @HostBinding('class') class = 'rds-expansion-panel';

  @Input()
  get togglePosition(): RdsAccordionTogglePosition {
    return (
      this._togglePosition || (this.accordion && this.accordion.togglePosition)
    );
  }

  set togglePosition(value: RdsAccordionTogglePosition) {
    this._togglePosition = value;
  }

  @Input({ transform: booleanAttribute })
  @HostBinding('class.rds-disabled')
  override disabled: boolean;

  @Input({ transform: booleanAttribute })
  @HostBinding('class.rds-expanded')
  override set expanded(value: boolean) {
    super.expanded = value;
  }

  override get expanded(): boolean {
    return super.expanded;
  }

  @Output() readonly afterExpand = new EventEmitter<void>();
  @Output() readonly afterCollapse = new EventEmitter<void>();
  @Output() override readonly opened = new EventEmitter<void>();
  @Output() override readonly closed = new EventEmitter<void>();
  @Output() override readonly expandedChange = new EventEmitter<boolean>();

  readonly _inputChanges = new Subject<SimpleChanges>();

  @ContentChild(RdsExpansionPanelContentDirective)
  _lazyContent: RdsExpansionPanelContentDirective;
  @ViewChild('body') _body: ElementRef<HTMLElement>;
  _portal: TemplatePortal;
  _headerId = `rds-expansion-panel-header-${uniqueId++}`;

  readonly _bodyAnimationDone = new Subject<AnimationEvent>();

  constructor() {
    super(
      inject(RDS_ACCORDION, {
        optional: true,
        skipSelf: true,
      }) as RdsAccordionBase,
      inject(ChangeDetectorRef),
      inject(UniqueSelectionDispatcher),
    );
    this._bodyAnimationDone
      .pipe(
        distinctUntilChanged((x, y) => {
          return x.fromState === y.fromState && x.toState === y.toState;
        }),
      )
      .subscribe((event) => {
        if (event.fromState !== 'void') {
          if (event.toState === 'expanded') {
            this.afterExpand.emit();
          } else if (event.toState === 'collapsed') {
            this.afterCollapse.emit();
          }
        }
      });
  }

  _getExpandedState(): RdsExpansionPanelState {
    return this.expanded ? 'expanded' : 'collapsed';
  }

  override toggle(): void {
    this.expanded = !this.expanded;
  }

  override close(): void {
    this.expanded = false;
  }

  override open(): void {
    this.expanded = true;
  }

  ngAfterContentInit(): void {
    this.focusMonitor.monitor(this.elementRef, true);

    if (this._lazyContent) {
      this.opened
        .pipe(
          startWith(null),
          filter(() => this.expanded && !this._portal),
          take(1),
        )
        .subscribe(() => {
          this._portal = new TemplatePortal(
            this._lazyContent._template,
            this.viewContainerRef,
          );
        });
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    this._inputChanges.next(changes);
  }

  override ngOnDestroy(): void {
    super.ngOnDestroy();
    this.focusMonitor.stopMonitoring(this.elementRef);
    this._bodyAnimationDone.complete();
    this._inputChanges.complete();
  }

  focus(origin?: FocusOrigin, options?: FocusOptions): void {
    if (origin) {
      this.focusMonitor.focusVia(this.elementRef, origin, options);
    } else {
      this.elementRef.nativeElement.focus(options);
    }
  }

  _containsFocus(): boolean {
    if (this._body) {
      const focusedElement = this.document.activeElement;
      const bodyElement = this._body.nativeElement;

      return (
        focusedElement === bodyElement || bodyElement.contains(focusedElement)
      );
    }

    return false;
  }
}
