import {
  ChangeDetectionStrategy,
  Component,
  ViewEncapsulation,
} from '@angular/core';
import {
  RdsDatepickerBaseDirective,
  RdsDatepickerContentComponent,
  RdsDatepickerControl,
} from './datepicker-base';
import {
  DateRange,
  RDS_RANGE_DATE_SELECTION_MODEL_PROVIDER,
} from './date-selection.model';
import { RDS_CALENDAR_RANGE_STRATEGY_PROVIDER } from './date-range-selection.strategy';

export interface RdsDateRangePickerInput<D> extends RdsDatepickerControl<D> {
  comparisonStart: D | null;
  comparisonEnd: D | null;
}

@Component({
  selector: 'rds-date-range-picker',
  template: '',
  exportAs: 'rdsDateRangePicker',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  providers: [
    RDS_RANGE_DATE_SELECTION_MODEL_PROVIDER,
    RDS_CALENDAR_RANGE_STRATEGY_PROVIDER,
    {
      provide: RdsDatepickerBaseDirective,
      useExisting: RdsDateRangePickerComponent,
    },
  ],
})
export class RdsDateRangePickerComponent<D> extends RdsDatepickerBaseDirective<
  RdsDateRangePickerInput<D>,
  DateRange<D>,
  D
> {
  protected override _forwardContentValues(
    instance: RdsDatepickerContentComponent<
      RdsDateRangePickerInput<D>,
      DateRange<D>,
      D
    >,
  ): void {
    super._forwardContentValues(instance);

    const input = this.datepickerInput;

    if (input) {
      instance.comparisonStart = input.comparisonStart;
      instance.comparisonEnd = input.comparisonEnd;
    }
  }
}
