import { Directive, HostBinding, InjectionToken, Input } from '@angular/core';
import {
  CdkCell,
  CdkCellDef,
  CdkColumnDef,
  CdkFooterCell,
  CdkFooterCellDef,
  CdkHeaderCell,
  CdkHeaderCellDef,
} from '@angular/cdk/table';

@Directive({
  selector: '[rds-cell-def], [rdsCellDef]',
  providers: [{ provide: CdkCellDef, useExisting: RdsCellDefDirective }],
})
export class RdsCellDefDirective extends CdkCellDef {}

@Directive({
  selector: '[rds-header-cell-def], [rdsHeaderCellDef]',
  providers: [
    { provide: CdkHeaderCellDef, useExisting: RdsHeaderCellDefDirective },
  ],
})
export class RdsHeaderCellDefDirective extends CdkHeaderCellDef {}

@Directive({
  selector: '[rds-footer-cell-def], [rdsFooterCellDef]',
  providers: [
    { provide: CdkFooterCellDef, useExisting: RdsFooterCellDefDirective },
  ],
})
export class RdsFooterCellDefDirective extends CdkFooterCellDef {}

export const RDS_SORT_HEADER_COLUMN_DEF =
  new InjectionToken<RdsColumnDefDirective>('RDS_SORT_HEADER_COLUMN_DEF');

@Directive({
  selector: '[rds-column-def], [rdsColumnDef]',
  providers: [
    { provide: CdkColumnDef, useExisting: RdsColumnDefDirective },
    {
      provide: RDS_SORT_HEADER_COLUMN_DEF,
      useExisting: RdsColumnDefDirective,
    },
  ],
})
export class RdsColumnDefDirective extends CdkColumnDef {
  @Input('rdsColumnDef')
  override get name(): string {
    return this._name;
  }

  override set name(name: string) {
    this._setNameInput(name);
  }

  protected override _updateColumnCssClassName(): void {
    super._updateColumnCssClassName();
    this._columnCssClassName.push(`rds-column-${this.cssClassFriendlyName}`);
  }
}

@Directive({
  selector: 'rds-header-cell, th[rdsHeaderCell], th[rds-header-cell]',
})
export class RdsHeaderCellDirective extends CdkHeaderCell {
  @HostBinding('class') class = 'rds-header-cell';
  @HostBinding('attr.role') role = 'columnheader';
}

@Directive({
  selector: 'rds-footer-cell, td[rdsFooterCell], td[rds-footer-cell]',
})
export class RdsFooterCellDirective extends CdkFooterCell {
  @HostBinding('class') class = 'rds-footer-cell';
  @HostBinding('attr.role') role = 'gridcell';
}

@Directive({
  selector: 'rds-cell, td[rdsCell], td[rds-cell]',
})
export class RdsCellDirective extends CdkCell {
  @HostBinding('class') class = 'rds-cell';
  @HostBinding('attr.role') role = 'gridcell';
}
