import { Directive, Input } from '@angular/core';
import { CdkTreeNodeDef } from '@angular/cdk/tree';

@Directive({
  selector: '[rdsTreeNodeDef], [rds-tree-node-def]',
  providers: [
    { provide: CdkTreeNodeDef, useExisting: RdsTreeNodeDefDirective },
  ],
})
export class RdsTreeNodeDefDirective<T> extends CdkTreeNodeDef<T> {
  // eslint-disable-next-line @angular-eslint/no-input-rename
  @Input('rdsTreeNode') data: T;
  @Input('rdsTreeNodeDefWhen') override when: (
    index: number,
    nodeData: T,
  ) => boolean;
}
