import { ElementRef } from '@angular/core';
import { RdsDialogRef } from './model';

export function getClosestDialog<T, D, R>(
  element: ElementRef<HTMLElement>,
  openDialogs: RdsDialogRef<T, D, R>[],
): RdsDialogRef<T, D, R> | null {
  let parent: HTMLElement | null = element.nativeElement.parentElement;

  while (parent && !parent.classList.contains('rds-dialog')) {
    parent = parent.parentElement;
  }

  return parent
    ? openDialogs.find((dialog) => dialog.id === parent?.id) || null
    : null;
}
