// this basically is rdsTooltip directive, it's needed only to provide rdsPopover* input names
// and make sure that hide and show delays are = 0
import { Directive, HostBinding, Input, TemplateRef } from '@angular/core';
import { BooleanInput } from '@angular/cdk/coercion';
import { RdsTooltipDirective } from '../tooltip';
import {
  RdsPopoverClass,
  RdsPopoverPosition,
  RdsPopoverTouchGestures,
} from './popover.types';
import { fromEvent } from 'rxjs';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

@Directive({
  selector: '[rdsPopover], [rds-popover]',
  exportAs: 'rdsPopover',
})
export class RdsPopoverDirective extends RdsTooltipDirective {
  protected override _hasBackdrop = true;
  protected override readonly isPopover = true;
  override showDelay = 0;
  override hideDelay = 0;

  @Input()
  @HostBinding('attr.aria-haspopup')
  rdsPopoverRole = 'dialog';

  @Input('rdsPopoverAriaDescription') override ariaDescription = '';

  override get hideArrow(): boolean {
    return super.hideArrow;
  }

  override set hideArrow(shouldHideArrow: BooleanInput) {
    super.hideArrow = shouldHideArrow;
  }

  @Input('rdsPopoverTouchGestures')
  override touchGestures: RdsPopoverTouchGestures = 'auto';

  @Input('rdsPopoverPosition')
  override get position(): RdsPopoverPosition {
    return super.position;
  }

  override set position(value: RdsPopoverPosition) {
    super.position = value;
  }

  @Input('rdsPopoverDisabled')
  override set disabled(value: BooleanInput) {
    super.disabled = value;
  }

  override get disabled(): boolean {
    return super.disabled;
  }

  @Input('rdsPopover')
  override get content(): string | TemplateRef<unknown> {
    return super.content;
  }

  override set content(value: string | TemplateRef<unknown>) {
    super.content = value;
  }

  @Input('rdsPopoverClass')
  override get tooltipClass(): RdsPopoverClass {
    return super.tooltipClass;
  }

  override set tooltipClass(value: RdsPopoverClass) {
    super.tooltipClass = value;
  }

  @Input('rdsPopoverHasBackdrop')
  override set hasBackdrop(v: BooleanInput) {
    super.hasBackdrop = v;
  }
  override get hasBackdrop(): boolean {
    return this._hasBackdrop;
  }

  @Input('rdsPopoverShowCloseButton')
  override set showCloseButton(v: BooleanInput) {
    super.showCloseButton = v;
  }

  override get showCloseButton(): boolean {
    return super.showCloseButton;
  }
  @HostBinding('attr.aria-expanded')
  protected override ariaExpanded: boolean | null = false;

  protected override _hideArrow = false;

  protected override handleFocus(): void {
    setTimeout(() => {
      if (!this._tooltipInstance) {
        return;
      }

      const firstInteractive = this._tooltipInstance.getFirstFocusableElement();
      const lastInteractive = this._tooltipInstance.getLastFocusableElement();

      if (!firstInteractive) {
        return;
      }

      this.focusMonitor.focusVia(firstInteractive, this.origin);
      this.createTabHandler(firstInteractive, true);

      if (lastInteractive) {
        this.createTabHandler(lastInteractive, false);
      }
    });
  }

  private createTabHandler(el: HTMLElement, withShiftKey: boolean): void {
    fromEvent(el, 'keydown')
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((event: Event) => {
        const { code, shiftKey } = event as KeyboardEvent;

        if (code === 'Tab' && shiftKey === withShiftKey) {
          event.preventDefault();
          this.hide();
        }
      });
  }
}
