import {
  booleanAttribute,
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  HostBinding,
  inject,
  Input,
  isDevMode,
  Output,
  ViewEncapsulation,
} from '@angular/core';
import { RDS_CHIP_LIST } from '../chip.tokens';
import { RdsChipListComponent } from '../chip-list/chip-list.component';
import { RdsChipComponent } from '../chip/chip.component';
import { BooleanInput } from '@angular/cdk/coercion';
import { RdsChipSelectEvent } from '../chip.types';

@Component({
  selector: 'rds-chip-selectable',
  templateUrl: './chip-selectable.component.html',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [
    { provide: RdsChipComponent, useExisting: RdsChipSelectableComponent },
  ],
})
export class RdsChipSelectableComponent<T> extends RdsChipComponent<T> {
  private readonly parentList = inject<RdsChipListComponent<T>>(RDS_CHIP_LIST, {
    optional: true,
  });
  private _selected = false;

  protected _selectable = true;

  chipListSelectable = true;
  chipListMulti = false;

  constructor() {
    super();

    if (isDevMode() && !this.parentList) {
      throw new Error(
        'RdsChipSelectableComponent can only be used as a child of RdsChipListComponent',
      );
    }

    this.role = 'option';
  }

  @HostBinding('class')
  override get hostClasses(): Record<string, boolean> {
    return {
      ...super.hostClasses,
      'rds-chips__chip--selected': this.selected,
    };
  }

  @HostBinding('attr.aria-checked')
  get isChecked(): boolean | null {
    return this.chipListMulti ? this.selected : null;
  }

  @HostBinding('attr.aria-selected')
  get isSelected(): boolean | null {
    return this.chipListMulti ? null : this.selected;
  }

  @Input()
  set selectable(value: BooleanInput) {
    this._selectable = booleanAttribute(value);
  }

  get selectable(): boolean {
    return this._selectable && this.chipListSelectable;
  }

  @Input()
  set selected(value: BooleanInput) {
    this.setSelectedState(booleanAttribute(value), false, true);
  }

  get selected(): boolean {
    return this._selected;
  }

  @Output()
  readonly selectedChange = new EventEmitter<RdsChipSelectEvent<T>>();

  override handlePrimaryActionInteraction(): void {
    if (this.selectable && !this.disabled) {
      this.toggleSelected(true);
    }
  }

  toggleSelected(isUserInput = false): void {
    this.setSelectedState(!this.selected, isUserInput, true);
  }

  setSelectedState(
    isSelected: boolean,
    isUserInput: boolean,
    emitEvent: boolean,
  ): void {
    if (!this.disabled && isSelected !== this.selected) {
      this._selected = isSelected;

      if (emitEvent) {
        this.selectedChange.emit({
          source: this,
          isUserInput,
          selected: this.selected,
        });
      }

      this.cdr.markForCheck();
    }
  }

  selectViaInteraction(): void {
    this.setSelectedState(true, true, true);
  }

  select(): void {
    this.setSelectedState(true, false, true);
  }

  deselect(): void {
    this.setSelectedState(false, false, true);
  }
}
