/* eslint-disable @typescript-eslint/no-non-null-assertion */
import { coerceElement } from '@angular/cdk/coercion';
import { ElementRef, inject, Injectable, OnDestroy } from '@angular/core';
import { Observable, Observer, Subject } from 'rxjs';

@Injectable({ providedIn: 'root' })
export class IntersectionObserverFactory {
  create(
    callback: IntersectionObserverCallback,
    options?: IntersectionObserverInit,
  ): IntersectionObserver | null {
    return typeof IntersectionObserver === 'undefined'
      ? null
      : new IntersectionObserver(callback, options);
  }
}

@Injectable()
export class IntersectionObserverRx implements OnDestroy {
  private readonly intersectionObserverFactory = inject(
    IntersectionObserverFactory,
  );

  private _observedElements = new Map<
    Element,
    {
      observer: IntersectionObserver | null;
      readonly stream: Subject<IntersectionObserverEntry[]>;
      count: number;
    }
  >();

  ngOnDestroy(): void {
    this._observedElements.forEach((_, element) =>
      this._cleanupObserver(element),
    );
  }

  observe(
    element: Element,
    options?: IntersectionObserverInit,
  ): Observable<IntersectionObserverEntry[]>;
  observe(
    element: ElementRef<Element>,
    options?: IntersectionObserverInit,
  ): Observable<IntersectionObserverEntry[]>;
  observe(
    elementOrRef: Element | ElementRef<Element>,
    options?: IntersectionObserverInit,
  ): Observable<IntersectionObserverEntry[]> {
    const element = coerceElement(elementOrRef);

    return new Observable((observer: Observer<IntersectionObserverEntry[]>) => {
      const stream = this._observeElement(element, options);
      const subscription = stream.subscribe(observer);

      return () => {
        subscription.unsubscribe();
        this._unobserveElement(element);
      };
    });
  }

  private _observeElement(
    element: Element,
    options?: IntersectionObserverInit,
  ): Subject<IntersectionObserverEntry[]> {
    if (!this._observedElements.has(element)) {
      const stream = new Subject<IntersectionObserverEntry[]>();
      const observer = this.intersectionObserverFactory.create(
        (mutations) => stream.next(mutations),
        options,
      );

      if (observer) {
        observer.observe(element);
      }

      this._observedElements.set(element, { observer, stream, count: 1 });
    } else {
      this._observedElements.get(element)!.count++;
    }

    return this._observedElements.get(element)!.stream;
  }

  private _unobserveElement(element: Element): void {
    if (this._observedElements.has(element)) {
      this._observedElements.get(element)!.count--;

      if (!this._observedElements.get(element)!.count) {
        this._cleanupObserver(element);
      }
    }
  }

  private _cleanupObserver(element: Element): void {
    if (this._observedElements.has(element)) {
      const { observer, stream } = this._observedElements.get(element)!;

      if (observer) {
        observer.disconnect();
      }

      stream.complete();
      this._observedElements.delete(element);
    }
  }
}
