import {
  ChangeDetectionStrategy,
  Component,
  ViewEncapsulation,
} from '@angular/core';
import { RdsButtonBaseComponent } from './button-base';

@Component({
  selector: `
    button[rds-fab-button],
    button[rdsFabButton],
  `,
  template: '<ng-content />',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class RdsFabButtonComponent extends RdsButtonBaseComponent {
  protected override buttonHostAttributesToClassMapper = {
    'rds-fab-button': 'rds-button--fab',
    rdsFabButton: 'rds-button--fab',
  };
}
