import { BooleanInput } from '@angular/cdk/coercion';
import {
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  EventEmitter,
  inject,
  Input,
  Output,
} from '@angular/core';
import { RdsButtonModule } from '../button';
import { RdsIconComponent } from '../icons';

export type RdsAlertType = 'info' | 'warning' | 'error' | 'success';
type RdsAlertIcon =
  | 'info'
  | 'warning_triangle'
  | 'close_circle'
  | 'checkmark_circle';

const TYPE_TO_ICON: Readonly<Record<RdsAlertType, RdsAlertIcon>> =
  Object.freeze({
    info: 'info',
    warning: 'warning_triangle',
    error: 'close_circle',
    success: 'checkmark_circle',
  });

@Component({
  selector: 'rds-alert',
  templateUrl: './alert.component.html',
  styleUrls: ['./alert.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  standalone: true,
  imports: [RdsIconComponent, RdsButtonModule],
})
export class RdsAlertComponent {
  private readonly cdr = inject(ChangeDetectorRef);

  @Output() closeAlert = new EventEmitter<void>();

  protected icon: RdsAlertIcon = 'info';

  @Input()
  set type(value: RdsAlertType) {
    this._type = value;
    this.icon = TYPE_TO_ICON[this._type];
    this.cdr.markForCheck();
  }

  get type(): RdsAlertType {
    return this._type;
  }

  private _type: RdsAlertType = 'info';

  @Input()
  set opened(value: BooleanInput) {
    const newValue = booleanAttribute(value);

    if (this._opened !== newValue) {
      this._opened = newValue;

      if (!this._opened) {
        this.closeAlert.emit();
      }
    }
  }

  get opened(): boolean {
    return this._opened;
  }

  private _opened = true;

  @Input({ transform: booleanAttribute }) showIcon = true;
  @Input({ transform: booleanAttribute }) closable = false;

  onClose(): void {
    this.opened = false;
  }
}
