import { BooleanInput, coerceStringArray } from '@angular/cdk/coercion';
import { BACKSPACE, hasModifierKey } from '@angular/cdk/keycodes';
import {
  AfterContentInit,
  booleanAttribute,
  Directive,
  ElementRef,
  EventEmitter,
  HostBinding,
  HostListener,
  inject,
  Input,
  isDevMode,
  OnChanges,
  OnDestroy,
  Output,
} from '@angular/core';
import {
  RDS_FORM_FIELD,
  RdsChipInputEvent,
  RdsFormFieldComponent,
} from '../form-field';
import { RdsChipGridComponent } from './chip-grid/chip-grid.component';
import { RDS_CHIPS_DEFAULT_OPTIONS } from '../chips/chip.tokens';
import { NgControl } from '@angular/forms';

let nextUniqueId = 0;

@Directive({
  selector: 'input[rdsChipInput], input[rds-chip-input]',
  exportAs: 'rdsChipInput',
})
export class RdsChipInputDirective<T>
  implements AfterContentInit, OnChanges, OnDestroy
{
  private readonly inputElement =
    inject<ElementRef<HTMLInputElement>>(ElementRef).nativeElement;
  private readonly formField = inject<RdsFormFieldComponent<T[]>>(
    RDS_FORM_FIELD,
    { optional: true },
  );
  private readonly ngControl = inject(NgControl, { optional: true });

  private focusLastChipOnBackspace = false;
  private _disabled = false;
  private _chipGrid?: RdsChipGridComponent<T>;

  focused = false;
  hidePlaceholder = false;

  @Input({ transform: booleanAttribute }) required = false;

  @HostBinding('class')
  get classes(): Record<string, boolean> {
    return {
      'rds-chip-input': true,
      'rds-chip-input-no-placeholder': this.hidePlaceholder,
    };
  }

  @HostBinding('attr.required')
  @HostBinding('attr.aria-required')
  get ariaRequired(): boolean | null {
    return this.required || null;
  }

  @HostBinding('attr.aria-invalid')
  get ariaInvalid(): boolean | null {
    return this.ngControl ? this.ngControl.invalid : null;
  }

  /* eslint-disable-next-line @angular-eslint/no-input-rename */
  @Input('rdsChipInputChipGrid')
  set chipGrid(value: RdsChipGridComponent<T>) {
    if (value) {
      this._chipGrid = value;
      this._chipGrid.registerInput(this);
    } else if (isDevMode()) {
      throw new Error(
        'RdsChipInputDirective: rdsChipInputChipGrid received falsy value',
      );
    }
  }

  /* eslint-disable-next-line @angular-eslint/no-input-rename */
  @Input({ alias: 'rdsChipInputAddOnBlur', transform: booleanAttribute })
  addOnBlur = false;
  /* eslint-disable-next-line @angular-eslint/no-input-rename */
  @Input({ alias: 'rdsChipInputSeparatorKeys', transform: coerceStringArray })
  separatorKeys = inject(RDS_CHIPS_DEFAULT_OPTIONS).separatorKeys;

  @HostBinding('attr.placeholder')
  @Input()
  placeholder = '';

  @HostBinding('id')
  @Input()
  id = `rds-chip-list-input-${nextUniqueId++}`;

  @HostBinding('disabled')
  @Input()
  set disabled(value: BooleanInput) {
    this._disabled = booleanAttribute(value);
  }

  get disabled(): boolean {
    return this._disabled || !!(this._chipGrid && this._chipGrid.disabled);
  }

  /* eslint-disable-next-line @angular-eslint/no-output-rename */
  @Output('rdsChipInputTokenEnd')
  readonly chipEnd = new EventEmitter<RdsChipInputEvent<T>>();

  get empty(): boolean {
    return !this.inputElement.value;
  }

  constructor() {
    if (this.formField) {
      this.inputElement.classList.add('rds-form-field-input-control');
    }
  }

  ngOnChanges(): void {
    this._chipGrid?.stateChanges?.next();
  }

  ngAfterContentInit(): void {
    this.focusLastChipOnBackspace = this.empty;
  }

  ngOnDestroy(): void {
    this.chipEnd.complete();
  }

  @HostListener('keydown', ['$event'])
  private onKeydown(event?: KeyboardEvent): void {
    if (event) {
      // To prevent the user from accidentally deleting chips when pressing BACKSPACE continuously,
      // We focus the last chip on backspace only after the user has released the backspace button,
      // And the input is empty (see behaviour in _keyup)
      if (event.keyCode === BACKSPACE && this.focusLastChipOnBackspace) {
        this._chipGrid?.focusLastChip();
        event.preventDefault();

        return;
      } else {
        this.focusLastChipOnBackspace = false;
      }
    }

    this.emitChipEnd(event);
  }

  @HostListener('keyup', ['$event'])
  onKeyup(event: KeyboardEvent): void {
    // Allow user to move focus to chips next time he presses backspace
    if (
      !this.focusLastChipOnBackspace &&
      event.keyCode === BACKSPACE &&
      this.empty
    ) {
      this.focusLastChipOnBackspace = true;
      event.preventDefault();
    }
  }

  @HostListener('blur', ['$event'])
  onBlur(): void {
    if (this.addOnBlur) {
      this.emitChipEnd();
    }

    this.focused = false;

    // Blur the chip list if it is not focused
    if (!this._chipGrid?.focused) {
      this._chipGrid?.blur();
    }

    this._chipGrid?.stateChanges.next();
  }

  @HostListener('focus', ['$event'])
  onFocus(): void {
    this.focused = true;
    this.focusLastChipOnBackspace = this.empty;
    this._chipGrid?.stateChanges.next();
  }

  @HostListener('input')
  onInput(): void {
    // Let chip list know whenever the value changes.
    this._chipGrid?.stateChanges.next();
  }

  emitChipEnd(event?: KeyboardEvent): void {
    if (!event || this.isSeparatorKey(event)) {
      this.chipEnd.emit({
        input: this.inputElement,
        value: this.inputElement.value,
        chipInput: this,
      });

      event?.preventDefault();
    }
  }

  focus(): void {
    this.inputElement.focus();
  }

  clear(): void {
    this.inputElement.value = '';
    this.focusLastChipOnBackspace = true;
  }

  setDescribedByIds(ids: string[]): void {
    // Set the value directly in the DOM since this binding
    // is prone to "changed after checked" errors.
    if (ids.length) {
      this.inputElement.setAttribute('aria-describedby', ids.join(' '));
    } else {
      this.inputElement.removeAttribute('aria-describedby');
    }
  }

  private isSeparatorKey(event: KeyboardEvent): boolean {
    return !hasModifierKey(event) && this.separatorKeys.includes(event.key);
  }
}
