import { ChangeDetectionStrategy, Component, Input } from '@angular/core';

let nextId = 0;

@Component({
  selector: 'rds-anchor-menu',
  templateUrl: './anchor-menu.component.html',
  styleUrls: ['./anchor-menu.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsAnchorMenuComponent {
  readonly headerId = `rds-anchor-menu-header-${nextId++}`;

  @Input()
  navAriaLabel?: string;

  get labelledBy(): string | undefined {
    return this.navAriaLabel ? undefined : this.headerId;
  }
}
