import {
  FocusMonitor,
  FocusOrigin,
  isFakeMousedownFromScreenReader,
  isFakeTouchstartFromScreenReader,
} from '@angular/cdk/a11y';
import { Direction, Directionality } from '@angular/cdk/bidi';
import { ENTER, LEFT_ARROW, RIGHT_ARROW, SPACE } from '@angular/cdk/keycodes';
import {
  FlexibleConnectedPositionStrategy,
  HorizontalConnectionPos,
  Overlay,
  OverlayConfig,
  OverlayRef,
  ScrollStrategy,
  VerticalConnectionPos,
} from '@angular/cdk/overlay';
import { normalizePassiveListenerOptions } from '@angular/cdk/platform';
import { TemplatePortal } from '@angular/cdk/portal';
import {
  AfterContentInit,
  booleanAttribute,
  Directive,
  ElementRef,
  EventEmitter,
  HostBinding,
  HostListener,
  inject,
  InjectionToken,
  Input,
  OnDestroy,
  Output,
  ViewContainerRef,
} from '@angular/core';
import {
  EMPTY,
  Observable,
  Subscription,
  asapScheduler,
  merge,
  of,
} from 'rxjs';
import { delay, filter, take, takeUntil } from 'rxjs/operators';
import {
  throwRdsMenuMissingError,
  throwRdsMenuRecursiveError,
} from './menu-error';
import { RdsMenuItemComponent } from './menu-item.component';
import { RDS_MENU_PANEL, RdsMenuPanel } from './menu-panel';
import { RdsMenuComponent } from './menu.component';
import {
  RdsMenuPositionX,
  RdsMenuPositionY,
  RdsMenuCloseReason,
} from './menu.types';

export const RDS_MENU_SCROLL_STRATEGY = new InjectionToken<
  () => ScrollStrategy
>('RDS_MENU_SCROLL_STRATEGY');

export function RDS_MENU_SCROLL_STRATEGY_FACTORY(
  overlay: Overlay,
): () => ScrollStrategy {
  return () => overlay.scrollStrategies.reposition();
}

export const RDS_MENU_SCROLL_STRATEGY_FACTORY_PROVIDER = {
  provide: RDS_MENU_SCROLL_STRATEGY,
  deps: [Overlay],
  useFactory: RDS_MENU_SCROLL_STRATEGY_FACTORY,
};

export const MENU_PANEL_TOP_PADDING = 8;

const passiveEventListenerOptions = normalizePassiveListenerOptions({
  passive: true,
});

@Directive({
  selector: `[rds-menu-trigger-for], [rdsMenuTriggerFor]`,
  exportAs: 'rdsMenuTrigger',
})
export class RdsMenuTriggerDirective implements AfterContentInit, OnDestroy {
  private readonly overlay = inject(Overlay);
  private readonly element = inject<ElementRef<HTMLElement>>(ElementRef);
  private readonly viewContainerRef = inject(ViewContainerRef);
  private readonly scrollStrategy = inject<() => ScrollStrategy>(
    RDS_MENU_SCROLL_STRATEGY,
  );
  private readonly parentMenu = inject(RDS_MENU_PANEL, { optional: true });
  private readonly menuItemInstance = inject(RdsMenuItemComponent, {
    optional: true,
    self: true,
  });
  private readonly _dir = inject(Directionality, {
    optional: true,
  }) as Directionality;
  private readonly focusMonitor = inject(FocusMonitor, { optional: true });

  @Input() rdsMenuTriggerData: any;
  @Input({ transform: booleanAttribute }) rdsMenuTriggerRestoreFocus = true;
  @Output() readonly menuOpened: EventEmitter<void> = new EventEmitter<void>();
  @Output() readonly menuClosed: EventEmitter<void> = new EventEmitter<void>();

  @HostBinding('attr.aria-haspopup') _ariaHaspopup = true;
  @HostBinding('class') class = 'rds-menu-trigger';
  _openedBy: Exclude<FocusOrigin, 'program' | null> | undefined = undefined;

  private _menu: RdsMenuPanel;

  private _portal: TemplatePortal;
  private _overlayRef: OverlayRef | null = null;
  private _menuOpen = false;
  private _closingActionsSubscription = Subscription.EMPTY;
  private _hoverSubscription = Subscription.EMPTY;
  private _menuCloseSubscription = Subscription.EMPTY;
  private _parentMenu =
    this.parentMenu instanceof RdsMenuComponent ? this.parentMenu : undefined;

  private _handleTouchStart = (event: TouchEvent): void => {
    if (!isFakeTouchstartFromScreenReader(event)) {
      this._openedBy = 'touch';
    }
  };

  @HostBinding('attr.aria-expanded')
  get _ariaExpanded(): boolean | null {
    return this.menuOpen || null;
  }

  @HostBinding('attr.aria-controls')
  get _ariaControl(): string | undefined | null {
    return this.menuOpen ? this.menu.panelId : null;
  }

  @Input('rdsMenuTriggerFor')
  get menu(): RdsMenuPanel {
    return this._menu;
  }

  set menu(menu: RdsMenuPanel) {
    if (menu === this._menu) {
      return;
    }

    this._menu = menu;
    this._menuCloseSubscription.unsubscribe();

    if (menu) {
      if (menu === this._parentMenu) {
        throwRdsMenuRecursiveError();
      }

      this._menuCloseSubscription = menu.closed.subscribe(
        (reason: RdsMenuCloseReason) => {
          this._destroyMenu(reason);

          if ((reason === 'click' || reason === 'tab') && this._parentMenu) {
            this._parentMenu.closed.emit(reason);
          }
        },
      );
    }
  }

  constructor() {
    this.element.nativeElement.addEventListener(
      'touchstart',
      this._handleTouchStart,
      passiveEventListenerOptions,
    );

    if (this.menuItemInstance) {
      this.menuItemInstance.hasChildItems = this.triggersSubmenu();
    }
  }

  ngAfterContentInit(): void {
    this._checkMenu();
    this._handleHover();
  }

  ngOnDestroy(): void {
    if (this._overlayRef) {
      this._overlayRef.dispose();
      this._overlayRef = null;
    }

    this.element.nativeElement.removeEventListener(
      'touchstart',
      this._handleTouchStart,
      passiveEventListenerOptions,
    );

    this._menuCloseSubscription.unsubscribe();
    this._closingActionsSubscription.unsubscribe();
    this._hoverSubscription.unsubscribe();
  }

  get menuOpen(): boolean {
    return this._menuOpen;
  }

  get dir(): Direction {
    return this._dir && this._dir.value === 'rtl' ? 'rtl' : 'ltr';
  }

  triggersSubmenu(): boolean {
    return !!(this.menuItemInstance && this._parentMenu);
  }

  toggleMenu(): void {
    return this._menuOpen ? this.closeMenu() : this.openMenu();
  }

  openMenu(): void {
    if (this._menuOpen) {
      return;
    }

    this._checkMenu();

    const overlayRef = this._createOverlay();
    const overlayConfig = overlayRef.getConfig();

    this._setPosition(
      overlayConfig.positionStrategy as FlexibleConnectedPositionStrategy,
    );
    overlayConfig.hasBackdrop = !this.triggersSubmenu();
    overlayRef.attach(this._getPortal());

    if (this.menu.lazyContent) {
      this.menu.lazyContent.attach(this.rdsMenuTriggerData);
    }

    this._closingActionsSubscription = this._menuClosingActions().subscribe(
      () => this.closeMenu(),
    );
    this._initMenu();

    if (this.menu instanceof RdsMenuComponent) {
      this.menu._startAnimation();
    }
  }

  closeMenu(): void {
    this.menu.closed.emit();
  }

  focus(origin?: FocusOrigin, options?: FocusOptions): void {
    if (this.focusMonitor && origin) {
      this.focusMonitor.focusVia(this.element, origin, options);
    } else {
      this.element.nativeElement.focus(options);
    }
  }

  updatePosition(): void {
    this._overlayRef?.updatePosition();
  }

  private _destroyMenu(reason: RdsMenuCloseReason): void {
    if (!this._overlayRef || !this.menuOpen) {
      return;
    }

    const menu = this.menu;
    this._closingActionsSubscription.unsubscribe();
    this._overlayRef.detach();

    if (
      this.rdsMenuTriggerRestoreFocus &&
      (reason === 'keydown' || !this._openedBy || !this.triggersSubmenu())
    ) {
      this.focus(this._openedBy);
    }

    this._openedBy = undefined;

    if (menu instanceof RdsMenuComponent) {
      menu._resetAnimation();

      if (menu.lazyContent) {
        menu._animationDone
          .pipe(
            filter((event) => event.toState === 'void'),
            take(1),
            takeUntil(menu.lazyContent._attached),
          )
          .subscribe({
            next: () => menu.lazyContent.detach(),
            complete: () => this._setIsMenuOpen(false),
          });
      } else {
        this._setIsMenuOpen(false);
      }
    } else {
      this._setIsMenuOpen(false);

      if (menu.lazyContent) {
        menu.lazyContent.detach();
      }
    }
  }

  private _initMenu(): void {
    this.menu.parentMenu = this.triggersSubmenu()
      ? this._parentMenu
      : undefined;
    this.menu.direction = this.dir;
    this._setMenuElevation();
    this.menu.focusFirstItem(this._openedBy || 'program');

    this._setIsMenuOpen(true);
  }

  private _setMenuElevation(): void {
    if (this.menu.setElevation) {
      let depth = 0;
      let parentMenu = this.menu.parentMenu;

      while (parentMenu) {
        depth++;
        parentMenu = parentMenu.parentMenu;
      }

      this.menu.setElevation(depth);
    }
  }

  private _setIsMenuOpen(isOpen: boolean): void {
    this._menuOpen = isOpen;
    this._menuOpen ? this.menuOpened.emit() : this.menuClosed.emit();

    if (this.triggersSubmenu() && this.menuItemInstance) {
      this.menuItemInstance.activated = isOpen;
    }
  }

  private _checkMenu(): void {
    if (!this.menu) {
      throwRdsMenuMissingError();
    }
  }

  private _createOverlay(): OverlayRef {
    const config = this._getOverlayConfig();

    if (
      !this._overlayRef ||
      this._overlayRef.getConfig().backdropClass !== config.backdropClass ||
      this._overlayRef.getConfig().panelClass !== config.panelClass ||
      this._overlayRef.getConfig().direction !== config.direction
    ) {
      if (this._overlayRef) {
        this._overlayRef.dispose();
      }

      this._subscribeToPositions(
        config.positionStrategy as FlexibleConnectedPositionStrategy,
      );
      this._overlayRef = this.overlay.create(config);
      this._overlayRef.keydownEvents().subscribe();
    }

    return this._overlayRef;
  }

  private _getOverlayConfig(): OverlayConfig {
    return new OverlayConfig({
      positionStrategy: this.overlay
        .position()
        .flexibleConnectedTo(this.element)
        .withLockedPosition()
        .withGrowAfterOpen()
        .withTransformOriginOn('.rds-menu-panel, .rds-mdc-menu-panel'),
      backdropClass:
        this.menu.backdrop === 'transparent'
          ? 'cdk-overlay-transparent-backdrop'
          : undefined,
      panelClass: this.menu.overlayPanelClass,
      scrollStrategy: this.scrollStrategy(),
      direction: this._dir,
    });
  }

  private _subscribeToPositions(
    position: FlexibleConnectedPositionStrategy,
  ): void {
    if (this.menu.setPositionClasses) {
      position.positionChanges.subscribe((change) => {
        const posX: RdsMenuPositionX =
          change.connectionPair.overlayX === 'start' ? 'after' : 'before';
        const posY: RdsMenuPositionY =
          change.connectionPair.overlayY === 'top' ? 'below' : 'above';

        if (this.menu.setPositionClasses) {
          this.menu.setPositionClasses(posX, posY);
        }
      });
    }
  }

  private _setPosition(
    positionStrategy: FlexibleConnectedPositionStrategy,
  ): void {
    let [originX, originFallbackX]: HorizontalConnectionPos[] =
      this.menu.xPosition === 'before' ? ['end', 'start'] : ['start', 'end'];

    const [overlayY, overlayFallbackY]: VerticalConnectionPos[] =
      this.menu.yPosition === 'above' ? ['bottom', 'top'] : ['top', 'bottom'];

    let [originY, originFallbackY] = [overlayY, overlayFallbackY];
    let [overlayX, overlayFallbackX] = [originX, originFallbackX];
    let offsetY = 0;

    if (this.triggersSubmenu()) {
      overlayFallbackX = originX =
        this.menu.xPosition === 'before' ? 'start' : 'end';
      originFallbackX = overlayX = originX === 'end' ? 'start' : 'end';
      offsetY =
        overlayY === 'bottom'
          ? MENU_PANEL_TOP_PADDING
          : -MENU_PANEL_TOP_PADDING;
    } else if (!this.menu.overlapTrigger) {
      originY = overlayY === 'top' ? 'bottom' : 'top';
      originFallbackY = overlayFallbackY === 'top' ? 'bottom' : 'top';
    }

    positionStrategy.withPositions([
      { originX, originY, overlayX, overlayY, offsetY },
      {
        originX: originFallbackX,
        originY,
        overlayX: overlayFallbackX,
        overlayY,
        offsetY,
      },
      {
        originX,
        originY: originFallbackY,
        overlayX,
        overlayY: overlayFallbackY,
        offsetY: -offsetY,
      },
      {
        originX: originFallbackX,
        originY: originFallbackY,
        overlayX: overlayFallbackX,
        overlayY: overlayFallbackY,
        offsetY: -offsetY,
      },
    ]);
  }

  private _menuClosingActions(): Observable<
    MouseEvent | RdsMenuCloseReason | RdsMenuItemComponent | void
  > {
    const backdrop = this._overlayRef
      ? this._overlayRef.backdropClick()
      : EMPTY;
    const detachments = this._overlayRef
      ? this._overlayRef.detachments()
      : EMPTY;
    const parentClose = this._parentMenu ? this._parentMenu.closed : of();
    const hover = this._parentMenu
      ? this._parentMenu._hovered().pipe(
          filter((active) => active !== this.menuItemInstance),
          filter(() => this._menuOpen),
        )
      : of();

    return merge(
      backdrop,
      parentClose as Observable<RdsMenuCloseReason>,
      hover,
      detachments,
    );
  }

  @HostListener('mousedown', ['$event'])
  _handleMousedown(event: MouseEvent): void {
    if (!isFakeMousedownFromScreenReader(event)) {
      this._openedBy = event.button === 0 ? 'mouse' : undefined;

      if (this.triggersSubmenu()) {
        event.preventDefault();
      }
    }
  }

  @HostListener('keydown', ['$event'])
  _handleKeydown(event: KeyboardEvent): void {
    const keyCode = event.keyCode;

    if (keyCode === ENTER || keyCode === SPACE) {
      this._openedBy = 'keyboard';

      if (
        !['BUTTON', 'A'].includes(
          this.element.nativeElement.tagName.toUpperCase(),
        )
      ) {
        this.openMenu();
      }
    }

    if (
      this.triggersSubmenu() &&
      ((keyCode === RIGHT_ARROW && this.dir === 'ltr') ||
        (keyCode === LEFT_ARROW && this.dir === 'rtl'))
    ) {
      this._openedBy = 'keyboard';
      this.openMenu();
    }
  }

  @HostListener('click', ['$event'])
  _handleClick(event: MouseEvent): void {
    if (this.triggersSubmenu()) {
      event.stopPropagation();
      this.openMenu();
    } else {
      this.toggleMenu();
    }
  }

  private _handleHover(): void {
    if (!this.triggersSubmenu() || !this._parentMenu) {
      return;
    }

    this._hoverSubscription = this._parentMenu
      ._hovered()
      .pipe(
        filter(
          (active) => active === this.menuItemInstance && !active.disabled,
        ),
        delay(0, asapScheduler),
      )
      .subscribe(() => {
        this._openedBy = 'mouse';

        if (this.menu instanceof RdsMenuComponent && this.menu._isAnimating) {
          const untilHovered = this._parentMenu
            ? this._parentMenu._hovered()
            : EMPTY;

          this.menu._animationDone
            .pipe(take(1), delay(0, asapScheduler), takeUntil(untilHovered))
            .subscribe(() => this.openMenu());
        } else {
          this.openMenu();
        }
      });
  }

  private _getPortal(): TemplatePortal {
    if (!this._portal || this._portal.templateRef !== this.menu.templateRef) {
      this._portal = new TemplatePortal(
        this.menu.templateRef,
        this.viewContainerRef,
      );
    }

    return this._portal;
  }
}
