import { FocusKeyManager, FocusOrigin } from '@angular/cdk/a11y';
import { Direction } from '@angular/cdk/bidi';
import {
  DOWN_ARROW,
  ESCAPE,
  hasModifierKey,
  LEFT_ARROW,
  RIGHT_ARROW,
  UP_ARROW,
} from '@angular/cdk/keycodes';
import {
  AfterContentInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  Component,
  ContentChild,
  ContentChildren,
  DestroyRef,
  ElementRef,
  EventEmitter,
  HostBinding,
  HostListener,
  inject,
  InjectionToken,
  Input,
  NgZone,
  OnDestroy,
  OnInit,
  Output,
  QueryList,
  TemplateRef,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { merge, Observable, Subject, Subscription } from 'rxjs';
import { startWith, switchMap, take, tap } from 'rxjs/operators';
import { rdsMenuAnimations } from './menu-animations';
import {
  RDS_MENU_CONTENT,
  RdsMenuContentDirective,
} from './menu-content.directive';
import {
  throwRdsMenuInvalidPositionX,
  throwRdsMenuInvalidPositionY,
} from './menu-error';
import { RdsMenuItemComponent } from './menu-item.component';
import { RDS_MENU_PANEL, RdsMenuPanel } from './menu-panel';
import { AnimationEvent } from '@angular/animations';
import {
  RdsMenuBackdrop,
  RdsMenuCloseReason,
  RdsMenuPositionX,
  RdsMenuPositionY,
  RdsMenuSize,
} from './menu.types';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { TAB_INDEX_DEFAULT, TAB_INDEX_NOT_FOCUSABLE } from '../utils';

export interface RdsMenuDefaultOptions {
  xPosition: RdsMenuPositionX;
  yPosition: RdsMenuPositionY;
  overlapTrigger: boolean;
  backdrop: RdsMenuBackdrop;
  overlayPanelClass?: string | string[];
}

export const RDS_MENU_DEFAULT_OPTIONS =
  new InjectionToken<RdsMenuDefaultOptions>('RDS_MENU_DEFAULT_OPTIONS', {
    providedIn: 'root',
    factory: RDS_MENU_DEFAULT_OPTIONS_FACTORY,
  });

export function RDS_MENU_DEFAULT_OPTIONS_FACTORY(): RdsMenuDefaultOptions {
  return {
    overlapTrigger: false,
    xPosition: 'after',
    yPosition: 'below',
    backdrop: 'transparent',
  };
}

let menuPanelUid = 0;

@Component({
  selector: 'rds-menu',
  template: ` <ng-template>
    <div
      class="rds-menu-panel rds-menu-panel--{{ size }}"
      [id]="panelId"
      [ngClass]="_classList"
      (keydown)="_handleKeydown($event)"
      (click)="closed.emit('click')"
      [@transformMenu]="_panelAnimationState"
      (@transformMenu.start)="_onAnimationStart($event)"
      (@transformMenu.done)="_onAnimationDone($event)"
      tabindex="-1"
      role="menu"
      [attr.aria-label]="ariaLabel || null"
      [attr.aria-labelledby]="ariaLabelledby || null"
      [attr.aria-describedby]="ariaDescribedby || null"
    >
      <div class="rds-menu-content">
        <ng-content />
      </div>
    </div>
  </ng-template>`,
  styleUrls: ['./menu.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  exportAs: 'rdsMenu',
  animations: [rdsMenuAnimations.transformMenu, rdsMenuAnimations.fadeInItems],
  providers: [{ provide: RDS_MENU_PANEL, useExisting: RdsMenuComponent }],
})
export class RdsMenuComponent
  implements
    AfterContentInit,
    RdsMenuPanel<RdsMenuItemComponent>,
    OnInit,
    OnDestroy
{
  private readonly destroyRef = inject(DestroyRef);
  private readonly elementRef = inject<ElementRef<HTMLElement>>(ElementRef);
  private readonly ngZone = inject(NgZone);
  private readonly defaultOptions = inject(RDS_MENU_DEFAULT_OPTIONS);

  private _keyManager: FocusKeyManager<RdsMenuItemComponent>;
  private _xPosition: RdsMenuPositionX = this.defaultOptions.xPosition;
  private _yPosition: RdsMenuPositionY = this.defaultOptions.yPosition;
  private _previousElevation: string;
  private _size: RdsMenuSize = 'm';
  protected _elevationPrefix = 'rds-elevation-z';
  protected _baseElevation = 4;

  @ContentChildren(RdsMenuItemComponent, { descendants: true })
  _allItems: QueryList<RdsMenuItemComponent>;

  @HostBinding('attr.aria-label') attrAriaLabel = null;
  @HostBinding('attr.aria-labelledby') attrAriaLabelledBy = null;
  @HostBinding('attr.aria-describedby') attrAriaDescribedBy = null;

  @HostBinding('tabIndex')
  protected tabIndex = TAB_INDEX_NOT_FOCUSABLE;

  private _directDescendantItems = new QueryList<RdsMenuItemComponent>();

  private _tabSubscription = Subscription.EMPTY;

  _classList: { [key: string]: boolean } = {};

  _panelAnimationState: 'void' | 'enter' = 'void';

  readonly _animationDone = new Subject<AnimationEvent>();

  _isAnimating: boolean;

  parentMenu: RdsMenuPanel | undefined;

  direction: Direction;

  overlayPanelClass: string | string[] =
    this.defaultOptions.overlayPanelClass || '';

  @Input() backdrop: RdsMenuBackdrop = this.defaultOptions.backdrop;

  @Input('aria-label') ariaLabel: string | null = null;
  @Input('aria-labelledby') ariaLabelledby: string | null = null;
  @Input('aria-describedby') ariaDescribedby: string | null = null;

  @Input()
  get size(): RdsMenuSize {
    return this._size;
  }

  set size(size: RdsMenuSize) {
    this._size = size;
    this._allItems?.forEach((item) => (item.size = size));
  }

  @Input()
  get xPosition(): RdsMenuPositionX {
    return this._xPosition;
  }

  set xPosition(value: RdsMenuPositionX) {
    if (value !== 'before' && value !== 'after') {
      throwRdsMenuInvalidPositionX();
    }

    this._xPosition = value;
    this.setPositionClasses();
  }

  @Input()
  get yPosition(): RdsMenuPositionY {
    return this._yPosition;
  }

  set yPosition(value: RdsMenuPositionY) {
    if (value !== 'above' && value !== 'below') {
      throwRdsMenuInvalidPositionY();
    }

    this._yPosition = value;
    this.setPositionClasses();
  }

  @ViewChild(TemplateRef) templateRef: TemplateRef<any>;

  @ContentChild(RDS_MENU_CONTENT) lazyContent: RdsMenuContentDirective;

  @Input({ transform: booleanAttribute }) overlapTrigger =
    this.defaultOptions.overlapTrigger;

  /**
   * This method takes classes set on the host rds-menu-component element and applies them on the
   * menu template that displays in the overlay container.  Otherwise, it's difficult
   * to style the containing menu from outside the component.
   * @param classes list of class names
   */
  // eslint-disable-next-line @angular-eslint/no-input-rename
  @Input('class')
  set panelClass(classes: string) {
    const previousPanelClass = this._previousPanelClass;

    if (previousPanelClass && previousPanelClass.length) {
      previousPanelClass.split(' ').forEach((className: string) => {
        this._classList[className] = false;
      });
    }

    this._previousPanelClass = classes;

    if (classes && classes.length) {
      classes.split(' ').forEach((className: string) => {
        this._classList[className] = true;
      });

      this.elementRef.nativeElement.className = '';
    }
  }

  private _previousPanelClass: string;

  @Output() readonly closed = new EventEmitter<RdsMenuCloseReason>();

  readonly panelId = `rds-menu-panel-${menuPanelUid++}`;

  ngOnInit(): void {
    this.setPositionClasses();
  }

  ngAfterContentInit(): void {
    this._updateDirectDescendants();
    this._keyManager = new FocusKeyManager(this._directDescendantItems)
      .skipPredicate((i) => i.disabled)
      .withWrap()
      .withTypeAhead()
      .withHomeAndEnd();

    this._tabSubscription = this._keyManager.tabOut.subscribe(() =>
      this.closed.emit('tab'),
    );

    this._directDescendantItems.changes
      .pipe(
        startWith(this._directDescendantItems),
        tap(() => {
          this.tabIndex = this._directDescendantItems.filter((i) => !i.disabled)
            ? TAB_INDEX_DEFAULT
            : TAB_INDEX_NOT_FOCUSABLE;
        }),
        switchMap((items) =>
          merge(...items.map((item: RdsMenuItemComponent) => item._focused)),
        ),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe((focusedItem) => {
        this._keyManager.updateActiveItem(focusedItem as RdsMenuItemComponent);
      });
  }

  ngOnDestroy(): void {
    this._directDescendantItems.destroy();
    this._tabSubscription.unsubscribe();
    this.closed.complete();
  }

  _hovered(): Observable<RdsMenuItemComponent> {
    const itemChanges = this._directDescendantItems.changes as Observable<
      QueryList<RdsMenuItemComponent>
    >;

    return itemChanges.pipe(
      startWith(this._directDescendantItems),
      switchMap((items) =>
        merge(...items.map((item: RdsMenuItemComponent) => item._hovered)),
      ),
    ) as Observable<RdsMenuItemComponent>;
  }

  _handleKeydown(event: KeyboardEvent): void {
    const keyCode = event.keyCode;
    const manager = this._keyManager;

    switch (keyCode) {
      case ESCAPE:
        if (!hasModifierKey(event)) {
          event.preventDefault();
          this.closed.emit('keydown');
        }

        break;
      case LEFT_ARROW:
        if (this.parentMenu && this.direction === 'ltr') {
          this.closed.emit('keydown');
        }

        break;
      case RIGHT_ARROW:
        if (this.parentMenu && this.direction === 'rtl') {
          this.closed.emit('keydown');
        }

        break;
      default:
        if (keyCode === UP_ARROW || keyCode === DOWN_ARROW) {
          manager.setFocusOrigin('keyboard');
        }

        manager.onKeydown(event);
    }
  }

  @HostListener('focus')
  focus(): void {
    if (!this._keyManager.activeItem) {
      this._keyManager.setFirstItemActive();
    }

    if (this._keyManager.activeItem) {
      const firstActivated = this._directDescendantItems
        .toArray()
        .findIndex((i) => (i as RdsMenuItemComponent).activated);

      if (firstActivated) {
        this._directDescendantItems.get(firstActivated)?.focus('keyboard');
      }

      this._keyManager.activeItem.focus('keyboard');
      // this.ariaActiveDescendant = this._keyManager.activeItem.getLabel();
    }
  }

  focusFirstItem(origin: FocusOrigin = 'program'): void {
    if (this.lazyContent) {
      this.ngZone.onStable
        .pipe(take(1))
        .subscribe(() => this._focusFirstItem(origin));
    } else {
      this._focusFirstItem(origin);
    }
  }

  private _focusFirstItem(origin: FocusOrigin): void {
    const manager = this._keyManager;

    manager.setFocusOrigin(origin).setFirstItemActive();

    if (!manager.activeItem && this._directDescendantItems.length) {
      let element =
        this._directDescendantItems.first._getHostElement().parentElement;

      while (element) {
        if (element.getAttribute('role') === 'menu') {
          element.focus();
          break;
        } else {
          element = element.parentElement;
        }
      }
    }
  }

  resetActiveItem(): void {
    this._keyManager.activeItem?.blur();
    this._keyManager.setActiveItem(-1);
  }

  setElevation(depth: number): void {
    const elevation = Math.min(this._baseElevation + depth, 24);
    const newElevation = `${this._elevationPrefix}${elevation}`;
    const customElevation = Object.keys(this._classList).find((className) => {
      return className.startsWith(this._elevationPrefix);
    });

    if (!customElevation || customElevation === this._previousElevation) {
      if (this._previousElevation) {
        this._classList[this._previousElevation] = false;
      }

      this._classList[newElevation] = true;
      this._previousElevation = newElevation;
    }
  }

  setPositionClasses(
    posX: RdsMenuPositionX = this.xPosition,
    posY: RdsMenuPositionY = this.yPosition,
  ): void {
    const classes = this._classList;
    classes['rds-menu-before'] = posX === 'before';
    classes['rds-menu-after'] = posX === 'after';
    classes['rds-menu-above'] = posY === 'above';
    classes['rds-menu-below'] = posY === 'below';
  }

  _startAnimation(): void {
    this._panelAnimationState = 'enter';
  }

  _resetAnimation(): void {
    this._panelAnimationState = 'void';
  }

  _onAnimationDone(event: AnimationEvent): void {
    this._animationDone.next(event);
    this._isAnimating = false;
  }

  _onAnimationStart(event: AnimationEvent): void {
    this._isAnimating = true;

    if (event.toState === 'enter' && this._keyManager.activeItemIndex === 0) {
      event.element.scrollTop = 0;
    }
  }

  private _updateDirectDescendants(): void {
    this._allItems.changes
      .pipe(startWith(this._allItems), takeUntilDestroyed(this.destroyRef))
      .subscribe((items: QueryList<RdsMenuItemComponent>) => {
        this._directDescendantItems.reset(
          items.filter((item) => item._parentMenu === this),
        );
        this._directDescendantItems.notifyOnChanges();
      });
  }
}
