import { BooleanInput, NumberInput } from '@angular/cdk/coercion';
import {
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  EventEmitter,
  HostBinding,
  inject,
  InjectionToken,
  Input,
  numberAttribute,
  OnInit,
  Output,
  ViewEncapsulation,
} from '@angular/core';
import { Observable, Subscriber } from 'rxjs';
import { RdsPaginatorIntl } from './paginator-intl';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

const DEFAULT_PAGE_SIZE = 50;

export interface PageEvent {
  pageIndex: number;
  previousPageIndex?: number;
  pageSize: number;
  length: number;
}

export interface RdsPaginatorDefaultOptions {
  pageSize?: number;
  pageSizeOptions?: number[];
  hidePageSize?: boolean;
  showFirstLastButtons?: boolean;
  showItemsOfPageLabel?: boolean;
}

export type RdsPaginatorPresentationMode = 'full' | 'basic';

export const RDS_PAGINATOR_DEFAULT_OPTIONS =
  new InjectionToken<RdsPaginatorDefaultOptions>(
    'RDS_PAGINATOR_DEFAULT_OPTIONS',
  );

@Component({
  selector: 'rds-paginator',
  exportAs: 'rdsPaginator',
  templateUrl: './paginator.component.html',
  styleUrls: ['./paginator.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class RdsPaginatorComponent implements OnInit {
  protected readonly intl = inject(RdsPaginatorIntl);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly defaults = inject(RDS_PAGINATOR_DEFAULT_OPTIONS, {
    optional: true,
  });

  private _isInitialized = false;
  private _pendingSubscribers: Subscriber<void>[] | null = [];
  private _length = 0;

  readonly initialized = new Observable<void>((subscriber) => {
    if (this._isInitialized) {
      this._notifySubscriber(subscriber);
    } else if (this._pendingSubscribers) {
      this._pendingSubscribers.forEach(this._notifySubscriber);
    }
  });

  private _initialized = false;

  @HostBinding('class') class = 'rds-paginator';

  @Input()
  set pageIndex(value: NumberInput) {
    const newValue = Math.max(numberAttribute(value), 0);

    if (newValue !== this.pageIndex) {
      this._pageIndex = Math.max(numberAttribute(value), 0);
      this.cdr.markForCheck();
    }
  }

  get pageIndex(): number {
    return this._pageIndex;
  }

  private _pageIndex = 0;

  @Input()
  set length(v: NumberInput) {
    const newValue = numberAttribute(v);

    if (newValue !== this._length) {
      this._length = newValue;
      this.cdr.markForCheck();
    }
  }
  get length(): number {
    return this._length;
  }

  @Input({ transform: booleanAttribute }) showItemsOfPageLabel = true;

  /* eslint-disable-next-line @angular-eslint/no-input-rename */
  @Input({ transform: (v: unknown) => Math.max(numberAttribute(v, 0), 0) })
  set pageSize(value: NumberInput) {
    this._pageSize = Math.max(numberAttribute(value), 0);
    this._updateDisplayedPageSizeOptions();
  }

  get pageSize(): number {
    return this._pageSize;
  }

  private _pageSize = 0;

  @Input({ transform: booleanAttribute }) disabled = false;

  @Input()
  set pageSizeOptions(value: NumberInput[]) {
    this._pageSizeOptions = (value || []).map((p) => numberAttribute(p));
    this._updateDisplayedPageSizeOptions();
  }

  get pageSizeOptions(): number[] {
    return this._pageSizeOptions;
  }

  private _pageSizeOptions: number[] = [];

  @Input()
  set showButtons(value: BooleanInput) {
    const newValue = booleanAttribute(value);

    if (this._presentationMode === 'full' && !newValue) {
      throw new Error(
        `showButtons property set on false can be used only with basic presentation mode`,
      );
    } else {
      this._showButtons = newValue;
    }
  }

  get showButtons(): boolean {
    return this._showButtons;
  }

  private _showButtons = true;

  @Input()
  set presentationMode(value: RdsPaginatorPresentationMode) {
    if (value === 'full' && !this.showButtons) {
      throw new Error(
        `showButtons property set on false can be used only with basic presentation mode`,
      );
    } else {
      this._presentationMode = value;
    }
  }

  get presentationMode(): RdsPaginatorPresentationMode {
    return this._presentationMode;
  }

  private _presentationMode: RdsPaginatorPresentationMode = 'full';

  @Input({ transform: booleanAttribute }) hidePageSize = false;
  @Input({ transform: booleanAttribute }) showFirstLastButtons = true;

  @Output() readonly page = new EventEmitter<PageEvent>();

  _displayedPageSizeOptions: number[] = [];

  constructor() {
    this.intl.changes
      .pipe(takeUntilDestroyed())
      .subscribe(() => this.cdr.markForCheck());

    if (this.defaults) {
      if (this.defaults.pageSize != null) {
        this._pageSize = this.defaults.pageSize;
      }

      if (this.defaults.pageSizeOptions != null) {
        this._pageSizeOptions = this.defaults.pageSizeOptions;
      }

      if (this.defaults.hidePageSize != null) {
        this.hidePageSize = this.defaults.hidePageSize;
      }

      if (this.defaults.showFirstLastButtons != null) {
        this.showFirstLastButtons = this.defaults.showFirstLastButtons;
      }

      if (this.defaults.showItemsOfPageLabel != null) {
        this.showItemsOfPageLabel = this.defaults.showItemsOfPageLabel;
      }
    }
  }

  ngOnInit(): void {
    this._initialized = true;
    this._updateDisplayedPageSizeOptions();
    this._markInitialized();
  }

  nextPage(): void {
    if (!this.hasNextPage()) {
      return;
    }

    const previousPageIndex = this.pageIndex;
    this.pageIndex = this.pageIndex + 1;
    this.emitPageEvent({ previousPageIndex });
  }

  previousPage(): void {
    if (!this.hasPreviousPage()) {
      return;
    }

    const previousPageIndex = this.pageIndex;
    this.pageIndex = this.pageIndex - 1;
    this.emitPageEvent({ previousPageIndex });
  }

  firstPage(): void {
    if (!this.hasPreviousPage()) {
      return;
    }

    const previousPageIndex = this.pageIndex;
    this.pageIndex = 0;
    this.emitPageEvent({ previousPageIndex });
  }

  lastPage(): void {
    if (!this.hasNextPage()) {
      return;
    }

    const previousPageIndex = this.pageIndex;
    this.pageIndex = this.getNumberOfPages() - 1;
    this.emitPageEvent({ previousPageIndex });
  }

  hasPreviousPage(): boolean {
    return this.pageIndex >= 1 && this.pageSize != 0;
  }

  hasNextPage(): boolean {
    const maxPageIndex = this.getNumberOfPages() - 1;

    return this.pageIndex < maxPageIndex && this.pageSize != 0;
  }

  getNumberOfPages(): number {
    if (!this.pageSize) {
      return 0;
    }

    return Math.ceil(this.length / this.pageSize);
  }

  get(): PageEvent {
    return {
      previousPageIndex: 0,
      pageIndex: this.pageIndex,
      pageSize: this.pageSize,
      length: this.length,
    };
  }

  _onPageIndexChange(input: HTMLInputElement): void {
    const newPageIndex = Math.min(
      Math.max(numberAttribute(input.value), 0),
      this.getNumberOfPages(),
    );

    if (newPageIndex && newPageIndex - 1 !== this.pageIndex) {
      const previousPageIndex = this.pageIndex;
      this.pageIndex = newPageIndex - 1;
      this.emitPageEvent({ previousPageIndex });
    }

    input.value = String(this.pageIndex + 1);
  }

  _changePageSize(pageSize: number): void {
    const startIndex = this.pageIndex * this.pageSize;
    const previousPageIndex = this.pageIndex;

    this.pageIndex = Math.floor(startIndex / pageSize) || 0;
    this.pageSize = pageSize;
    this.emitPageEvent({ previousPageIndex });
  }

  _nextButtonsDisabled(): boolean {
    return this.disabled || !this.hasNextPage();
  }

  _previousButtonsDisabled(): boolean {
    return this.disabled || !this.hasPreviousPage();
  }

  _onPageKeyDown($event: KeyboardEvent): boolean {
    return $event.key !== '.';
  }

  _getPageNumberInputWidth(): number {
    return this.getNumberOfPages().toString(10).split('').length * 10 + 24;
  }

  private _updateDisplayedPageSizeOptions(): void {
    if (!this._initialized) {
      return;
    }

    if (!this.pageSize) {
      this._pageSize =
        this.pageSizeOptions.length != 0
          ? this.pageSizeOptions[0]
          : DEFAULT_PAGE_SIZE;
    }

    this._displayedPageSizeOptions = this.pageSizeOptions.slice();

    if (this._displayedPageSizeOptions.indexOf(this.pageSize) === -1) {
      this._displayedPageSizeOptions.push(this.pageSize);
    }

    this._displayedPageSizeOptions.sort((a, b) => a - b);
    this.cdr.markForCheck();
  }

  emitPageEvent(data: { previousPageIndex: number; pageIndex?: number }): void {
    const { previousPageIndex, pageIndex } = data;
    this.page.emit({
      previousPageIndex,
      pageIndex: pageIndex ?? this.pageIndex,
      pageSize: this.pageSize,
      length: this.length,
    });
  }

  protected _markInitialized(): void {
    if (this._isInitialized) {
      throw Error(
        'This directive has already been marked as initialized and ' +
          'should not be called twice.',
      );
    }

    this._isInitialized = true;

    if (this._pendingSubscribers) {
      this._pendingSubscribers.forEach(this._notifySubscriber);
    }

    this._pendingSubscribers = null;
  }

  protected _notifySubscriber(subscriber: Subscriber<void>): void {
    subscriber.next();
    subscriber.complete();
  }
}
