import { FocusableOption, FocusMonitor, FocusOrigin } from '@angular/cdk/a11y';
import { ENTER, hasModifierKey, SPACE } from '@angular/cdk/keycodes';
import {
  AfterViewInit,
  Attribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  Directive,
  ElementRef,
  HostBinding,
  HostListener,
  inject,
  Input,
  numberAttribute,
  OnDestroy,
  ViewEncapsulation,
} from '@angular/core';
import { EMPTY, merge, Subscription } from 'rxjs';
import { filter } from 'rxjs/operators';
import { rdsExpansionAnimations } from './expansion.animations';
import { RdsExpansionPanelComponent } from './expansion-panel.component';
import { TAB_INDEX_DEFAULT, TAB_INDEX_NOT_FOCUSABLE } from '../utils';

@Component({
  selector: 'rds-expansion-panel-header',
  template: `<span class="rds-content">
      <ng-content select="rds-panel-title" />
      <ng-content />
    </span>
    <rds-icon
      [@indicatorRotate]="panel._getExpandedState()"
      class="rds-expansion-indicator"
      namespace="filled"
      icon="chevron_down"
    />`,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  animations: [rdsExpansionAnimations.indicatorRotate],
})
export class RdsExpansionPanelHeaderComponent
  implements AfterViewInit, OnDestroy, FocusableOption
{
  public readonly panel = inject(RdsExpansionPanelComponent, { host: true });
  private readonly element = inject(ElementRef);
  private readonly focusMonitor = inject(FocusMonitor);
  private readonly cdr = inject(ChangeDetectorRef);
  private _parentChangeSubscription = Subscription.EMPTY;
  private _tabIndex = TAB_INDEX_DEFAULT;

  @HostBinding('class') class =
    'rds-expansion-panel-header rds-focus-indicator';
  @HostBinding('attr.role') role = 'button';

  @HostBinding('attr.id')
  get attrId(): string {
    return this.panel._headerId;
  }

  @HostBinding('attr.aria-disabled')
  get attrDisabled(): boolean {
    return this.panel.disabled;
  }

  @Input()
  @HostBinding('tabIndex')
  get tabIndex(): number {
    return this.disabled ? TAB_INDEX_NOT_FOCUSABLE : this._tabIndex;
  }

  set tabIndex(value: number) {
    this._tabIndex = numberAttribute(value, TAB_INDEX_DEFAULT);
  }

  get disabled(): boolean {
    return this.panel.disabled;
  }

  @HostBinding('attr.aria-expanded')
  @HostBinding('class.rds-expanded')
  get _isExpanded(): boolean {
    return this.panel.expanded;
  }

  @HostBinding('attr.aria-controls')
  get _getPanelId(): string {
    return this.panel.id;
  }

  @HostBinding('class.rds-expansion-toggle-indicator-after')
  get _isAfterTogglePosition(): boolean {
    return this.panel.togglePosition === 'after';
  }

  @HostBinding('class.rds-expansion-toggle-indicator-before')
  get _isBeforeTogglePosition(): boolean {
    return this.panel.togglePosition === 'before';
  }

  constructor(@Attribute('tabindex') tabIndex?: string) {
    const accordionHideToggleChange = this.panel.accordion
      ? this.panel.accordion._stateChanges.pipe(
          filter((changes) => !!changes['togglePosition']),
        )
      : EMPTY;
    this.tabIndex = numberAttribute(tabIndex, TAB_INDEX_DEFAULT);

    this._parentChangeSubscription = merge(
      this.panel.opened,
      this.panel.closed,
      accordionHideToggleChange,
      this.panel._inputChanges.pipe(
        filter((changes) => {
          return !!(changes['disabled'] || changes['togglePosition']);
        }),
      ),
    ).subscribe(() => this.cdr.markForCheck());

    this.panel.closed
      .pipe(filter(() => this.panel._containsFocus()))
      .subscribe(() => this.focusMonitor.focusVia(this.element, 'program'));
  }

  @HostListener('click')
  _toggle(): void {
    if (!this.disabled) {
      this.panel.toggle();
    }
  }

  @HostListener('keydown', ['$event'])
  _keydown(event: KeyboardEvent): void {
    switch (event.keyCode) {
      case SPACE:
      case ENTER:
        if (!hasModifierKey(event)) {
          event.preventDefault();
          this._toggle();
        }

        break;
      default:
        if (this.panel.accordion) {
          this.panel.accordion._handleHeaderKeydown(event);
        }

        return;
    }
  }

  focus(origin?: FocusOrigin, options?: FocusOptions): void {
    if (origin) {
      this.focusMonitor.focusVia(this.element, origin, options);
    } else {
      this.element.nativeElement.focus(options);
    }
  }

  ngAfterViewInit(): void {
    this.focusMonitor.monitor(this.element).subscribe((origin) => {
      if (origin && this.panel.accordion) {
        this.panel.accordion._handleHeaderFocus(this);
      }
    });
  }

  ngOnDestroy(): void {
    this._parentChangeSubscription.unsubscribe();
    this.focusMonitor.stopMonitoring(this.element);
  }
}

@Directive({
  selector: 'rds-panel-title',
})
export class RdsExpansionPanelTitleDirective {
  @HostBinding('class') class = 'rds-expansion-panel-header-title';
}
