import { Observable, Subject } from 'rxjs';
import { RdsToastOverlayRef } from './overlay/rds-toast-overlay-ref';

export class RdsToastRef<T> {
  componentInstance: T;
  private readonly _afterClosed = new Subject<void>();
  private readonly _activate = new Subject<void>();
  private readonly _manualClose = new Subject<void>();
  private _isClosed = false;

  constructor(private readonly _rdsToastOverlayRef: RdsToastOverlayRef<T>) {}

  manualClose(): void {
    this._manualClose.next();
    this._manualClose.complete();
  }

  manualClosed(): Observable<void> {
    return this._manualClose.asObservable();
  }

  close(): void {
    this._isClosed = true;
    this._rdsToastOverlayRef.detach();
    this._afterClosed.next();
    this._manualClose.next();
    this._afterClosed.complete();
    this._manualClose.complete();
    this._activate.complete();
  }

  isClosed(): boolean {
    return this._isClosed;
  }

  afterClosed(): Observable<void> {
    return this._afterClosed.asObservable();
  }

  isInactive(): boolean {
    return this._activate.isStopped;
  }

  activate(): void {
    this._activate.next();
    this._activate.complete();
  }

  afterActivate(): Observable<void> {
    return this._activate.asObservable();
  }
}
