import { CdkTreeNode } from '@angular/cdk/tree';
import {
  AfterContentInit,
  Attribute,
  booleanAttribute,
  Directive,
  ElementRef,
  HostBinding,
  HostListener,
  inject,
  Input,
  numberAttribute,
  OnChanges,
  OnDestroy,
  Renderer2,
  SimpleChanges,
} from '@angular/core';
import { FocusableOption, FocusMonitor } from '@angular/cdk/a11y';
import { RdsTreeComponent } from './tree.component';
import { TAB_INDEX_DEFAULT, TAB_INDEX_NOT_FOCUSABLE } from '../utils';

@Directive({
  selector: 'rds-tree-node',
  exportAs: 'rdsTreeNode',
  providers: [{ provide: CdkTreeNode, useExisting: RdsTreeNodeDirective }],
})
export class RdsTreeNodeDirective<T>
  extends CdkTreeNode<T>
  implements AfterContentInit, OnChanges, OnDestroy, FocusableOption
{
  protected readonly renderer = inject(Renderer2);
  protected readonly focusMonitor = inject(FocusMonitor);
  protected readonly tree = inject<RdsTreeComponent<T>>(RdsTreeComponent);

  @HostBinding('class') get cssClasses(): string {
    const cssClasses: string[] = ['rds-tree-node'];

    if (this.disabled) {
      cssClasses.push('rds-tree-node--disabled');
    }

    if (!this.disabled && this.active) {
      cssClasses.push('rds-tree-node--active');
    }

    if (this.tree) {
      cssClasses.push(`rds-tree-node--size-${this.tree.size}`);
    }

    return cssClasses.join(' ');
  }

  @HostBinding('attr.aria-expanded') get attrAriaExpanded():
    | boolean
    | undefined {
    // don't set aria-expanded for leafs as it violates accessibility
    if (!this._tree.treeControl.isExpandable(this._data)) {
      return undefined;
    }

    return this.isExpanded;
  }

  @HostBinding('attr.aria-level') get attrAriaLevel(): number {
    return this.level + 1;
  }

  @HostBinding('style.--rds-tree-node-level') get styleLevel(): number {
    return this.level + 1;
  }

  @HostBinding('tabIndex')
  get attrTabIndex(): number {
    return this.disabled ? TAB_INDEX_NOT_FOCUSABLE : this.tabIndex;
  }

  @Input({ transform: numberAttribute })
  tabIndex = TAB_INDEX_DEFAULT;

  @Input({ transform: booleanAttribute }) disabled = false;
  @Input({ transform: booleanAttribute }) active = false;

  @HostListener('keydown.arrowDown')
  onArrowDown(): void {
    const nextNode = this._elementRef.nativeElement
      .nextElementSibling as HTMLElement | null;

    if (!nextNode) {
      return;
    }

    this.focusMonitor.focusVia(nextNode, 'keyboard');
  }

  @HostListener('keydown.arrowUp')
  onArrowUp(): void {
    const previousNode = this._elementRef.nativeElement
      .previousElementSibling as HTMLElement | null;

    if (!previousNode) {
      return;
    }

    this.focusMonitor.focusVia(previousNode, 'keyboard');
  }

  @HostListener('keydown.arrowRight', ['$event'])
  onExpand(event: KeyboardEvent): void {
    if (this.disabled) {
      return;
    }

    if (
      this._tree.treeControl.isExpandable(this._data) &&
      !this._tree.treeControl.isExpanded(this._data)
    ) {
      this._tree.treeControl.expand(this.data);
      event.stopPropagation();
    }
  }

  @HostListener('keydown.arrowLeft', ['$event'])
  onCollapse(event: KeyboardEvent): void {
    if (this.disabled) {
      return;
    }

    if (
      this._tree.treeControl.isExpandable(this._data) &&
      this._tree.treeControl.isExpanded(this._data)
    ) {
      this._tree.treeControl.collapse(this._data);
      event.stopPropagation();
    }
  }

  constructor(@Attribute('tabindex') tabIndex?: string) {
    super(inject(ElementRef), inject(RdsTreeComponent));
    this.tabIndex = numberAttribute(tabIndex, TAB_INDEX_DEFAULT);
  }

  ngAfterContentInit(): void {
    if (!this.disabled) {
      this.focusMonitor.monitor(this._elementRef, true);
    } else {
      this.focusMonitor.stopMonitoring(this._elementRef);
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (
      changes['disabled'] &&
      changes['disabled'].previousValue !== changes['disabled'].currentValue
    ) {
      if (this.disabled) {
        this.focusMonitor.stopMonitoring(this._elementRef);
        this.renderer.setAttribute(
          this._elementRef.nativeElement,
          'disabled',
          'disabled',
        );
      } else {
        this.renderer.removeAttribute(
          this._elementRef.nativeElement,
          'disabled',
        );
        this.focusMonitor.monitor(this._elementRef, true);
      }

      this._checkButtons();
    }
  }

  override ngOnDestroy(): void {
    super.ngOnDestroy();
    this.focusMonitor.stopMonitoring(this._elementRef);
  }

  override focus(): void {
    super.focus();
  }

  private _checkButtons(): void {
    const buttonElements =
      this._elementRef.nativeElement.querySelectorAll<HTMLButtonElement>(
        'button',
      );
    buttonElements.forEach((b) =>
      this.renderer.setProperty(b, 'disabled', this.disabled),
    );
  }
}
