import { ComponentRef, Injector } from '@angular/core';
import { ComponentType } from '@angular/cdk/overlay';

export class RdsComponentPortal<T> {
  private _attachedHost?: RdsBasePortalHost<T>;

  constructor(
    public readonly component: ComponentType<T>,
    public readonly injector: Injector,
  ) {}

  attach(host: RdsBasePortalHost<T>, newestOnTop: boolean): ComponentRef<T> {
    this._attachedHost = host;

    return host.attach(this, newestOnTop);
  }

  detach(): void {
    const host = this._attachedHost;

    if (host) {
      this._attachedHost = undefined;

      return host.detach();
    }
  }

  get isAttached(): boolean {
    return this._attachedHost !== undefined;
  }

  setAttachedHost(host?: RdsBasePortalHost<T>): void {
    this._attachedHost = host;
  }
}

export abstract class RdsBasePortalHost<T> {
  private _attachedPortal?: RdsComponentPortal<T>;
  private _disposeFn?: () => void;

  attach(portal: RdsComponentPortal<T>, newestOnTop: boolean): ComponentRef<T> {
    this._attachedPortal = portal;

    return this.attachComponentPortal(portal, newestOnTop);
  }

  abstract attachComponentPortal(
    portal: RdsComponentPortal<T>,
    newestOnTop: boolean,
  ): ComponentRef<T>;

  detach(): void {
    if (this._attachedPortal) {
      this._attachedPortal.setAttachedHost();
    }

    this._attachedPortal = undefined;

    if (this._disposeFn) {
      this._disposeFn();
      this._disposeFn = undefined;
    }
  }

  setDisposeFn(fn: () => void): void {
    this._disposeFn = fn;
  }
}
