import { Observable, Subject } from 'rxjs';
import { RdsDialogState } from './dialog.state';
import { GlobalPositionStrategy, OverlayRef } from '@angular/cdk/overlay';
import { RdsDialogContainerComponent } from '../dialog-container.component';
import { filter, take } from 'rxjs/operators';
import { ESCAPE, hasModifierKey } from '@angular/cdk/keycodes';
import { AnimationEvent } from '@angular/animations';

let uniqueId = 0;

// eslint-disable-next-line @typescript-eslint/no-explicit-any
export class RdsDialogRef<T, D = any, R = any> {
  private readonly afterOpened$ = new Subject<void>();
  private readonly afterClosed$ = new Subject<R | undefined>();
  private readonly beforeClosed$ = new Subject<R | undefined>();

  private result: R | undefined;

  private dialogState = RdsDialogState.OPEN;

  private closeFallbackTimeout: null | ReturnType<typeof setTimeout>;

  componentInstance: T | null;

  disableClose: boolean | undefined = this.dialogContainer.config.disableClose;

  constructor(
    private readonly overlayRef: OverlayRef,
    public readonly dialogContainer: RdsDialogContainerComponent<D>,
    public readonly id = `rds-dialog-${uniqueId++}`,
  ) {
    this.dialogContainer.id = id;

    // Emit when opening animation completes
    this.dialogContainer.animationStateChanged
      .pipe(
        filter(
          (event: AnimationEvent) =>
            event.phaseName === 'done' && event.toState === 'enter',
        ),
        take(1),
      )
      .subscribe(() => {
        this.afterOpened$.next();
        this.afterOpened$.complete();
      });

    dialogContainer.animationStateChanged
      .pipe(
        filter(
          (event: AnimationEvent) =>
            event.phaseName === 'done' && event.toState === 'exit',
        ),
        take(1),
      )
      .subscribe(() => {
        if (this.closeFallbackTimeout) {
          clearTimeout(this.closeFallbackTimeout);
        }

        this.finishDialogClose();
      });

    overlayRef.detachments().subscribe(() => {
      this.beforeClosed$.next(this.result);
      this.beforeClosed$.complete();
      this.afterClosed$.next(this.result);
      this.afterClosed$.complete();
      this.componentInstance = null;
      this.overlayRef.dispose();
    });

    overlayRef
      .keydownEvents()
      .pipe(
        filter(
          (event) =>
            event.keyCode === ESCAPE &&
            !this.disableClose &&
            !hasModifierKey(event),
        ),
      )
      .subscribe((event) => {
        event.preventDefault();
        this.close();
      });

    overlayRef.backdropClick().subscribe(() => {
      if (this.disableClose) {
        this.dialogContainer.recaptureFocus();
      } else {
        this.close();
      }
    });
  }

  close(dialogResult?: R): void {
    this.result = dialogResult;

    this.dialogContainer.animationStateChanged
      .pipe(
        filter((event: AnimationEvent) => event.phaseName === 'start'),
        take(1),
      )
      .subscribe((event: AnimationEvent) => {
        this.beforeClosed$.next(dialogResult);
        this.beforeClosed$.complete();
        this.overlayRef.detachBackdrop();

        this.closeFallbackTimeout = setTimeout(
          () => this.finishDialogClose(),
          event.totalTime + 100,
        );
      });

    this.dialogContainer.startExitAnimation();
    this.dialogState = RdsDialogState.CLOSING;
  }

  afterOpened(): Observable<void> {
    return this.afterOpened$.asObservable();
  }

  afterClosed(): Observable<R | undefined> {
    return this.afterClosed$.asObservable();
  }

  beforeClosed(): Observable<R | undefined> {
    return this.beforeClosed$.asObservable();
  }

  backdropClick(): Observable<MouseEvent> {
    return this.overlayRef.backdropClick();
  }

  centerDialog(): void {
    const positionStrategy = this.getPositionStrategy();
    positionStrategy.centerHorizontally();
    positionStrategy.centerVertically();
  }

  changeSize(width = '', height = ''): void {
    this.getPositionStrategy().width(width).height(height);
  }

  updateOverlayPosition(): void {
    this.overlayRef.updatePosition();
  }

  private finishDialogClose(): void {
    this.dialogState = RdsDialogState.CLOSED;
    this.overlayRef.dispose();
  }

  private getPositionStrategy(): GlobalPositionStrategy {
    return this.overlayRef.getConfig()
      .positionStrategy as GlobalPositionStrategy;
  }
}
