import { FocusableOption, FocusMonitor, FocusOrigin } from '@angular/cdk/a11y';
import { BooleanInput, NumberInput } from '@angular/cdk/coercion';
import {
  AfterContentInit,
  AfterViewInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  DestroyRef,
  DoCheck,
  ElementRef,
  EventEmitter,
  forwardRef,
  HostBinding,
  inject,
  Input,
  NgZone,
  numberAttribute,
  OnDestroy,
  OnInit,
  Output,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import {
  AbstractControl,
  ControlContainer,
  ControlValueAccessor,
  FormGroupDirective,
  NG_VALUE_ACCESSOR,
  NgForm,
  Validators,
} from '@angular/forms';
import { noop } from 'rxjs';
import { RdsCheckboxChangeEvent } from './checkbox-change.event';
import {
  animate,
  AnimationTriggerMetadata,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import {
  TAB_INDEX_DEFAULT,
  TAB_INDEX_NOT_FOCUSABLE,
  updateErrorState,
} from '../utils';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

let nextUniqueId = 0;

export const rdsCheckboxAnimations: {
  readonly transitionMessages: AnimationTriggerMetadata;
} = {
  /** Animation that transitions the form field's error and hint messages. */
  transitionMessages: trigger('transitionMessages', [
    state('enter', style({ opacity: 1, transform: 'translateY(0%)' })),
    transition('void => enter', [
      style({ opacity: 0, transform: 'translateY(-100%)' }),
      animate('200ms cubic-bezier(0.55, 0, 0.55, 0.2)'),
    ]),
  ]),
};

export type RdsCheckboxSize = 's' | 'm' | 'l';

export const enum TransitionCheckState {
  Init,
  Checked,
  Unchecked,
  Indeterminate,
}

@Component({
  selector: 'rds-checkbox',
  template: `
    <div class="rds-checkbox__wrapper">
      <label [attr.for]="inputId" class="rds-checkbox__container" #label>
        <div class="rds-checkbox__checkbox-container">
          <input
            #input
            class="rds-checkbox__native-checkbox cdk-visually-hidden"
            type="checkbox"
            [id]="inputId"
            [required]="required"
            [checked]="checked"
            [attr.value]="value"
            [disabled]="disabled"
            [attr.name]="name"
            [tabIndex]="tabIndex"
            [attr.aria-label]="ariaLabel || null"
            [attr.aria-labelledby]="ariaLabelledby"
            [attr.aria-checked]="ariaChecked"
            [attr.aria-describedby]="ariaDescribedby"
            (click)="onInputClick($event)"
          />
          <div class="rds-checkbox__overlay"></div>
          <div class="rds-checkbox__frame"></div>
          <div class="rds-checkbox__background">
            <svg
              focusable="false"
              class="rds-checkbox__checkmark"
              viewBox="0 0 24 24"
              xml:space="preserve"
            >
              <path
                class="rds-checkbox__checkmark-path"
                fill="none"
                d="M4.1,12.7 9,17.6 20.3,6.3"
              />
            </svg>
            <div class="rds-checkbox__indeterminate-mark"></div>
          </div>
        </div>
        <span
          class="rds-checkbox__label"
          [ngClass]="'rds-checkbox__label--' + size"
          #checkboxLabel
          (cdkObserveContent)="onLabelTextChange()"
        >
          @if (required && !disabled) {
            <span class="rds-asterisk">&#32;* </span>
          }
          <!-- Add an invisible span so JAWS can read the label -->
          <span style="display:none">&nbsp;</span>
          <ng-content />
        </span>
      </label>
    </div>
    <div class="rds-checkbox__subscript-wrapper">
      @switch (isErrorMessageVisible) {
        @case (true) {
          <div
            class="rds-checkbox__subscript rds-checkbox__subscript--error"
            [@transitionMessages]="subscriptAnimationState"
          >
            <ng-content select="rds-control-error" />
          </div>
        }
        @case (false) {
          <div
            class="rds-checkbox__subscript rds-checkbox__subscript--hint"
            [@transitionMessages]="subscriptAnimationState"
          >
            <ng-content select="rds-control-hint" />
          </div>
        }
      }
    </div>
  `,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => RdsCheckboxComponent),
      multi: true,
    },
  ],
  styleUrls: ['./checkbox.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: [rdsCheckboxAnimations.transitionMessages],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsCheckboxComponent
  implements
    ControlValueAccessor,
    AfterViewInit,
    AfterContentInit,
    DoCheck,
    OnDestroy,
    OnInit,
    FocusableOption
{
  private readonly destroyRef = inject(DestroyRef);
  private readonly elementRef = inject<ElementRef<HTMLElement>>(ElementRef);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly focusMonitor = inject(FocusMonitor);
  private readonly ngZone = inject(NgZone);
  private readonly parentForm = inject(NgForm, { optional: true });
  private readonly parentFormGroup = inject(FormGroupDirective, {
    optional: true,
  });
  private readonly controlContainer = inject(ControlContainer, {
    optional: true,
    host: true,
    skipSelf: true,
  });

  private _required: boolean;
  private _uniqueId = `rds-checkbox-${++nextUniqueId}`;
  private _currentAnimationClass = '';
  private _errorState = false;
  private _tabIndex = TAB_INDEX_DEFAULT;
  private _indeterminate = false;
  private _control: AbstractControl | null | undefined;

  private _currentCheckState = TransitionCheckState.Init;

  subscriptAnimationState: '' | 'enter' = '';

  @ViewChild('checkboxLabel') checkboxLabel: ElementRef;

  @HostBinding('class') readonly hostClass = 'rds-checkbox';
  @HostBinding('tabIndex') readonly hostTabindex: string;

  @HostBinding('attr.id') get getHostId(): string {
    return this._uniqueId;
  }

  @HostBinding('class.rds-checkbox--indeterminate')
  get hostIndeterminate(): boolean {
    return this.indeterminate;
  }

  @HostBinding('class.rds-checkbox--checked') get hostChecked(): boolean {
    return this.checked;
  }

  @HostBinding('class.rds-checkbox--disabled') get hostDisabled(): boolean {
    return this.disabled;
  }

  @HostBinding('class.rds-checkbox--invalid')
  get errorState(): boolean {
    return this._errorState;
  }

  @Input() id = this._uniqueId;
  @Input() value: string;
  @Input() name: string;
  @Input() size: RdsCheckboxSize = 'l';

  @Input('aria-label') ariaLabel = '';
  @Input('aria-labelledby') ariaLabelledby: string;
  @Input('aria-describedby') ariaDescribedby: string;
  @Input() formControlName: string;

  @Input()
  set tabIndex(value: NumberInput) {
    this._tabIndex = numberAttribute(value, TAB_INDEX_DEFAULT);
  }

  get tabIndex(): number {
    return this.disabled ? TAB_INDEX_NOT_FOCUSABLE : this._tabIndex;
  }

  @Input({ transform: booleanAttribute }) checked = false;
  @Input({ transform: booleanAttribute }) disabled = false;

  @Input()
  set indeterminate(value: BooleanInput) {
    this._indeterminate = booleanAttribute(value);

    if (this.indeterminate) {
      this.transitionCheckState(TransitionCheckState.Indeterminate);
    } else {
      this.transitionCheckState(
        this.checked
          ? TransitionCheckState.Checked
          : TransitionCheckState.Unchecked,
      );
    }

    this.syncIndeterminate(this.indeterminate);
  }

  get indeterminate(): boolean {
    return this._indeterminate;
  }

  @HostBinding('attr.required')
  get attrRequired(): boolean | null {
    if (this._control && this._control?.hasValidator(Validators.required)) {
      return this._control?.hasValidator(Validators.required) ?? null;
    }

    return this._required ?? null;
  }

  @Input()
  set required(value: BooleanInput) {
    this._required = booleanAttribute(value);
  }

  get required(): boolean {
    return this.attrRequired ?? false;
  }

  @Output() readonly changed = new EventEmitter<RdsCheckboxChangeEvent>();

  @ViewChild('input') _inputElement: ElementRef<HTMLInputElement>;

  _onChange: (value: boolean) => void = noop;
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  _onTouched: () => any = noop;

  get inputId(): string {
    return `${this.id || this._uniqueId}-input`;
  }

  get ariaChecked(): 'true' | 'false' | 'mixed' {
    if (this.checked) {
      return 'true';
    }

    return this.indeterminate ? 'mixed' : 'false';
  }

  ngOnInit(): void {
    if (this.controlContainer) {
      if (this.formControlName) {
        this._control = this.controlContainer.control?.get(
          this.formControlName,
        );
      } else {
        // Missing FormControlName directive from host element of the component
      }
    } else {
      // Can't find parent FormGroup directive
    }
  }

  ngDoCheck(): void {
    if (this._control) {
      // We need to re-evaluate this on every change detection cycle, because there are some
      // error triggers that we can't subscribe to (e.g. parent form submissions). This means
      // that whatever logic is in here has to be super lean or we risk destroying the performance.
      this._errorState = updateErrorState(
        this.errorState,
        this.parentFormGroup,
        this.parentForm,
        this._control,
      );
    }
  }

  ngAfterContentInit(): void {
    const control = this._control;

    if (control && control.valueChanges) {
      control.valueChanges
        .pipe(takeUntilDestroyed(this.destroyRef))
        .subscribe(() => {
          control?.markAsDirty();
          control?.markAsTouched();
          this.cdr.markForCheck();
        });
    }

    const parent = this.parentFormGroup || this.parentForm;

    if (parent) {
      parent.ngSubmit
        .pipe(takeUntilDestroyed(this.destroyRef))
        .subscribe(() => {
          this.cdr.markForCheck();
        });
    }
  }

  ngAfterViewInit(): void {
    if (this.checkboxLabel.nativeElement.innerText) {
      this.elementRef.nativeElement.classList.add('rds-checkbox-with-label');
    }

    this.focusMonitor
      .monitor(this.elementRef, true)
      .subscribe((focusOrigin) => {
        if (!focusOrigin) {
          // When a focused element becomes disabled, the browser *immediately* fires a blur event.
          // Angular does not expect events to be raised during change detection, so any state change
          // (such as a form control's 'ng-touched') will cause a changed-after-checked error.
          // See https://github.com/angular/angular/issues/17793. To work around this, we defer
          // telling the form control it has been touched until the next tick.
          Promise.resolve().then(() => {
            this._onTouched();
            this.cdr.markForCheck();
          });
        }
      });

    this.subscriptAnimationState = 'enter'; // Avoid animations on load.
    this.cdr.detectChanges();

    this.syncIndeterminate(this.indeterminate);
  }

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.elementRef);
  }

  get isErrorMessageVisible(): boolean {
    return this.errorState;
  }

  onLabelTextChange(): void {
    if (this.checkboxLabel.nativeElement.innerText) {
      this.elementRef.nativeElement.classList.add('rds-checkbox-with-label');
    } else {
      this.elementRef.nativeElement.classList.remove('rds-checkbox-with-label');
    }

    this.cdr.detectChanges();
  }

  // start ControlValueAccessor - part
  writeValue(value: boolean): void {
    this.checked = value;
  }

  registerOnChange(fn: (value: boolean) => void): void {
    this._onChange = fn;
  }

  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  registerOnTouched(fn: any): void {
    this._onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    if (this.disabled !== isDisabled) {
      this.disabled = isDisabled;
      this.cdr.markForCheck();
    }
  }

  // end ControlValueAccessor - part

  onInputClick(event: Event): void {
    // We have to stop propagation for click events on the visual hidden input element.
    // By default, when a user clicks on a label element, a generated click event will be
    // dispatched on the associated input element. Since we are using a label element as our
    // root container, the click event on the `checkbox` will be executed twice.
    // The real click event will bubble up, and the generated click event also tries to bubble up.
    // This will lead to multiple click events.
    // Preventing bubbling for the second event will solve that issue.
    event.stopPropagation();

    if (this.disabled) {
      return;
    }

    Promise.resolve().then(() => (this.indeterminate = false));

    this.toggle();
    this.transitionCheckState(
      this.checked
        ? TransitionCheckState.Checked
        : TransitionCheckState.Unchecked,
    );
    this.emitChangedEvent();
  }

  focus(origin: FocusOrigin = 'keyboard', options?: FocusOptions): void {
    this.focusMonitor.focusVia(this._inputElement, origin, options);
  }

  private getAnimationClassForCheckStateTransition(
    oldState: TransitionCheckState,
    newState: TransitionCheckState,
  ): string {
    let animSuffix = '';

    switch (oldState) {
      case TransitionCheckState.Init:
        if (newState === TransitionCheckState.Checked) {
          animSuffix = 'unchecked-checked';
        } else if (newState === TransitionCheckState.Indeterminate) {
          animSuffix = 'unchecked-indeterminate';
        } else {
          return '';
        }

        break;
      case TransitionCheckState.Unchecked:
        animSuffix =
          newState === TransitionCheckState.Checked
            ? 'unchecked-checked'
            : 'unchecked-indeterminate';
        break;
      case TransitionCheckState.Checked:
        animSuffix =
          newState === TransitionCheckState.Unchecked
            ? 'checked-unchecked'
            : 'checked-indeterminate';
        break;
      case TransitionCheckState.Indeterminate:
        animSuffix =
          newState === TransitionCheckState.Checked
            ? 'indeterminate-checked'
            : 'indeterminate-unchecked';
        break;
    }

    return `rds-checkbox-animation--${animSuffix}`;
  }

  private transitionCheckState(newState: TransitionCheckState): void {
    const oldState = this._currentCheckState;
    const element: HTMLElement = this.elementRef.nativeElement;

    if (oldState === newState) {
      return;
    }

    if (this._currentAnimationClass.length > 0) {
      element.classList.remove(this._currentAnimationClass);
    }

    this._currentAnimationClass = this.getAnimationClassForCheckStateTransition(
      oldState,
      newState,
    );
    this._currentCheckState = newState;

    if (this._currentAnimationClass.length > 0) {
      element.classList.add(this._currentAnimationClass);

      // Remove the animation class to avoid animation when the checkbox is moved between containers
      const animationClass = this._currentAnimationClass;

      this.ngZone.runOutsideAngular(() => {
        setTimeout(() => {
          element.classList.remove(animationClass);
        }, 1000);
      });
    }
  }

  private toggle(): void {
    this.checked = !this.checked;
  }

  private emitChangedEvent(): void {
    const event = new RdsCheckboxChangeEvent();
    event.source = this;
    event.checked = this.checked;

    this._onChange(this.checked);
    this.changed.emit(event);
  }

  private syncIndeterminate(value: boolean): void {
    const nativeCheckbox = this._inputElement;

    if (nativeCheckbox) {
      nativeCheckbox.nativeElement.indeterminate = value;
    }
  }
}
