import { Directive, HostBinding, Input } from '@angular/core';

let nextUniqueId = 0;

@Directive({
  selector: 'rds-control-hint',
})
export class RdsHintDirective {
  private _id = `rds-control-hint-${nextUniqueId++}`;

  @Input()
  @HostBinding('attr.id')
  get id(): string {
    return this._id;
  }
  set id(newId: string) {
    this._id = newId;
  }
}
