import {
  Directive,
  ElementRef,
  HostBinding,
  inject,
  Input,
  isDevMode,
  OnInit,
} from '@angular/core';

@Directive({
  selector: '[rds-anchor-menu-section], [rdsAnchorMenuSection]',
})
export class RdsAnchorMenuSectionDirective implements OnInit {
  readonly _element = inject(ElementRef<HTMLElement>);

  @HostBinding('id')
  @Input()
  id: string;

  @Input()
  label: string;

  ngOnInit(): void {
    if (isDevMode() && !this.id) {
      throw new Error(
        'You must specify an id input for rds-anchor-menu-section',
      );
    }
  }
}
