import { Directive, HostBinding } from '@angular/core';
import { CdkStepperPrevious } from '@angular/cdk/stepper';

@Directive({
  selector: 'button[rdsStepperPrevious],button[rds-stepper-previous]',
})
export class RdsStepperPreviousDirective extends CdkStepperPrevious {
  @HostBinding('class') readonly hostClass = 'rds-stepper-previous';
}
