import { FocusKeyManager } from '@angular/cdk/a11y';
import { Directionality } from '@angular/cdk/bidi';
import { BooleanInput, NumberInput } from '@angular/cdk/coercion';
import {
  AfterContentInit,
  AfterViewInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  Component,
  ContentChildren,
  DestroyRef,
  ElementRef,
  HostBinding,
  HostListener,
  inject,
  Input,
  numberAttribute,
  OnChanges,
  OnDestroy,
  QueryList,
  SimpleChanges,
  ViewEncapsulation,
} from '@angular/core';
import { merge, noop, Observable } from 'rxjs';
import { startWith, switchMap } from 'rxjs/operators';
import { RdsChipActionDirective } from './chip-action';
import { RdsChipEvent, RdsChipsSize, RdsChipType } from './chip.types';
import { RdsChipComponent } from './chip/chip.component';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { TAB_INDEX_DEFAULT, TAB_INDEX_NOT_FOCUSABLE } from '../utils';

@Component({
  selector: 'rds-chip-set',
  template: '',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsChipSetComponent<T>
  implements AfterContentInit, AfterViewInit, OnDestroy, OnChanges
{
  protected readonly destroyRef = inject(DestroyRef);
  protected readonly elementRef = inject<ElementRef<HTMLElement>>(ElementRef);
  protected readonly dir = inject(Directionality, { optional: true });

  private _lastDestroyedFocusedChipIndex: number | null = null;
  private _tabIndex = TAB_INDEX_DEFAULT;

  protected keyManager: FocusKeyManager<RdsChipActionDirective>;
  protected _defaultRole = 'presentation';
  protected _disabled = false;
  protected _size: RdsChipsSize = 's';

  get chipFocusChanges(): Observable<RdsChipEvent<T>> {
    return this.getChipStream((chip) => chip.focus$);
  }

  get chipDestroyedChanges(): Observable<RdsChipEvent<T>> {
    return this.getChipStream((chip) => chip.destroyed);
  }

  @Input()
  set tabIndex(value: NumberInput) {
    this._tabIndex = numberAttribute(value, TAB_INDEX_DEFAULT);
  }

  get tabIndex(): number {
    return this.disabled ? TAB_INDEX_NOT_FOCUSABLE : this._tabIndex;
  }

  @Input()
  type: RdsChipType = 'blue';

  @Input()
  set disabled(value: BooleanInput) {
    this._disabled = booleanAttribute(value);
    this.syncChipsState();
  }

  get disabled(): boolean {
    return this._disabled;
  }

  get empty(): boolean {
    return this.chips.length === 0;
  }

  @Input()
  @HostBinding('attr.role')
  get role(): string | null {
    if (this._explicitRole) {
      return this._explicitRole;
    }

    return this.empty ? null : this._defaultRole;
  }

  set role(value: string | null) {
    this._explicitRole = value;
  }

  private _explicitRole: string | null = null;

  get focused(): boolean {
    return this.hasFocusedChip();
  }

  @ContentChildren(RdsChipComponent, { descendants: true })
  chips: QueryList<RdsChipComponent<T>>;

  chipActions = new QueryList<RdsChipActionDirective>();

  ngAfterContentInit(): void {
    this.chips.changes
      .pipe(startWith(this.chips), takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this.checkAndSetChipSizes());
  }

  ngAfterViewInit(): void {
    this.setUpFocusManagement();
    this.trackChipSetChanges();
    this.trackDestroyedFocusedChip();
  }

  ngOnDestroy(): void {
    this.keyManager?.destroy();
    this.chipActions.destroy();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (
      (changes['size'] && !changes['size'].isFirstChange()) ||
      (changes['type'] && !changes['type'].isFirstChange())
    ) {
      this.checkAndSetChipSizes();
    }
  }

  protected hasFocusedChip(): boolean {
    return this.chips && this.chips.some((chip) => chip.hasFocus());
  }

  protected syncChipsState(): void {
    if (this.chips) {
      this.chips.forEach((chip) => {
        chip.disabled = this._disabled;
        chip.cdr.markForCheck();
      });
    }
  }

  /** Dummy method for subclasses to override. Base chip set cannot be focused. */
  focus(): void {
    noop();
  }

  @HostListener('keydown', ['$event'])
  handleKeydown(event: KeyboardEvent): void {
    if (this.originatesFromChip(event)) {
      this.keyManager.onKeydown(event);
    }
  }

  protected isValidIndex(index: number): boolean {
    return index >= 0 && index < this.chips.length;
  }

  protected allowFocusEscape(): void {
    if (this.tabIndex !== TAB_INDEX_NOT_FOCUSABLE) {
      const previousTabIndex = this.tabIndex;
      this.tabIndex = TAB_INDEX_NOT_FOCUSABLE;

      setTimeout(() => (this.tabIndex = previousTabIndex));
    }
  }

  /**
   * Gets a stream of events from all the chips within the set.
   * The stream will automatically incorporate any newly-added chips.
   */
  protected getChipStream<E, C extends RdsChipComponent<T>>(
    mappingFunction: (chip: C) => Observable<E>,
  ): Observable<E> {
    return this.chips.changes.pipe(
      startWith(null),
      switchMap(() =>
        merge(...(this.chips as QueryList<C>).map(mappingFunction)),
      ),
    );
  }

  /** Checks whether an event comes from inside a chip element. */
  protected originatesFromChip(event: Event): boolean {
    let currentElement = event.target as HTMLElement | null;

    while (currentElement && currentElement !== this.elementRef.nativeElement) {
      if (currentElement.classList.contains('rds-chips__chip')) {
        return true;
      }

      currentElement = currentElement.parentElement;
    }

    return false;
  }

  private setUpFocusManagement(): void {
    // Create a flat `QueryList` containing the actions of all the chips.
    // This allows us to navigate both within the chip and move to the next/previous
    // one using the existing `ListKeyManager`.
    this.chips.changes
      .pipe(startWith(this.chips))
      .subscribe((chips: QueryList<RdsChipComponent<T>>) => {
        const actions: RdsChipActionDirective[] = [];
        chips.forEach((chip) =>
          chip.getActions().forEach((action) => actions.push(action)),
        );
        this.chipActions.reset(actions);
        this.chipActions.notifyOnChanges();
      });

    this.keyManager = new FocusKeyManager(this.chipActions)
      .withVerticalOrientation()
      .withHorizontalOrientation(this.dir ? this.dir.value : 'ltr')
      .withHomeAndEnd()
      .skipPredicate((action) => action.disabled);

    // Keep the manager active index in sync so that navigation picks
    // up from the current chip if the user clicks into the list directly.
    this.chipFocusChanges
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(({ source: chip }) => {
        const action = chip.getSourceAction(document.activeElement as Element);

        if (action) {
          this.keyManager.updateActiveItem(action);
        }
      });

    this.dir?.change
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((direction) =>
        this.keyManager.withHorizontalOrientation(direction),
      );
  }

  /** Listens to changes in the chip set and syncs up the state of the individual chips. */
  private trackChipSetChanges(): void {
    this.chips.changes
      .pipe(startWith(null), takeUntilDestroyed(this.destroyRef))
      .subscribe(() => {
        if (this.disabled) {
          // Since this happens after the content has been
          // checked, we need to defer it to the next tick.
          Promise.resolve().then(() => this.syncChipsState());
        }

        this._redirectDestroyedChipFocus();
      });
  }

  private trackDestroyedFocusedChip(): void {
    this.chipDestroyedChanges
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((event) => {
        const chipArray = this.chips.toArray();
        const chipIndex = chipArray.indexOf(event.source);

        // If the focused chip is destroyed, save its index so that we can move focus to the next
        // chip. We only save the index here, rather than move the focus immediately, because we want
        // to wait until the chip is removed from the chip list before focusing the next one. This
        // allows us to keep focus on the same index if the chip gets swapped out.
        if (this.isValidIndex(chipIndex) && event.source.hasFocus()) {
          this._lastDestroyedFocusedChipIndex = chipIndex;
        }
      });
  }

  private _redirectDestroyedChipFocus(): void {
    if (this._lastDestroyedFocusedChipIndex == null) {
      return;
    }

    if (this.chips.length) {
      const newIndex = Math.min(
        this._lastDestroyedFocusedChipIndex,
        this.chips.length - 1,
      );
      const chipToFocus = this.chips.toArray()[newIndex];

      if (chipToFocus.disabled) {
        // If we're down to one disabled chip, move focus back to the set.
        if (this.chips.length === 1) {
          this.focus();
        } else {
          this.keyManager.setPreviousItemActive();
        }
      } else {
        chipToFocus.focus();
      }
    } else {
      this.focus();
    }

    this._lastDestroyedFocusedChipIndex = null;
  }

  private checkAndSetChipSizes(): void {
    this.chips.forEach((a) => {
      if (a.size !== this._size) {
        a.size = this._size;
      }

      if (a.type !== this.type) {
        a.type = this.type;
      }
    });
  }
}
