import { ContentObserver } from '@angular/cdk/observers';
import {
  AfterContentInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  DestroyRef,
  ElementRef,
  HostBinding,
  inject,
  Input,
  numberAttribute,
  OnDestroy,
  QueryList,
  ViewEncapsulation,
} from '@angular/core';
import { merge, Subject } from 'rxjs';
import { RDS_AVATAR_MAX_IN_GROUP } from './avatar-group-max-items';
import { RdsAvatarComponent, RdsAvatarSize } from './avatar.component';
import { startWith } from 'rxjs/operators';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { BooleanInput, NumberInput } from '@angular/cdk/coercion';

@Component({
  selector: 'rds-avatar-group',
  template: `
    <ng-content select="rds-avatar" />
    @if (hiddenElements) {
      <rds-avatar [size]="size" [disableInteractions]="disableInteractions">
        <span rds-avatar-initials>+{{ hiddenElements }}</span>
      </rds-avatar>
    }
  `,
  styleUrls: ['./avatar.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  standalone: true,
  imports: [RdsAvatarComponent],
})
export class RdsAvatarGroupComponent implements AfterContentInit, OnDestroy {
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly contentObserver = inject(ContentObserver);
  private readonly elementRef = inject(ElementRef);
  private readonly destroyRef = inject(DestroyRef);
  private readonly defaultMaxInGroup = inject(RDS_AVATAR_MAX_IN_GROUP);
  private readonly _stateChanges = new Subject<void>();

  @HostBinding('class') class = 'rds-avatar-group';

  hiddenElements = 0;

  @Input() size: RdsAvatarSize = 'm';

  @Input()
  set disableInteractions(value: BooleanInput) {
    this._disableInteractions = booleanAttribute(value);
    this.checkChildInputs();
  }

  get disableInteractions(): boolean {
    return this._disableInteractions;
  }

  private _disableInteractions = false;

  @Input()
  set maxInGroup(maxInGroup: NumberInput) {
    const value = numberAttribute(maxInGroup, -1);
    const previousValue = this._maxInGroup;
    this._maxInGroup = value >= 0 ? value : this.defaultMaxInGroup;

    if (this._maxInGroup !== previousValue) {
      this._stateChanges.next();
    }
  }

  get maxInGroup(): number {
    return this._maxInGroup;
  }

  private _maxInGroup = this.defaultMaxInGroup;

  @ContentChildren(RdsAvatarComponent)
  avatarList: QueryList<RdsAvatarComponent>;

  ngAfterContentInit(): void {
    merge(
      this._stateChanges,
      this.avatarList.changes,
      this.contentObserver.observe(this.elementRef),
    )
      .pipe(startWith(null), takeUntilDestroyed(this.destroyRef))
      .subscribe(() => {
        const visibilityChanged = this.checkVisibility();

        if (visibilityChanged) {
          this.cdr.markForCheck();
        }
      });
  }

  ngOnDestroy(): void {
    this._stateChanges.complete();
  }

  private checkVisibility(): boolean {
    let visibilityChanged = this.checkHiddenElements();

    this.avatarList.forEach((item, index) => {
      if (index < this.maxInGroup) {
        if (item._hasHiddenStyle()) {
          item._clearHiddenStyles();
          visibilityChanged = true;
        }
      } else if (!item._hasHiddenStyle()) {
        item._setHiddenStyles();
        visibilityChanged = true;
      }
    });

    this.checkChildInputs();

    return visibilityChanged;
  }

  private checkChildInputs(): void {
    Promise.resolve().then(() => {
      this.avatarList
        .filter((i) => i.size !== this.size)
        .forEach((i) => (i.size = this.size));
      this.avatarList
        .filter((i) => i.disableInteractions !== this.disableInteractions)
        .forEach((i) => (i.disableInteractions = this.disableInteractions));
    });
  }

  private checkHiddenElements(): boolean {
    const previous = this.hiddenElements;
    this.hiddenElements =
      this.avatarList?.length > this.maxInGroup
        ? this.avatarList.length - this.maxInGroup
        : 0;

    return previous !== this.hiddenElements;
  }
}
