import { BooleanInput } from '@angular/cdk/coercion';
import {
  CDK_ROW_TEMPLATE,
  CdkFooterRow,
  CdkFooterRowDef,
  CdkHeaderRow,
  CdkHeaderRowDef,
  CdkNoDataRow,
  CdkRow,
  CdkRowDef,
} from '@angular/cdk/table';
import {
  booleanAttribute,
  ChangeDetectionStrategy,
  Component,
  Directive,
  HostBinding,
  Input,
  ViewEncapsulation,
} from '@angular/core';

@Directive({
  selector: '[rds-header-row-def], [rdsHeaderRowDef]',
  providers: [
    { provide: CdkHeaderRowDef, useExisting: RdsHeaderRowDefDirective },
  ],
})
export class RdsHeaderRowDefDirective extends CdkHeaderRowDef {
  @Input('rdsHeaderRowDef') override columns: Iterable<string>;

  @Input('rdsHeaderRowDefSticky')
  override get sticky(): boolean {
    return super.sticky;
  }

  override set sticky(v: BooleanInput) {
    super.sticky = booleanAttribute(v);
  }
}

@Directive({
  selector: '[rds-footer-row-def], [rdsFooterRowDef]',
  providers: [
    { provide: CdkFooterRowDef, useExisting: RdsFooterRowDefDirective },
  ],
})
export class RdsFooterRowDefDirective extends CdkFooterRowDef {
  @Input('rdsFooterRowDef') override columns: Iterable<string>;

  @Input('rdsFooterRowDefSticky')
  override set sticky(v: BooleanInput) {
    super.sticky = booleanAttribute(v);
  }

  override get sticky(): boolean {
    return super.sticky;
  }
}

@Directive({
  selector: '[rds-row-def], [rdsRowDef]',
  providers: [{ provide: CdkRowDef, useExisting: RdsRowDefDirective }],
})
export class RdsRowDefDirective<T> extends CdkRowDef<T> {
  @Input('rdsRowDefColumns') override columns: Iterable<string>;
  @Input('rdsRowDefWhen') override when: (index: number, rowData: T) => boolean;
}

@Component({
  selector: 'rds-header-row, tr[rds-header-row], tr[rdsHeaderRow]',
  template: CDK_ROW_TEMPLATE,
  changeDetection: ChangeDetectionStrategy.Default,
  encapsulation: ViewEncapsulation.None,
  exportAs: 'rdsHeaderRow',
  providers: [{ provide: CdkHeaderRow, useExisting: RdsHeaderRowComponent }],
})
export class RdsHeaderRowComponent extends CdkHeaderRow {
  @HostBinding('class') class = 'rds-header-row';
  @HostBinding('attr.role') role = 'row';
}

@Component({
  selector: 'rds-footer-row, tr[rds-footer-row], tr[rdsFooterRow]',
  template: CDK_ROW_TEMPLATE,
  changeDetection: ChangeDetectionStrategy.Default,
  encapsulation: ViewEncapsulation.None,
  exportAs: 'rdsFooterRow',
  providers: [{ provide: CdkFooterRow, useExisting: RdsFooterRowComponent }],
})
export class RdsFooterRowComponent extends CdkFooterRow {
  @HostBinding('class') class = 'rds-footer-row';
  @HostBinding('attr.role') role = 'row';
}

@Component({
  selector: 'rds-row, tr[rds-row], tr[rdsRow]',
  template: CDK_ROW_TEMPLATE,
  changeDetection: ChangeDetectionStrategy.Default,
  encapsulation: ViewEncapsulation.None,
  exportAs: 'rdsRow',
  providers: [{ provide: CdkRow, useExisting: RdsRowComponent }],
})
export class RdsRowComponent extends CdkRow {
  @HostBinding('class') class = 'rds-row';
  @HostBinding('attr.role') role = 'row';

  @HostBinding('class.selected')
  @Input({ transform: booleanAttribute })
  selected = false;

  @HostBinding('class.disabled')
  @Input({ transform: booleanAttribute })
  disabled = false;
}

@Directive({
  selector: 'ng-template[rds-no-data-row], ng-template[rdsNoDataRow]',
  providers: [{ provide: CdkNoDataRow, useExisting: RdsNoDataRowDirective }],
})
export class RdsNoDataRowDirective extends CdkNoDataRow {}
