import {
  booleanAttribute,
  Directive,
  ElementRef,
  HostBinding,
  inject,
  Input,
  numberAttribute,
} from '@angular/core';
import { FocusableOption } from '@angular/cdk/a11y';
import { NumberInput } from '@angular/cdk/coercion';
import { TAB_INDEX_NOT_FOCUSABLE } from '../utils';

@Directive({
  selector: '[rds-chip-action], [rdsChipAction]',
})
export class RdsChipActionDirective implements FocusableOption {
  public readonly nativeElement = inject(ElementRef).nativeElement;

  private _tabIndex = TAB_INDEX_NOT_FOCUSABLE;

  @HostBinding('class')
  readonly classes = 'rds-chip-action';

  @Input()
  set tabIndex(value: NumberInput) {
    this._tabIndex = numberAttribute(value, TAB_INDEX_NOT_FOCUSABLE);
  }

  get tabIndex(): number {
    return this.disabled ? TAB_INDEX_NOT_FOCUSABLE : this._tabIndex;
  }

  @Input({ transform: booleanAttribute })
  @HostBinding('attr.aria-disabled')
  disabled = false;

  @HostBinding('attr.disabled')
  protected get disabledAttribute(): string | null {
    // When this chip action is disabled and focusing disabled chips is not permitted, return empty
    // string to indicate that disabled attribute should be included.
    return this.disabled ? '' : null;
  }

  @HostBinding('tabIndex')
  protected get tabindexAttribute(): string | null {
    return this.disabled ? null : this.tabIndex.toString();
  }

  focus(): void {
    this.nativeElement.focus();
  }
}
