import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  HostBinding,
  inject,
  Input,
  ViewEncapsulation,
} from '@angular/core';
import { RdsButtonComponent } from '../button';
import { RdsEmptyStateImageDirective } from './empty-state-image.directive';

export type RdsEmptyStateSize = 'xs' | 's' | 'm' | 'l';

export type RdsEmptyStateAlignment = 'left' | 'center';

@Component({
  selector: 'rds-empty-state',
  templateUrl: './empty-state.component.html',
  styleUrls: ['./empty-state.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsEmptyStateComponent {
  protected readonly cdr = inject(ChangeDetectorRef);

  @ContentChild(RdsEmptyStateImageDirective, { static: false })
  _image: RdsEmptyStateImageDirective | null = null;
  @ContentChild(RdsButtonComponent, { static: false })
  button: RdsButtonComponent | null = null;

  private _size: RdsEmptyStateSize = 'l';

  @Input()
  set size(value: RdsEmptyStateSize) {
    this._size = value;

    if (this.button) {
      if (this._size === 'l') {
        this.button.size = 'm';
      } else {
        this.button.size = 's';
      }
    }
  }
  get size(): RdsEmptyStateSize {
    return this._size;
  }

  @Input()
  alignment: RdsEmptyStateAlignment = 'left';

  @HostBinding('class') get class(): string[] {
    const classList = ['rds-empty-state-container'];

    if (this.size) {
      classList.push(`rds-empty-state--${this.size}`);
    }

    if (this.alignment) {
      classList.push(`rds-empty-state--${this.alignment}`);
    }

    return classList;
  }
}
