import {
  AfterContentInit,
  booleanAttribute,
  ChangeDetectorRef,
  ContentChild,
  DestroyRef,
  Directive,
  ElementRef,
  EventEmitter,
  HostListener,
  inject,
  Input,
  NgZone,
  OnChanges,
  OnDestroy,
  Output,
  Renderer2,
  SimpleChanges,
} from '@angular/core';
import { fromEvent, noop, PartialObserver, race, Subject } from 'rxjs';
import {
  delay,
  filter,
  map,
  switchMap,
  take,
  takeUntil,
  tap,
  timeout,
  withLatestFrom,
} from 'rxjs/operators';
import { RdsDropdownToggleDirective } from './dropdown-toggle.directive';
import { RdsDropdownMenuDirective } from './dropdown-menu.directive';
import {
  CdkOverlayOrigin,
  ConnectedPosition,
  Overlay,
  OverlayRef,
  PositionStrategy,
} from '@angular/cdk/overlay';
import { ComponentPortal } from '@angular/cdk/portal';
import { RdsDropdownContainerComponent } from './dropdown-container.component';
import {
  RdsDropdownAutoClose,
  RdsDropdownBackdrop,
  RdsDropdownCloseSource,
  RdsDropdownPlacement,
} from './dropdown.types';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

const SECONDARY_MOUSE_BUTTON_CLICK = 2;

@Directive({
  selector: '[rdsDropdown], [rds-dropdown]',
  exportAs: 'rdsDropdown',
})
export class RdsDropdownDirective
  implements AfterContentInit, OnChanges, OnDestroy
{
  protected readonly cdr = inject(ChangeDetectorRef);
  protected readonly destroyRef = inject(DestroyRef);
  protected readonly ngZone = inject(NgZone);
  protected readonly elementRef = inject<ElementRef<HTMLElement>>(ElementRef);
  protected readonly renderer = inject(Renderer2);
  protected readonly overlay = inject(Overlay);

  @ContentChild(RdsDropdownMenuDirective, { static: false })
  private _menu: RdsDropdownMenuDirective;

  @ContentChild(RdsDropdownToggleDirective, { static: false })
  private _toggle: RdsDropdownToggleDirective;

  @Input() autoClose: RdsDropdownAutoClose = true;
  @Input() placement: RdsDropdownPlacement = 'bottom-left';
  @Input({ transform: booleanAttribute }) disabled = false;
  @Input() backdrop: RdsDropdownBackdrop = false;
  @Output() openChanged = new EventEmitter<boolean>();
  @Output() shown = new EventEmitter<void>();
  @Output() hidden = new EventEmitter<void>();

  private _open = false;
  private _overlayRef: OverlayRef | null = null;
  private _portal: ComponentPortal<RdsDropdownContainerComponent>;
  private readonly _closed$ = new Subject<void>();

  @HostListener('mouseover')
  onHover(): void {
    this._handleMouseOver(this.elementRef);
  }

  @HostListener('mouseleave')
  onLeave(): void {
    this._handleMouseLeave(this.elementRef);
  }

  ngAfterContentInit(): void {
    const target = this.elementRef.nativeElement.firstElementChild;

    if (target) {
      fromEvent(target, 'focus')
        .pipe(takeUntilDestroyed(this.destroyRef))
        .subscribe(() => {
          this._handleFocus(this.elementRef);
        });
      fromEvent(target, 'blur')
        .pipe(takeUntilDestroyed(this.destroyRef))
        .subscribe(() => {
          this._handleBlur(this.elementRef);
        });
    }

    this.ngZone.onStable
      .pipe(take(1), takeUntilDestroyed(this.destroyRef))
      .subscribe(() => {
        if (this._open) {
          this._setCloseHandlers();
        }
      });

    this._toggle.toggle
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((event) => {
        if (this._toggle.trigger === 'mouseenter, mouseleave') {
          if (event.type === 'mouseenter' && !this.isOpen()) {
            this.show();
          } else if (event.type === 'mouseleave' && this.isOpen()) {
            const hideObserver: PartialObserver<Event> = {
              next: noop,
              error: () => this.hide(),
            };

            if (this._overlayRef) {
              fromEvent(this._overlayRef.overlayElement, 'mouseenter')
                .pipe(
                  take(1),
                  tap(() => {
                    if (this._overlayRef) {
                      fromEvent(this._overlayRef.overlayElement, 'mouseleave')
                        .pipe(
                          take(1),
                          switchMap(() =>
                            fromEvent(
                              this._toggle.elementRef.nativeElement,
                              'mouseenter',
                            ).pipe(
                              take(1),
                              timeout(1),
                              takeUntil(this._closed$),
                            ),
                          ),
                          takeUntil(this._closed$),
                        )
                        .subscribe(hideObserver);
                    }
                  }),
                  timeout(1),
                  takeUntil(this._closed$),
                )
                .subscribe(hideObserver);
            }
          }
        } else {
          this.toggle();
        }
      });

    this._toggle.disabled = this.disabled;
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['backdrop']) {
      this._checkToggle();

      this.hide();
      this._overlayRef = null;
    }

    if (changes['placement']) {
      if (this.placement === null || this.placement === undefined) {
        this.placement = 'bottom-left';
      }

      if (this._overlayRef !== null) {
        this._overlayRef.updatePositionStrategy(this._getPositionStrategy());
      }
    }

    if (
      this._toggle &&
      changes['disabled'] &&
      changes['disabled'].previousValue !== changes['disabled'].currentValue
    ) {
      this._toggle.disabled = this.disabled;

      if (this.disabled) {
        this.hide();
      }
    }
  }

  ngOnDestroy(): void {
    if (this._overlayRef) {
      this._overlayRef.detach();
    }

    this._closed$.next();
    this._closed$.complete();
  }

  isOpen(): boolean {
    return this._open;
  }

  show(): void {
    if (!this.disabled && !this._open) {
      this._checkToggle();
      this._open = true;

      if (!this._overlayRef) {
        this._overlayRef = this.overlay.create({
          hasBackdrop: this.backdrop !== false,
          backdropClass:
            this.backdrop === 'transparent'
              ? 'cdk-overlay-transparent-backdrop'
              : undefined,
          positionStrategy: this._getPositionStrategy(),
        });

        this._overlayRef.backdropClick().subscribe(() => this.hide());
        this._portal = new ComponentPortal(RdsDropdownContainerComponent, null);
      }

      const dropdownContainerComponentRef = this._overlayRef.attach(
        this._portal,
      );
      dropdownContainerComponentRef.instance.templateRef =
        this._menu.templateRef;
      dropdownContainerComponentRef.changeDetectorRef.markForCheck();

      this.openChanged.emit(true);
      this.shown.emit();
      this._setCloseHandlers();

      if (this._toggle) {
        this._toggle.isOpen = true;
        this._toggle.elementRef.nativeElement.focus();
      }
    }
  }

  hide(): void {
    if (this._open) {
      this._open = false;
      this._toggle.isOpen = false;

      if (this._overlayRef) {
        this._overlayRef.detach();
      }

      this._closed$.next();
      this.openChanged.emit(false);
      this.hidden.emit();
      this.cdr.markForCheck();
    }
  }

  toggle(): void {
    if (this.isOpen()) {
      this.hide();
    } else {
      this.show();
    }
  }

  private _getPlacement(): RdsDropdownPlacement {
    return this.placement !== null && this.placement !== undefined
      ? this.placement
      : 'bottom-left';
  }

  private _setCloseHandlers(): void {
    if (this.autoClose) {
      this.ngZone.runOutsideAngular(() => {
        const shouldCloseOnClick = (event: MouseEvent): boolean => {
          const element = event.target as HTMLElement;

          if (
            event.button === SECONDARY_MOUSE_BUTTON_CLICK ||
            this._toggle.elementRef.nativeElement.contains(element)
          ) {
            return false;
          }

          if (this.autoClose === 'inside') {
            return this._overlayRef?.overlayElement.contains(element) || true;
          } else if (this.autoClose === 'outside') {
            return !this._overlayRef?.overlayElement.contains(element) || false;
          } else {
            return true;
          }
        };

        const escapes$ = fromEvent<KeyboardEvent>(document, 'keydown').pipe(
          filter((e) => e.key === 'Escape'),
          tap((e) => e.preventDefault()),
          takeUntil(this._closed$),
        );

        const mouseDowns$ = fromEvent<MouseEvent>(document, 'mousedown').pipe(
          map((e) => shouldCloseOnClick(e)),
          takeUntil(this._closed$),
        );

        const closeableClicks$ = fromEvent<MouseEvent>(
          document,
          'mouseup',
        ).pipe(
          withLatestFrom(mouseDowns$),
          // eslint-disable-next-line @typescript-eslint/no-unused-vars
          filter(([_, shouldClose]) => shouldClose),
          delay(0),
          takeUntil(this._closed$),
        );

        race<RdsDropdownCloseSource[]>([
          escapes$.pipe(map(() => 'ESCAPE')),
          closeableClicks$.pipe(map(() => 'CLICK')),
        ])
          .pipe(takeUntilDestroyed(this.destroyRef))
          .subscribe((source: RdsDropdownCloseSource) =>
            this.ngZone.run(() => {
              this.hide();

              if (source === 'ESCAPE') {
                this._toggle.elementRef.nativeElement.focus();
              }
            }),
          );
      });
    }
  }

  private _handleMouseOver(el: ElementRef): void {
    this.handleIsPrevButton();
    this.handleIsPrevFocused();
    this.renderer.addClass(el.nativeElement, 'rds-dropdown--hover');
  }

  private _handleMouseLeave(el: ElementRef): void {
    this.handleIsPrevButton();
    this.handleIsPrevFocused();
    this.renderer.removeClass(el.nativeElement, 'rds-dropdown--hover');
  }

  private _handleFocus(el: ElementRef): void {
    this.handleIsPrevButton();
    this.renderer.addClass(el.nativeElement, 'rds-dropdown--focused');
  }

  private _handleBlur(el: ElementRef): void {
    this.handleIsPrevButton();
    this.renderer.removeClass(el.nativeElement, 'rds-dropdown--focused');
  }

  private _getPositions(): ConnectedPosition {
    const placement = this._getPlacement();
    const positions: ConnectedPosition = {
      originX: 'start',
      originY: 'bottom',
      overlayX: 'start',
      overlayY: 'top',
      offsetX: 0,
      offsetY: 0,
    };

    if (placement.includes('top')) {
      positions.originY = 'top';
      positions.overlayY = 'bottom';
    }

    if (placement.includes('center')) {
      positions.originX = 'center';
      positions.overlayX = 'center';
    }

    if (placement.includes('right')) {
      positions.originX = placement.includes('start') ? 'start' : 'end';
      positions.overlayX = 'end';
    } else if (placement.includes('end')) {
      positions.originX = 'end';
    }

    return positions;
  }

  private _getPositionStrategy(): PositionStrategy {
    const overlayOrigin = new CdkOverlayOrigin(this._toggle.elementRef);

    return this.overlay
      .position()
      .flexibleConnectedTo(overlayOrigin.elementRef)
      .withPositions([this._getPositions()]);
  }

  private _checkToggle(): void {
    if (this.backdrop && this._toggle && this._toggle.trigger !== 'click') {
      throw new Error(`Backdrop is only allowed for 'click' trigger`);
    }
  }

  private handleIsPrevButton(): void {
    const isPrevButton =
      this.elementRef?.nativeElement?.nextElementSibling?.tagName === 'BUTTON';

    if (isPrevButton) {
      this.renderer.addClass(
        this.elementRef.nativeElement,
        'rds-dropdown--prev-button',
      );
    } else {
      this.renderer.removeClass(
        this.elementRef.nativeElement,
        'rds-dropdown--prev-button',
      );
    }
  }

  private handleIsPrevFocused(): void {
    const input =
      this.elementRef.nativeElement.nextElementSibling?.querySelector('input');

    if (input) {
      if (
        input.parentElement?.classList.contains(
          'rds-form-field_content-infix--focused',
        )
      ) {
        this.renderer.addClass(
          this.elementRef.nativeElement,
          'rds-dropdown--prev-focused',
        );
      } else {
        this.renderer.removeClass(
          this.elementRef.nativeElement,
          'rds-dropdown--prev-focused',
        );
      }
    } else {
      this.renderer.removeClass(
        this.elementRef.nativeElement,
        'rds-dropdown--prev-focused',
      );
    }
  }
}
