import {
  AfterViewInit,
  ChangeDetectionStrategy,
  Component,
  DestroyRef,
  ElementRef,
  HostBinding,
  HostListener,
  inject,
  OnDestroy,
  OnInit,
  ViewEncapsulation,
} from '@angular/core';
import { Subject } from 'rxjs';
import { FocusableOption, FocusMonitor, FocusOrigin } from '@angular/cdk/a11y';
import { RdsSidenavService } from '../sidenav.service';
import { filter } from 'rxjs/operators';
import { rdsExpansionAnimations } from '../../accordion';
import { NativeElement, RDS_SIDENAV_MENU_PANEL } from '../sidenav.types';
import { SidenavItem } from '../sidenav-item';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

@Component({
  selector:
    'rds-sidenav-menuitem, [rds-sidenav-menuitem], [rdsSidenavMenuitem]',
  exportAs: 'rdsSidenavMenuitem',
  templateUrl: './sidenav-menuitem.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  animations: [rdsExpansionAnimations.bodyExpansion],
  providers: [
    { provide: SidenavItem, useExisting: RdsSidenavMenuitemComponent },
  ],
})
export class RdsSidenavMenuitemComponent
  implements OnInit, AfterViewInit, OnDestroy, FocusableOption, NativeElement
{
  public readonly _parentMenu = inject(RDS_SIDENAV_MENU_PANEL, {
    optional: true,
  });
  private readonly sidenavService = inject(RdsSidenavService);
  private readonly destroyRef = inject(DestroyRef);
  private readonly elementRef = inject<ElementRef<HTMLElement>>(ElementRef);
  private readonly focusMonitor = inject(FocusMonitor);

  @HostBinding('attr.role')
  role = 'menuitem';

  readonly _hovered = new Subject<RdsSidenavMenuitemComponent>();
  readonly _focused = new Subject<RdsSidenavMenuitemComponent>();
  readonly nativeElement = this.elementRef.nativeElement;

  @HostBinding('class.rds-sidenav-minimal') isMinimal: boolean;
  @HostBinding('class.rds-sidenav-menuitem') rdsMenuItemClass = true;
  @HostBinding('class.rds-focus-indicator') rdsFocusIndicatorClass = true;

  focus(origin?: FocusOrigin, options?: FocusOptions): void {
    if (origin) {
      this.focusMonitor.focusVia(this.nativeElement, origin, options);
    } else {
      this.nativeElement.focus(options);
    }
  }

  ngOnInit(): void {
    this.sidenavService.isMinimal$
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((v) => (this.isMinimal = v));
  }

  ngAfterViewInit(): void {
    this.focusMonitor
      .monitor(this.elementRef, false)
      .pipe(
        filter((v) => !!v),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe(() => this._focused.next(this));
  }

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.elementRef);

    this._hovered.complete();
    this._focused.complete();
  }

  @HostListener('mouseenter')
  _handleMouseEnter(): void {
    this._hovered.next(this);
  }

  getLabel(): string {
    const clone = this.elementRef.nativeElement.cloneNode(true) as HTMLElement;
    const icons = clone.querySelectorAll('rds-icon');

    for (let i = 0; i < icons.length; i++) {
      icons[i].remove();
    }

    return clone.textContent?.trim() || '';
  }
}
