export const RDS_LIST_ITEM_TEMPLATE = `<ng-content select="rds-icon"></ng-content>
<span class="rds-list-item-text">
  <ng-content />
</span>
`;

export const RDS_NESTABLE_LIST_ITEM_TEMPLATE = `${RDS_LIST_ITEM_TEMPLATE}
@if (hasChildItems) {
  <rds-icon
    namespace="outlined"
    icon="chevron_right"
    class="rds-list-item__has-child-items"
  />
}`;

export const RDS_SINGLE_SELECT_LIST_ITEM_TEMPLATE = `<ng-content select="rds-avatar"/>
${RDS_LIST_ITEM_TEMPLATE}
<rds-icon icon="checkmark" class="rds-list-item-checkmark"/>`;

export const RDS_MULTI_SELECT_LIST_ITEM_TEMPLATE = `<ng-content select="rds-avatar"/>
<ng-content select="rds-icon"/>
@if (!avatar && !icon) {
    <rds-pseudo-checkbox
      noSpacing
      [checked]="checked"
      [disabled]="disabled"
    />
}
<span class="rds-list-item-text">
  <ng-content/>
</span>
@if (avatar || icon) {
    <rds-pseudo-checkbox
      noSpacing
      [checked]="checked"
      [disabled]="disabled"
      class="rds-list-item__checkbox-right"
    />
}
`;
