import {
  booleanAttribute,
  Component,
  ContentChild,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  SimpleChanges,
  ViewEncapsulation,
} from '@angular/core';
import { RdsTabLabelDirective } from './tab-label.directive';
import { BooleanInput } from '@angular/cdk/coercion';

@Component({
  selector: 'rds-tab',
  template: `
    <div [hidden]="!active" (cdkObserveContent)="stateChanges.next()">
      <ng-content />
    </div>
  `,
  encapsulation: ViewEncapsulation.None,
})
export class RdsTabComponent implements OnChanges, OnDestroy {
  private _templateLabel: RdsTabLabelDirective;

  @Input() label: string;

  @Input()
  set active(value: BooleanInput) {
    const newValue = booleanAttribute(value);

    if (newValue !== this.active) {
      this._active = newValue;
      this.stateChanges.next();
    }
  }
  get active(): boolean {
    return this._active;
  }
  private _active = false;

  @Input({ transform: booleanAttribute }) disabled = false;

  readonly stateChanges = new EventEmitter<void>();

  @ContentChild(RdsTabLabelDirective)
  get templateLabel(): RdsTabLabelDirective {
    return this._templateLabel;
  }

  set templateLabel(value: RdsTabLabelDirective) {
    if (value) {
      this._templateLabel = value;
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['label'] || changes['disabled']) {
      this.stateChanges.next();
    }
  }

  ngOnDestroy(): void {
    this.stateChanges.complete();
  }
}
