import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ComponentRef,
  ElementRef,
  EmbeddedViewRef,
  EventEmitter,
  HostBinding,
  HostListener,
  inject,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import {
  BasePortalOutlet,
  CdkPortalOutlet,
  ComponentPortal,
  DomPortal,
  TemplatePortal,
} from '@angular/cdk/portal';
import {
  ConfigurableFocusTrap,
  ConfigurableFocusTrapFactory,
} from '@angular/cdk/a11y';
import {
  animate,
  AnimationEvent,
  AnimationTriggerMetadata,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import { DOCUMENT } from '@angular/common';
import { RdsDialogConfig } from './model';
import { TAB_INDEX_NOT_FOCUSABLE } from '../utils';

const animations: {
  readonly dialogComponent: AnimationTriggerMetadata;
} = {
  dialogComponent: trigger('dialogContainer', [
    state('void, exit', style({ opacity: 0, transform: 'scale(0.7)' })),
    state('enter', style({ transform: 'none' })),
    transition(
      '* => enter',
      animate(
        '150ms cubic-bezier(0, 0, 0.2, 1)',
        style({ transform: 'none', opacity: 1 }),
      ),
    ),
    transition(
      '* => void, * => exit',
      animate('75ms cubic-bezier(0.4, 0.0, 0.2, 1)', style({ opacity: 0 })),
    ),
  ]),
};

@Component({
  selector: 'rds-dialog',
  template: ` <ng-template cdkPortalOutlet /> `,
  styleUrls: ['./dialog-container.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  animations: [animations.dialogComponent],
})
export class RdsDialogContainerComponent<D> extends BasePortalOutlet {
  private readonly elementRef = inject(ElementRef);
  private readonly focusTrapFactory = inject(ConfigurableFocusTrapFactory);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly document = inject(DOCUMENT);
  public readonly config = inject<RdsDialogConfig<D>>(RdsDialogConfig);

  private focusTrap: ConfigurableFocusTrap;
  private elementFocusedBeforeDialogWasOpened: HTMLElement;

  ariaLabelledBy = this.config.ariaLabelledBy || null;
  id: string;

  animationState: 'void' | 'enter' | 'exit' = 'enter';

  readonly animationStateChanged = new EventEmitter<AnimationEvent>();

  @ViewChild(CdkPortalOutlet, { static: true }) portalOutlet: CdkPortalOutlet;

  @HostBinding('class') readonly hostClass = 'rds-dialog';
  @HostBinding('tabIndex') readonly hostTabindex = TAB_INDEX_NOT_FOCUSABLE;
  @HostBinding('attr.aria-dialog') readonly hostAriaDialog = true;

  @HostBinding('attr.id') get hostId(): string {
    return this.id;
  }

  @HostBinding('attr.size') get hostSize(): string {
    return this.config.size;
  }

  @HostBinding('attr.role') get hostRole(): string {
    return this.config.role;
  }

  @HostBinding('attr.aria-labelledby') get hostAriaLabelledby(): string | null {
    return this.config.ariaLabel ? null : this.ariaLabelledBy;
  }

  @HostBinding('attr.aria-label') get hostAriaLabel(): string | null {
    return this.config.ariaLabel || null;
  }

  @HostBinding('attr.aria-describedby') get hostAriaDescribedby():
    | string
    | null {
    return this.config.ariaDescribedBy || null;
  }

  @HostBinding('@dialogContainer') get hostDialogContainer(): string {
    return this.animationState;
  }

  @HostListener('@dialogContainer.start', ['$event'])
  onAnimationStart($event: AnimationEvent): void {
    this.animationStateChanged.emit($event);
  }

  @HostListener('@dialogContainer.done', ['$event'])
  onAnimationDone(event: AnimationEvent): void {
    if (event.toState === 'enter') {
      this.trapFocus();
    } else if (event.toState === 'exit') {
      this.restoreFocus();
    }

    this.animationStateChanged.emit(event);
  }

  attachComponentPortal<T>(portal: ComponentPortal<T>): ComponentRef<T> {
    if (this.portalOutlet.hasAttached()) {
      this.throwCannotAttachError();
    }

    this.setupFocusTrap();

    return this.portalOutlet.attachComponentPortal(portal);
  }

  attachTemplatePortal<C>(portal: TemplatePortal<C>): EmbeddedViewRef<C> {
    if (this.portalOutlet.hasAttached()) {
      this.throwCannotAttachError();
    }

    this.setupFocusTrap();

    return this.portalOutlet.attachTemplatePortal(portal);
  }

  override attachDomPortal = (portal: DomPortal): void => {
    if (this.portalOutlet.hasAttached()) {
      this.throwCannotAttachError();
    }

    this.setupFocusTrap();

    return this.portalOutlet.attachDomPortal(portal);
  };

  startExitAnimation(): void {
    this.animationState = 'exit';

    this.cdr.markForCheck();
  }

  recaptureFocus(): void {
    if (!this.isFocusInsideDialog()) {
      const focusContainer = !this.focusTrap.focusInitialElement();

      if (focusContainer) {
        this.elementRef.nativeElement.focus();
      }
    }
  }

  private trapFocus(): void {
    this.focusTrap.focusInitialElementWhenReady();
  }

  private restoreFocus(): void {
    const elementToFocus = this.elementFocusedBeforeDialogWasOpened;

    // extra check, because IE can set the `activeElement` to null
    if (elementToFocus && typeof elementToFocus.focus === 'function') {
      const activeElement = this.document.activeElement;
      const hostNativeElement = this.elementRef.nativeElement;

      // Make sure that focus is still inside the dialog or is on the body
      if (
        !activeElement ||
        activeElement === this.document.body ||
        activeElement === hostNativeElement ||
        hostNativeElement.contains(activeElement)
      ) {
        elementToFocus.focus();
      }
    }

    if (this.focusTrap) {
      this.focusTrap.destroy();
    }
  }

  private setupFocusTrap(): void {
    if (!this.focusTrap) {
      this.focusTrap = this.focusTrapFactory.create(
        this.elementRef.nativeElement,
      );
    }

    if (this.document) {
      this.elementFocusedBeforeDialogWasOpened = this.document
        .activeElement as HTMLElement;
    }
  }

  private isFocusInsideDialog(): boolean {
    const element = this.elementRef.nativeElement;
    const activeElement = this.document.activeElement;

    return element === activeElement || element.contains(activeElement);
  }

  private throwCannotAttachError(): void {
    throw Error(
      'Attempting to attach dialog content after content is already attached',
    );
  }
}
