import { RdsUploadFilter, RdsUploadConfig } from './upload.types';

// those consts exist to avoid circular reference in type definition
export const FILTER_QUEUE_LIMIT_DESCRIPTION_INTL_TOKEN =
  'queueLimitFilterDescription';
export const FILTER_FILE_SIZE_DESCRIPTION_INTL_TOKEN =
  'fileSizeFilterDescription';
export const FILTER_FILE_TYPE_DESCRIPTION_INTL_TOKEN =
  'fileTypeFilterDescription';

export class RdsUploadFilters {
  static readonly queueLimit: RdsUploadFilter = {
    name: 'queueLimit',
    descriptionIntlToken: FILTER_QUEUE_LIMIT_DESCRIPTION_INTL_TOKEN,
    preventsAddingToQueue: true,
    fn: (item, config, queue) =>
      !config.queueLimit || queue.length < config.queueLimit,
  };

  static readonly fileSize: RdsUploadFilter = {
    name: 'fileSize',
    descriptionIntlToken: FILTER_FILE_SIZE_DESCRIPTION_INTL_TOKEN,
    fn: (item, config) =>
      !(config.maxFileSize && item.size > config.maxFileSize),
  };

  static readonly fileType: RdsUploadFilter = {
    name: 'fileType',
    descriptionIntlToken: FILTER_FILE_TYPE_DESCRIPTION_INTL_TOKEN,
    fn: verifyAccept,
  };

  static compose(
    currentFilters: RdsUploadFilter[],
    config: RdsUploadConfig,
  ): RdsUploadFilter[] {
    const filters = new Set([...currentFilters, RdsUploadFilters.queueLimit]);

    config.maxFileSize && filters.add(RdsUploadFilters.fileSize);
    config.accept && filters.add(RdsUploadFilters.fileType);

    return Array.from(filters);
  }
}

function verifyAccept(file: File, config: RdsUploadConfig): boolean {
  const accept = config.accept?.filter((v) => !!v);

  if (!accept || !accept.length) {
    return true;
  }

  const allowed = parseAccept(accept);
  const extension = extractExtension(file.name);

  return (
    allowed.extension.includes(extension) ||
    allowed.mime.some((regex) => regex.test(file.type))
  );
}

function parseAccept(accept: string[]): ParsedAccept {
  return accept.reduce(
    (acc, curr) => {
      curr = curr.trim().toLowerCase();

      if (curr.startsWith('.')) {
        acc.extension.push(...unifyJpgExtensionsIfNeeded(curr));
      } else {
        acc.mime.push(new RegExp(curr.replace('*', '.*')));
      }

      return acc;
    },
    { extension: [], mime: [] } as ParsedAccept,
  );
}

function unifyJpgExtensionsIfNeeded(extension: string): string[] {
  if (extension === '.jpg' || extension === '.jpeg') {
    return ['.jpeg', '.jpg'];
  } else {
    return [extension];
  }
}

function extractExtension(filename: string): string {
  const splitName = filename.split('.');

  return '.' + splitName[splitName.length - 1].toLowerCase();
}

type ParsedAccept = { extension: string[]; mime: RegExp[] };
