import {
  animate,
  AnimationEvent,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import { CdkStepper, StepContentPositionState } from '@angular/cdk/stepper';
import {
  AfterContentInit,
  AfterViewInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  ElementRef,
  EventEmitter,
  forwardRef,
  HostBinding,
  inject,
  Input,
  Output,
  QueryList,
  ViewChildren,
  ViewEncapsulation,
} from '@angular/core';
import { RdsStepHeaderComponent } from './step-header.component';
import { RdsStepComponent } from './step.component';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { Directionality } from '@angular/cdk/bidi';
import { ViewportRuler } from '@angular/cdk/scrolling';

@Component({
  selector: 'rds-stepper, [rdsStepper], [rds-stepper]',
  exportAs: 'rdsStepper',
  templateUrl: './stepper.component.html',
  styleUrls: ['stepper.component.scss'],
  animations: [
    trigger('stepTransition', [
      state(
        'previous',
        style({ transform: 'translate3d(-100%, 0, 0)', visibility: 'hidden' }),
      ),
      state('current', style({ transform: 'none', visibility: 'inherit' })),
      state(
        'next',
        style({ transform: 'translate3d(100%, 0, 0)', visibility: 'hidden' }),
      ),
      transition('* => *', animate('500ms cubic-bezier(0.35, 0, 0.25, 1)')),
    ]),
    trigger('stepTransitionVertical', [
      state('previous', style({ height: '0px', visibility: 'hidden' })),
      state('next', style({ height: '0px', visibility: 'hidden' })),
      state('current', style({ height: '*', visibility: 'inherit' })),
      transition(
        '* <=> current',
        animate('500ms cubic-bezier(0.4, 0.0, 0.2, 1)'),
        {},
      ),
    ]),
    trigger('stepTransitionSideBySide', [
      state('previous', style({ opacity: '0' })),
      state('current', style({ opacity: '100%' })),
      state('next', style({ opacity: '0' })),
      transition('* => *', animate('500ms cubic-bezier(0.35, 0, 0.25, 1)')),
    ]),
    trigger('stepProgress', [
      state('void', style({ width: 0 })),
      state('max', style({ width: '100%' })),
      transition('* => *', animate('.3s linear')),
    ]),
    trigger('stepProgressVertical', [
      state('void', style({ height: 0 })),
      state('max', style({ height: '100%' })),
      transition('* => *', animate('.3s linear')),
    ]),
    trigger('stepProgressSideBySide', [
      state('void', style({ height: 0 })),
      state('max', style({ height: '100%' })),
      transition('* => *', animate('.3s linear')),
    ]),
  ],
  providers: [{ provide: CdkStepper, useExisting: RdsStepperComponent }],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsStepperComponent
  extends CdkStepper
  implements AfterContentInit, AfterViewInit
{
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly viewportRuler = inject(ViewportRuler);

  @HostBinding('class') get class(): string[] {
    const classList = ['rds-stepper'];

    if (this.orientation === 'horizontal') {
      classList.push('rds-stepper-horizontal');
    } else if (this.orientation === 'vertical') {
      classList.push('rds-stepper-vertical');

      if (this.sideBySide) {
        classList.push('rds-stepper-side-by-side');
      }
    }

    return classList;
  }
  @HostBinding('attr.role') readonly hostRole = 'tablist';

  /** The list of step headers of the steps in the stepper. */
  @ViewChildren(RdsStepHeaderComponent)
  override _stepHeader: QueryList<RdsStepHeaderComponent>;

  /** Full list of steps inside the stepper, including inside nested steppers. */
  @ContentChildren(forwardRef(() => RdsStepComponent), { descendants: true })
  override _steps: QueryList<RdsStepComponent>;

  @Input({ transform: booleanAttribute }) sideBySide = false;

  /** Event emitted when the current step is done transitioning in. */
  @Output() readonly animationDone = new EventEmitter<void>();

  override readonly steps = new QueryList<RdsStepComponent>(); // Steps that belong to current stepper, excluding from nested

  /** Stream of animation `done` events when the body expands/collapses. */
  readonly _animationDone = new Subject<AnimationEvent>(); //

  _horizontalLineStyle: Partial<CSSStyleDeclaration> = {};

  constructor() {
    super(
      inject(Directionality),
      inject(ChangeDetectorRef),
      inject(ElementRef),
    );
  }

  override ngAfterContentInit(): void {
    super.ngAfterContentInit();

    // Mark the component for change detection whenever the content children query changes
    this.steps.changes.pipe(takeUntil(this._destroyed)).subscribe(() => {
      this._stateChanged();
    });

    this._animationDone.pipe(takeUntil(this._destroyed)).subscribe((event) => {
      if ((event.toState as StepContentPositionState) === 'current') {
        this.animationDone.emit();
      }
    });
  }

  override ngAfterViewInit(): void {
    super.ngAfterViewInit();

    if (this._stepHeader.first) {
      Promise.resolve().then(() => {
        this.calculateHorizontalLineStyle();
      });
    }

    this.viewportRuler
      .change()
      .pipe(takeUntil(this._destroyed))
      .subscribe(() => this.calculateHorizontalLineStyle());
  }

  private calculateHorizontalLineStyle(): void {
    const headerPadding = 8;
    const stepperGap = 32;
    const offsetWidth =
      this._stepHeader.first?._elementRef.nativeElement.offsetWidth || 0;
    this._horizontalLineStyle.minWidth = `calc(100% + ${offsetWidth}px)`;
    this._horizontalLineStyle.transform = `translateX(-${
      offsetWidth - headerPadding - stepperGap
    }px)`;
    this.cdr.detectChanges();
  }
}
