import {
  AfterContentInit,
  AfterViewInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  DestroyRef,
  ElementRef,
  EventEmitter,
  HostBinding,
  inject,
  Input,
  OnDestroy,
  OnInit,
  Output,
  QueryList,
  Renderer2,
  RendererStyleFlags2,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { RdsSidenavService } from './sidenav.service';
import { BooleanInput } from '@angular/cdk/coercion';
import { merge } from 'rxjs';
import { startWith, switchMap } from 'rxjs/operators';
import { DOCUMENT } from '@angular/common';
import { FocusKeyManager } from '@angular/cdk/a11y';
import { DOWN_ARROW, TAB, UP_ARROW } from '@angular/cdk/keycodes';
import { RdsSidenavMenuTriggerComponent } from './sidenav-menuitem-trigger/sidenav-menu-trigger.component';
import { RdsSidenavMenuitemComponent } from './sidenav-menuitem/sidenav-menuitem.component';
import { SidenavItem } from './sidenav-item';
import { getScrollingAncestor } from '../utils';
import { NativeElement } from './sidenav.types';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

@Component({
  selector: 'rds-sidenav',
  exportAs: 'rdsSidenav',
  templateUrl: './sidenav.component.html',
  styleUrls: ['sidenav.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  providers: [RdsSidenavService],
})
export class RdsSidenavComponent
  implements OnInit, AfterContentInit, AfterViewInit, OnDestroy
{
  private readonly destroyRef = inject(DestroyRef);
  private readonly nativeElement: HTMLElement =
    inject(ElementRef).nativeElement;
  private readonly document: Document = inject(DOCUMENT);
  private readonly renderer = inject(Renderer2);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly sidenavService = inject(RdsSidenavService);

  private menubarHeight: number | null = null;
  private scrollingAncestor: HTMLElement;

  noScroll: boolean;

  @Input({ transform: booleanAttribute }) isExpandable = true;

  @Input()
  set isMinimal(v: BooleanInput) {
    this.sidenavService.isMinimal = booleanAttribute(v);
  }

  get isMinimal(): boolean {
    return this.sidenavService.isMinimal;
  }

  @HostBinding('class.rds-sidenav-minimal')
  get isInMinimalState(): boolean {
    return this.isMinimal;
  }

  @HostBinding('class.rds-sidenav-full')
  get isInFullState(): boolean {
    return !this.isMinimal;
  }

  @Output()
  isMinimalChange = new EventEmitter<boolean>();

  @ContentChildren(SidenavItem, { descendants: true })
  private children: QueryList<
    RdsSidenavMenuTriggerComponent | RdsSidenavMenuitemComponent
  >;

  @ViewChild('footer', { read: ElementRef })
  private footerRef?: ElementRef<HTMLElement>;

  @ViewChild('nav', { read: ElementRef })
  private navRef: ElementRef<HTMLElement>;

  private directDescendantItems = new QueryList<
    RdsSidenavMenuTriggerComponent | RdsSidenavMenuitemComponent
  >();

  private keyManager: FocusKeyManager<
    RdsSidenavMenuTriggerComponent | RdsSidenavMenuitemComponent
  >;

  ngOnInit(): void {
    this.sidenavService.isMinimal$
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((isMinimal) => this.onMinimalStateChange(isMinimal));
  }

  ngAfterContentInit(): void {
    this.onContentChanged();
    this.updateDirectDescendants();
    this.keyManager = new FocusKeyManager(this.directDescendantItems)
      .withWrap()
      .withHomeAndEnd();

    this.directDescendantItems.changes
      .pipe(
        startWith(this.directDescendantItems),
        switchMap((menus) =>
          merge(
            ...menus.map(
              (
                menu:
                  | RdsSidenavMenuTriggerComponent
                  | RdsSidenavMenuitemComponent,
              ) => menu._focused,
            ),
          ),
        ),
      )
      .subscribe((focusedMenu) => {
        this.keyManager.updateActiveItem(
          focusedMenu as
            | RdsSidenavMenuTriggerComponent
            | RdsSidenavMenuitemComponent,
        );
      });
  }

  ngAfterViewInit(): void {
    this.scrollingAncestor =
      getScrollingAncestor(this.nativeElement) ?? this.document.body;

    // update other layout work like @HostBindings and other components destruction is finished
    setTimeout(() => {
      this.updateSelfAppearance();
    });
  }

  ngOnDestroy(): void {
    this.directDescendantItems.destroy();
  }

  toggleIsMinimal(): void {
    this.sidenavService.toggleIsMinimal();
  }

  onContentChanged(entries?: ResizeObserverEntry[]): void {
    this.scrollingAncestor =
      getScrollingAncestor(this.nativeElement) ?? this.document.body;

    const newHeight = this.getHeight(entries);

    if (newHeight === null || newHeight !== this.menubarHeight) {
      this.menubarHeight = newHeight;
      this.noScroll = this.calculateNoScroll();
      this.cdr.detectChanges();
    }
  }

  handleKeydown(event: KeyboardEvent): void {
    const keyCode = event.keyCode;
    const manager = this.keyManager;
    const delta = event.shiftKey ? -1 : 1;
    const activeItemIndex = manager.activeItemIndex ?? -1;

    switch (keyCode) {
      case TAB:
        manager.setFocusOrigin('keyboard');
        manager.updateActiveItem(activeItemIndex + delta);

        if (!event.shiftKey) {
          this.scrollIfNeeded(manager.activeItem as NativeElement | null);
        }

        break;
      default:
        if (keyCode === UP_ARROW || keyCode === DOWN_ARROW) {
          manager.setFocusOrigin('keyboard');
        }

        manager.onKeydown(event);
    }
  }

  private scrollIfNeeded(activeItem: NativeElement | null): void {
    if (!activeItem || !this.footerRef) {
      return;
    }

    const elY = activeItem.nativeElement.offsetTop;
    const elHeight = activeItem.nativeElement.offsetHeight;
    const paddingTop = 20;

    if (elY + elHeight >= this.footerRef.nativeElement.offsetTop) {
      const scroller = this.isMinimal
        ? this.scrollingAncestor
        : this.navRef.nativeElement;

      scroller.scrollTo(0, elY - paddingTop);
    }
  }

  private getHeight(entries?: ResizeObserverEntry[]): number | null {
    if (!entries || !entries[0]) {
      return null;
    }

    return entries[0].contentRect.height;
  }

  private onMinimalStateChange(isMinimal: boolean): void {
    this.updateSelfAppearance();
    this.isMinimalChange.emit(isMinimal);
  }

  private updateSelfAppearance(): void {
    const { top } = this.nativeElement.getBoundingClientRect();

    this.renderer.setStyle(
      this.nativeElement,
      '--rds-sidenav-offset-top',
      `${top}px`,
      RendererStyleFlags2.DashCase,
    );
  }

  private calculateNoScroll(): boolean {
    if (!this.nativeElement.firstElementChild) {
      return false;
    }

    return (
      this.scrollingAncestor.offsetHeight >=
      this.nativeElement.firstElementChild.scrollHeight
    );
  }

  private updateDirectDescendants(): void {
    this.children.changes
      .pipe(startWith(this.children))
      .subscribe(
        (
          items: QueryList<
            RdsSidenavMenuTriggerComponent | RdsSidenavMenuitemComponent
          >,
        ) => {
          this.directDescendantItems.reset(
            items.filter(
              (item) =>
                item instanceof RdsSidenavMenuTriggerComponent ||
                item._parentMenu == undefined,
            ),
          );
          this.directDescendantItems.notifyOnChanges();
        },
      );
  }
}
