import {
  FocusableOption,
  FocusMonitor,
  FocusOptions,
  FocusOrigin,
} from '@angular/cdk/a11y';
import {
  AfterViewInit,
  booleanAttribute,
  DestroyRef,
  Directive,
  ElementRef,
  inject,
  Input,
  OnDestroy,
} from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

@Directive({
  selector: '[rdsTabLabelWrapper]',
})
export class RdsTabLabelWrapperDirective
  implements AfterViewInit, FocusableOption, OnDestroy
{
  private readonly elementRef = inject(ElementRef);
  private readonly focusMonitor = inject(FocusMonitor);
  private readonly destroyRef = inject(DestroyRef);

  @Input({ transform: booleanAttribute }) disabled = false;
  public readonly _focused = new Subject<RdsTabLabelWrapperDirective>();

  ngAfterViewInit(): void {
    this.focusMonitor
      .monitor(this.elementRef, true)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this._focused.next(this));
  }

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.elementRef);
    this._focused.complete();
  }

  focus(origin: FocusOrigin, options?: FocusOptions): void {
    this.focusMonitor.focusVia(this.elementRef.nativeElement, origin, options);
    this._focused.next(this);
  }
}
