import { Directive, HostBinding, inject } from '@angular/core';
import { RdsAvatarComponent } from '../avatar';

@Directive({
  selector: `
  img[rds-chip-avatar], img[rdsChipAvatar],
  svg[rds-chip-avatar], svg[rdsChipAvatar],
  rds-avatar[rds-chip-avatar], rds-avatar[rdsChipAvatar],
  `,
})
export class RdsChipAvatarDirective {
  private readonly avatar = inject(RdsAvatarComponent, {
    host: true,
    self: true,
    optional: true,
  });

  @HostBinding('class') class = 'rds-chip__avatar';

  constructor() {
    if (this.avatar) {
      this.avatar.disableInteractions = true;
    }
  }
}
