import { Directive, forwardRef, HostBinding, inject } from '@angular/core';
import { RdsDividerComponent } from '../divider';

@Directive({
  selector: 'rds-divider[rds-header-start-divider]',
})
export class RdsHeaderStartDividerDirective {
  private readonly divider = inject<RdsDividerComponent>(
    forwardRef(() => RdsDividerComponent),
  );

  @HostBinding('class') class = 'rds-header-start-divider';

  constructor() {
    this.divider.vertical = true;
  }
}
