import { getClosestDialog } from './get-closet-dialog';
import {
  Directive,
  ElementRef,
  HostBinding,
  HostListener,
  inject,
  Input,
  OnInit,
} from '@angular/core';
import { RdsDialogRef } from './model';
import { RdsDialogService } from './dialog.service';

export type RdsDialogHostType = 'submit' | 'button' | 'reset';

@Directive({
  selector: '[rds-dialog-close], [rdsDialogClose]',
})
export class RdsDialogCloseDirective<T, D, R> implements OnInit {
  private readonly elementRef = inject(ElementRef);
  private readonly dialogService = inject(RdsDialogService);
  private dialogRef = inject<RdsDialogRef<T, D, R>>(RdsDialogRef, {
    optional: true,
  });

  private _type: RdsDialogHostType = 'button';

  @Input('rds-dialog-close') dialogResult: R;

  @Input('type')
  @HostBinding('attr.type')
  get hostType(): RdsDialogHostType {
    return this._type;
  }

  set hostType(newValue: RdsDialogHostType) {
    this._type = newValue;
  }

  @HostListener('click') onHostClick(): void {
    this.dialogRef?.close(this.dialogResult);
  }

  ngOnInit(): void {
    if (!this.dialogRef) {
      this.dialogRef = getClosestDialog<T, D, R>(
        this.elementRef,
        this.dialogService.openDialogs,
      ) as RdsDialogRef<T, D, R>;
    }
  }
}
