import { RdsTooltipDefaultOptions } from './types';
import { InjectionToken } from '@angular/core';
import { Overlay, ScrollStrategy } from '@angular/cdk/overlay';

const SCROLL_THROTTLE_MS = 20;

export const RDS_TOOLTIP_SCROLL_STRATEGY = new InjectionToken<
  () => ScrollStrategy
>('RDS_TOOLTIP_SCROLL_STRATEGY');

/** @docs-private */
export function RDS_TOOLTIP_SCROLL_STRATEGY_FACTORY(
  overlay: Overlay,
): () => ScrollStrategy {
  return () =>
    overlay.scrollStrategies.reposition({ scrollThrottle: SCROLL_THROTTLE_MS });
}

/** @docs-private */
export const RDS_TOOLTIP_SCROLL_STRATEGY_FACTORY_PROVIDER = {
  provide: RDS_TOOLTIP_SCROLL_STRATEGY,
  deps: [Overlay],
  useFactory: RDS_TOOLTIP_SCROLL_STRATEGY_FACTORY,
};

export const RDS_TOOLTIP_DEFAULT_OPTIONS =
  new InjectionToken<RdsTooltipDefaultOptions>('RDS_TOOLTIP_DEFAULT_OPTIONS', {
    providedIn: 'root',
    factory: RDS_TOOLTIP_DEFAULT_OPTIONS_FACTORY,
  });

/** @docs-private */
export function RDS_TOOLTIP_DEFAULT_OPTIONS_FACTORY(): RdsTooltipDefaultOptions {
  return {
    showDelay: 0,
    hideDelay: 0,
    touchendHideDelay: 1500,
    hideArrow: false,
  };
}
