import { FocusMonitor } from '@angular/cdk/a11y';
import { BooleanInput } from '@angular/cdk/coercion';
import { UniqueSelectionDispatcher } from '@angular/cdk/collections';
import {
  AfterViewInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  DestroyRef,
  ElementRef,
  EventEmitter,
  HostBinding,
  HostListener,
  inject,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import {
  RDS_RADIO_GROUP,
  RdsRadioGroupDirective,
} from './radio-button-group.directive';
import { RdsRadioChangedEvent } from './radio-button-changed-event';
import {
  animate,
  AnimationTriggerMetadata,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { FormGroupDirective } from '@angular/forms';
import { noop } from 'rxjs';

export type RdsRadioButtonPosition = 'before' | 'after';
export type RdsRadioButtonSize = 's' | 'm' | 'l';

let nextUniqueId = 0;

export const rdsRadioAnimations: {
  readonly transitionMessages: AnimationTriggerMetadata;
} = {
  /** Animation that transitions the form field's error and hint messages. */
  transitionMessages: trigger('transitionMessages', [
    state('enter', style({ opacity: 1, transform: 'translateY(0%)' })),
    transition('void => enter', [
      style({ opacity: 0, transform: 'translateY(-100%)' }),
      animate('200ms cubic-bezier(0.55, 0, 0.55, 0.2)'),
    ]),
  ]),
};

@Component({
  selector: 'rds-radio-button',
  template: `
    <label [attr.for]="inputId" class="rds-radio-label">
      <!-- The actual 'radio' part of the control. -->
      <div class="rds-radio-container">
        <div class="rds-radio-checkmark-outer-circle"></div>
        <div class="rds-radio-checkmark-inner-circle"></div>
        <input
          class="rds-radio-input"
          type="radio"
          [id]="inputId"
          [checked]="checked"
          [disabled]="disabled"
          [attr.name]="name"
          [attr.value]="value"
          [required]="required"
          [attr.aria-label]="ariaLabel"
          [attr.aria-labelledby]="ariaLabelledby"
          [attr.aria-describedby]="ariaDescribedby"
          (change)="onInputChange($event)"
          (click)="onInputClick($event)"
        />
        <div class="rds-checkmark-hover"></div>
      </div>

      <!-- The label content for radio control. -->
      <div
        class="rds-radio-label-content"
        [class.rds-radio-label-before]="labelPosition === 'before'"
        [ngClass]="'rds-radio-label-content--' + size"
      >
        <!-- Add an invisible span so JAWS can read the label -->
        <span style="display:none">&nbsp;</span>
        <ng-content />
      </div>
    </label>
    <div class="rds-radio__subscript-wrapper">
      @switch (isErrorMessageVisible) {
        @case (true) {
          <div
            class="rds-radio__subscript rds-radio__subscript--error"
            [@transitionMessages]="subscriptAnimationState"
          >
            <ng-content select="rds-control-error" />
          </div>
        }
        @case (false) {
          <div
            class="rds-radio__subscript rds-radio__subscript--hint"
            [@transitionMessages]="subscriptAnimationState"
          >
            <ng-content select="rds-control-hint" />
          </div>
        }
      }
    </div>
  `,
  styleUrls: ['./radio-button.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: [rdsRadioAnimations.transitionMessages],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsRadioButtonComponent<T>
  implements OnInit, AfterViewInit, OnDestroy
{
  private readonly radioGroup = inject<RdsRadioGroupDirective<T>>(
    RDS_RADIO_GROUP,
    { optional: true },
  );
  private readonly destroyRef = inject(DestroyRef);
  private readonly elementRef = inject(ElementRef);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly focusMonitor = inject(FocusMonitor);
  private readonly radioDispatcher = inject(UniqueSelectionDispatcher);

  private _uniqueId = `rds-radio-${++nextUniqueId}`;
  private _disabled: boolean;
  private _required: boolean;
  private _value: T;
  private _labelPosition: 'before' | 'after';

  subscriptAnimationState: '' | 'enter' = '';

  get inputId(): string {
    return `${this.id || this._uniqueId}-input`;
  }

  @ViewChild('input') _inputElement: ElementRef<HTMLInputElement>;

  @HostBinding('class') readonly hostClass = 'rds-radio-button';

  @HostBinding('class.rds-radio--invalid')
  get errorState(): boolean {
    if (this.radioGroup) {
      const parent =
        this.radioGroup.parentFormGroup || this.radioGroup.parentForm;
      const control = this.radioGroup.control;

      if (control) {
        return (
          control.invalid && (control.touched || parent?.submitted || false)
        );
      }
    }

    return false;
  }

  @Input()
  @HostBinding('attr.id')
  id: string = this._uniqueId;

  @Input() name: string;

  @Input() size: RdsRadioButtonSize = 'l';

  @Input()
  @HostBinding('attr.aria-label')
  ariaLabel: string;

  @Input('aria-labelledby')
  @HostBinding('attr.aria-labelledby')
  ariaLabelledby: string;

  @Input('aria-describedby')
  @HostBinding('attr.aria-describedby')
  ariaDescribedby: string;

  @Input()
  @HostBinding('class.rds-radio-checked')
  set checked(value: BooleanInput) {
    const newCheckedState = booleanAttribute(value);

    if (this._checked !== newCheckedState) {
      this._checked = newCheckedState;

      if (
        newCheckedState &&
        this.radioGroup &&
        this.radioGroup.value !== this.value
      ) {
        this.radioGroup.selected = this;
      } else if (
        !newCheckedState &&
        this.radioGroup &&
        this.radioGroup.value === this.value
      ) {
        this.radioGroup.selected = null;
      }

      if (newCheckedState) {
        this.radioDispatcher.notify(this.id, this.name);
      }
    }
  }

  get checked(): boolean {
    return this._checked;
  }

  private _checked = false;

  @Input()
  get value(): T {
    return this._value;
  }
  set value(value: T) {
    if (this._value !== value) {
      this._value = value;

      if (this.radioGroup !== null) {
        if (!this.checked) {
          this.checked = this.radioGroup.value === value;
        }

        if (this.checked) {
          this.radioGroup.selected = this;
        }
      }
    }
  }

  @Input()
  get labelPosition(): RdsRadioButtonPosition {
    return (
      this._labelPosition ||
      (this.radioGroup && this.radioGroup.labelPosition) ||
      'after'
    );
  }
  set labelPosition(value: RdsRadioButtonPosition) {
    this._labelPosition = value;
  }

  @HostBinding('class.rds-radio-disabled')
  @Input()
  set disabled(value: BooleanInput) {
    this._disabled = booleanAttribute(value);
  }

  get disabled(): boolean {
    return (
      this._disabled || (this.radioGroup !== null && this.radioGroup.disabled)
    );
  }

  @Input()
  set required(value: BooleanInput) {
    this._required = booleanAttribute(value);
  }

  get required(): boolean {
    return (
      this._required || (this.radioGroup && this.radioGroup.required) || false
    );
  }

  @Output() buttonChanged = new EventEmitter<RdsRadioChangedEvent<T>>();

  @HostListener('focus')
  onFocus(): void {
    this._inputElement.nativeElement.focus();
  }

  get isErrorMessageVisible(): boolean {
    return this.errorState;
  }

  private readonly removeUniqueSelectionListener: () => void = noop;

  constructor() {
    this.removeUniqueSelectionListener = this.radioDispatcher.listen(
      (id: string, name: string) => {
        if (id !== this.id && name === this.name) {
          this.checked = false;
        }
      },
    );
  }

  focus(options?: FocusOptions): void {
    this.focusMonitor.focusVia(this._inputElement, 'keyboard', options);
  }

  markForCheck(): void {
    this.cdr.markForCheck();
  }

  ngOnInit(): void {
    if (this.radioGroup) {
      this.checked = this.radioGroup.value === this._value;
      this.name = this.radioGroup.name;
    }
  }

  ngAfterViewInit(): void {
    this.focusMonitor
      .monitor(this.elementRef, true)
      .subscribe((focusOrigin) => {
        if (!focusOrigin && this.radioGroup) {
          this.radioGroup.touch();
        }
      });

    this.subscriptAnimationState = 'enter'; // Avoid animations on load.
    this.cdr.detectChanges();
    // Run change detection if the value changes.

    if (this.radioGroup) {
      if (this.radioGroup.control && this.radioGroup.control.valueChanges) {
        this.radioGroup.control.valueChanges
          .pipe(takeUntilDestroyed(this.destroyRef))
          .subscribe(() => {
            this.radioGroup?.control?.markAsDirty();
            this.radioGroup?.control?.markAsTouched();
            this.cdr.markForCheck();
          });
      }

      const parent =
        this.radioGroup.parentFormGroup || this.radioGroup.control.parent;

      if (parent instanceof FormGroupDirective) {
        parent.ngSubmit
          .pipe(takeUntilDestroyed(this.destroyRef))
          .subscribe(() => {
            this.cdr.markForCheck();
          });
      }
    }
  }

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.elementRef);
    this.removeUniqueSelectionListener();
  }

  private emitChangeEvent(): void {
    this.buttonChanged.emit(new RdsRadioChangedEvent(this, this._value));
  }

  onInputClick(event: Event): void {
    // We have to stop propagation for click events on the visual hidden input element.
    // By default, when a user clicks on a label element, a generated click event will be
    // dispatched on the associated input element. Since we are using a label element as our
    // root container, the click event on the `radio-button` will be executed twice.
    // The real click event will bubble up, and the generated click event also tries to bubble up.
    // This will lead to multiple click events.
    // Preventing bubbling for the second event will solve that issue.
    event.stopPropagation();
  }

  onInputChange(event: Event): void {
    // We always have to stop propagation on the change event.
    // Otherwise the change event, from the input element, will bubble up and
    // emit its event object to the `buttonChange` output.
    event.stopPropagation();

    const groupValueChanged =
      this.radioGroup && this.value !== this.radioGroup.value;
    this.checked = true;
    this.emitChangeEvent();

    if (this.radioGroup) {
      this.radioGroup._controlValueAccessorChangeFn(this.value);

      if (groupValueChanged) {
        this.radioGroup.emitChangeEvent();
      }
    }
  }
}
