import { CdkScrollable, ScrollDispatcher } from '@angular/cdk/scrolling';
import {
  AfterContentInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  DestroyRef,
  ElementRef,
  forwardRef,
  HostBinding,
  inject,
  NgZone,
  ViewEncapsulation,
} from '@angular/core';
import { RdsDrawerContainerComponent } from './drawer-container.component';
import { RdsDrawerContainerContentMargins } from './drawer-types';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

@Component({
  selector: 'rds-drawer-content',
  template: '<ng-content />',
  styleUrls: ['./drawer-content.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  standalone: true,
})
export class RdsDrawerContentComponent
  extends CdkScrollable
  implements AfterContentInit
{
  private readonly destroyRef = inject(DestroyRef);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly container = inject<RdsDrawerContainerComponent>(
    forwardRef(() => RdsDrawerContainerComponent),
  );

  @HostBinding('class') class = 'rds-drawer-content';

  @HostBinding('style.margin-right.px') get marginRight(): number | null {
    return this.contentMargins.right ?? 0;
  }

  @HostBinding('style.margin-left.px') get marginLeft(): number | null {
    return this.contentMargins.left ?? 0;
  }

  private contentMargins: RdsDrawerContainerContentMargins;

  constructor() {
    super(inject(ElementRef), inject(ScrollDispatcher), inject(NgZone));
  }

  ngAfterContentInit(): void {
    this.container.contentMargins$
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((m) => {
        this.contentMargins = m;
        this.cdr.markForCheck();
      });
  }
}
