import { _isNumberValue } from '@angular/cdk/coercion';
import { DataSource } from '@angular/cdk/table';
import {
  BehaviorSubject,
  catchError,
  combineLatest,
  merge,
  Observable,
  of,
  Subject,
  Subscription,
} from 'rxjs';
import { delay, map, switchMap, tap } from 'rxjs/operators';
import { RdsSortDirective, Sort } from './sort.directive';
import { PageEvent, RdsPaginatorComponent } from '../paginator';
import { isDevMode } from '@angular/core';

declare const ngDevMode: boolean;

interface Paginator {
  page: Subject<PageEvent>;
  pageIndex: number;
  initialized: Observable<void>;
  pageSize: number;
  length: number;
}

export abstract class _RdsTableDataSource<
  T,
  P extends Paginator,
> extends DataSource<T> {
  protected readonly _data: BehaviorSubject<T[]>;
  protected readonly _renderData = new BehaviorSubject<T[]>([]);
  protected readonly _internalPageChanges = new Subject<void>();
  protected _isInitialized = false;

  _renderChangesSubscription: Subscription | null = null;

  get data(): T[] {
    return this._data.value;
  }

  set data(data: T[]) {
    if (!Array.isArray(data)) {
      if (ngDevMode) {
        throw new Error(
          "Data passed to the table's data source is not an array.",
        );
      } else {
        data = [];
      }
    }

    this._data.next(data);
  }

  get sort(): RdsSortDirective | null {
    return this._sort;
  }

  set sort(sort: RdsSortDirective | null) {
    this._sort = sort;
    this._updateChangeSubscription();
  }

  protected _sort: RdsSortDirective | null = null;

  get paginator(): P | null {
    return this._paginator;
  }

  set paginator(paginator: P | null) {
    this._paginator = paginator;
    this._updateChangeSubscription();
  }

  protected _paginator: P | null = null;

  sortingDataAccessor: (data: T, sortHeaderId: string) => string | number = (
    data: T,
    sortHeaderId: string,
  ): string | number => {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    const value = (data as { [key: string]: any })[sortHeaderId];

    if (_isNumberValue(value)) {
      const numberValue = Number(value);

      return numberValue < Number.MAX_SAFE_INTEGER ? numberValue : value;
    }

    return value;
  };

  sortData: (data: T[], sort: RdsSortDirective) => T[] = (
    data: T[],
    sort: RdsSortDirective,
  ): T[] => {
    const active = sort.active;
    const direction = sort.direction;

    if (!active || direction == '') {
      return data;
    }

    return data.sort((a, b) => {
      let valueA = this.sortingDataAccessor(a, active);
      let valueB = this.sortingDataAccessor(b, active);

      const valueAType = typeof valueA;
      const valueBType = typeof valueB;

      if (valueAType !== valueBType) {
        if (valueAType === 'number') {
          valueA += '';
        }

        if (valueBType === 'number') {
          valueB += '';
        }
      }

      let comparatorResult = 0;

      if (valueA != null && valueB != null) {
        if (valueA > valueB) {
          comparatorResult = 1;
        } else if (valueA < valueB) {
          comparatorResult = -1;
        }
      } else if (valueA != null) {
        comparatorResult = 1;
      } else if (valueB != null) {
        comparatorResult = -1;
      }

      return comparatorResult * (direction == 'asc' ? 1 : -1);
    });
  };

  constructor(initialData: T[] = []) {
    super();
    this._data = new BehaviorSubject<T[]>(initialData);
  }

  connect(): Observable<T[]> {
    if (!this._renderChangesSubscription) {
      this._updateChangeSubscription();
    }

    return this._renderData.asObservable();
  }

  disconnect(): void {
    this._renderChangesSubscription?.unsubscribe();
    this._renderChangesSubscription = null;
  }

  init(): this {
    this._isInitialized = true;
    this._updateChangeSubscription();

    return this;
  }

  protected abstract _updateChangeSubscription(): void;

  protected createSortObservable(): Observable<Sort | void> {
    return this._sort
      ? merge(this._sort.rdsSortChange, this._sort.initialized)
      : of(undefined);
  }

  protected createPageObservable(): Observable<PageEvent | void> {
    return this._paginator
      ? merge(
          this._paginator.page,
          this._internalPageChanges,
          this._paginator.initialized,
        )
      : of(undefined);
  }

  protected _orderData(data: T[]): T[] {
    if (!this.sort) {
      return data;
    }

    return this.sortData(data.slice(), this.sort);
  }

  protected _pageData(data: T[]): T[] {
    if (!this.paginator) {
      return data;
    }

    const startIndex = this.paginator.pageIndex * this.paginator.pageSize;

    return data.slice(startIndex, startIndex + this.paginator.pageSize);
  }

  protected _updatePaginator(filteredDataLength: number): void {
    Promise.resolve().then(() => {
      if (!this.paginator) {
        return;
      }

      this.paginator.length = filteredDataLength;

      if (this.paginator.pageIndex > 0) {
        const lastPageIndex =
          Math.ceil(this.paginator.length / this.paginator.pageSize) - 1 || 0;
        const newPageIndex = Math.min(this.paginator.pageIndex, lastPageIndex);

        if (newPageIndex !== this.paginator.pageIndex) {
          this.paginator.pageIndex = newPageIndex;

          this._internalPageChanges.next();
        }
      }
    });
  }
}

export class RdsTableDataSource<T> extends _RdsTableDataSource<
  T,
  RdsPaginatorComponent
> {
  protected readonly _filter = new BehaviorSubject<string>('');
  protected _filteredData: T[] = [];

  get filteredData(): T[] {
    return this._filteredData;
  }

  private set filteredData(v: T[]) {
    this._filteredData = v;
  }

  override set data(data: T[]) {
    super.data = data;

    if (!this._renderChangesSubscription) {
      this._filterData(data);
    }
  }

  override get data(): T[] {
    return super.data;
  }

  get filter(): string {
    return this._filter.value;
  }

  set filter(filter: string) {
    this._filter.next(filter);

    if (!this._renderChangesSubscription) {
      this._filterData(this.data);
    }
  }

  protected _filterData(data: T[], skipPaginatorUpdate = false): T[] {
    this.filteredData =
      this.filter == null || this.filter === ''
        ? data
        : data.filter((obj) => this.filterPredicate(obj, this.filter));

    if (this.paginator && !skipPaginatorUpdate) {
      this._updatePaginator(this.filteredData.length);
    }

    return this.filteredData;
  }

  filterPredicate: (data: T, filter: string) => boolean = (
    data: T,
    filter: string,
  ): boolean => {
    const dataStr = Object.keys(data as unknown as Record<string, unknown>)
      .reduce((currentTerm: string, key: string) => {
        // Use an obscure Unicode character to delimit the words in the concatenated string.
        // This avoids matches where the values of two columns combined will match the user's query
        // (e.g. `Flute` and `Stop` will match `Test`). The character is intended to be something
        // that has a very low chance of being typed in by somebody in a text field. This one in
        // particular is "White up-pointing triangle with dot" from
        // https://en.wikipedia.org/wiki/List_of_Unicode_characters
        return (
          currentTerm + (data as unknown as Record<string, unknown>)[key] + ''
        );
      }, '')
      .toLowerCase();

    const transformedFilter = filter.trim().toLowerCase();

    return dataStr.indexOf(transformedFilter) != -1;
  };

  protected _updateChangeSubscription(): void {
    if (!this._isInitialized) {
      return;
    }

    const sortChange = this.createSortObservable();
    const pageChange = this.createPageObservable();
    const dataStream = this._data;
    const filteredData = combineLatest([dataStream, this._filter]).pipe(
      map(([data]) => this._filterData(data)),
    );
    const orderedData = combineLatest([filteredData, sortChange]).pipe(
      tap(([data]) => this._updatePaginator(data.length)),
      map(([data]) => this._orderData(data)),
    );
    const paginatedData = combineLatest([orderedData, pageChange]).pipe(
      map(([data]) => this._pageData(data)),
    );
    this._renderChangesSubscription?.unsubscribe();
    this._renderChangesSubscription = paginatedData.subscribe((data) => {
      this._renderData.next(data);
    });
  }
}

export interface RdsAsyncDataSourceEvent {
  sort: Sort | void | null;
  page: PageEvent | void | null;
}

export interface RdsAsyncDataSourceResponse<T, E = unknown> {
  offset: number;
  length: number;
  data: T[];
  error?: E;
}

export class RdsTableAsyncDataSource<T> extends _RdsTableDataSource<
  T,
  RdsPaginatorComponent
> {
  protected readonly _loadingData$ = new BehaviorSubject<boolean>(false);
  protected readonly _error$ = new BehaviorSubject<unknown | null>(null);
  protected readonly _externalTrigger$ = new BehaviorSubject<void>(void 0);

  get isLoading(): boolean {
    return this._loadingData$.getValue();
  }

  get isError(): boolean {
    return !this._loadingData$.getValue() && this._error$.getValue() !== null;
  }

  private readonly _fetchData: (
    event: RdsAsyncDataSourceEvent,
  ) => Observable<RdsAsyncDataSourceResponse<T>>;

  constructor(
    fetchData: (
      event: RdsAsyncDataSourceEvent,
    ) => Observable<RdsAsyncDataSourceResponse<T>>,
  ) {
    super([]);
    this._fetchData = fetchData;
  }

  override disconnect(): void {
    super.disconnect();
    this._loadingData$.complete();
    this._error$.complete();
  }

  fetchData(): void {
    if (this._isInitialized) {
      this._externalTrigger$.next();
    } else if (isDevMode()) {
      throw new Error(
        `RdsAsyncTableDataSource needs to be initialized before manual fetchData trigger.`,
      );
    }
  }

  getLoadingData(): Observable<boolean> {
    return this._loadingData$.asObservable();
  }

  getError(): Observable<unknown | null> {
    return this._error$.asObservable();
  }

  protected _updateChangeSubscription(): void {
    if (!this._isInitialized) {
      return;
    }

    const sortChange = this.createSortObservable();
    const pageChange = this.createPageObservable();

    const dataStream = combineLatest([
      sortChange,
      pageChange,
      this._externalTrigger$,
    ]).pipe(
      delay(0),
      switchMap(([sort, page]) => {
        if (typeof this._fetchData === 'function') {
          this._loadingData$.next(true);

          return this._fetchData({
            sort,
            page: page || this._paginator?.get(),
          });
        } else {
          throw new Error('FetchData must be function');
        }
      }),
      tap(() => {
        this._loadingData$.next(false);

        if (this._error$.getValue() !== null) {
          this._error$.next(null);
        }
      }),
      catchError((err) => {
        this._loadingData$.next(false);
        this._error$.next(err);

        return of({
          data: [],
          length: 0,
          offset: 0,
          error: err,
        } as RdsAsyncDataSourceResponse<T>);
      }),
    );

    this._renderChangesSubscription?.unsubscribe();
    this._renderChangesSubscription = dataStream.subscribe((result) => {
      this._renderData.next(result.data);
      this._updatePaginator(result.length);
    });
  }
}
