import {
  animate,
  AnimationTriggerMetadata,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import { NumberInput } from '@angular/cdk/coercion';
import { CdkOverlayOrigin } from '@angular/cdk/overlay';
import {
  AfterContentChecked,
  AfterContentInit,
  AfterViewInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  ContentChildren,
  DestroyRef,
  ElementRef,
  HostBinding,
  inject,
  InjectionToken,
  Input,
  numberAttribute,
  OnChanges,
  QueryList,
  Renderer2,
  SimpleChanges,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { EMPTY, fromEvent, merge } from 'rxjs';
import { startWith } from 'rxjs/operators';
import { RdsAbstractFormFieldControl } from './abstract-form-field-control';
import { RdsErrorDirective } from './error.directive';
import { RdsHintDirective } from './hint.directive';
import { RdsLabelDirective } from './label.directive';
import { RdsPrefixDirective } from './prefix.directive';
import { RdsSelectComponent } from './select/select.component';
import { RdsSuffixDirective } from './suffix.directive';
import { RdsInputDirective } from './input.directive';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { FormControl } from '@angular/forms';
import {
  RdsAutocompleteComponent,
  RdsMultiAutocompleteComponent,
} from '../autocomplete';

let nextUniqueId = 0;
const DEFAULT_LABEL_WIDTH = 25;
export type RdsFormFieldSize = 's' | 'm' | 'l';

export const rdsFormFieldAnimations: {
  readonly transitionMessages: AnimationTriggerMetadata;
} = {
  /** Animation that transitions the form field's error and hint messages. */
  transitionMessages: trigger('transitionMessages', [
    state('enter', style({ opacity: 1, transform: 'translateY(0%)' })),
    transition('void => enter', [
      style({ opacity: 0, transform: 'translateY(-100%)' }),
      animate('200ms cubic-bezier(0.55, 0, 0.55, 0.2)'),
    ]),
  ]),
};

export const RDS_FORM_FIELD = new InjectionToken<
  RdsFormFieldComponent<unknown>
>('RDS_FORM_FIELD');

export type RdsFormFieldLabelPosition =
  | 'floating'
  | 'side-left'
  | 'side-right'
  | 'top'
  | 'none';

@Component({
  selector: 'rds-form-field',
  template: `
    @if (labelPosition !== 'floating' && labelPosition !== 'none') {
      <div class="rds-form-field__label-wrapper">
        <ng-container [ngTemplateOutlet]="label" />
      </div>
    }
    <div
      class="rds-form-field__container"
      [ngClass]="formFieldType"
      [class.rds-form-field--static-label]="
        labelPosition !== 'floating' && labelPosition !== 'none'
      "
      [class.rds-form-field--hint-or-error-or-counter]="
        inputHints.length || inputErrors.length || maxCharacters
      "
    >
      <div
        class="rds-form-field__control-container"
        [ngClass]="{
          'rds-form-field-textarea':
            this.control.controlType === 'textarea-field'
        }"
        cdk-overlay-origin
        (mouseover)="mouseover = true"
        (mouseout)="mouseover = false"
        [attr.aria-hidden]="control.controlType !== 'chip-grid'"
        #origin="cdkOverlayOrigin"
        #trigger
        (click)="control.onContainerClick($event)"
      >
        <div class="rds-form-field__content">
          @if (inputPrefixes.length > 0) {
            <div class="rds-form-field__content-prefix">
              <ng-content
                select="[rds-control-prefix], rds-control-prefix-separator"
              />
            </div>
          }
          <div
            class="rds-form-field__content-infix"
            [class.rds-form-field__content-infix--floating-label]="
              labelPosition === 'floating'
            "
            [class.rds-form-field_content-infix--focused]="this.control.focused"
          >
            @if (labelPosition === 'floating') {
              <span
                class="rds-form-field__label-wrapper"
                [class.rds-form-field__label-wrapper--float]="
                  control.shouldLabelFloat
                "
              >
                <ng-container [ngTemplateOutlet]="label" />
              </span>
            }
            <ng-content />
          </div>
          @if (inputSuffixes.length > 0) {
            <div
              class="rds-form-field__content-suffix"
              [ngClass]="{
                'rds-form-field__content-suffix-datepicker':
                  this.control.controlType === 'date-picker' ||
                  this.control.controlType === 'date-range'
              }"
              #suffix
            >
              <ng-content
                select="[rds-control-suffix], rds-control-suffix-separator"
              />
            </div>
          }
        </div>
      </div>
      <div class="rds-form-field__subscript-wrapper">
        @switch (isErrorMessageVisible) {
          @case (true) {
            <div
              class="rds-form-field__subscript rds-form-field__subscript--error"
              [@transitionMessages]="subscriptAnimationState"
            >
              <ng-content select="rds-control-error" />
            </div>
          }
          @case (false) {
            <div
              class="rds-form-field__subscript rds-form-field__subscript--hint"
              [@transitionMessages]="subscriptAnimationState"
            >
              <ng-content select="rds-control-hint" />
            </div>
          }
        }
        @if (
          this.control.controlType === 'textarea-field' &&
          !hideTextareaCharacterCounter &&
          maxCharacters
        ) {
          <div class="rds-control-counter">
            {{ numberOfCharacters }}/{{ maxCharacters }}
          </div>
        }
      </div>
    </div>
    <ng-template #label>
      <label
        [id]="labelId"
        [attr.for]="control.id"
        class="rds-form-field__label"
        [class.rds-form-field__label--float]="control.shouldLabelFloat"
      >
        <span>
          <ng-content select="rds-control-label" />
        </span>
        @if (!hideRequiredMarker && control.required && !control.disabled) {
          <span class="rds-form-field__asterisk"> &#32;* </span>
        }
        @if (labelPosition !== 'floating') {
          <span class="rds-form-field-icon-wrapper">
            <ng-content
              select="rds-icon[rds-label-icon], rds-icon[rdsLabelIcon]"
            />
          </span>
        }
      </label>
    </ng-template>
  `,
  styleUrls: ['./form-field.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  animations: [rdsFormFieldAnimations.transitionMessages],
  providers: [{ provide: RDS_FORM_FIELD, useExisting: RdsFormFieldComponent }],
})
export class RdsFormFieldComponent<T>
  implements AfterContentInit, AfterContentChecked, AfterViewInit, OnChanges
{
  private readonly destroyRef = inject(DestroyRef);
  private readonly elementRef = inject(ElementRef);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly renderer = inject(Renderer2);

  subscriptAnimationState: '' | 'enter' = '';

  labelId = `rds-form-field-label-${nextUniqueId++}`;

  mouseover: boolean;

  @ContentChild(RdsAbstractFormFieldControl)
  abstractControl: RdsAbstractFormFieldControl<T>;

  get control(): RdsAbstractFormFieldControl<T> {
    return this.abstractControl;
  }

  get numberOfCharacters(): number {
    return (this.abstractControl.value as string).length;
  }

  @ContentChildren(RdsErrorDirective, { descendants: true })
  inputErrors: QueryList<RdsErrorDirective>;

  @ContentChildren(RdsHintDirective, { descendants: true })
  inputHints: QueryList<RdsHintDirective>;

  @ContentChildren(RdsSuffixDirective, { descendants: true })
  inputSuffixes: QueryList<RdsSuffixDirective>;

  @ContentChildren(RdsPrefixDirective, { descendants: true })
  inputPrefixes: QueryList<RdsPrefixDirective>;

  @ContentChild(RdsLabelDirective, { descendants: true })
  label: RdsLabelDirective;

  @ContentChild(RdsInputDirective, { descendants: true, read: ElementRef })
  inputEl: ElementRef<HTMLInputElement>;

  @ContentChild(RdsSelectComponent, { descendants: true, read: ElementRef })
  selectEl: ElementRef<HTMLElement>;

  @ViewChild('trigger') private trigger: ElementRef<HTMLDivElement>;
  @ViewChild('suffix') private suffix: ElementRef<HTMLDivElement>;
  @ViewChild(CdkOverlayOrigin) private origin: CdkOverlayOrigin;

  @HostBinding('class') get class(): string[] {
    const classList = ['rds-form-field'];

    if (this.control.disabled) {
      classList.push('rds-form-field--disabled');
    }

    if (this.control.errorState) {
      classList.push('rds-form-field--invalid');
    }

    if (this.control.focused) {
      classList.push('rds-form-field--focused');
    }

    if (
      this.control.controlType === 'autocomplete-multi' &&
      (this.control as unknown as RdsMultiAutocompleteComponent<T>).hasValue()
    ) {
      classList.push('rds-form-field--has-value');
    }

    if (this.label && !!this.labelPosition && this.labelPosition !== 'none') {
      if (this.labelPosition === 'side-left') {
        classList.push('rds-form-field--side');
        classList.push('rds-form-field--side-left');
      } else if (this.labelPosition === 'side-right') {
        classList.push('rds-form-field--side');
        classList.push('rds-form-field--side-right');
      } else if (this.labelPosition === 'floating') {
        classList.push('rds-form-field--floating-label');
      }
    } else {
      classList.push('rds-form-field--no-label');
    }

    if (
      this.inputHints.length ||
      this.inputErrors.length ||
      this.maxCharacters
    ) {
      classList.push('rds-form-field--hint-or-error-or-counter');
    }

    if (this.control.controlType === 'textarea-field') {
      classList.push('rds-form-field__wrapper-textarea-field');
    }

    if (this.inputSuffixes.length) {
      classList.push('rds-form-field-with-suffix');
    }

    if (this.mouseover) {
      classList.push('rds-form-field--hover');
    } else {
      const index = classList.indexOf('rds-form-field--hover');

      if (index !== -1) {
        classList.splice(index, 1);
      }
    }

    if (this._isPrevSelect) {
      classList.push('rds-form-field--prev-select');
    }

    if (this._isPrevButton) {
      classList.push('rds-form-field--prev-button');
    }

    if (this._isPrevFocused) {
      classList.push('rds-form-field--prev-focused');
    }

    if (this.size) {
      classList.push(`rds-form-field--${this.size}`);
    }

    return [...classList];
  }

  @Input({ transform: booleanAttribute }) hideRequiredMarker = false;

  @Input({ transform: booleanAttribute }) hideTextareaCharacterCounter = false;

  _labelPosition: RdsFormFieldLabelPosition = 'floating';

  @Input()
  get labelPosition(): RdsFormFieldLabelPosition {
    return this._labelPosition;
  }

  set labelPosition(labelPosition: RdsFormFieldLabelPosition) {
    this._labelPosition = labelPosition;
    this.control?.stateChanges.emit();
  }

  _size: RdsFormFieldSize = 'l';

  @Input()
  get size(): RdsFormFieldSize {
    return this._size;
  }

  set size(size: RdsFormFieldSize) {
    this._size = size;
    this.control?.stateChanges.emit();
  }

  private _labelWidth = DEFAULT_LABEL_WIDTH;

  @HostBinding('style.--rds-control-label-width')
  @Input()
  set labelWidth(value: NumberInput) {
    const val = numberAttribute(value);

    if (val < 0 || val > 100) {
      throw new Error(
        `The labelWidth value should be less 100 than and greater than 0.`,
      );
    }

    this._labelWidth = numberAttribute(value, DEFAULT_LABEL_WIDTH);
  }
  get labelWidth(): number {
    return this._labelWidth;
  }

  get formFieldType(): string {
    if (!this.control) {
      return '';
    }

    if (
      this.elementRef.nativeElement.parentElement.classList.contains(
        'rds-global-search',
      )
    ) {
      return 'rds-form-field--global-search';
    }

    return `rds-form-field--${this.control.controlType}`;
  }

  protected maxCharacters: number | null = 0;
  private _isPrevSelect = false;
  private _isPrevButton = false;
  private _isPrevFocused = false;

  ngAfterContentInit(): void {
    this.validateControlExistence();
    merge(
      this.inputEl ? fromEvent(this.inputEl.nativeElement, 'focus') : EMPTY,
      this.inputEl ? fromEvent(this.inputEl.nativeElement, 'blur') : EMPTY,
      this.selectEl ? fromEvent(this.selectEl.nativeElement, 'focus') : EMPTY,
      this.selectEl ? fromEvent(this.selectEl.nativeElement, 'blur') : EMPTY,
    )
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this.checkFormFieldStatus());

    const control = this.control;

    // Run change detection if the value changes.
    if (control.ngControl && control.ngControl.valueChanges) {
      control.ngControl.valueChanges
        .pipe(takeUntilDestroyed(this.destroyRef))
        .subscribe(() => this.cdr.markForCheck());
    }

    merge(
      control.stateChanges,
      this.inputHints.changes,
      this.inputErrors.changes,
    )
      .pipe(startWith([null]), takeUntilDestroyed(this.destroyRef))
      .subscribe(() => {
        const describedByIds = this.buildDescribedByIds();
        control.setDescribedByIds(describedByIds);
        this.cdr.markForCheck();
      });

    this.inputSuffixes.changes
      .pipe(startWith([null]), takeUntilDestroyed(this.destroyRef))
      .subscribe(() => {
        this.control.suffixed = !!this.inputSuffixes.first;
        this.control.stateChanges.emit();
      });

    this.inputPrefixes.changes
      .pipe(startWith([null]), takeUntilDestroyed(this.destroyRef))
      .subscribe(() => {
        this.control.prefixed = !!this.inputPrefixes.first;
        this.control.stateChanges.emit();
      });
  }

  ngAfterContentChecked(): void {
    this.checkFormFieldStatus();
    this.validateControlExistence();
    this.checkControlMaxCharacters();

    if (this.formFieldType && this.elementRef.nativeElement) {
      this.renderer.addClass(this.elementRef.nativeElement, this.formFieldType);
    }
  }

  ngAfterViewInit(): void {
    if (
      this.control.controlType === 'select' ||
      this.control.controlType === 'select-with-chips'
    ) {
      (this.control as unknown as RdsSelectComponent<T>).trigger = this.trigger;
      (this.control as unknown as RdsSelectComponent<T>).origin = this.origin;
    } else if (
      this.control.controlType === 'autocomplete' ||
      this.control.controlType === 'autocomplete-multi'
    ) {
      (this.control as unknown as RdsAutocompleteComponent<T>)._trigger =
        this.trigger;
      (this.control as unknown as RdsAutocompleteComponent<T>)._origin =
        this.origin;
    }

    this.subscriptAnimationState = 'enter'; // Avoid animations on load.
    this.cdr.detectChanges();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['labelPosition'] && this.labelPosition === 'floating') {
      if (changes['size'] && this.size !== 'l') {
        throw Error(
          `Form field with '${this.size}' size cannot have floating label`,
        );
      }
    }
  }

  private checkFormFieldStatus(): void {
    this._isPrevSelect =
      this.elementRef?.nativeElement?.nextElementSibling?.classList.contains(
        'rds-form-field--select',
      );
    this._isPrevButton =
      this.elementRef?.nativeElement?.nextElementSibling?.classList.contains(
        'rds-button',
      );
    this._isPrevFocused =
      this.elementRef?.nativeElement?.nextElementSibling?.classList.contains(
        'rds-form-field--focused',
      ) ||
      this.elementRef?.nativeElement?.nextElementSibling?.classList.contains(
        'rds-dropdown--focused',
      );
  }

  getConnectedOverlayOrigin(): ElementRef {
    return this.trigger || this.elementRef;
  }

  get isErrorMessageVisible(): boolean {
    return (
      this.inputErrors && this.inputErrors.length > 0 && this.control.errorState
    );
  }

  private buildDescribedByIds(): string[] {
    let ids: string[] = [];

    if (this.isErrorMessageVisible) {
      ids = this.inputErrors.map((error) => error.id);
    } else if (this.inputHints) {
      ids = this.inputHints.map((hint) => hint.id);
    }

    return ids;
  }

  private validateControlExistence(): void {
    if (!this.control) {
      throw new Error('Cannot use form field without control component');
    }
  }

  private checkControlMaxCharacters(): void {
    if (this.control.controlType === 'textarea-field') {
      if (this.control?.ngControl?.control?.validator) {
        const result = this.control.ngControl.control.validator(
          new FormControl({ length: Infinity }),
        );

        if (result && result['maxlength']) {
          this.maxCharacters = numberAttribute(
            result['maxlength']['requiredLength'],
            0,
          );
        }
      } else if (
        this.control instanceof RdsInputDirective &&
        this.control.getMaxlengthAttr()
      ) {
        this.maxCharacters = this.control.getMaxlengthAttr();
      } else {
        this.maxCharacters = null;
      }
    }
  }
}
