import {
  DOWN_ARROW,
  END,
  ENTER,
  HOME,
  LEFT_ARROW,
  PAGE_DOWN,
  PAGE_UP,
  RIGHT_ARROW,
  SPACE,
  UP_ARROW,
} from '@angular/cdk/keycodes';
import {
  AfterContentInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  EventEmitter,
  inject,
  Input,
  isDevMode,
  OnDestroy,
  Output,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { DateAdapter } from './adapter';
import { Directionality } from '@angular/cdk/bidi';
import {
  RdsCalendarBodyComponent,
  RdsCalendarCell,
  RdsCalendarCellClassFunction,
  RdsCalendarUserEvent,
} from './calendar-body.component';
import { createMissingDateImplError } from './datepicker-errors';
import { Subscription } from 'rxjs';
import { startWith } from 'rxjs/operators';
import { DateRange } from './date-selection.model';

export const yearsPerPage = 28;
export const yearsPerRow = 4;

@Component({
  selector: 'rds-multi-year-view',
  templateUrl: './multi-year-view.component.html',
  exportAs: 'rdsMultiYearView',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsMultiYearViewComponent<D>
  implements AfterContentInit, OnDestroy
{
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly dateAdapter = inject(DateAdapter, {
    optional: true,
  }) as DateAdapter<D>;
  private readonly dir = inject(Directionality, { optional: true });

  private _rerenderSubscription = Subscription.EMPTY;

  @Input()
  get activeDate(): D {
    return this._activeDate;
  }

  set activeDate(value: D) {
    const oldActiveDate = this._activeDate;
    const validDate =
      this.dateAdapter.getValidDateOrNull(
        this.dateAdapter.deserialize(value),
      ) || this.dateAdapter.today();
    this._activeDate = this.dateAdapter.clampDate(
      validDate,
      this.minDate,
      this.maxDate,
    );

    if (
      !isSameMultiYearView(
        this.dateAdapter,
        oldActiveDate,
        this._activeDate,
        this.minDate,
        this.maxDate,
      )
    ) {
      this._init();
    }
  }

  private _activeDate: D;

  @Input()
  get selected(): DateRange<D> | D | null {
    return this._selected;
  }

  set selected(value: DateRange<D> | D | null) {
    if (value instanceof DateRange) {
      this._selected = value;
    } else {
      this._selected = this.dateAdapter.getValidDateOrNull(
        this.dateAdapter.deserialize(value),
      );
    }

    this._setSelectedYear(value);
  }

  private _selected: DateRange<D> | D | null = null;

  @Input()
  get minDate(): D | null {
    return this._minDate;
  }

  set minDate(value: D | null) {
    this._minDate = this.dateAdapter.getValidDateOrNull(
      this.dateAdapter.deserialize(value),
    );
  }

  private _minDate: D | null = null;

  @Input()
  get maxDate(): D | null {
    return this._maxDate;
  }

  set maxDate(value: D | null) {
    this._maxDate = this.dateAdapter.getValidDateOrNull(
      this.dateAdapter.deserialize(value),
    );
  }

  private _maxDate: D | null = null;

  @Input() dateFilter: ((date: D) => boolean) | null = null;
  @Input() dateClass: RdsCalendarCellClassFunction<D> | null = null;

  @Output() readonly selectedChange = new EventEmitter<D>();
  @Output() readonly yearSelected = new EventEmitter<RdsCalendarUserEvent<D>>();
  @Output() readonly activeDateChange = new EventEmitter<D>();

  @ViewChild(RdsCalendarBodyComponent)
  _rdsCalendarBody: RdsCalendarBodyComponent<D> | null = null;

  _years: RdsCalendarCell[][] | null = null;
  _todayYear: number | null = null;
  _selectedYear: number | null = null;

  constructor() {
    if (!this.dateAdapter && isDevMode()) {
      throw createMissingDateImplError('DateAdapter');
    }

    this._activeDate = this.dateAdapter.today();
  }

  ngAfterContentInit(): void {
    this._rerenderSubscription = this.dateAdapter.localeChanges
      .pipe(startWith(null))
      .subscribe(() => this._init());
  }

  ngOnDestroy(): void {
    this._rerenderSubscription.unsubscribe();
  }

  _init(): void {
    this._todayYear = this.dateAdapter.getYear(this.dateAdapter.today());
    const activeYear = this.dateAdapter.getYear(this._activeDate);
    const minYearOfPage =
      activeYear -
      getActiveOffset(
        this.dateAdapter,
        this.activeDate,
        this.minDate,
        this.maxDate,
      );

    this._years = [];

    for (let i = 0, row: number[] = []; i < yearsPerPage; i++) {
      row.push(minYearOfPage + i);

      if (row.length == yearsPerRow) {
        this._years.push(row.map((year) => this._createCellForYear(year)));
        row = [];
      }
    }

    this.cdr.markForCheck();
  }

  _onYearSelected(event: RdsCalendarUserEvent<RdsCalendarCell<D>>): void {
    this._yearSelected({
      value: this.dateAdapter.clone(event.value.rawValue),
      event: event.event,
    });
  }

  _yearSelected(event: RdsCalendarUserEvent<D>): void {
    const month = this.dateAdapter.getMonth(this.activeDate);
    const daysInMonth = this.dateAdapter.getNumDaysInMonth(
      this.dateAdapter.createDate(
        this.dateAdapter.getYear(event.value),
        month,
        1,
      ),
    );
    const date = this.dateAdapter.createDate(
      this.dateAdapter.getYear(event.value),
      month,
      Math.min(this.dateAdapter.getDate(this.activeDate), daysInMonth),
    );
    this.yearSelected.emit({ value: date, event: event.event });
    this.selectedChange.emit(date);
  }

  _handleCalendarBodyKeydown(event: KeyboardEvent): void {
    const oldActiveDate = this._activeDate;
    const isRtl = this._isRtl();

    switch (event.keyCode) {
      case LEFT_ARROW:
        this.activeDate = this.dateAdapter.addCalendarYears(
          this._activeDate,
          isRtl ? 1 : -1,
        );
        break;
      case RIGHT_ARROW:
        this.activeDate = this.dateAdapter.addCalendarYears(
          this._activeDate,
          isRtl ? -1 : 1,
        );
        break;
      case UP_ARROW:
        this.activeDate = this.dateAdapter.addCalendarYears(
          this._activeDate,
          -yearsPerRow,
        );
        break;
      case DOWN_ARROW:
        this.activeDate = this.dateAdapter.addCalendarYears(
          this._activeDate,
          yearsPerRow,
        );
        break;
      case HOME:
        this.activeDate = this.dateAdapter.addCalendarYears(
          this._activeDate,
          -getActiveOffset(
            this.dateAdapter,
            this.activeDate,
            this.minDate,
            this.maxDate,
          ),
        );
        break;
      case END:
        this.activeDate = this.dateAdapter.addCalendarYears(
          this._activeDate,
          yearsPerPage -
            getActiveOffset(
              this.dateAdapter,
              this.activeDate,
              this.minDate,
              this.maxDate,
            ) -
            1,
        );
        break;
      case PAGE_UP:
        this.activeDate = this.dateAdapter.addCalendarYears(
          this._activeDate,
          event.altKey ? -yearsPerPage * 10 : -yearsPerPage,
        );
        break;
      case PAGE_DOWN:
        this.activeDate = this.dateAdapter.addCalendarYears(
          this._activeDate,
          event.altKey ? yearsPerPage * 10 : yearsPerPage,
        );
        break;
      case ENTER:
      case SPACE:
        this._yearSelected({
          value: this.dateAdapter.createDate(
            this.dateAdapter.getYear(this._activeDate),
            0,
            1,
          ),
          event,
        });
        break;
      default:
        return;
    }

    if (this.dateAdapter.compareDate(oldActiveDate, this.activeDate)) {
      this.activeDateChange.emit(this.activeDate);
    }

    this._focusActiveCell();
    event.preventDefault();
  }

  _getActiveCell(): RdsCalendarCell<D> | null {
    if (this._years) {
      for (let i = 0; i < this._years.length; i++) {
        for (let j = 0; j < this._years[i].length; j++) {
          const cell = this._years[i][j];

          if (
            cell &&
            this.dateAdapter.sameDate(cell.rawValue, this.activeDate, 'year')
          ) {
            return cell;
          }
        }
      }
    }

    return null;
  }

  _focusActiveCell(): void {
    this._rdsCalendarBody?._focusActiveCell();
  }

  private _createCellForYear(year: number): RdsCalendarCell<D> {
    const date = this.dateAdapter.createDate(year, 0, 1);
    const yearName = this.dateAdapter.getYearName(date);
    const cellClasses = this.dateClass
      ? this.dateClass(date, 'multi-year')
      : undefined;

    return new RdsCalendarCell(
      year,
      yearName,
      yearName,
      this._shouldEnableYear(year),
      cellClasses,
      date,
    );
  }

  private _shouldEnableYear(year: number): boolean {
    if (
      year === undefined ||
      year === null ||
      (this.maxDate && year > this.dateAdapter.getYear(this.maxDate)) ||
      (this.minDate && year < this.dateAdapter.getYear(this.minDate))
    ) {
      return false;
    }

    if (!this.dateFilter) {
      return true;
    }

    const firstOfYear = this.dateAdapter.createDate(year, 0, 1);

    for (
      let date = firstOfYear;
      this.dateAdapter.getYear(date) == year;
      date = this.dateAdapter.addCalendarDays(date, 1)
    ) {
      if (this.dateFilter(date)) {
        return true;
      }
    }

    return false;
  }

  private _isRtl(): boolean {
    return !!(this.dir && this.dir.value === 'rtl');
  }

  private _setSelectedYear(value: DateRange<D> | D | null): void {
    this._selectedYear = null;

    if (value instanceof DateRange) {
      const displayValue = value.start || value.end;

      if (displayValue) {
        this._selectedYear = this.dateAdapter.getYear(displayValue);
      }
    } else if (value) {
      this._selectedYear = this.dateAdapter.getYear(value);
    }
  }
}

export function isSameMultiYearView<D>(
  dateAdapter: DateAdapter<D>,
  date1: D,
  date2: D,
  minDate: D | null,
  maxDate: D | null,
): boolean {
  const year1 = dateAdapter.getYear(date1);
  const year2 = dateAdapter.getYear(date2);
  const startingYear = getStartingYear(dateAdapter, minDate, maxDate);

  return (
    Math.floor((year1 - startingYear) / yearsPerPage) ===
    Math.floor((year2 - startingYear) / yearsPerPage)
  );
}

export function getActiveOffset<D>(
  dateAdapter: DateAdapter<D>,
  activeDate: D,
  minDate: D | null,
  maxDate: D | null,
): number {
  const activeYear = dateAdapter.getYear(activeDate);

  return euclideanModulo(
    activeYear - getStartingYear(dateAdapter, minDate, maxDate),
    yearsPerPage,
  );
}

function getStartingYear<D>(
  dateAdapter: DateAdapter<D>,
  minDate: D | null,
  maxDate: D | null,
): number {
  let startingYear = 0;

  if (maxDate) {
    const maxYear = dateAdapter.getYear(maxDate);
    startingYear = maxYear - yearsPerPage + 1;
  } else if (minDate) {
    startingYear = dateAdapter.getYear(minDate);
  }

  return startingYear;
}

function euclideanModulo(a: number, b: number): number {
  return ((a % b) + b) % b;
}
