import {
  Directive,
  ElementRef,
  HostBinding,
  inject,
  Input,
  OnDestroy,
  OnInit,
  Renderer2,
} from '@angular/core';
import { RdsAbstractAutocompleteComponent } from './abstract-autocomplete.component';

@Directive({
  selector:
    'rds-autocomplete-option, [rds-autocomplete-option], [rdsAutocompleteOption]',
  exportAs: 'rdsAutocompleteOption',
  standalone: true,
})
export class RdsAutocompleteOptionDirective<T, ArrayOfFlatType extends T | T[]>
  implements OnInit, OnDestroy
{
  private readonly nativeElement =
    inject<ElementRef<HTMLElement>>(ElementRef).nativeElement;
  private readonly renderer = inject(Renderer2);

  @Input({ required: true, alias: 'rdsAutocompleteOption' })
  autocomplete: RdsAbstractAutocompleteComponent<T, ArrayOfFlatType>;

  @Input({ required: true })
  set keyboardFocusedItem(
    keyboardFocusedItem: RdsAutocompleteOptionDirective<
      T,
      ArrayOfFlatType
    > | null,
  ) {
    this._keyboardFocusedItem = keyboardFocusedItem;

    if (this === keyboardFocusedItem) {
      this.renderer.addClass(this.nativeElement, 'cdk-focused');
      this.renderer.addClass(this.nativeElement, 'cdk-keyboard-focused');
    } else {
      this.renderer.removeClass(this.nativeElement, 'cdk-focused');
      this.renderer.removeClass(this.nativeElement, 'cdk-keyboard-focused');
    }
  }

  get keyboardFocusedItem(): RdsAutocompleteOptionDirective<
    T,
    ArrayOfFlatType
  > | null {
    return this._keyboardFocusedItem;
  }

  private _keyboardFocusedItem: RdsAutocompleteOptionDirective<
    T,
    ArrayOfFlatType
  > | null;

  @HostBinding('class') protected readonly class = 'rds-autocomplete-option';

  ngOnInit(): void {
    this.autocomplete._addOption(this.nativeElement, this);
  }

  ngOnDestroy(): void {
    this.autocomplete._deleteOption(this.nativeElement);
  }
}
