import {
  ChangeDetectionStrategy,
  Component,
  HostBinding,
  Input,
  ViewEncapsulation,
} from '@angular/core';
import { RdsIconComponent } from '../icons';
import { RdsButtonModule } from '../button';

/**
 * Component intended to be used within the `<rds-card>` component. It adds styles for a
 * preset header section (i.e. a title, subtitle, and avatar layout).
 * @docs-private
 */
@Component({
  selector: 'rds-card-header',
  templateUrl: 'card-header.component.html',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  standalone: true,
  imports: [RdsIconComponent, RdsButtonModule],
})
export class RdsCardHeaderComponent {
  @HostBinding('class') readonly class = 'rds-card-header';

  @Input() action: string;
}
