import {
  booleanAttribute,
  ChangeDetectionStrategy,
  Component,
  HostBinding,
  Input,
  ViewEncapsulation,
} from '@angular/core';

export type RdsDividerOrientation = 'horizontal' | 'vertical';

@Component({
  selector: 'rds-divider',
  template: '',
  styleUrls: ['./divider.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsDividerComponent {
  @HostBinding('attr.role') readonly hostRole = 'separator';
  @HostBinding('class') readonly class = 'rds-divider';

  @HostBinding('attr.aria-orientation')
  get orientation(): RdsDividerOrientation {
    return this.vertical ? 'vertical' : 'horizontal';
  }

  @HostBinding('class.rds-divider-vertical')
  @Input({ transform: booleanAttribute })
  vertical = false;
}
