import {
  AbstractType,
  InjectFlags,
  InjectionToken,
  InjectOptions,
  Injector,
  Type,
} from '@angular/core';
import { RdsToastPackage } from './rds-toast.package';

export class RdsToastInjector<T, A> implements Injector {
  constructor(
    private readonly _toastPackage: RdsToastPackage<T, A>,
    private readonly _parentInjector: Injector,
  ) {}

  get(
    token:
      | Type<T | RdsToastPackage<T, A>>
      | InjectionToken<T>
      | AbstractType<T>,
    notFoundValue: undefined,
    options?: InjectFlags | InjectOptions,
  ): T | RdsToastPackage<T, A> | null {
    if (token === RdsToastPackage) {
      return this._toastPackage;
    }

    return this._parentInjector.get<T>(token, notFoundValue, options);
  }
}
