import { RdsCarouselItemDirective } from './carousel-item.directive';
import {
  AfterContentInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  DestroyRef,
  HostBinding,
  inject,
  Input,
  numberAttribute,
  OnChanges,
  OnDestroy,
  QueryList,
  SimpleChanges,
  ViewEncapsulation,
} from '@angular/core';
import { RdsCarouselActiveItemChangedEvent } from './carousel.types';
import { BreakpointUtil } from '../utils';
import { delay, takeUntil, tap } from 'rxjs/operators';
import { interval, Subject, timer } from 'rxjs';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

const INITIAL_AUTOPLAY_DELAY = 2000;

@Component({
  selector: 'rds-carousel',
  template: `
    <div class="rds-carousel__carousel-items">
      <ng-content />
    </div>
    <div class="rds-carousel__carousel-pagination">
      @if (showButtons) {
        <div
          class="rds-carousel__carousel-navigation-button-wrapper rds-carousel__carousel-navigation-button-wrapper--left"
        >
          <button
            rds-icon-button
            (click)="prev()"
            [size]="_isMobile() ? 's' : 'm'"
          >
            <rds-icon namespace="filled" icon="chevron_left" />
          </button>
        </div>
      }
      <div class="rds-carousel__carousel-pagination--text">
        {{ indexOfActiveCarouselItem + 1 }}/{{ carouselItems.length || 0 }}
      </div>
      @if (showButtons) {
        <div
          class="rds-carousel__carousel-navigation-button-wrapper rds-carousel__carousel-navigation-button-wrapper--right"
        >
          <button
            rds-icon-button
            (click)="next()"
            [size]="_isMobile() ? 's' : 'm'"
          >
            <rds-icon namespace="filled" icon="chevron_right" />
          </button>
        </div>
      }
    </div>
  `,
  styleUrls: ['./carousel.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  exportAs: 'rdsCarousel',
})
export class RdsCarouselComponent
  implements AfterContentInit, OnDestroy, OnChanges
{
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly destroyRef = inject(DestroyRef);

  protected indexOfActiveCarouselItem = 0;

  @HostBinding('class') hostClass = 'rds-carousel';

  @Input({ transform: booleanAttribute }) autoplay = false;
  @Input({
    transform: (v: unknown) => numberAttribute(v, INITIAL_AUTOPLAY_DELAY),
  })
  autoplayDelay = INITIAL_AUTOPLAY_DELAY;
  @Input({ transform: booleanAttribute }) showButtons = false;

  @ContentChildren(RdsCarouselItemDirective)
  carouselItems: QueryList<RdsCarouselItemDirective>;

  get indexOfLastElement(): number {
    return this.carouselItems.length - 1;
  }

  private readonly destroyTimeout$ = new Subject<void>();

  ngAfterContentInit(): void {
    this.selectActiveItem();
    this.carouselItems.changes
      .pipe(delay(0), takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this.selectActiveItem());
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['autoplay'] || changes['autoplayDelay']) {
      this.defineAutoplayInterval();
    }
  }

  ngOnDestroy(): void {
    this.destroyTimeout$.next();
    this.destroyTimeout$.complete();
  }

  prev(): void {
    this.hideCurrentVisibleCarouselItem();

    let indexOfNewCarouselItemToShow = this.indexOfActiveCarouselItem - 1;

    if (this.indexOfActiveCarouselItem === 0) {
      indexOfNewCarouselItemToShow = this.indexOfLastElement;
    }

    this.indexOfActiveCarouselItem = indexOfNewCarouselItemToShow;

    this.showCarouselItemMatchedWithIndexOfActive();
    this.defineAutoplayInterval();
  }

  next(): void {
    this.hideCurrentVisibleCarouselItem();

    let indexOfNewCarouselItemToShow = this.indexOfActiveCarouselItem + 1;

    if (this.indexOfActiveCarouselItem === this.indexOfLastElement) {
      indexOfNewCarouselItemToShow = 0;
    }

    this.indexOfActiveCarouselItem = indexOfNewCarouselItemToShow;

    this.showCarouselItemMatchedWithIndexOfActive();
    this.defineAutoplayInterval();
  }

  _changeActive({ index }: RdsCarouselActiveItemChangedEvent): void {
    this.hideCurrentVisibleCarouselItem();
    this.indexOfActiveCarouselItem = index;
    this.showCarouselItemMatchedWithIndexOfActive();
  }

  _isMobile(): boolean {
    return BreakpointUtil.isMobile();
  }

  private defineAutoplayInterval(): void {
    this.destroyTimeout$.next();

    if (this.autoplay) {
      interval(this.autoplayDelay)
        .pipe(
          tap(() => this.next()),
          takeUntil(this.destroyTimeout$),
          takeUntilDestroyed(this.destroyRef),
        )
        .subscribe();
    }
  }

  private showCarouselItemMatchedWithIndexOfActive(): void {
    const carouselItemToShow = this.carouselItems.get(
      this.indexOfActiveCarouselItem,
    );

    if (carouselItemToShow) {
      carouselItemToShow._show();
      timer(0)
        .pipe(tap(() => this.cdr.markForCheck()))
        .subscribe();
    }
  }

  private hideCurrentVisibleCarouselItem(): void {
    const carouselItemToHide = this.carouselItems.get(
      this.indexOfActiveCarouselItem,
    );

    if (carouselItemToHide) {
      carouselItemToHide._hide();
    }
  }

  private selectActiveItem(): void {
    this.indexOfActiveCarouselItem = Math.max(
      this.carouselItems.toArray().findIndex((item) => item.isShown()),
      0,
    );
    this.carouselItems.forEach((item, index) => {
      if (index !== this.indexOfActiveCarouselItem && item.isShown()) {
        item._hide();
      }
    });
    this.showCarouselItemMatchedWithIndexOfActive();
    this.defineAutoplayInterval();
  }
}
