import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  Output,
} from '@angular/core';
import { RdsIconComponent } from '../icons';
import {
  RdsBreadcrumbItem,
  RdsBreadcrumbItemComponent,
} from './breadcrumb-item.component';

export type RdsBreadcrumbsSize = 'm' | 'l';

@Component({
  selector: 'rds-breadcrumbs',
  templateUrl: './breadcrumbs.component.html',
  styleUrls: ['./breadcrumbs.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  standalone: true,
  imports: [RdsIconComponent, RdsBreadcrumbItemComponent],
})
export class RdsBreadcrumbsComponent<T extends RdsBreadcrumbItem> {
  @Input() size: RdsBreadcrumbsSize = 'l';
  @Input() items: T[];
  @Output() itemClick = new EventEmitter<T>();

  trackBy(index: number, item: T): string | number {
    return item.title || item.text || index;
  }
}
