import { DomPortalOutlet, TemplatePortal } from '@angular/cdk/portal';
import { DOCUMENT } from '@angular/common';
import {
  ApplicationRef,
  ChangeDetectorRef,
  ComponentFactoryResolver,
  Directive,
  inject,
  InjectionToken,
  Injector,
  OnDestroy,
  TemplateRef,
  ViewContainerRef,
} from '@angular/core';
import { Subject } from 'rxjs';

export const RDS_MENU_CONTENT = new InjectionToken<RdsMenuContentDirective>(
  'RDS_MENU_CONTENT',
);

@Directive({
  selector: 'ng-template[rds-menu-content], ng-template[rdsMenuContent]',
  providers: [
    { provide: RDS_MENU_CONTENT, useExisting: RdsMenuContentDirective },
  ],
})
export class RdsMenuContentDirective implements OnDestroy {
  private readonly template = inject<TemplateRef<HTMLElement>>(TemplateRef);
  private readonly componentFactoryResolver = inject(ComponentFactoryResolver);
  private readonly appRef = inject(ApplicationRef);
  private readonly injector = inject(Injector);
  private readonly viewContainerRef = inject(ViewContainerRef);
  private readonly document = inject(DOCUMENT);
  private readonly cdr = inject(ChangeDetectorRef, { optional: true });

  readonly _attached = new Subject<void>();
  private _portal: TemplatePortal;
  private _outlet: DomPortalOutlet;

  attach(context: any = {}): void {
    if (!this._portal) {
      this._portal = new TemplatePortal(this.template, this.viewContainerRef);
    }

    this.detach();

    if (!this._outlet) {
      this._outlet = new DomPortalOutlet(
        this.document.createElement('div'),
        this.componentFactoryResolver,
        this.appRef,
        this.injector,
      );
    }

    const element: HTMLElement = this.template.elementRef.nativeElement;

    if (element.parentNode) {
      element.parentNode.insertBefore(this._outlet.outletElement, element);
    }

    if (this.cdr) {
      this.cdr.markForCheck();
    }

    this._portal.attach(this._outlet, context);
    this._attached.next();
  }

  detach(): void {
    if (this._portal.isAttached) {
      this._portal.detach();
    }
  }

  ngOnDestroy(): void {
    if (this._outlet) {
      this._outlet.dispose();
    }
  }
}
