import {
  AfterViewInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  Component,
  HostBinding,
  Input,
  numberAttribute,
  OnChanges,
  OnDestroy,
  SimpleChanges,
  ViewEncapsulation,
} from '@angular/core';
import { AnimationItem } from 'lottie-web';
import { AnimationOptions } from 'ngx-lottie';
import {
  RdsProgressSpinnerMode,
  RdsProgressSpinnerSize,
} from './progress-spinner.types';
import { NumberInput } from '@angular/cdk/coercion';
import { Subject } from 'rxjs';
import { debounceTime, delay } from 'rxjs/operators';

const ANIMATION_SPEED_FACTOR = 10;
type RdsProgressSpinnerDirection = 1 | -1;
const ANIMATION_DIRECTION: {
  FORWARD: RdsProgressSpinnerDirection;
  BACKWARD: RdsProgressSpinnerDirection;
} = {
  FORWARD: 1,
  BACKWARD: -1,
};
const DEFAULT_MAX_VALUE = 100;
const DEFAULT_MIN_VALUE = 0;

@Component({
  selector: 'rds-progress-spinner',
  template: `
    <div class="rds-progress-spinner-container">
      <ng-lottie
        [options]="animationConfigDebounced$ | async"
        (animationCreated)="animationCreated($event)"
      />
    </div>
    @if (label) {
      <span class="rds-progress-spinner-label">{{ label }}</span>
    }
  `,
  styleUrls: [`./progress-spinner.component.scss`],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsProgressSpinnerComponent
  implements AfterViewInit, OnChanges, OnDestroy
{
  private animationItem: AnimationItem;
  private readonly animationConfig$ = new Subject<AnimationOptions>();
  protected readonly animationConfigDebounced$ = this.animationConfig$.pipe(
    delay(1),
    debounceTime(1),
  );

  @HostBinding('class') get hostClasses(): string[] {
    return ['rds-progress-spinner', `rds-progress-spinner-${this.size}`];
  }

  @HostBinding('attr.role') readonly hostRole = 'progressbar';

  @HostBinding('attr.aria-valuemin')
  get attrValueMin(): number | null {
    return this.mode === 'determinate' ? this.minValue : null;
  }

  @HostBinding('attr.aria-valuemax')
  get attrValueMax(): number | null {
    return this.mode === 'determinate' ? this.maxValue : null;
  }

  @HostBinding('attr.aria-valuenow')
  get attrValueNow(): number | null {
    return this.mode === 'determinate' ? this.value : null;
  }

  @Input()
  set value(newValue: NumberInput) {
    const value = numberAttribute(newValue, DEFAULT_MIN_VALUE);

    if (this._value !== value) {
      this._value = value;

      if (this.animationItem) {
        this.animationItem.setDirection(this.getAnimationDirection());
        this.playAnimation();
      }
    }
  }

  get value(): number {
    return this._value;
  }

  private _value = DEFAULT_MIN_VALUE;

  @Input()
  set minValue(value: NumberInput) {
    const min = numberAttribute(value, DEFAULT_MIN_VALUE);

    if (min > this.maxValue) {
      throw new Error(`The minimal value should be less than maximum value`);
    }

    if (this._minValue !== min) {
      this._minValue = min;
      this.animationItem.setDirection(this.getAnimationDirection());
      this.playAnimation();
    }
  }

  get minValue(): number {
    return this._minValue;
  }

  private _minValue = DEFAULT_MIN_VALUE;

  @Input()
  set maxValue(value: NumberInput) {
    const max = numberAttribute(value, DEFAULT_MAX_VALUE);

    if (max < this.minValue) {
      throw new Error(`The maximal value should be greater than minimal value`);
    }

    if (this._maxValue !== max) {
      this._maxValue = max;
      this.animationItem.setDirection(this.getAnimationDirection());
      this.playAnimation();
    }
  }

  get maxValue(): number {
    return this._maxValue;
  }

  private _maxValue = DEFAULT_MAX_VALUE;

  @Input() mode: RdsProgressSpinnerMode = 'indeterminate';
  @Input() size: RdsProgressSpinnerSize = 'xl';
  @Input() label: string | null;

  @HostBinding('class.rds-progress-spinner--white')
  @Input({ transform: booleanAttribute })
  white = false;

  ngAfterViewInit(): void {
    this._setAnimationConfig();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['mode'] || changes['size'] || changes['white']) {
      this._setAnimationConfig();
    }
  }

  ngOnDestroy(): void {
    this.animationItem?.destroy();
  }

  animationCreated($event: AnimationItem): void {
    if (this.mode === 'determinate') {
      this.animationItem = $event;
      this.playAnimation();
    }
  }

  private playAnimation(): void {
    if (this.mode === 'determinate' && this.animationItem) {
      this.animationItem.pause();
      this.removeAnimationItemListener();

      if (
        (this.animationItem.playDirection === ANIMATION_DIRECTION.FORWARD &&
          this.animationItem.currentFrame < this.getPercentageValue()) ||
        (this.animationItem.playDirection === ANIMATION_DIRECTION.BACKWARD &&
          this.animationItem.currentFrame > this.getPercentageValue())
      ) {
        this.animationItem.setSpeed(
          Math.abs(
            this.animationItem.currentFrame - this.getPercentageValue(),
          ) / ANIMATION_SPEED_FACTOR,
        );
        this.animationItem.play();

        this.animationItem.addEventListener('enterFrame', (event) => {
          if (
            (this.animationItem.playDirection === ANIMATION_DIRECTION.FORWARD &&
              event.currentTime >= this.getPercentageValue()) ||
            (this.animationItem.playDirection ===
              ANIMATION_DIRECTION.BACKWARD &&
              event.currentTime < this.getPercentageValue())
          ) {
            this.animationItem.pause();
            this.removeAnimationItemListener();
            this.animationItem.goToAndStop(this.getPercentageValue(), true);
          }
        });
      }
    }
  }

  private _getPath(): string {
    return `assets/spinner/spinner-${
      this.mode === 'determinate' ? 'determinate-' : ''
    }${this.white ? 'white-' : ''}${this.size}.json`;
  }

  private _setAnimationConfig(): void {
    if (this.animationItem) {
      this.animationItem.destroy();
    }

    if (this.mode === 'indeterminate') {
      this.animationConfig$.next({
        path: this._getPath(),
        autoplay: true,
        loop: true,
      });
    } else {
      this.animationConfig$.next({
        path: this._getPath(),
        loop: false,
      });
    }
  }

  private removeAnimationItemListener(): void {
    if (this.animationItem) {
      this.animationItem.removeEventListener('enterFrame');
    }
  }

  private getPercentageValue(): number {
    const value =
      ((this.value - this.minValue) / (this.maxValue - this.minValue)) * 100;
    const percentage = Math.round(value + Number.EPSILON);

    if (percentage <= DEFAULT_MIN_VALUE) {
      return DEFAULT_MIN_VALUE;
    } else if (percentage >= this.animationItem.totalFrames) {
      return this.animationItem.totalFrames || DEFAULT_MAX_VALUE;
    } else {
      return percentage;
    }
  }

  private getAnimationDirection(): RdsProgressSpinnerDirection {
    return this.getPercentageValue() < this.animationItem.currentFrame
      ? ANIMATION_DIRECTION.BACKWARD
      : ANIMATION_DIRECTION.FORWARD;
  }
}
