import {
  _COALESCED_STYLE_SCHEDULER,
  _CoalescedStyleScheduler,
  CDK_TABLE,
  CDK_TABLE_TEMPLATE,
  CdkTable,
  STICKY_POSITIONING_LISTENER,
} from '@angular/cdk/table';
import {
  ChangeDetectionStrategy,
  Component,
  DestroyRef,
  HostBinding,
  inject,
  Input,
  OnChanges,
  OnDestroy,
  SimpleChanges,
  ViewEncapsulation,
} from '@angular/core';
import {
  _DisposeViewRepeaterStrategy,
  _VIEW_REPEATER_STRATEGY,
} from '@angular/cdk/collections';
import { RdsTableAsyncDataSource } from './table-data-source';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import {
  RdsTableAlternation,
  RdsTableDensity,
  RdsTableSize,
  RdsTableVisualAccent,
} from './table.types';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

@Component({
  selector: 'rds-table, table[rds-table],  table[rdsTable]',
  exportAs: 'rdsTable',
  template: CDK_TABLE_TEMPLATE,
  styleUrls: ['./table.component.scss'],
  providers: [
    {
      provide: _VIEW_REPEATER_STRATEGY,
      useClass: _DisposeViewRepeaterStrategy,
    },
    { provide: CdkTable, useExisting: RdsTableComponent },
    { provide: CDK_TABLE, useExisting: RdsTableComponent },
    { provide: _COALESCED_STYLE_SCHEDULER, useClass: _CoalescedStyleScheduler },
    // Prevent nested tables from seeing this table's StickyPositioningListener.
    { provide: STICKY_POSITIONING_LISTENER, useValue: null },
  ],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.Default,
})
export class RdsTableComponent<T>
  extends CdkTable<T>
  implements OnChanges, OnDestroy
{
  protected readonly _isLoadingDestroyer = new Subject<void>();

  _isLoading = false;

  @Input()
  density: RdsTableDensity = 'normal';

  @Input()
  size: RdsTableSize = 'l';

  @Input()
  visualAccent: RdsTableVisualAccent = 'basic';

  @Input()
  alternation: RdsTableAlternation = 'none';

  @HostBinding('class') get class(): string[] {
    return [
      'rds-table',
      `rds-table-density-${this.density}`,
      `rds-table-size-${this.size}`,
      `rds-table-accent-${this.visualAccent}`,
      `rds-table-alternation-${this.alternation}`,
      this._isLoading && 'rds-table-loading-data',
      this.fixedLayout && 'rds-table-fixed-layout',
    ].filter((v) => typeof v === 'string') as string[];
  }

  private readonly destroyRef = inject(DestroyRef);
  protected override stickyCssClass = 'rds-table-sticky';
  protected override needsPositionStickyOnElement = false;

  ngOnChanges(changes: SimpleChanges): void {
    if (
      changes['dataSource'] &&
      changes['dataSource'].currentValue instanceof RdsTableAsyncDataSource
    ) {
      this._isLoadingDestroyer.next();
      (this.dataSource as RdsTableAsyncDataSource<T>)
        .getLoadingData()
        .pipe(
          takeUntil(this._isLoadingDestroyer),
          takeUntilDestroyed(this.destroyRef),
        )
        .subscribe((isLoading) => {
          this._isLoading = isLoading;
          this._changeDetectorRef.markForCheck();
        });
    }
  }

  override ngOnDestroy(): void {
    super.ngOnDestroy();
    this._isLoadingDestroyer.complete();
  }

  override updateStickyColumnStyles(): void {
    super.updateStickyColumnStyles();
  }
}
