import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';

@Injectable({ providedIn: 'root' })
export class RdsSearchIntl {
  private readonly _changes = new Subject<void>();
  private _searchInputPlaceholder = 'Search...';
  private _searchButtonText = 'Search';
  private _emptyValue = '';
  private _ariaInputLabel = 'Global';
  private _ariaClearLabel = 'Clear search';

  get changes(): Observable<void> {
    return this._changes.asObservable();
  }

  get searchInputPlaceholder(): string {
    return this._searchInputPlaceholder;
  }

  set searchInputPlaceholder(placeholderText: string) {
    this._searchInputPlaceholder = placeholderText;
    this._changes.next();
  }

  get searchButtonText(): string {
    return this._searchButtonText;
  }

  set searchButtonText(buttonText: string) {
    this._searchButtonText = buttonText;
    this._changes.next();
  }

  get emptyValue(): string {
    return this._emptyValue;
  }

  set emptyValue(emptyValue: string) {
    this._emptyValue = emptyValue;
    this._changes.next();
  }

  get ariaInputLabel(): string {
    return this._ariaInputLabel;
  }

  set ariaInputLabel(ariaInputLabel: string) {
    this._ariaInputLabel = ariaInputLabel;
    this._changes.next();
  }

  get ariaClearLabel(): string {
    return this._ariaClearLabel;
  }

  set ariaClearLabel(ariaClearLabel: string) {
    this._ariaClearLabel = ariaClearLabel;
    this._changes.next();
  }
}
