import { Directive, HostBinding, Input } from '@angular/core';
import { RdsListItemSize } from './list-item.component';

@Directive({
  selector: 'rds-header-list-item, [rds-header-list-item], [rdsHeaderListItem]',
})
export class RdsHeaderListItemDirective {
  @Input() size: RdsListItemSize = 'l';

  @HostBinding('class')
  protected get class(): string {
    return `rds-header-list-item rds-header-list-item--${this.size}`;
  }
}
