import { Directive, HostBinding, Input } from '@angular/core';

export type RdsTextareaResize = 'vertical' | 'horizontal' | 'both';

@Directive({
  selector: `textarea[resize]`,
})
export class RdsTextareaResizeDirective {
  @Input() resize: RdsTextareaResize | '' = 'vertical';

  @HostBinding('class') get class(): string {
    return `rds-textarea-resize-${this.resize || 'vertical'}`;
  }
}
