import {
  AfterContentInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  ContentChildren,
  EventEmitter,
  HostBinding,
  inject,
  Input,
  OnDestroy,
  OnInit,
  Output,
  QueryList,
  TemplateRef,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { RdsSidenavMenuitemComponent } from '../sidenav-menuitem/sidenav-menuitem.component';
import { FocusKeyManager, FocusOrigin } from '@angular/cdk/a11y';
import {
  RDS_SIDENAV_MENU_PANEL,
  RdsSidenavMenuCloseReason,
  RdsSidenavMenuPanel,
} from '../sidenav.types';
import { merge, Subject, Subscription } from 'rxjs';
import { AnimationEvent } from '@angular/animations';
import { Direction } from '@angular/cdk/bidi';
import { startWith, switchMap } from 'rxjs/operators';
import {
  DOWN_ARROW,
  ESCAPE,
  hasModifierKey,
  LEFT_ARROW,
  RIGHT_ARROW,
  UP_ARROW,
} from '@angular/cdk/keycodes';
import { RdsSidenavService } from '../sidenav.service';
import { rdsSidenavExpansionAnimations } from './sidenav-menu-expansion.animation';
import { RdsMenuPositionX, RdsMenuPositionY } from '../../menu';
import { RdsSidenavMenuHeaderComponent } from '../sidenav-menu-header/sidenav-menu-header.component';

let menuPanelUid = 0;

@Component({
  selector: 'rds-sidenav-menu',
  exportAs: 'rdsSidenavMenu',
  templateUrl: './sidenav-menu.component.html',
  styleUrls: ['sidenav-menu.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  providers: [
    { provide: RDS_SIDENAV_MENU_PANEL, useExisting: RdsSidenavMenuComponent },
  ],
  animations: [rdsSidenavExpansionAnimations.bodyExpansion],
})
export class RdsSidenavMenuComponent
  implements OnInit, AfterContentInit, OnDestroy, RdsSidenavMenuPanel
{
  private readonly sidenav = inject(RdsSidenavService);
  private readonly cdr = inject(ChangeDetectorRef);

  private _keyManager: FocusKeyManager<
    RdsSidenavMenuitemComponent | RdsSidenavMenuHeaderComponent
  >;

  _hovered = new Subject<boolean>();
  hovered = this._hovered.asObservable();

  @ContentChild(RdsSidenavMenuHeaderComponent)
  menuHeader?: RdsSidenavMenuHeaderComponent;

  @ContentChildren(RdsSidenavMenuitemComponent, { descendants: true })
  _allItems: QueryList<RdsSidenavMenuitemComponent>;

  @HostBinding('attr.aria-label') attrAriaLabel = null;
  @HostBinding('attr.aria-labelledby') attrAriaLabelledBy = null;
  @HostBinding('attr.aria-describedby') attrAriaDescribedBy = null;

  private _directDescendantItems = new QueryList<
    RdsSidenavMenuitemComponent | RdsSidenavMenuHeaderComponent
  >();

  private _tabSubscription = Subscription.EMPTY;

  _classList: { [key: string]: boolean } = {};

  _panelAnimationState: 'void' | 'enter' = 'void';

  readonly _animationDone = new Subject<AnimationEvent>();

  _isAnimating: boolean;

  parentMenu: RdsSidenavMenuPanel | undefined;

  direction: Direction;

  expandedState: 'expanded' | 'collapsed' = 'collapsed';

  private _isOpened = false;
  get isOpened(): boolean {
    return this._isOpened;
  }

  set isOpened(value: boolean) {
    if (this._isOpened !== value) {
      this._isOpened = value;
      this.cdr.markForCheck();
    }
  }

  private _title: string;
  set title(v: string) {
    if (v !== this._title) {
      this._title = v;
      this.cdr.markForCheck();
    }
  }

  get title(): string {
    return this._title;
  }

  overlayPanelClass: string | string[] = '';
  backdropClass = 'cdk-overlay-transparent-backdrop';

  @Input('aria-label') ariaLabel: string | null = null;
  @Input('aria-labelledby') ariaLabelledby: string | null = null;
  @Input('aria-describedby') ariaDescribedby: string | null = null;

  @Input({ transform: booleanAttribute }) openOnInit = false;

  @ViewChild('panel') templateRef: TemplateRef<unknown>;

  @Output() readonly closed = new EventEmitter<RdsSidenavMenuCloseReason>();

  readonly panelId = `rds-menu-panel-${menuPanelUid++}`;
  readonly isMinimal$ = this.sidenav.isMinimal$;

  ngOnInit(): void {
    this.setPositionClasses();
  }

  ngAfterContentInit(): void {
    this._updateDirectDescendants();
    this._keyManager = new FocusKeyManager(this._directDescendantItems)
      .withWrap()
      .withTypeAhead()
      .withHomeAndEnd();
    this._tabSubscription = this._keyManager.tabOut.subscribe(() =>
      this.closed.emit('tab'),
    );

    this._directDescendantItems.changes
      .pipe(
        startWith(this._directDescendantItems),
        switchMap((items) =>
          merge(
            ...items.map(
              (
                item:
                  | RdsSidenavMenuitemComponent
                  | RdsSidenavMenuHeaderComponent,
              ) => item._focused,
            ),
          ),
        ),
      )
      .subscribe((focusedItem) =>
        this._keyManager.updateActiveItem(
          focusedItem as RdsSidenavMenuitemComponent,
        ),
      );
  }

  ngOnDestroy(): void {
    this._directDescendantItems.destroy();
    this._tabSubscription.unsubscribe();
    this.closed.complete();
  }

  _handleKeydown(event: KeyboardEvent): void {
    event.stopPropagation();

    const keyCode = event.keyCode;
    const manager = this._keyManager;

    switch (keyCode) {
      case ESCAPE:
        if (!hasModifierKey(event)) {
          event.preventDefault();
          this.closed.emit('keydown');
        }

        break;
      case LEFT_ARROW:
        if (this.direction === 'ltr') {
          this.closed.emit('keydown');
        }

        break;
      case RIGHT_ARROW:
        if (this.direction === 'rtl') {
          this.closed.emit('keydown');
        }

        break;
      default:
        if (keyCode === UP_ARROW || keyCode === DOWN_ARROW) {
          manager.setFocusOrigin('keyboard');
        }

        manager.onKeydown(event);
    }
  }

  focusFirstItem(origin: FocusOrigin = 'program'): void {
    if (!this.sidenav.isMinimal) {
      setTimeout(() => this._focusFirstItem(origin));

      return;
    }

    this._focusFirstItem(origin);
  }

  private _focusFirstItem(origin: FocusOrigin): void {
    const manager = this._keyManager;
    manager.setFocusOrigin(origin).setFirstItemActive();

    if (!manager.activeItem && this._directDescendantItems.length) {
      let element =
        this._directDescendantItems.first.nativeElement.parentElement;

      while (element) {
        if (element.getAttribute('role') === 'menu') {
          element.focus();
          break;
        } else {
          element = element.parentElement;
        }
      }
    }
  }

  resetActiveItem(): void {
    this._keyManager.setActiveItem(-1);
  }

  setPositionClasses(
    posX: RdsMenuPositionX = 'after',
    posY: RdsMenuPositionY = 'below',
  ): void {
    const classes = this._classList;
    classes['rds-menu-before'] = posX === 'before';
    classes['rds-menu-after'] = posX === 'after';
    classes['rds-menu-above'] = posY === 'above';
    classes['rds-menu-below'] = posY === 'below';
  }

  _startAnimation(): void {
    this._panelAnimationState = 'enter';
    this.expandedState = 'expanded';
  }

  _resetAnimation(): void {
    this._panelAnimationState = 'void';
    this.expandedState = 'collapsed';
  }

  _onAnimationDone(event: AnimationEvent): void {
    this._animationDone.next(event);
    this._isAnimating = false;
  }

  private _updateDirectDescendants(): void {
    this._allItems.changes
      .pipe(startWith(this._allItems))
      .subscribe((items: QueryList<RdsSidenavMenuitemComponent>) => {
        const results: (
          | RdsSidenavMenuHeaderComponent
          | RdsSidenavMenuitemComponent
        )[] = items.filter((item) => item._parentMenu === this);

        if (this.menuHeader) {
          results.push(this.menuHeader);
        }

        this._directDescendantItems.reset(results);
        this._directDescendantItems.notifyOnChanges();
      });
  }
}
