import { RdsSingleSelectOptionComponent } from './single-select-option.component';
import { RdsMultiSelectOptionComponent } from './multi-select-option.component';
import { RdsSelectOption } from './select-types';

export class RdsAbstractSelectOptionSelectionChangedEvent<
  T,
  O extends RdsSelectOption<T>,
> {
  constructor(
    public readonly source: O,
    public readonly isUserInput = false,
  ) {}
}

export class RdsSelectOptionSelectionChangedEvent<
  T,
> extends RdsAbstractSelectOptionSelectionChangedEvent<
  T,
  RdsSingleSelectOptionComponent<T>
> {}

export class RdsMultiSelectOptionSelectionChangedEvent<
  T,
> extends RdsAbstractSelectOptionSelectionChangedEvent<
  T,
  RdsMultiSelectOptionComponent<T>
> {}
