import { AnimationEvent } from '@angular/animations';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  HostBinding,
  HostListener,
  inject,
  OnDestroy,
  TemplateRef,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { Observable, Subject } from 'rxjs';
import {
  RdsTooltipClass,
  RdsTooltipPosition,
  RdsTooltipVisibility,
} from './types';
import { tooltipAnimation } from './tooltip.animations';
import { RdsTooltipDirective } from './tooltip.directive';
import { getFirstFocusableChild } from '../utils';

let rdsTooltipCounter = 0;

@Component({
  selector: 'rds-tooltip',
  templateUrl: './tooltip.component.html',
  styleUrls: ['./tooltip.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  animations: [tooltipAnimation.tooltipState],
})
export class RdsTooltipComponent implements OnDestroy {
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly nativeElement =
    inject<ElementRef<HTMLElement>>(ElementRef).nativeElement;

  _content: string | TemplateRef<unknown>;

  _tooltipClass: RdsTooltipClass;
  _tooltipArrow = false;

  _showTimeout: ReturnType<typeof setTimeout> | null;
  _hideTimeout: ReturnType<typeof setTimeout> | null;

  _position: RdsTooltipPosition;
  _visibility: RdsTooltipVisibility = 'initial';

  // close tooltip on body click
  private _closeOnInteraction = false;
  private readonly _onHide = new Subject<void>();

  triggerDirectiveRef?: RdsTooltipDirective;
  isPopover: boolean;
  showCloseButton: boolean;

  @ViewChild('closeButton', { read: ElementRef })
  closeButton: ElementRef<HTMLElement> | undefined;

  @HostBinding('attr.role')
  role: 'dialog' | null;

  @HostBinding('attr.id')
  id = `rdsTooltip-${rdsTooltipCounter++}`;

  @HostBinding('attr.aria-modal')
  isModal = false;

  @HostListener('body:click')
  closeOnBodyClick(): void {
    if (!this.isPopover && this._closeOnInteraction) {
      this.hide();
    }
  }

  get contentIsString(): boolean {
    return typeof this._content === 'string';
  }

  get tooltipIconClass(): string {
    return this._tooltipArrow ? 'rds-tooltip--hide-arrow ' : '';
  }

  ngOnDestroy(): void {
    this._onHide.complete();
  }

  /**
   * Show tooltip
   * @param delay in ms
   */
  show(delay = 0): void {
    // Cancel hide if it is scheduled
    if (this._hideTimeout) {
      clearTimeout(this._hideTimeout);
      this._hideTimeout = null;
    }

    this._closeOnInteraction = !this.isPopover;
    this._showTimeout = setTimeout(() => {
      this._visibility = 'visible';
      this._showTimeout = null;

      // Mark for check so if any parent component has set the
      // ChangeDetectionStrategy to OnPush it will be checked anyway
      this._markForCheck();
    }, delay);
  }

  /**
   * Hide tooltip
   * @param delay in ms
   */
  hide(delay = 0): void {
    // Cancel show if it is scheduled
    if (this._showTimeout) {
      clearTimeout(this._showTimeout);
      this._showTimeout = null;
    }

    this._hideTimeout = setTimeout(() => {
      this._visibility = 'hidden';
      this._hideTimeout = null;

      // Mark for check so if any parent component has set the
      // ChangeDetectionStrategy to OnPush it will be checked anyway
      this._markForCheck();
    }, delay);
  }

  getFirstFocusableElement(): HTMLElement | null {
    return getFirstFocusableChild(this.nativeElement);
  }

  getLastFocusableElement(): HTMLElement | null {
    return this.closeButton?.nativeElement ?? null;
  }

  afterHidden(): Observable<void> {
    return this._onHide;
  }

  isVisible(): boolean {
    return this._visibility === 'visible';
  }

  onCloseButtonClick(): void {
    this.triggerDirectiveRef?.hide();
  }

  _animationStart(): void {
    this._closeOnInteraction = false;
  }

  _animationDone(event: AnimationEvent): void {
    const toState = event.toState as RdsTooltipVisibility;

    if (toState === 'hidden' && !this.isVisible()) {
      this._onHide.next();
    }

    if (toState === 'visible' || toState === 'hidden') {
      this._closeOnInteraction = !this.isPopover;
    }
  }

  _markForCheck(): void {
    this.cdr.markForCheck();
  }
}
