import { FocusOptions, FocusOrigin } from '@angular/cdk/a11y';
import { A, hasModifierKey, SPACE } from '@angular/cdk/keycodes';
import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  DestroyRef,
  ElementRef,
  EventEmitter,
  HostBinding,
  HostListener,
  inject,
  Input,
  Output,
  Renderer2,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { FormControl } from '@angular/forms';
import { noop } from 'rxjs';
import { tap } from 'rxjs/operators';
import { RdsInputDirective } from '../input.directive';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

let uniqueId = 0;

export type RdsSelectSearchSize = 's' | 'm' | 'l';

@Component({
  styleUrls: ['select-search-input.component.scss'],
  selector: 'rds-select-search-input',
  template: `
    <rds-icon
      class="rds-search-icon rds-select__option--search-input-prefix"
      namespace="filled"
      icon="search"
      aria-hidden="true"
    />
    <input
      [formControl]="control"
      #search
      rdsInput
      (search)="$event.preventDefault(); $event.stopPropagation()"
      [placeholder]="placeholder"
    />
    <button
      rds-icon-button
      [size]="'s'"
      [class.hidden]="!isDirtyValue()"
      (click)="resetInput(); $event.stopPropagation()"
      class="rds-search-reset-button rds-select__option--search-input-suffix"
    >
      <rds-icon namespace="filled" icon="close" aria-hidden="true" />
    </button>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class RdsSelectSearchInputComponent implements AfterViewInit {
  protected readonly renderer = inject(Renderer2);
  protected readonly cdr = inject(ChangeDetectorRef);
  private readonly elementRef = inject(ElementRef);
  private readonly destroyRef = inject(DestroyRef);

  control = new FormControl<string>('');

  @Input()
  size: RdsSelectSearchSize = 'm';

  id = `rds-select__search-input-${uniqueId++}`;

  @ViewChild(RdsInputDirective) searchInput: RdsInputDirective;
  @ViewChild('search', { static: true, read: ElementRef }) search: ElementRef;

  @HostBinding('class')
  protected get cssClasses(): string[] {
    return [
      'rds-select__option',
      'rds-select__option--search-input',
      `rds-select__option--search-input--${this.size}`,
    ];
  }

  @Input()
  placeholder = 'Search...';

  @Output()
  changed = new EventEmitter<string>();

  @HostListener('click')
  @HostListener('focus')
  handleClick(): void {
    this.search.nativeElement.focus();
    this.renderer.addClass(this.elementRef.nativeElement, 'cdk-focused');
    this.renderer.addClass(
      this.elementRef.nativeElement,
      'cdk-keyboard-focused',
    );
    this.cdr.markForCheck();
  }

  @HostListener('keydown', ['$event'])
  stopPropagation($event: KeyboardEvent): void {
    if (
      $event.keyCode === SPACE ||
      ($event.keyCode === A && hasModifierKey($event, 'ctrlKey'))
    ) {
      $event.stopPropagation();
    }
  }

  rect(): DOMRect {
    return this.elementRef.nativeElement.getBoundingClientRect();
  }

  isVisible(): boolean {
    return true;
  }

  ngAfterViewInit(): void {
    if (this.searchInput && this.searchInput.ngControl) {
      this.searchInput.ngControl.valueChanges
        ?.pipe(
          tap((v) => this.changed.emit(v)),
          takeUntilDestroyed(this.destroyRef),
        )
        .subscribe({ next: noop, error: noop });
    }

    this.focus();
  }

  reset(): void {
    this.searchInput?.ngControl?.reset();
  }

  focus(origin?: FocusOrigin, options?: FocusOptions): void {
    const element = this.elementRef.nativeElement;

    if (typeof element.focus === 'function') {
      element.focus(options);
      this.handleClick();
    }
  }

  isDirtyValue(): boolean {
    return this.control.value !== '';
  }

  resetInput(): void {
    if (this.isDirtyValue()) {
      this.control.setValue('');
      this.search.nativeElement.focus();
    }
  }
}
