import {
  AfterViewInit,
  ChangeDetectionStrategy,
  Component,
  Directive,
  HostListener,
  inject,
  OnDestroy,
  TemplateRef,
  ViewChild,
  ViewContainerRef,
  ViewEncapsulation,
} from '@angular/core';
import { TemplatePortal } from '@angular/cdk/portal';
import {
  RdsDatepickerBaseDirective,
  RdsDatepickerControl,
} from './datepicker-base';

@Directive({
  selector:
    '[rdsDatepickerApply], [rdsDateRangePickerApply], [rds-datepicker-apply], [rds-date-range-picker-apply]',
})
export class RdsDatepickerApplyDirective {
  private readonly datepicker = inject<
    RdsDatepickerBaseDirective<
      RdsDatepickerControl<Record<string, unknown>>,
      unknown
    >
  >(RdsDatepickerBaseDirective);

  @HostListener('click')
  _applySelection(): void {
    this.datepicker._applyPendingSelection();
    this.datepicker.close();
  }
}

@Directive({
  selector:
    '[rdsDatepickerCancel], [rdsDateRangePickerCancel], [rds-datepicker-cancel], [rds-date-range-picker-cancel]',
})
export class RdsDatepickerCancelDirective {
  private readonly datepicker = inject<
    RdsDatepickerBaseDirective<
      RdsDatepickerControl<Record<string, unknown>>,
      unknown
    >
  >(RdsDatepickerBaseDirective);

  @HostListener('click')
  _applySelection(): void {
    this.datepicker.close();
  }
}

@Component({
  selector: 'rds-datepicker-actions, rds-date-range-picker-actions',
  styleUrls: ['./datepicker-actions.component.scss'],
  template: `
    <ng-template>
      <div class="rds-datepicker-actions">
        <ng-content />
      </div>
    </ng-template>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class RdsDatepickerActionsComponent implements AfterViewInit, OnDestroy {
  private readonly datepicker = inject<
    RdsDatepickerBaseDirective<
      RdsDatepickerControl<Record<string, unknown>>,
      unknown
    >
  >(RdsDatepickerBaseDirective);
  private readonly viewContainerRef = inject(ViewContainerRef);

  @ViewChild(TemplateRef) _template: TemplateRef<unknown> | null = null;
  private _portal: TemplatePortal | null = null;

  ngAfterViewInit(): void {
    this._portal = new TemplatePortal(
      this._template as TemplateRef<unknown>,
      this.viewContainerRef,
    );
    this.datepicker.registerActions(this._portal);
  }

  ngOnDestroy(): void {
    this.datepicker.removeActions(this._portal as TemplatePortal);

    if (this._portal && this._portal.isAttached) {
      this._portal?.detach();
    }
  }
}
