export function throwRdsMenuMissingError(): void {
  throw Error(`rdsMenuTriggerFor: must pass in an rds-menu instance.
    Example:
      <rds-menu-component #menu="rdsMenu"></rds-menu-component>
      <button [rdsMenuTriggerFor]="menu"></button>`);
}

export function throwRdsMenuInvalidPositionX(): void {
  throw Error(`xPosition value must be either 'before' or after'.
      Example: <rds-menu xPosition="before" #menu="rdsMenu"></rds-menu>`);
}

export function throwRdsMenuInvalidPositionY(): void {
  throw Error(`yPosition value must be either 'above' or below'.
      Example: <rds-menu yPosition="above" #menu="rdsMenu"></rds-menu>`);
}

export function throwRdsMenuRecursiveError(): void {
  throw Error(
    `rdsMenuTriggerFor: menu cannot contain its own trigger. Assign a menu that is ` +
      `not a parent of the trigger or move the trigger outside of the menu.`,
  );
}
