import { ComponentFactoryResolver, ViewContainerRef } from '@angular/core';

export type RdsDialogRole = 'dialog' | 'alertdialog';
export type RdsDialogSize = 's' | 'm' | 'l' | 'xl';

// if you changed this value then adjust $rds-dialog-margin accordingly (dialog-container.component.scss)
export const DialogTotalInlineMargin = 128;
export enum DialogSizeWidth {
  xl = 1480 + DialogTotalInlineMargin,
  l = 976 + DialogTotalInlineMargin,
  m = 724 + DialogTotalInlineMargin,
  s = 472 + DialogTotalInlineMargin,
}

export class RdsDialogConfig<D> {
  ariaDescribedBy?: string;
  ariaLabel?: string;
  ariaLabelledBy?: string;
  closeOnNavigation = true;
  componentFactoryResolver?: ComponentFactoryResolver;
  data?: D | null = null;
  disableClose? = false;
  hasBackdrop? = true;
  height? = '';
  id?: string;
  maxHeight: number | string = '684px';
  minHeight?: number | string;
  panelClass: string | string[] = '';
  role: RdsDialogRole = 'dialog';
  viewContainerRef?: ViewContainerRef;
  size: RdsDialogSize = 'm';
}
