/* eslint-disable @typescript-eslint/no-non-null-assertion */
import {
  BooleanInput,
  coerceElement,
  NumberInput,
} from '@angular/cdk/coercion';
import {
  AfterContentInit,
  booleanAttribute,
  Directive,
  ElementRef,
  EventEmitter,
  inject,
  Injectable,
  Input,
  numberAttribute,
  OnDestroy,
  Output,
} from '@angular/core';
import { Observable, Observer, Subject, Subscription } from 'rxjs';
import { debounceTime } from 'rxjs/operators';

@Injectable({ providedIn: 'root' })
export class ResizeObserverFactory {
  create(callback: ResizeObserverCallback): ResizeObserver | null {
    return typeof ResizeObserver === 'undefined'
      ? null
      : new ResizeObserver(callback);
  }
}

@Injectable({ providedIn: 'root' })
export class DimensionsObserver implements OnDestroy {
  private readonly resizeObserverFactory = inject(ResizeObserverFactory);

  private _observedElements = new Map<
    Element,
    {
      observer: ResizeObserver | null;
      readonly stream: Subject<ResizeObserverEntry[]>;
      count: number;
    }
  >();

  ngOnDestroy(): void {
    this._observedElements.forEach((_, element) =>
      this._cleanupObserver(element),
    );
  }

  observe(element: Element): Observable<ResizeObserverEntry[]>;
  observe(element: ElementRef<Element>): Observable<ResizeObserverEntry[]>;
  observe(
    elementOrRef: Element | ElementRef<Element>,
  ): Observable<ResizeObserverEntry[]> {
    const element = coerceElement(elementOrRef);

    return new Observable((observer: Observer<ResizeObserverEntry[]>) => {
      const stream = this._observeElement(element);
      const subscription = stream.subscribe(observer);

      return () => {
        subscription.unsubscribe();
        this._unobserveElement(element);
      };
    });
  }

  private _observeElement(element: Element): Subject<ResizeObserverEntry[]> {
    if (!this._observedElements.has(element)) {
      const stream = new Subject<ResizeObserverEntry[]>();
      const observer = this.resizeObserverFactory.create((mutations) =>
        stream.next(mutations),
      );

      if (observer) {
        observer.observe(element);
      }

      this._observedElements.set(element, { observer, stream, count: 1 });
    } else {
      this._observedElements.get(element)!.count++;
    }

    return this._observedElements.get(element)!.stream;
  }

  private _unobserveElement(element: Element): void {
    if (this._observedElements.has(element)) {
      this._observedElements.get(element)!.count--;

      if (!this._observedElements.get(element)!.count) {
        this._cleanupObserver(element);
      }
    }
  }

  private _cleanupObserver(element: Element): void {
    if (this._observedElements.has(element)) {
      const { observer, stream } = this._observedElements.get(element)!;

      if (observer) {
        observer.disconnect();
      }

      stream.complete();
      this._observedElements.delete(element);
    }
  }
}

/**
 * Directive that triggers a callback whenever the content of
 * its associated element has changed.
 */
@Directive({
  selector: '[rdsObserveResize]',
  exportAs: 'rdsObserveResize',
  standalone: true,
})
export class RdsObserveResizeDirective implements AfterContentInit, OnDestroy {
  private readonly dimensionsObserver = inject(DimensionsObserver);
  private readonly elementRef = inject<ElementRef<HTMLElement>>(ElementRef);

  @Output()
  readonly rdsObserveResize = new EventEmitter<ResizeObserverEntry[]>();

  @Input('rdsObserveResizeDisabled')
  set disabled(value: BooleanInput) {
    this._disabled = booleanAttribute(value);
    this._disabled ? this._unsubscribe() : this._subscribe();
  }

  get disabled(): boolean {
    return this._disabled;
  }

  private _disabled = false;

  @Input()
  set debounce(value: NumberInput) {
    this._debounce = numberAttribute(value);
    this._subscribe();
  }

  get debounce(): number {
    return this._debounce;
  }

  private _debounce: number;

  private _currentSubscription: Subscription | null = null;

  ngAfterContentInit(): void {
    if (!this._currentSubscription && !this.disabled) {
      this._subscribe();
    }
  }

  ngOnDestroy(): void {
    this._unsubscribe();
  }

  private _subscribe(): void {
    this._unsubscribe();
    const stream = this.dimensionsObserver.observe(this.elementRef);

    this._currentSubscription = (
      this.debounce ? stream.pipe(debounceTime(this.debounce)) : stream
    ).subscribe(this.rdsObserveResize);
  }

  private _unsubscribe(): void {
    this._currentSubscription?.unsubscribe();
  }
}
