import { BooleanInput } from '@angular/cdk/coercion';
import { DOWN_ARROW } from '@angular/cdk/keycodes';
import {
  AfterViewInit,
  booleanAttribute,
  Directive,
  ElementRef,
  EventEmitter,
  inject,
  Input,
  isDevMode,
  OnChanges,
  OnDestroy,
  Output,
  SimpleChanges,
} from '@angular/core';
import {
  AbstractControl,
  ControlValueAccessor,
  ValidationErrors,
  Validator,
  ValidatorFn,
} from '@angular/forms';
import { DateAdapter, RDS_DATE_FORMATS, RdsDateFormats } from './adapter';
import { noop, Subject, Subscription } from 'rxjs';
import { createMissingDateImplError } from './datepicker-errors';
import {
  DateSelectionModelChange,
  ExtractDateTypeFromSelection,
  RdsDateSelectionModel,
} from './date-selection.model';

export class RdsDatepickerInputEvent<D, S = unknown> {
  value: D | null;

  constructor(
    public readonly target: RdsDatepickerInputBaseDirective<S, D>,
    public readonly targetElement: HTMLElement,
  ) {
    this.value = this.target.value;
  }
}

export type DateFilterFn<D> = (date: D | null) => boolean;

@Directive()
export abstract class RdsDatepickerInputBaseDirective<
    S,
    D = ExtractDateTypeFromSelection<S>,
  >
  implements
    ControlValueAccessor,
    AfterViewInit,
    OnChanges,
    OnDestroy,
    Validator
{
  protected readonly elementRef =
    inject<ElementRef<HTMLInputElement>>(ElementRef);
  public readonly dateAdapter = inject<DateAdapter<D>>(DateAdapter, {
    optional: true,
  }) as DateAdapter<D>;
  protected readonly dateFormats = inject(RDS_DATE_FORMATS, {
    optional: true,
  }) as RdsDateFormats;
  private _isInitialized = false;

  @Input()
  get value(): D | null {
    return this._model
      ? this._getValueFromModel(this._model.selection)
      : this._pendingValue;
  }

  set value(value: D | null) {
    this._assignValueProgrammatically(value);
  }

  protected _model: RdsDateSelectionModel<S, D> | undefined;

  @Input()
  set disabled(value: BooleanInput) {
    const newValue = booleanAttribute(value);
    const element = this.elementRef.nativeElement;

    if (this._disabled !== newValue) {
      this._disabled = newValue;
      this.stateChanges.next();
    }

    if (newValue && this._isInitialized && element.blur) {
      element.blur();
    }
  }

  get disabled(): boolean {
    return this._disabled || this._parentDisabled();
  }

  private _disabled?: boolean;
  private _valueChangesSubscription = Subscription.EMPTY;
  private _localeSubscription = Subscription.EMPTY;

  @Output() readonly dateChange = new EventEmitter<
    RdsDatepickerInputEvent<D, S>
  >();

  @Output() readonly dateInput = new EventEmitter<
    RdsDatepickerInputEvent<D, S>
  >();

  stateChanges = new Subject<void>();

  _onTouched = noop;
  _validatorOnChange = noop;
  private _cvaOnChange: (value: D | null) => void = noop;
  private _pendingValue: D | null = null;
  protected _lastValueValid = false;

  private _parseValidator: ValidatorFn = (): ValidationErrors | null => {
    return this._lastValueValid
      ? null
      : { rdsDatepickerParse: { text: this.elementRef.nativeElement.value } };
  };

  private _filterValidator: ValidatorFn = (
    control: AbstractControl,
  ): ValidationErrors | null => {
    const controlValue = this.dateAdapter.getValidDateOrNull(
      this.dateAdapter.deserialize(control.value),
    );

    return !controlValue || this._matchesFilter(controlValue)
      ? null
      : { rdsDatepickerFilter: true };
  };

  private _minValidator: ValidatorFn = (
    control: AbstractControl,
  ): ValidationErrors | null => {
    const controlValue = this.dateAdapter.getValidDateOrNull(
      this.dateAdapter.deserialize(control.value),
    );
    const min = this._getMinDate();

    return !min ||
      !controlValue ||
      this.dateAdapter.compareDate(min, controlValue) <= 0
      ? null
      : { rdsDatepickerMin: { min: min, actual: controlValue } };
  };

  private _maxValidator: ValidatorFn = (
    control: AbstractControl,
  ): ValidationErrors | null => {
    const controlValue = this.dateAdapter.getValidDateOrNull(
      this.dateAdapter.deserialize(control.value),
    );
    const max = this._getMaxDate();

    return !max ||
      !controlValue ||
      this.dateAdapter.compareDate(max, controlValue) >= 0
      ? null
      : { rdsDatepickerMax: { max: max, actual: controlValue } };
  };

  protected _getValidators(): ValidatorFn[] {
    return [
      this._parseValidator,
      this._minValidator,
      this._maxValidator,
      this._filterValidator,
    ];
  }

  abstract _getMinDate(): D | null;

  abstract _getMaxDate(): D | null;

  protected abstract _getDateFilter(): DateFilterFn<D> | undefined;

  _registerModel(model: RdsDateSelectionModel<S, D>): void {
    this._model = model;
    this._valueChangesSubscription.unsubscribe();

    if (this._pendingValue) {
      this._assignValue(this._pendingValue);
    }

    this._valueChangesSubscription = this._model.selectionChanged.subscribe(
      (event) => {
        if (this._shouldHandleChangeEvent(event)) {
          const value = this._getValueFromModel(event.selection);
          this._lastValueValid = this._isValidValue(value);
          this._cvaOnChange(value);
          this._onTouched();
          this._formatValue(value);
          this.dateInput.emit(
            new RdsDatepickerInputEvent(this, this.elementRef.nativeElement),
          );
          this.dateChange.emit(
            new RdsDatepickerInputEvent(this, this.elementRef.nativeElement),
          );
        }
      },
    );
  }

  protected abstract _openPopup(): void;

  protected abstract _assignValueToModel(model: D | null): void;

  protected abstract _getValueFromModel(modelValue: S | null): D | null;

  protected abstract _validator: ValidatorFn | null;

  protected abstract _shouldHandleChangeEvent(
    event: DateSelectionModelChange<S>,
  ): boolean;

  protected constructor() {
    if (!this.dateAdapter && isDevMode()) {
      throw createMissingDateImplError('DateAdapter');
    }

    if (!this.dateFormats && isDevMode()) {
      throw createMissingDateImplError('RDS_DATE_FORMATS');
    }

    this._localeSubscription = this.dateAdapter.localeChanges.subscribe(() => {
      this._assignValueProgrammatically(this.value);
    });
  }

  ngAfterViewInit(): void {
    this._isInitialized = true;
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (dateInputsHaveChanged(changes, this.dateAdapter)) {
      this.stateChanges.next(undefined);
    }
  }

  ngOnDestroy(): void {
    this._valueChangesSubscription.unsubscribe();
    this._localeSubscription.unsubscribe();
    this.stateChanges.complete();
  }

  registerOnValidatorChange(fn: () => void): void {
    this._validatorOnChange = fn;
  }

  validate(c: AbstractControl): ValidationErrors | null {
    return this._validator ? this._validator(c) : null;
  }

  writeValue(value: D): void {
    this._assignValueProgrammatically(value);
  }

  registerOnChange(fn: (value: D | null) => void): void {
    this._cvaOnChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this._onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  _onKeydown(event: KeyboardEvent): void {
    const isAltDownArrow = event.altKey && event.keyCode === DOWN_ARROW;

    if (isAltDownArrow && !this.elementRef.nativeElement.readOnly) {
      this._openPopup();
      event.preventDefault();
    }
  }

  _onInput(value: string): void {
    const lastValueWasValid = this._lastValueValid;
    let date = this.dateAdapter.parse(value, this.dateFormats.parse.dateInput);
    this._lastValueValid = this._isValidValue(date);
    date = this.dateAdapter.getValidDateOrNull(date);

    if (!this.dateAdapter.sameDate(date, this.value)) {
      this._assignValue(date);
      this._cvaOnChange(date);
      this.dateInput.emit(
        new RdsDatepickerInputEvent(this, this.elementRef.nativeElement),
      );
    } else {
      // Call the CVA change handler for invalid values
      // since this is what marks the control as dirty.
      if (value && !this.value) {
        this._cvaOnChange(date);
      }

      if (lastValueWasValid !== this._lastValueValid) {
        this._validatorOnChange();
      }
    }
  }

  _onChange(): void {
    this.dateChange.emit(
      new RdsDatepickerInputEvent(this, this.elementRef.nativeElement),
    );
  }

  _onBlur(): void {
    if (this.value) {
      this._formatValue(this.value);
    }

    this._onTouched();
  }

  protected _formatValue(value: D | null): void {
    this.elementRef.nativeElement.value = value
      ? this.dateAdapter.format(value, this.dateFormats.display.dateInput)
      : '';
  }

  private _assignValue(value: D | null): void {
    if (this._model) {
      this._assignValueToModel(value);
      this._pendingValue = null;
    } else {
      this._pendingValue = value;
    }
  }

  private _isValidValue(value: D | null): boolean {
    return !value || this.dateAdapter.isValid(value);
  }

  protected _parentDisabled(): boolean {
    return false;
  }

  protected _assignValueProgrammatically(value: D | null): void {
    value = this.dateAdapter.deserialize(value);
    this._lastValueValid = this._isValidValue(value);
    value = this.dateAdapter.getValidDateOrNull(value);
    this._assignValue(value);
    this._formatValue(value);
  }

  _matchesFilter(value: D | null): boolean {
    const filter = this._getDateFilter();

    return !filter || filter(value);
  }
}

export function dateInputsHaveChanged<D>(
  changes: SimpleChanges,
  adapter: DateAdapter<D>,
): boolean {
  const keys = Object.keys(changes);

  for (const key of keys) {
    const { previousValue, currentValue } = changes[key];

    if (
      adapter.isDateInstance(previousValue) &&
      adapter.isDateInstance(currentValue)
    ) {
      if (!adapter.sameDate(previousValue, currentValue)) {
        return true;
      }
    } else {
      return true;
    }
  }

  return false;
}
