import { DOCUMENT } from '@angular/common';
import {
  ApplicationRef,
  ComponentFactoryResolver,
  inject,
  Injectable,
} from '@angular/core';

import { RdsDomPortalHost } from '../portal/rds-dom-portal-host';
import { RdsToastContainerDirective } from '../rds-toast-container.directive';
import { RdsToastOverlayContainerService } from './rds-toast-overlay-container.service';
import { RdsToastOverlayRef } from './rds-toast-overlay-ref';
import { RdsToastModule } from '../rds-toast.module';
import { RdsToastPosition } from '../rds-toast.config';

@Injectable({
  providedIn: RdsToastModule,
})
export class RdsToastOverlayService {
  private readonly toastOverlayContainerService = inject(
    RdsToastOverlayContainerService,
  );
  private readonly componentFactoryResolver = inject(ComponentFactoryResolver);
  private readonly applicationRef = inject(ApplicationRef);
  private readonly document = inject(DOCUMENT);

  private readonly _paneElements: Map<
    RdsToastContainerDirective,
    Record<string, HTMLElement>
  > = new Map();

  create<T>(
    position: RdsToastPosition,
    overlayContainer?: RdsToastContainerDirective,
  ): RdsToastOverlayRef<T> {
    return this._createOverlayRef<T>(
      this._getPaneElement(position, overlayContainer),
    );
  }

  private _getPaneElement(
    positionClass: RdsToastPosition,
    overlayContainer?: RdsToastContainerDirective,
  ): HTMLElement {
    const posClass = `rds-toast-${positionClass}`;

    if (!this._paneElements.get(<RdsToastContainerDirective>overlayContainer)) {
      this._paneElements.set(<RdsToastContainerDirective>overlayContainer, {});
    }

    const paneElement = this._paneElements.get(
      <RdsToastContainerDirective>overlayContainer,
    ) as Record<string, HTMLElement>;

    if (!paneElement[posClass]) {
      paneElement[posClass] = this._createPaneElement(
        posClass,
        overlayContainer,
      );
    }

    return paneElement[posClass];
  }

  private _createPaneElement(
    positionClass: string,
    overlayContainer?: RdsToastContainerDirective,
  ): HTMLElement {
    const pane = this.document.createElement('div');

    pane.id = 'toast-container';
    pane.classList.add(positionClass);
    pane.classList.add('rds-toast-container');

    if (!overlayContainer) {
      this.toastOverlayContainerService.getContainerElement().appendChild(pane);
    } else {
      overlayContainer.getContainerElement().appendChild(pane);
    }

    return pane;
  }

  private _createPortalHost<T>(pane: HTMLElement): RdsDomPortalHost<T> {
    return new RdsDomPortalHost<T>(
      pane,
      this.componentFactoryResolver,
      this.applicationRef,
    );
  }

  private _createOverlayRef<T>(pane: HTMLElement): RdsToastOverlayRef<T> {
    return new RdsToastOverlayRef(this._createPortalHost(pane));
  }
}
