import { Observable, Subject } from 'rxjs';

export type AllSelectedType = 'none' | 'partial' | 'all';

export class AsyncSelectionModel<T> {
  protected _length = -1;
  protected _isAllSelected = false;
  protected _removedToEmit: T[] = [];
  protected _addedToEmit: T[] = [];
  protected _selected: T[] = [];
  protected _deselected: T[] = [];
  protected readonly _changed = new Subject<AsyncSelectionChange<T>>();

  get changed(): Observable<AsyncSelectionChange<T>> {
    return this._changed.asObservable();
  }

  get length(): number {
    return this._length;
  }

  protected _compareFunc: (a: T, b: T) => boolean = (a, b) => a === b;

  constructor(
    length: number,
    protected _multiple = false,
    compareFunc: (a: T, b: T) => boolean = (a, b) => a === b,
    initiallySelectedValues?: T[],
    initiallyDeselectedValues?: T[],
    protected _emitChanges = true,
  ) {
    this._length = length;

    if (initiallySelectedValues && initiallySelectedValues.length) {
      if (_multiple) {
        initiallySelectedValues.forEach((value) => this._markSelected(value));
      } else {
        this._markSelected(initiallySelectedValues[0]);
      }

      this._addedToEmit.length = 0;
    }

    if (initiallyDeselectedValues && initiallyDeselectedValues.length) {
      initiallyDeselectedValues.forEach((value) => this._markDeselected(value));
      this._removedToEmit.length = 0;
    }

    if (typeof compareFunc === 'function') {
      this._compareFunc = compareFunc;
    }
  }

  updateLength(length: number): void {
    this._length = length;
    this._checkIfAllSelected();
  }

  isAllSelected(): boolean {
    return this._isAllSelected || this._length === 0;
  }

  getAllSelectedType(): AllSelectedType {
    if (this.isAllSelected() && this._deselected.length === 0) {
      return 'all';
    } else if (!this.isAllSelected() && this._selected.length === 0) {
      return 'none';
    } else {
      return 'partial';
    }
  }

  selectAll(): void {
    if (this._length > 1 && !this._multiple) {
      throw getMultipleValuesInSingleSelectionError();
    }

    this._isAllSelected = true;
    this._deselected = [];
    this._removedToEmit = [];
    this._selected = [];
    this._addedToEmit = [];
    this._emitChangeEvent();
  }

  deselectAll(): void {
    this._isAllSelected = false;
    this._deselected = [];
    this._removedToEmit = [];
    this._selected = [];
    this._addedToEmit = [];
    this._emitChangeEvent();
  }

  toggleAll(): void {
    this.isAllSelected() ? this.deselectAll() : this.selectAll();
  }

  clear(): void {
    this._isAllSelected = false;
    this._deselected = [];
    this._removedToEmit = [];
    this._selected = [];
    this._addedToEmit = [];
    this._emitChangeEvent();
  }

  select(...values: T[]): void {
    this._verifyValueAssignment(values);
    values.forEach((value) => this._markSelected(value));
    this._checkIfAllSelected();
    this._emitChangeEvent();
  }

  deselect(...values: T[]): void {
    this._verifyValueAssignment(values);
    values.forEach((value) => this._markDeselected(value));
    this._checkIfAllSelected();
    this._emitChangeEvent();
  }

  toggle(value: T): void {
    this.isSelected(value) ? this.deselect(value) : this.select(value);
  }

  isSelected(value: T): boolean {
    return this.isAllSelected()
      ? !this._deselected.some((v) => this._compareFunc(v, value))
      : this._selected.some((v) => this._compareFunc(v, value));
  }

  isDeselected(value: T): boolean {
    return !this.isSelected(value);
  }

  isEmpty(): boolean {
    if (this._length === 0) {
      return true;
    }

    return (
      (!this.isAllSelected() && this._selected.length === 0) ||
      (this.isAllSelected() && this._deselected.length === this._length)
    );
  }

  hasValue(): boolean {
    return !this.isEmpty();
  }

  sort(predicate?: (a: T, b: T) => number): void {
    if (this._multiple) {
      if (this._selected) {
        this._selected.sort(predicate);
      }
    }

    if (this._deselected) {
      this._deselected.sort(predicate);
    }
  }

  isMultipleSelection(): boolean {
    return this._multiple;
  }

  protected _emitChangeEvent(): void {
    this._changed.next({
      source: this,
      isAllSelected: this.isAllSelected(),
      allSelectedType: this.getAllSelectedType(),
      added: this._addedToEmit,
      removed: this._removedToEmit,
    });

    this._removedToEmit = [];
    this._addedToEmit = [];
  }

  protected _markSelected(value: T): void {
    if (this.isDeselected(value)) {
      this._unmarkDeselected(value);

      if (!this._multiple) {
        this._unmarkAll();
      }

      this._selected.push(value);

      if (this._emitChanges) {
        this.pushIfNotExists(this._addedToEmit, value);
      }
    }
  }

  protected _unmarkSelected(value: T): void {
    if (this.isSelected(value)) {
      const index = this._selected.findIndex((v) =>
        this._compareFunc(v, value),
      );

      if (index > -1) {
        this._selected.splice(index, 1);

        if (this._emitChanges) {
          this.pushIfNotExists(this._removedToEmit, value);
        }
      }
    }
  }

  protected _markDeselected(value: T): void {
    if (this.isSelected(value)) {
      this._unmarkSelected(value);
      const index = this._deselected.findIndex((v) =>
        this._compareFunc(v, value),
      );

      if (index === -1) {
        this._deselected.push(value);

        if (this._emitChanges) {
          this.pushIfNotExists(this._removedToEmit, value);
        }
      }
    }
  }

  protected _unmarkDeselected(value: T): void {
    if (this.isDeselected(value)) {
      const index = this._deselected.findIndex((v) =>
        this._compareFunc(v, value),
      );

      if (index > -1) {
        this._deselected.splice(index, 1);

        if (this._emitChanges) {
          this.pushIfNotExists(this._addedToEmit, value);
        }
      }
    }
  }

  protected _unmarkAll(): void {
    if (!this.isEmpty()) {
      this._selected.forEach((value) => this._unmarkSelected(value));
    }
  }

  protected _verifyValueAssignment(values: T[]): void {
    if (values.length > 1 && !this._multiple) {
      throw getMultipleValuesInSingleSelectionError();
    }
  }

  protected _checkIfAllSelected(): void {
    if (this._length === this._selected.length) {
      this._isAllSelected = true;
      this._removedToEmit = this._deselected;
      this._deselected = [];
    } else if (this._length === this._deselected.length) {
      this._isAllSelected = false;
      this._addedToEmit = [];
      this._selected = [];
    }
  }

  protected pushIfNotExists(array: T[], value: T): void {
    if (array.every((v) => !this._compareFunc(v, value))) {
      array.push(value);
    }
  }
}

export interface AsyncSelectionChange<T> {
  source: AsyncSelectionModel<T>;
  isAllSelected: boolean;
  allSelectedType: AllSelectedType;
  added: T[];
  removed: T[];
}

export function getMultipleValuesInSingleSelectionError(): Error {
  return Error(
    'Cannot pass multiple values into AsyncSelectionModel with single-value mode.',
  );
}

export function isEqual<T>(...property: (keyof T)[]): (a: T, b: T) => boolean {
  if (!property || (Array.isArray(property) && property.length === 0)) {
    throw new Error('Property needs to be defined to compare function');
  }

  return (a: T, b: T) => {
    for (let i = 0; i < property.length; i++) {
      if (a[property[i]] === b[property[i]]) {
        continue;
      }

      return false;
    }

    return true;
  };
}
