import {
  AfterViewInit,
  ChangeDetectionStrategy,
  Component,
  DestroyRef,
  ElementRef,
  HostBinding,
  inject,
  Input,
  OnChanges,
  Output,
  SimpleChanges,
} from '@angular/core';
import { BehaviorSubject, fromEvent } from 'rxjs';
import { distinctUntilChanged, startWith } from 'rxjs/operators';
import { BooleanInput, coerceBooleanProperty } from '@angular/cdk/coercion';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

const COUNT_BADGE_MIN_WIDTH = 48;

@Component({
  selector: '[rds-trigger-values]',
  template: '',
  changeDetection: ChangeDetectionStrategy.OnPush,
  exportAs: 'rdsTriggerValues',
})
export class RdsTriggerValuesComponent implements AfterViewInit, OnChanges {
  private readonly destroyRef = inject(DestroyRef);
  private readonly elementRef = inject(ElementRef);
  private readonly _hiddenValues = new BehaviorSubject(0);

  @Output()
  readonly hiddenValues = this._hiddenValues.pipe(distinctUntilChanged());

  @Input()
  @HostBinding('title')
  get triggerValues(): string[] {
    return this._triggerValues;
  }

  set triggerValues(val: string[]) {
    if (this.hasDifferentValues(val)) {
      this._triggerValues = val;
      this.countVisibleCharacters();
    }
  }

  @Input() wrapperElement: HTMLElement | null;

  private _multiple = false;

  @Input()
  set multiple(multiple: BooleanInput) {
    this._multiple = coerceBooleanProperty(multiple);
  }

  get multiple(): boolean {
    return this._multiple;
  }

  private _triggerValues = [' '];

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['wrapperElement'] || changes['multiple']) {
      this.countVisibleCharacters();
    }
  }

  ngAfterViewInit(): void {
    fromEvent(window, 'resize')
      .pipe(startWith(null), takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this.countVisibleCharacters());
  }

  private countVisibleCharacters(): void {
    if (this.multiple) {
      if (
        this.triggerValues.length === 0 ||
        (this.triggerValues.length === 1 && this.triggerValues[0] === ' ')
      ) {
        this.elementRef.nativeElement.innerHTML = ' ';
        this._hiddenValues.next(0);
      }

      let visibleValues = 0;

      while (visibleValues < this.triggerValues.length) {
        this.elementRef.nativeElement.innerHTML = this.triggerValues
          .slice(0, visibleValues + 1)
          .join('; ');

        if (
          this.elementRef.nativeElement.offsetWidth >= this.getValuesMaxWidth()
        ) {
          break;
        }

        visibleValues++;
      }

      this._hiddenValues.next(this.triggerValues.length - visibleValues - 1);
    } else {
      this.elementRef.nativeElement.innerHTML = this.triggerValues[0];
      this._hiddenValues.next(0);
    }
  }

  private getValuesMaxWidth(): number {
    const wrapperWidth = this.wrapperElement?.offsetWidth || 0;
    const badgeWidth =
      (
        this.wrapperElement?.getElementsByClassName(
          'rds-form-field__select-hidden-trigger-values-placeholder',
        )[0] as HTMLElement | null
      )?.offsetWidth || 0;

    return Math.max(
      wrapperWidth - Math.max(badgeWidth, COUNT_BADGE_MIN_WIDTH),
      0,
    );
  }

  // provided arrays are sorted
  private hasDifferentValues(newValue: string[]): boolean {
    return (
      this._triggerValues.length !== newValue.length ||
      this._triggerValues.some((val, i) => val !== newValue[i])
    );
  }
}
