import { Directive, ElementRef, inject, Input } from '@angular/core';
import { RdsThemeService } from './theme.service';
import { RdsThemeName } from '@rds/core';

@Directive({
  selector: '[rds-theme], [rdsTheme]',
  standalone: true,
  providers: [RdsThemeService],
})
export class RdsThemeDirective<T extends string = RdsThemeName> {
  private readonly themeService = inject<RdsThemeService<T>>(RdsThemeService);
  private readonly nativeElement =
    inject<ElementRef<Element>>(ElementRef).nativeElement;

  @Input('rds-theme') set theme(theme: T) {
    if (!!theme && this.themeService.get(this.nativeElement) !== theme) {
      this._theme = theme;
      this.themeService.set(this.theme, this.nativeElement);
    }
  }

  get theme(): T {
    return this._theme;
  }

  private _theme: T;
}
