import {
  inject,
  Injectable,
  Injector,
  OnDestroy,
  StaticProvider,
  TemplateRef,
  ViewContainerRef,
} from '@angular/core';
import { DialogSizeWidth, RdsDialogConfig, RdsDialogRef } from './model';
import { defer, Observable, Subject } from 'rxjs';
import {
  ComponentType,
  Overlay,
  OverlayConfig,
  OverlayRef,
  ScrollStrategy,
  ScrollStrategyOptions,
} from '@angular/cdk/overlay';
import { startWith } from 'rxjs/operators';
import { RdsDialogContainerComponent } from './dialog-container.component';
import { ComponentPortal, TemplatePortal } from '@angular/cdk/portal';
import { RDS_DIALOG_DATA } from './tokens';

@Injectable({ providedIn: 'root' })
export class RdsDialogService implements OnDestroy {
  private readonly overlay = inject(Overlay);
  private readonly injector = inject(Injector);
  private readonly scrollStrategyOptions = inject(ScrollStrategyOptions);
  private readonly parentDialogService = inject(RdsDialogService, {
    optional: true,
    skipSelf: true,
  });

  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  private dialogsOpenAtCurrentLevel: RdsDialogRef<any>[] = [];
  private readonly afterAllClosedAtCurrentLevel = new Subject<void>();
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  private readonly afterOpenedAtCurrentLevel = new Subject<RdsDialogRef<any>>();
  private readonly scrollStrategy: () => ScrollStrategy = () =>
    this.scrollStrategyOptions.block();

  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  get openDialogs(): RdsDialogRef<any>[] {
    return this.parentDialogService
      ? this.parentDialogService.openDialogs
      : this.dialogsOpenAtCurrentLevel;
  }

  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  get afterOpened(): Subject<RdsDialogRef<any>> {
    return this.parentDialogService
      ? this.parentDialogService.afterOpened
      : this.afterOpenedAtCurrentLevel;
  }

  get _afterAllClosed(): Subject<void> {
    const parent = this.parentDialogService;

    return parent ? parent._afterAllClosed : this.afterAllClosedAtCurrentLevel;
  }

  readonly afterAllClosed: Observable<void> = defer(() =>
    this.openDialogs.length
      ? this._afterAllClosed
      : this._afterAllClosed.pipe(startWith([undefined])),
  ) as Observable<void>;

  ngOnDestroy(): void {
    this.closeDialogs(this.dialogsOpenAtCurrentLevel);
    this.afterAllClosedAtCurrentLevel.complete();
    this.afterOpenedAtCurrentLevel.complete();
  }

  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  open<T, D = any, R = any>(
    componentOrTemplateRef: ComponentType<T> | TemplateRef<T>,
    dialogConfig?: Partial<RdsDialogConfig<D>>,
  ): RdsDialogRef<T, D, R> {
    const mergedDialogConfig =
      this.mergeDialogConfigWithDefaultDialogConfigValues(dialogConfig);

    if (mergedDialogConfig.id && this.getDialogById(mergedDialogConfig.id)) {
      throw Error(
        `Dialog with id "${mergedDialogConfig.id}" exists already. The dialog id must be unique.`,
      );
    }

    const overlayRef = this.createOverlayRef(mergedDialogConfig);
    const dialogContainer = this.attachDialogContainer(
      overlayRef,
      mergedDialogConfig,
    );
    const dialogRef = this.attachDialogContent<T, D, R>(
      componentOrTemplateRef,
      dialogContainer,
      overlayRef,
      mergedDialogConfig,
    );

    this.openDialogs.push(dialogRef);
    dialogRef.afterClosed().subscribe(() => this.removeDialogRef(dialogRef));
    this.afterOpened.next(dialogRef);

    return dialogRef;
  }

  closeAll(): void {
    this.closeDialogs(this.openDialogs);
  }

  private createOverlayRef<T>(dialogConfig: RdsDialogConfig<T>): OverlayRef {
    const overlayConfig = this.buildOverlayConfig({
      panelClass: dialogConfig.panelClass,
      hasBackdrop: dialogConfig.hasBackdrop,
      size: dialogConfig.size,
      minHeight: dialogConfig.minHeight,
      maxHeight: dialogConfig.maxHeight,
      closeOnNavigation: dialogConfig.closeOnNavigation,
      role: dialogConfig.role,
    });

    return this.overlay.create(overlayConfig);
  }

  private getDialogById<T>(id: string): RdsDialogRef<T> | undefined {
    return this.openDialogs.find((dialog) => dialog.id === id);
  }

  private buildOverlayConfig<D>({
    panelClass,
    hasBackdrop,
    size,
    minHeight,
    maxHeight,
    closeOnNavigation,
  }: RdsDialogConfig<D>): OverlayConfig {
    return new OverlayConfig({
      positionStrategy: this.overlay.position().global(),
      scrollStrategy: this.scrollStrategy(),
      panelClass,
      hasBackdrop,
      minWidth: '0px',
      maxWidth: `${DialogSizeWidth[size]}px`,
      minHeight,
      maxHeight,
      disposeOnNavigation: closeOnNavigation,
    });
  }

  private attachDialogContainer<D>(
    overlayRef: OverlayRef,
    dialogConfig: RdsDialogConfig<D>,
  ): RdsDialogContainerComponent<D> {
    const userInjector =
      dialogConfig.viewContainerRef && dialogConfig.viewContainerRef.injector;
    const injector = Injector.create({
      parent: userInjector || this.injector,
      providers: [{ provide: RdsDialogConfig, useValue: dialogConfig }],
    });

    const containerPortal = new ComponentPortal<RdsDialogContainerComponent<D>>(
      RdsDialogContainerComponent,
      dialogConfig.viewContainerRef,
      injector,
      dialogConfig.componentFactoryResolver,
    );
    const containerRef =
      overlayRef.attach<RdsDialogContainerComponent<D>>(containerPortal);

    return containerRef.instance;
  }

  private attachDialogContent<T, D, R>(
    componentOrTemplateRef: ComponentType<T> | TemplateRef<T>,
    dialogContainer: RdsDialogContainerComponent<D>,
    overlayRef: OverlayRef,
    dialogConfig: RdsDialogConfig<D>,
  ): RdsDialogRef<T, D, R> {
    const dialogRef = new RdsDialogRef<T, D, R>(
      overlayRef,
      dialogContainer,
      dialogConfig.id,
    );

    if (componentOrTemplateRef instanceof TemplateRef) {
      dialogContainer.attachTemplatePortal(
        new TemplatePortal<T>(
          componentOrTemplateRef,
          null as unknown as ViewContainerRef,
          /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
          { $implicit: dialogConfig.data, dialogRef } as any,
        ),
      );
    } else {
      const injector = this.createInjector<T, D>(
        dialogConfig,
        dialogRef,
        dialogContainer,
      );
      const contentRef = dialogContainer.attachComponentPortal<T>(
        new ComponentPortal(
          componentOrTemplateRef,
          dialogConfig.viewContainerRef,
          injector,
        ),
      );
      dialogRef.componentInstance = contentRef.instance;
    }

    dialogRef.changeSize(
      `${DialogSizeWidth[dialogConfig.size]}px`,
      dialogConfig.height,
    );
    dialogRef.centerDialog();
    dialogRef.updateOverlayPosition();

    return dialogRef;
  }

  private createInjector<T, D>(
    dialogConfig: RdsDialogConfig<D>,
    dialogRef: RdsDialogRef<T>,
    dialogContainer: RdsDialogContainerComponent<D>,
  ): Injector {
    const userInjector =
      dialogConfig.viewContainerRef && dialogConfig.viewContainerRef.injector;

    const providers: StaticProvider[] = [
      { provide: RdsDialogContainerComponent, useValue: dialogContainer },
      { provide: RDS_DIALOG_DATA, useValue: dialogConfig.data },
      { provide: RdsDialogRef, useValue: dialogRef },
    ];

    return Injector.create({
      parent: userInjector || this.injector,
      providers,
    });
  }

  private removeDialogRef<T, D, R>(dialogRef: RdsDialogRef<T, D, R>): void {
    const index = this.openDialogs.indexOf(dialogRef);

    if (index > -1) {
      this.openDialogs.splice(index, 1);
    }
  }

  private closeDialogs<T, D, R>(dialogs: RdsDialogRef<T, D, R>[]): void {
    let i = dialogs.length;

    while (i--) {
      dialogs[i].close();
    }
  }

  private mergeDialogConfigWithDefaultDialogConfigValues<D>(
    dialogConfig?: Partial<RdsDialogConfig<D>>,
  ): RdsDialogConfig<D> {
    const config = { ...new RdsDialogConfig<D>(), ...dialogConfig };

    if (!config.panelClass) {
      config.panelClass = 'rds-dialog-overlay';
    } else if (typeof config.panelClass === 'string') {
      config.panelClass = ['rds-dialog-overlay', config.panelClass];
    } else {
      config.panelClass = ['rds-dialog-overlay', ...config.panelClass];
    }

    return config;
  }
}
