import { inject, Injectable, OnDestroy, SecurityContext } from '@angular/core';
import { Observable, of } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { DomSanitizer } from '@angular/platform-browser';
import { DOCUMENT } from '@angular/common';
import { finalize, map, share, tap } from 'rxjs/operators';
import { IconLoaderErrors } from './icon-loader-errors';
import {
  RDS_ICON_LOADER_CONFIG,
  RdsIconLoaderConfig,
  RdsIconName,
  RdsIconNamespace,
} from './icon-types';
import { RDS_VERSION_AS_FILENAME_SUFFIX } from '../core';

// in case of change remember to modify create-svg-symbols.js accordingly
const getAllIconsFilename = (namespace: RdsIconNamespace): RdsIconName =>
  `_all-${namespace}` as RdsIconName;

class IconConfig {
  url = '';
  svgElement: SVGElement;

  constructor(data: string | SVGElement) {
    if (isSVGElement(data)) {
      this.svgElement = data;
    } else {
      this.url = data;
    }
  }
}

function isSVGElement(data: string | SVGElement): data is SVGElement {
  return typeof data !== 'string' && 'nodeName' in data;
}

export function getSvgHref(
  namespace: RdsIconNamespace,
  name: RdsIconName,
): string {
  return `/assets/icons/${namespace}/${name}${RDS_VERSION_AS_FILENAME_SUFFIX}.svg#${name}`;
}

/**
 * Service to load icons used by the `<rds-icon>` component.
 * Icons are get from, assets/icons/{namespace}/{iconName}
 */
@Injectable({ providedIn: 'root' })
export class RdsIconLoader implements OnDestroy {
  private readonly config = inject(RDS_ICON_LOADER_CONFIG, { optional: true });
  private readonly httpClient = inject(HttpClient, { optional: true });
  private readonly document = inject(DOCUMENT, { optional: true }) as Document;
  private readonly domSanitizer = inject(DomSanitizer);

  // Keys are of in the format namespace:icon
  private static BASE_URL = '/';
  private static BASE_URL_FETCHED = false;
  private readonly iconConfigsCache = new Map<string, IconConfig>();
  private readonly iconFetchesInProgress = new Map<
    string,
    Observable<string>
  >();
  private allIcons: { filled?: SVGElement; outlined?: SVGElement } = {};
  private preloadAll = false;

  private static _getBaseUrl(): string {
    if (!RdsIconLoader.BASE_URL_FETCHED) {
      const baseTag = document.getElementsByTagName('base')[0];

      if (!baseTag || !baseTag.href || baseTag.href === '') {
        RdsIconLoader.BASE_URL = '/';
      } else {
        RdsIconLoader.BASE_URL = baseTag.href;
      }

      RdsIconLoader.BASE_URL_FETCHED = true;
    }

    return RdsIconLoader.BASE_URL;
  }

  constructor() {
    this.handleConfig(this.config);
  }

  preloadSelectedIcons(
    selectedIcons: Record<RdsIconNamespace, RdsIconName[]>,
  ): void {
    Object.entries(selectedIcons).forEach(([namespace, icons]) => {
      for (const iconName of icons) {
        this.getSvgIcon(iconName, namespace as RdsIconNamespace).subscribe();
      }
    });
  }

  /**
   * Returns an Observable with the icon (as an `<svg>` SVGelement) for the given name
   * and namespace.
   * @param name Name of the icon to be retrieved.
   * @param namespace Namespace in which to look for the icon.
   */
  getSvgIcon(
    name: RdsIconName,
    namespace: RdsIconNamespace,
  ): Observable<SVGElement> {
    if (this.preloadAll) {
      return this.getFromAllIconsFile(name, namespace);
    }

    const iconConfig = this.getOrCreateIconConfig(name, namespace);

    return this._getSvgElementFromIconConfig(iconConfig);
  }

  ngOnDestroy(): void {
    this.iconConfigsCache.clear();
  }

  private getFromAllIconsFile(
    name: RdsIconName,
    namespace: RdsIconNamespace,
  ): Observable<SVGElement> {
    if (this.allIcons[namespace]) {
      return of(this.buildFromSymbol(name, namespace));
    }

    const iconConfig = this.getOrCreateIconConfig(name, namespace);

    return this._fetchIcon(iconConfig).pipe(
      tap((svgText) => {
        this.allIcons[namespace] = this._convertSVGStringToSvgElement(svgText);
      }),
      map(() => this.buildFromSymbol(name, namespace)),
    );
  }

  private getOrCreateIconConfig(
    name: RdsIconName,
    namespace: RdsIconNamespace,
  ): IconConfig {
    const keyName = this.preloadAll ? getAllIconsFilename(namespace) : name;
    const key = iconKey(namespace, keyName);

    return (
      this.iconConfigsCache.get(key) ||
      this._addIconConfigToCache(namespace, name)
    );
  }

  private _addIconConfigToCache(
    namespace: RdsIconNamespace,
    iconName: RdsIconName,
  ): IconConfig {
    if (!namespace || !iconName) {
      throw IconLoaderErrors.invalidNamespaceOrIconNameError(
        namespace,
        iconName,
      );
    }

    const url = this._buildIconUrl(namespace, iconName);
    const iconConfig = new IconConfig(url);

    this.iconConfigsCache.set(iconKey(namespace, iconName), iconConfig);

    return iconConfig;
  }

  private _buildIconUrl(
    namespace: RdsIconNamespace,
    iconName: RdsIconName,
  ): string {
    if (this.preloadAll) {
      iconName = getAllIconsFilename(namespace);
    }

    const safeResourceUrl = this.domSanitizer.bypassSecurityTrustResourceUrl(
      RdsIconLoader._getBaseUrl() +
        `assets/icons/${namespace}/${iconName}${RDS_VERSION_AS_FILENAME_SUFFIX}.svg`,
    );

    const url = this.domSanitizer.sanitize(
      SecurityContext.RESOURCE_URL,
      safeResourceUrl,
    );

    if (!url) {
      throw IconLoaderErrors.iconUrlFailedToSanitize(safeResourceUrl);
    }

    return url;
  }

  private _getSvgElementFromIconConfig(
    iconConfig: IconConfig,
  ): Observable<SVGElement> {
    const svgElement = iconConfig.svgElement;

    if (svgElement) {
      // We already have the SVG element for this icon, return a copy.
      return of(cloneSvg(svgElement));
    } else {
      // Fetch the icon from the iconConfig's URL, cache it, and return a copy.
      return this._fetchIcon(iconConfig).pipe(
        map((svgText) => this._buildSvgElement(svgText)),
        tap((builtSvgElement) => (iconConfig.svgElement = builtSvgElement)),
        map((builtSvgElement) => cloneSvg(builtSvgElement)),
      );
    }
  }

  private _fetchIcon(iconConfig: IconConfig): Observable<string> {
    const { url } = iconConfig;

    if (!this.httpClient) {
      throw IconLoaderErrors.noHttpProviderError();
    }

    const inProgressFetch = this.iconFetchesInProgress.get(url);

    if (inProgressFetch) {
      return inProgressFetch;
    }

    const req = this.httpClient.get(url, { responseType: 'text' }).pipe(
      finalize(() => this.iconFetchesInProgress.delete(url)),
      share(),
    );

    this.iconFetchesInProgress.set(url, req);

    return req;
  }

  private _buildSvgElement(responseText: string): SVGElement {
    const svgElement = this._convertSVGStringToSvgElement(responseText);
    this._setDefaultSvgAttributes(svgElement);

    return svgElement;
  }

  private buildFromSymbol(
    name: RdsIconName,
    namespace: RdsIconNamespace,
  ): SVGElement {
    const symbol = this.allIcons[namespace]?.querySelector(`#${name}`);
    const svgString = symbol
      ? `<svg ${symbol.outerHTML.slice(7, -9)}</svg>`
      : '<svg></svg>';

    return this._convertSVGStringToSvgElement(svgString);
  }

  private _convertSVGStringToSvgElement(svgString: string): SVGElement {
    const div = this.document.createElement('DIV');
    div.innerHTML = svgString;
    const svg = div.querySelector('svg') as SVGElement;

    if (svg) {
      return svg;
    }

    throw IconLoaderErrors.svgTagNotFound();
  }

  private _setDefaultSvgAttributes(svg: SVGElement): SVGElement {
    svg.setAttribute('fit', '');
    svg.setAttribute('height', '100%');
    svg.setAttribute('width', '100%');
    svg.setAttribute('preserveAspectRatio', 'xMidYMid meet');
    svg.setAttribute('focusable', 'false'); // Disable IE11 default behavior to make SVGs focusable.
    svg.setAttribute('viewBox', '0 0 24 24');

    return svg;
  }

  private handleConfig(config: RdsIconLoaderConfig | null): void {
    if (config?.preloadSelected) {
      config.preloadSelected === 'all'
        ? (this.preloadAll = true)
        : this.preloadSelectedIcons(config.preloadSelected);
    }
  }
}

function cloneSvg(svg: SVGElement): SVGElement {
  return svg.cloneNode(true) as SVGElement;
}

function iconKey(namespace: RdsIconNamespace, name: RdsIconName): string {
  return namespace + ':' + name;
}
