import {
  AfterContentInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  DestroyRef,
  ElementRef,
  HostBinding,
  inject,
  Input,
  OnDestroy,
  OnInit,
  QueryList,
  ViewChild,
} from '@angular/core';
import { RdsUploadSize } from './upload.types';
import { RdsFileUploader } from './uploader';
import { RdsUploadIntlService } from './rds-upload-intl.service';
import { filter, startWith } from 'rxjs/operators';
import {
  AbstractControl,
  ControlValueAccessor,
  NgControl,
  ValidationErrors,
  Validator,
} from '@angular/forms';
import { noop } from 'rxjs';
import { RdsFileItem } from './file-item.class';
import { RdsUploadErrorDirective } from './upload-error.directive';
import { HttpClient } from '@angular/common/http';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

let nextId = 0;

@Component({
  selector: 'rds-upload',
  exportAs: 'rdsUpload',
  templateUrl: './upload.component.html',
  styleUrls: ['./upload.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsUploadComponent<T = unknown>
  implements
    OnInit,
    AfterContentInit,
    OnDestroy,
    ControlValueAccessor,
    Validator
{
  protected readonly intl = inject(RdsUploadIntlService);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly destroyRef = inject(DestroyRef);
  private readonly httpClient = inject(HttpClient);
  private readonly ngControl = inject(NgControl, {
    self: true,
    optional: true,
  });

  private _uploader: RdsFileUploader<T>;

  ariaErrorMessage: string | null = null;

  onChange: (files: RdsFileItem[]) => void = noop;
  onTouched = noop;

  get hasError(): boolean {
    return !!(this.ngControl && this.ngControl.errors);
  }

  @Input() size: RdsUploadSize = 'm';
  @Input({ transform: booleanAttribute }) hasBackground = false;

  @Input()
  set uploader(uploader: RdsFileUploader<T>) {
    this._uploader = uploader;

    if (!this._uploader.isHttpClientRegistered()) {
      this._uploader.registerHttpClient(this.httpClient);
    }
  }

  get uploader(): RdsFileUploader<T> {
    return this._uploader;
  }

  @Input({ transform: booleanAttribute }) hasDropZone = false;
  @Input({ transform: booleanAttribute }) disabled = false;

  @HostBinding('class') get class(): string[] {
    return [`rds-upload-size-${this.size}`];
  }

  @ViewChild('fileInput')
  fileInput: ElementRef<HTMLInputElement>;

  @ContentChildren(RdsUploadErrorDirective)
  errors: QueryList<RdsUploadErrorDirective>;

  // can be used to transform/filter the files before they get added to the queue
  @Input()
  beforeAddFn = (files: File[]): File[] => files;

  constructor() {
    if (this.ngControl != null) {
      // Setting the value accessor directly (instead of using
      // the providers) to avoid running into a circular import.
      this.ngControl.valueAccessor = this;
    }
  }

  ngOnInit(): void {
    this.uploader.config$
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this.cdr.markForCheck());

    this.uploader.queue$
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((v) => this.onChange(v));

    if (this.ngControl) {
      // Setting the validator directly (instead of using
      // the providers) to avoid running into a circular import.
      this.ngControl.control?.addValidators((control) =>
        this.validate(control),
      );

      this.ngControl.statusChanges
        ?.pipe(
          filter(
            (status) => status === 'VALID' && !!this.uploader.config.autoUpload,
          ),
          takeUntilDestroyed(this.destroyRef),
        )
        .subscribe(() => this.uploader.uploadAll());
    }
  }

  ngAfterContentInit(): void {
    this.errors.changes
      .pipe(startWith(this.errors), takeUntilDestroyed(this.destroyRef))
      .subscribe((errors) => this.setupErrors(errors));
  }

  ngOnDestroy(): void {
    this.uploader?.destroy();
  }

  onFilesSelected(files: File[]): void {
    this.onTouched();
    this.uploader.addToQueue(this.beforeAddFn(files), [], !!this.ngControl);
  }

  writeValue(files: File[]): void {
    this.uploader.addToQueue(files);
  }

  registerOnChange(onChange: (value: RdsFileItem[]) => void): void {
    this.onChange = onChange;
  }

  registerOnTouched(onTouched: () => void): void {
    this.onTouched = onTouched;
  }

  setDisabledState(disabled: boolean): void {
    if (this.disabled !== disabled) {
      this.disabled = disabled;
      this.cdr.markForCheck();
    }
  }

  validate(control: AbstractControl<RdsFileItem[]>): ValidationErrors | null {
    if (!control.value) {
      return null;
    }

    const errors = control.value.reduce((acc, curr) => {
      if (!curr.hasFailedFilters) {
        return acc;
      }

      curr.failedFilters.forEach((failedFilter) => {
        if (acc[failedFilter.name]) {
          acc[failedFilter.name].push(curr.file.name);
        } else {
          acc[failedFilter.name] = [curr.file.name];
        }
      });

      return acc;
    }, {} as ValidationErrors);

    if (Object.keys(errors).length > 0) {
      return errors;
    }

    return null;
  }

  openDialog(): void {
    this.fileInput?.nativeElement.click();
  }

  private setupErrors(errors: QueryList<RdsUploadErrorDirective>): void {
    const errorMessageIds: string[] = [];

    errors.forEach((err) => {
      const id = `rds-upload-error-${nextId++}`;
      err.setId(id);
      errorMessageIds.push(id);
    });

    this.ariaErrorMessage = errorMessageIds.length
      ? errorMessageIds.join(',')
      : null;
  }
}
