import {
  ChangeDetectionStrategy,
  Component,
  HostBinding,
  ViewEncapsulation,
} from '@angular/core';
import { RdsLinkComponent, RdsLinkType } from '../link';

@Component({
  selector: 'rds-footer-link',
  template: `<ng-content />`,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class RdsFooterLinkComponent extends RdsLinkComponent {
  @HostBinding('class') class = 'rds-footer-link';

  override readonly type: RdsLinkType = 'secondary';
  override readonly size = 's';
  protected override _standalone = true;
}
