import { BooleanInput } from '@angular/cdk/coercion';
import {
  AfterContentInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  Component,
  ContentChild,
  DestroyRef,
  DoCheck,
  HostBinding,
  inject,
  Input,
  OnDestroy,
  ViewEncapsulation,
} from '@angular/core';
import { ControlValueAccessor } from '@angular/forms';
import { RdsAbstractFormFieldControl } from '../abstract-form-field-control';
import { RdsChipGridComponent } from '../chip-grid/chip-grid.component';
import { RdsChipInputDirective } from '../chip-input';
import { noop } from 'rxjs';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { updateErrorState } from '../../utils';

@Component({
  selector: 'rds-input-with-chips',
  template: '<ng-content />',
  styleUrls: [
    '../../chips/chips.common.scss',
    './input-with-chips.component.scss',
  ],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [
    {
      provide: RdsAbstractFormFieldControl,
      useExisting: RdsInputWithChipsComponent,
    },
  ],
})
export class RdsInputWithChipsComponent<T>
  extends RdsAbstractFormFieldControl<T[]>
  implements ControlValueAccessor, AfterContentInit, DoCheck, OnDestroy
{
  private readonly destroyRef = inject(DestroyRef);

  private _placeholder: string;
  readonly controlType = 'chip-grid';

  get shouldLabelFloat(): boolean {
    return !this.empty || this.focused;
  }

  @HostBinding('attr.aria-invalid')
  errorState = false;

  @HostBinding('attr.aria-disabled')
  get ariaDisabled(): string {
    return this.disabled.toString();
  }

  @Input()
  override set disabled(value: BooleanInput) {
    this._disabled = booleanAttribute(value);

    if (this.chipGrid) {
      this.chipGrid.disabled = this._disabled;
    }
  }

  override get disabled(): boolean {
    return this.ngControl ? !!this.ngControl.disabled : this._disabled;
  }

  @Input()
  set placeholder(value: string) {
    this._placeholder = value;
    this.stateChanges.next();
  }

  get placeholder(): string {
    return this._placeholder;
  }

  @ContentChild(RdsChipGridComponent)
  chipGrid: RdsChipGridComponent<T>;

  @ContentChild(RdsChipInputDirective)
  input: RdsChipInputDirective<T>;

  _onTouched = noop;
  _onChange: (value: T[]) => void = noop;

  constructor() {
    super();

    if (this.ngControl) {
      this.ngControl.valueAccessor = this;
    }
  }

  ngAfterContentInit(): void {
    this.input.hidePlaceholder = !this.shouldLabelFloat;
    this.chipGrid.stateChanges
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => {
        this.input.hidePlaceholder = !this.shouldLabelFloat;
        this.stateChanges.next();
      });
    this.chipGrid._onChange = this._onChange;
    this.chipGrid._onTouched = this._onTouched;
    this.chipGrid.disabled = this._disabled;
    this.input.chipGrid = this.chipGrid;
  }

  ngDoCheck(): void {
    if (this.ngControl) {
      // We need to re-evaluate this on every change detection cycle, because there are some
      // error triggers that we can't subscribe to (e.g. parent form submissions).
      const oldState = this.errorState;
      this.errorState = updateErrorState(
        this.errorState,
        this.parentFormGroup,
        this.parentForm,
        this.ngControl.control,
      );

      if (this.errorState !== oldState) {
        this.stateChanges.next();
      }
    }
  }

  ngOnDestroy(): void {
    this.stateChanges.complete();
  }

  writeValue(value: T[]): void {
    // The user is responsible for creating the child chips, so we just store the value.
    this._value = value;
  }

  registerOnChange(fn: (value: T[]) => void): void {
    this._onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this._onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;

    if (this.chipGrid) {
      this.chipGrid.setDisabledState(isDisabled);
    }

    if (this.input) {
      this.input.disabled = isDisabled;
    }

    this.stateChanges.next();
  }

  get empty(): boolean {
    return !!this.chipGrid?.empty;
  }

  override get focused(): boolean {
    return !!this.chipGrid?.focused;
  }

  onContainerClick(event: MouseEvent): void {
    this.chipGrid?.onContainerClick(event);
  }

  setDescribedByIds(ids: string[]): void {
    this.chipGrid?.setDescribedByIds(ids);
  }
}
