import {
  FactoryProvider,
  inject,
  Injectable,
  InjectionToken,
  Optional,
  SkipSelf,
} from '@angular/core';
import { DateAdapter } from './adapter';
import { DateRange } from './date-selection.model';

export const RDS_DATE_RANGE_SELECTION_STRATEGY = new InjectionToken<
  RdsDateRangeSelectionStrategy<unknown>
>('RDS_DATE_RANGE_SELECTION_STRATEGY');

export interface RdsDateRangeSelectionStrategy<D> {
  selectionFinished(
    date: D | null,
    currentRange: DateRange<D>,
    event: Event,
  ): DateRange<D>;

  createPreview(
    activeDate: D | null,
    currentRange: DateRange<D>,
    event: Event,
  ): DateRange<D>;
}

@Injectable()
export class DefaultRdsCalendarRangeStrategy<D>
  implements RdsDateRangeSelectionStrategy<D>
{
  protected readonly dateAdapter = inject<DateAdapter<D>>(DateAdapter);

  selectionFinished(date: D, currentRange: DateRange<D>): DateRange<D> {
    let { start, end } = currentRange;

    if (start == null) {
      start = date;
    } else if (
      end == null &&
      date &&
      this.dateAdapter.compareDate(date, start) >= 0
    ) {
      end = date;
    } else {
      start = date;
      end = null;
    }

    return new DateRange<D>(start, end);
  }

  createPreview(
    activeDate: D | null,
    currentRange: DateRange<D>,
  ): DateRange<D> {
    let start: D | null = null;
    let end: D | null = null;

    if (currentRange.start && !currentRange.end && activeDate) {
      start = currentRange.start;
      end = activeDate;
    }

    return new DateRange<D>(start, end);
  }
}

export function RDS_CALENDAR_RANGE_STRATEGY_PROVIDER_FACTORY(
  parent: RdsDateRangeSelectionStrategy<unknown>,
): RdsDateRangeSelectionStrategy<unknown> {
  return parent || new DefaultRdsCalendarRangeStrategy();
}

export const RDS_CALENDAR_RANGE_STRATEGY_PROVIDER: FactoryProvider = {
  provide: RDS_DATE_RANGE_SELECTION_STRATEGY,
  deps: [
    [new Optional(), new SkipSelf(), RDS_DATE_RANGE_SELECTION_STRATEGY],
    DateAdapter,
  ],
  useFactory: RDS_CALENDAR_RANGE_STRATEGY_PROVIDER_FACTORY,
};
