import {
  DOWN_ARROW,
  END,
  ENTER,
  ESCAPE,
  hasModifierKey,
  HOME,
  LEFT_ARROW,
  PAGE_DOWN,
  PAGE_UP,
  RIGHT_ARROW,
  SPACE,
  UP_ARROW,
} from '@angular/cdk/keycodes';
import {
  AfterContentInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  EventEmitter,
  inject,
  Input,
  isDevMode,
  OnChanges,
  OnDestroy,
  Output,
  SimpleChanges,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { DateAdapter, RDS_DATE_FORMATS, RdsDateFormats } from './adapter';
import { Directionality } from '@angular/cdk/bidi';
import {
  RdsCalendarBodyComponent,
  RdsCalendarCell,
  RdsCalendarCellClassFunction,
  RdsCalendarCellCssClasses,
  RdsCalendarUserEvent,
} from './calendar-body.component';
import { createMissingDateImplError } from './datepicker-errors';
import { Subscription } from 'rxjs';
import { startWith } from 'rxjs/operators';
import { DateRange } from './date-selection.model';
import {
  RDS_DATE_RANGE_SELECTION_STRATEGY,
  RdsDateRangeSelectionStrategy,
} from './date-range-selection.strategy';

const DAYS_PER_WEEK = 7;

@Component({
  selector: 'rds-month-view',
  templateUrl: './month-view.component.html',
  exportAs: 'rdsMonthView',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsMonthViewComponent<D>
  implements AfterContentInit, OnChanges, OnDestroy
{
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly dateFormats = inject(RDS_DATE_FORMATS, {
    optional: true,
  }) as RdsDateFormats;
  private readonly dateAdapter = inject(DateAdapter, {
    optional: true,
  }) as DateAdapter<D>;
  private readonly dir = inject(Directionality, { optional: true });
  private readonly rangeStrategy = inject<RdsDateRangeSelectionStrategy<D>>(
    RDS_DATE_RANGE_SELECTION_STRATEGY,
    { optional: true },
  );

  private _rerenderSubscription = Subscription.EMPTY;

  @Input()
  get activeDate(): D {
    return this._activeDate;
  }

  set activeDate(value: D) {
    const oldActiveDate = this._activeDate;
    const validDate =
      this.dateAdapter.getValidDateOrNull(
        this.dateAdapter.deserialize(value),
      ) || this.dateAdapter.today();
    this._activeDate = this.dateAdapter.clampDate(
      validDate,
      this.minDate,
      this.maxDate,
    );

    if (!this._hasSameMonthAndYear(oldActiveDate, this._activeDate)) {
      this._init();
    }
  }

  private _activeDate: D;

  @Input()
  get selected(): DateRange<D> | D | null {
    return this._selected;
  }

  set selected(value: DateRange<D> | D | null) {
    if (value instanceof DateRange) {
      this._selected = value;
    } else {
      this._selected = this.dateAdapter.getValidDateOrNull(
        this.dateAdapter.deserialize(value),
      );
    }

    this._setRanges(this._selected);
  }

  private _selected: DateRange<D> | D | null = null;

  @Input()
  get minDate(): D | null {
    return this._minDate;
  }

  set minDate(value: D | null) {
    this._minDate = this.dateAdapter.getValidDateOrNull(
      this.dateAdapter.deserialize(value),
    );
  }

  private _minDate: D | null = null;

  @Input()
  get maxDate(): D | null {
    return this._maxDate;
  }

  set maxDate(value: D | null) {
    this._maxDate = this.dateAdapter.getValidDateOrNull(
      this.dateAdapter.deserialize(value),
    );
  }

  private _maxDate: D | null = null;

  @Input() dateFilter: ((date: D) => boolean) | null = null;
  @Input() dateClass: RdsCalendarCellClassFunction<D> | null = null;
  @Input() comparisonStart: D | null = null;
  @Input() comparisonEnd: D | null = null;

  @Output() readonly selectedChange = new EventEmitter<D | null>();
  @Output() readonly _userSelection = new EventEmitter<
    RdsCalendarUserEvent<D | null>
  >();
  @Output() readonly activeDateChange = new EventEmitter<D>();

  @ViewChild(RdsCalendarBodyComponent)
  _rdsCalendarBody: RdsCalendarBodyComponent<D> | null = null;

  _monthLabel: string | null = null;
  _weeks: RdsCalendarCell<D>[][] | null = null;
  _firstWeekOffset: number | null = null;
  _rangeStart: number | null = null;
  _rangeEnd: number | null = null;
  _comparisonRangeStart: number | null = null;
  _comparisonRangeEnd: number | null = null;
  _previewStart: number | null = null;
  _previewEnd: number | null = null;
  _isRange = false;
  _todayDate: number | null = null;
  _weekdays: { long: string; narrow: string }[] | null = null;

  constructor() {
    if (!this.dateAdapter && isDevMode()) {
      throw createMissingDateImplError('DateAdapter');
    }

    if (!this.dateFormats && isDevMode()) {
      throw createMissingDateImplError('RDS_DATE_FORMATS');
    }

    this._activeDate = this.dateAdapter.today();
  }

  ngAfterContentInit(): void {
    this._rerenderSubscription = this.dateAdapter.localeChanges
      .pipe(startWith(null))
      .subscribe(() => this._init());
  }

  ngOnChanges(changes: SimpleChanges): void {
    const comparisonChange =
      changes['comparisonStart'] || changes['comparisonEnd'];

    if (comparisonChange && !comparisonChange.firstChange) {
      this._setRanges(this.selected);
    }
  }

  ngOnDestroy(): void {
    this._rerenderSubscription.unsubscribe();
  }

  _onDateSelected(event: RdsCalendarUserEvent<RdsCalendarCell<D>>): void {
    this._dateSelected({
      value: this.dateAdapter.clone(event.value.rawValue),
      event: event.event,
    });
  }

  _dateSelected(event: RdsCalendarUserEvent<D>): void {
    let rangeStartDate: D | null;
    let rangeEndDate: D | null;

    if (this._selected instanceof DateRange) {
      rangeStartDate = this._selected.start;
      rangeEndDate = this._selected.end;
    } else {
      rangeStartDate = rangeEndDate = this._selected;
    }

    if (
      !this.dateAdapter.sameDate(rangeStartDate, event.value) ||
      !this.dateAdapter.sameDate(rangeEndDate, event.value)
    ) {
      this.selectedChange.emit(event.value);
    }

    this._userSelection.emit({ value: event.value, event: event.event });
    this._previewStart = this._previewEnd = null;
    this.cdr.markForCheck();
  }

  _handleCalendarBodyKeydown(event: KeyboardEvent): void {
    const oldActiveDate = this._activeDate;
    const isRtl = this._isRtl();

    switch (event.keyCode) {
      case LEFT_ARROW:
        this.activeDate = this.dateAdapter.addCalendarDays(
          this._activeDate,
          isRtl ? 1 : -1,
        );
        break;
      case RIGHT_ARROW:
        this.activeDate = this.dateAdapter.addCalendarDays(
          this._activeDate,
          isRtl ? -1 : 1,
        );
        break;
      case UP_ARROW:
        this.activeDate = this.dateAdapter.addCalendarDays(
          this._activeDate,
          -7,
        );
        break;
      case DOWN_ARROW:
        this.activeDate = this.dateAdapter.addCalendarDays(this._activeDate, 7);
        break;
      case HOME:
        this.activeDate = this.dateAdapter.addCalendarDays(
          this._activeDate,
          1 - this.dateAdapter.getDate(this._activeDate),
        );
        break;
      case END:
        this.activeDate = this.dateAdapter.addCalendarDays(
          this._activeDate,
          this.dateAdapter.getNumDaysInMonth(this._activeDate) -
            this.dateAdapter.getDate(this._activeDate),
        );
        break;
      case PAGE_UP:
        this.activeDate = event.altKey
          ? this.dateAdapter.addCalendarYears(this._activeDate, -1)
          : this.dateAdapter.addCalendarMonths(this._activeDate, -1);
        break;
      case PAGE_DOWN:
        this.activeDate = event.altKey
          ? this.dateAdapter.addCalendarYears(this._activeDate, 1)
          : this.dateAdapter.addCalendarMonths(this._activeDate, 1);
        break;
      case ENTER:
      case SPACE:
        if (!this.dateFilter || this.dateFilter(this._activeDate)) {
          this._dateSelected({
            value: this.dateAdapter.createDate(
              this.dateAdapter.getYear(this._activeDate),
              this.dateAdapter.getMonth(this._activeDate),
              this.dateAdapter.getDate(this._activeDate),
            ),
            event,
          });
          event.preventDefault();
        }

        return;
      case ESCAPE:
        if (this._previewEnd != null && !hasModifierKey(event)) {
          this._previewStart = this._previewEnd = null;
          this.selectedChange.emit(null);
          this._userSelection.emit({ value: null, event });
          event.preventDefault();
          event.stopPropagation();
        }

        return;
      default:
        return;
    }

    if (this.dateAdapter.compareDate(oldActiveDate, this.activeDate)) {
      this.activeDateChange.emit(this.activeDate);
    }

    this._focusActiveCell();
    event.preventDefault();
  }

  _init(): void {
    this._setRanges(this.selected);
    this._todayDate = this._getCellCompareValue(this.dateAdapter.today());
    this._monthLabel = this.dateFormats.display.monthLabel
      ? this.dateAdapter.format(
          this.activeDate,
          this.dateFormats.display.monthLabel,
        )
      : this.dateAdapter
          .getMonthNames('short')
          [this.dateAdapter.getMonth(this.activeDate)].toLocaleUpperCase();

    const firstOfMonth = this.dateAdapter.createDate(
      this.dateAdapter.getYear(this.activeDate),
      this.dateAdapter.getMonth(this.activeDate),
      1,
    );
    this._firstWeekOffset =
      (DAYS_PER_WEEK +
        this.dateAdapter.getDayOfWeek(firstOfMonth) -
        this.dateAdapter.getFirstDayOfWeek()) %
      DAYS_PER_WEEK;

    this._initWeekdays();
    this._createWeekCells();
    this.cdr.markForCheck();
  }

  _focusActiveCell(movePreview?: boolean): void {
    this._rdsCalendarBody?._focusActiveCell(movePreview);
  }

  _previewChanged({
    event,
    value: cell,
  }: RdsCalendarUserEvent<RdsCalendarCell<D> | null>): void {
    if (this.rangeStrategy) {
      const value = cell && cell.rawValue ? cell.rawValue : null;
      const previewRange = this.rangeStrategy.createPreview(
        value,
        this.selected as DateRange<D>,
        event,
      );
      this._previewStart = this._getCellCompareValue(previewRange.start);
      this._previewEnd = this._getCellCompareValue(previewRange.end);
      this.cdr.detectChanges();
    }
  }

  _getActiveCell(): RdsCalendarCell<D> | null {
    if (this._weeks) {
      for (let i = 0; i < this._weeks.length; i++) {
        for (let j = 0; j < this._weeks[i].length; j++) {
          const cell = this._weeks[i][j];

          if (
            cell &&
            this.dateAdapter.sameDate(cell.rawValue, this.activeDate)
          ) {
            return cell;
          }
        }
      }
    }

    return null;
  }

  private _initWeekdays(): void {
    const firstDayOfWeek = this.dateAdapter.getFirstDayOfWeek();
    const narrowWeekdays = this.dateAdapter.getDayOfWeekNames('narrow');
    const longWeekdays = this.dateAdapter.getDayOfWeekNames('long');

    const weekdays = longWeekdays.map((long, i) => {
      return { long, narrow: narrowWeekdays[i] };
    });
    this._weekdays = weekdays
      .slice(firstDayOfWeek)
      .concat(weekdays.slice(0, firstDayOfWeek));
  }

  private _createWeekCells(): void {
    const daysInMonth = this.dateAdapter.getNumDaysInMonth(this.activeDate);
    const dateNames = this.dateAdapter.getDateNames();
    this._weeks = [[]];

    // previous month
    for (let i = this._firstWeekOffset || 0; i > 0; i--) {
      const date = this._getPreviousMonthDate(i - 1);
      const enabled = this._shouldEnableDate(date);
      const ariaLabel = this.dateAdapter.format(
        date,
        this.dateFormats.display.dateA11yLabel,
      );
      const cellClasses = this._getClasses(date, [
        'rds-calendar-body-cell-prev-month',
      ]);

      this._weeks[this._weeks.length - 1].push(
        new RdsCalendarCell<D>(
          this.dateAdapter.getDate(date),
          dateNames[this.dateAdapter.getDate(date) - 1],
          ariaLabel,
          enabled,
          cellClasses,
          date,
          this._getCellCompareValue(date) as number,
        ),
      );
    }

    // current month
    for (
      let i = 0, cell = this._firstWeekOffset || 0;
      i < daysInMonth;
      i++, cell++
    ) {
      if (cell == DAYS_PER_WEEK) {
        this._weeks.push([]);
        cell = 0;
      }

      const date = this._getCurrentMonthDate(i + 1);
      const enabled = this._shouldEnableDate(date);
      const ariaLabel = this.dateAdapter.format(
        date,
        this.dateFormats.display.dateA11yLabel,
      );
      const cellClasses = this._getClasses(date);

      this._weeks[this._weeks.length - 1].push(
        new RdsCalendarCell<D>(
          i + 1,
          dateNames[i],
          ariaLabel,
          enabled,
          cellClasses,
          date,
          this._getCellCompareValue(date) as number,
        ),
      );
    }

    // next month
    const nextMonthDays =
      DAYS_PER_WEEK -
      this._weeks[this._weeks.length - 1].length +
      (this._weeks.length < 6 ? DAYS_PER_WEEK : 0);

    for (let i = 0; i < nextMonthDays; i++) {
      if (this._weeks[this._weeks.length - 1].length == DAYS_PER_WEEK) {
        this._weeks.push([]);
      }

      const date = this._getNextMonthDate(i + 1);
      const enabled = this._shouldEnableDate(date);
      const ariaLabel = this.dateAdapter.format(
        date,
        this.dateFormats.display.dateA11yLabel,
      );
      const cellClasses = this._getClasses(date, [
        'rds-calendar-body-cell-next-month',
      ]);

      this._weeks[this._weeks.length - 1].push(
        new RdsCalendarCell<D>(
          this.dateAdapter.getDate(date),
          dateNames[this.dateAdapter.getDate(date) - 1],
          ariaLabel,
          enabled,
          cellClasses,
          date,
          this._getCellCompareValue(date) as number,
        ),
      );
    }
  }

  private _shouldEnableDate(date: D): boolean {
    return (
      !!date &&
      (!this.minDate ||
        this.dateAdapter.compareDate(date, this.minDate) >= 0) &&
      (!this.maxDate ||
        this.dateAdapter.compareDate(date, this.maxDate) <= 0) &&
      (!this.dateFilter || this.dateFilter(date))
    );
  }

  private _hasSameMonthAndYear(d1: D | null, d2: D | null): boolean {
    return !!(
      d1 &&
      d2 &&
      this.dateAdapter.getMonth(d1) == this.dateAdapter.getMonth(d2) &&
      this.dateAdapter.getYear(d1) == this.dateAdapter.getYear(d2)
    );
  }

  private _getCellCompareValue(date: D | null): number | null {
    if (date) {
      const year = this.dateAdapter.getYear(date);
      const month = this.dateAdapter.getMonth(date);
      const day = this.dateAdapter.getDate(date);

      return new Date(year, month, day).getTime();
    }

    return null;
  }

  private _isRtl(): boolean {
    return !!(this.dir && this.dir.value === 'rtl');
  }

  private _setRanges(selectedValue: DateRange<D> | D | null): void {
    if (selectedValue instanceof DateRange) {
      this._rangeStart = this._getCellCompareValue(selectedValue.start);
      this._rangeEnd = this._getCellCompareValue(selectedValue.end);
      this._isRange = true;
    } else {
      this._rangeStart = this._rangeEnd =
        this._getCellCompareValue(selectedValue);
      this._isRange = false;
    }

    this._comparisonRangeStart = this._getCellCompareValue(
      this.comparisonStart,
    );
    this._comparisonRangeEnd = this._getCellCompareValue(this.comparisonEnd);
  }

  private _getCurrentMonthDate(day: number): D {
    return this.dateAdapter.createDate(
      this.dateAdapter.getYear(this.activeDate),
      this.dateAdapter.getMonth(this.activeDate),
      day,
    );
  }

  private _getPreviousMonthDate(minusDays: number): D {
    let year = this.dateAdapter.getYear(this.activeDate);
    let month = this.dateAdapter.getMonth(this.activeDate);

    if (month - 1 === -1) {
      year--;
      month = 11;
    } else {
      month--;
    }

    const day =
      this.dateAdapter.getNumDaysInMonth(
        this.dateAdapter.createDate(year, month, 1),
      ) - minusDays;

    return this.dateAdapter.createDate(year, month, day);
  }

  private _getNextMonthDate(day: number): D {
    let year = this.dateAdapter.getYear(this.activeDate);
    let month = this.dateAdapter.getMonth(this.activeDate);

    if (month + 1 === 12) {
      year++;
      month = 0;
    } else {
      month++;
    }

    return this.dateAdapter.createDate(year, month, day);
  }

  private _getClasses(
    date: D,
    cssClasses: string[] = [],
  ): RdsCalendarCellCssClasses | undefined {
    const classes = this.dateClass ? this.dateClass(date, 'month') : undefined;

    if (classes instanceof Set) {
      cssClasses.forEach((c) => classes.add(c));

      return classes;
    } else if (classes instanceof Array) {
      cssClasses.forEach((c) => classes.push(c));

      return classes;
    } else if (typeof classes === 'string') {
      return classes + ' ' + cssClasses.join(' ');
    } else if (typeof classes === 'object') {
      cssClasses.forEach((c) => (classes[c] = true));

      return cssClasses;
    } else {
      return cssClasses;
    }
  }
}
