import {
  booleanAttribute,
  ChangeDetectionStrategy,
  Component,
  HostBinding,
  Input,
  ViewEncapsulation,
} from '@angular/core';

@Component({
  selector: 'rds-pseudo-checkbox',
  template: `
    <div class="rds-pseudo-checkbox__container">
      <div class="rds-pseudo-checkbox__checkbox-container">
        <div class="rds-pseudo-checkbox__frame"></div>
        <div class="rds-pseudo-checkbox__background">
          <svg
            focusable="false"
            class="rds-pseudo-checkbox__checkmark"
            viewBox="0 0 24 24"
            xml:space="preserve"
          >
            <path
              class="rds-pseudo-checkbox__checkmark-path"
              fill="none"
              d="M4.1,12.7 9,17.6 20.3,6.3"
            />
          </svg>
          <div class="rds-pseudo-checkbox__indeterminate-mark"></div>
        </div>
      </div>
    </div>
  `,
  styleUrls: ['./pseudo-checkbox.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class RdsPseudoCheckboxComponent {
  @HostBinding('class') readonly hostClass = 'rds-pseudo-checkbox';

  @Input({ transform: booleanAttribute })
  @HostBinding('class.rds-pseudo-checkbox--checked')
  checked = false;

  @Input({ transform: booleanAttribute })
  @HostBinding('class.rds-pseudo-checkbox--disabled')
  disabled = false;

  @Input({ transform: booleanAttribute })
  @HostBinding('class.rds-pseudo-checkbox--indeterminate')
  indeterminate = false;

  @Input({ transform: booleanAttribute })
  @HostBinding('class.rds-pseudo-checkbox--no-spacing')
  noSpacing = false;
}
