const RDS_DEFAULT_BADGE_COLORS = [
  'info',
  'success',
  'warning',
  'error',
  'neutral',
] as const;
const RDS_MINIMAL_BADGE_COLORS = [
  'blue',
  'green',
  'yellow',
  'red',
  'neutral',
  'orange',
  'black',
  'purple',
  'pink',
] as const;
const RDS_DOT_BADGE_COLORS = [...RDS_DEFAULT_BADGE_COLORS, 'other'] as const;

export type RdsBadgeType = 'default' | 'minimal' | 'dot';
export type RdsBadgeSize = 's' | 'm' | 'l';
export type RdsDefaultBadgeColor = (typeof RDS_DEFAULT_BADGE_COLORS)[number];
export type RdsMinimalBadgeColor = (typeof RDS_MINIMAL_BADGE_COLORS)[number];
export type RdsDotBadgeColor = (typeof RDS_DOT_BADGE_COLORS)[number];
export type RdsBadgeColor =
  | RdsDefaultBadgeColor
  | RdsMinimalBadgeColor
  | RdsDotBadgeColor;

export const RDS_BADGE_COLORS: Readonly<{
  [key in RdsBadgeType]: Readonly<RdsBadgeColor[]>;
}> = {
  default: [...RDS_DEFAULT_BADGE_COLORS],
  minimal: [...RDS_MINIMAL_BADGE_COLORS],
  dot: [...RDS_DOT_BADGE_COLORS],
};
export const RDS_BADGE_DEFAULT_COLOR: Readonly<{
  [key in RdsBadgeType]: Readonly<RdsBadgeColor>;
}> = {
  default: 'info',
  minimal: 'blue',
  dot: 'info',
};
