import {
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  HostBinding,
  Input,
  numberAttribute,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { RdsProgressBarMode } from './progress.mode';

let progressbarId = 0;

@Component({
  selector: 'rds-progress-bar',
  template: `
    <svg
      width="100%"
      height="4"
      focusable="false"
      class="rds-progress-bar-background rds-progress-bar-element"
    >
      <defs>
        <pattern
          [id]="progressbarId"
          x="4"
          y="0"
          width="8"
          height="4"
          patternUnits="userSpaceOnUse"
        >
          <circle cx="2" cy="2" r="2" />
        </pattern>
      </defs>
      <rect [attr.fill]="rectangleFillValue" width="100%" height="100%" />
    </svg>

    <div
      class="rds-progress-bar-buffer rds-progress-bar-element"
      [ngStyle]="bufferTransform()"
    ></div>
    <div
      class="rds-progress-bar-primary rds-progress-bar-fill rds-progress-bar-element"
      [ngStyle]="primaryTransform()"
      #primaryValueBar
    ></div>
    <div
      class="rds-progress-bar-secondary rds-progress-bar-fill rds-progress-bar-element"
    ></div>
  `,
  styleUrls: ['./progress-bar.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class RdsProgressBarComponent {
  readonly progressbarId = `rds-progress-bar-${progressbarId++}`;

  @HostBinding('class.rds-progress-bar') readonly hostClass = true;

  @HostBinding('attr.role') readonly hostRole = 'progressbar';
  @HostBinding('attr.aria-valuemin') readonly hostAriaValuemin = '0';
  @HostBinding('attr.aria-valuemax') readonly hostAriaValuemax = '100';

  @HostBinding('attr.mode') get hostMode(): RdsProgressBarMode {
    return this.mode;
  }

  @HostBinding('attr.aria-valuenow') get hostValueNow(): number | null {
    let valueNow: number | null = this.value;

    if (this.mode === 'indeterminate') {
      valueNow = null;
    }

    return valueNow;
  }

  /* eslint-disable-next-line @angular-eslint/no-input-rename */
  @Input({ transform: (v: unknown) => clamp(numberAttribute(v, 0)) }) value = 0;
  /* eslint-disable-next-line @angular-eslint/no-input-rename */
  @Input({ transform: (v: unknown) => clamp(numberAttribute(v, 0)) })
  bufferValue = 0;
  @Input() mode: RdsProgressBarMode = 'indeterminate';

  @ViewChild('primaryValueBar') _primaryValueBar: ElementRef;

  rectangleFillValue: string;

  constructor() {
    // We need to prefix the SVG reference with the current path, otherwise they won't work
    // in Safari if the page has a `<base>` tag. Note that we need quotes inside the `url()`,
    // because named route URLs can contain parentheses
    const path = location ? location.href.split('#')[0] : '';
    this.rectangleFillValue = `url('${path}#${this.progressbarId}')`;
  }

  primaryTransform(): { transform: string } {
    const scale = this.value / 100;

    return { transform: `scaleX(${scale})` };
  }

  bufferTransform(): { transform: string } | null {
    if (this.mode === 'buffer') {
      const scale = this.bufferValue / 100;

      return { transform: `scaleX(${scale})` };
    }

    return null;
  }
}

/** Clamps a value to be between two numbers, by default 0 and 100. */
function clamp(value: number, min = 0, max = 100): number {
  return Math.max(min, Math.min(max, value));
}
