import { FocusableOption, FocusMonitor, FocusOrigin } from '@angular/cdk/a11y';
import { BooleanInput, NumberInput } from '@angular/cdk/coercion';
import { ContentObserver } from '@angular/cdk/observers';
import {
  AfterViewInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  DestroyRef,
  ElementRef,
  HostBinding,
  inject,
  Input,
  numberAttribute,
  OnDestroy,
} from '@angular/core';
import { RouterLink } from '@angular/router';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { TAB_INDEX_DEFAULT, TAB_INDEX_NOT_FOCUSABLE } from '../utils';

export type RdsLinkType = 'primary' | 'secondary';
export type RdsLinkSize = 's' | 'm' | 'l';

@Component({
  selector: 'a[rds-link], a[rdsLink], button[rds-link], button[rdsLink]',
  template: '<ng-content/>',
  changeDetection: ChangeDetectionStrategy.OnPush,
  standalone: true,
})
export class RdsLinkComponent
  implements FocusableOption, AfterViewInit, OnDestroy
{
  protected readonly destroyRef = inject(DestroyRef);
  protected readonly cdr = inject(ChangeDetectorRef);
  protected readonly elementRef = inject(ElementRef);
  protected readonly focusMonitor = inject(FocusMonitor);
  protected readonly contentObserver = inject(ContentObserver);
  protected readonly routerLink = inject(RouterLink, { optional: true });

  @HostBinding('class') get hostClasses(): string[] {
    const classes = ['rds-link'];

    if (this.standalone) {
      classes.push(`rds-link--${this.type}`, `rds-link-${this.size}`);
    } else if (this.elementRef) {
      classes.push(`rds-link-inline`);
    }

    return classes;
  }

  @HostBinding('class.rds-link-before-icon')
  get hasFirstIcon(): boolean {
    return (
      this.elementRef?.nativeElement.firstChild?.tagName?.toUpperCase() ===
      'RDS-ICON'
    );
  }

  @HostBinding('class.rds-link-after-icon')
  get hasLastIcon(): boolean {
    return (
      this.elementRef?.nativeElement.lastChild?.tagName?.toUpperCase() ===
      'RDS-ICON'
    );
  }

  @Input()
  @HostBinding('class.rds-link-standalone')
  set standalone(isStandalone: BooleanInput) {
    this._standalone = booleanAttribute(isStandalone);
  }

  get standalone(): boolean {
    return (
      this._standalone ||
      this.elementRef.nativeElement.tagName === 'BUTTON' ||
      this.hasFirstIcon ||
      this.hasLastIcon
    );
  }

  protected _standalone = false;

  @HostBinding('class.rds-link--active')
  @Input({ transform: booleanAttribute })
  active = false;

  @HostBinding('attr.disabled')
  get attrDisabled(): boolean | null {
    return this.disabled ? this.disabled : null;
  }

  @HostBinding('attr.aria-disabled')
  @Input({ transform: booleanAttribute })
  disabled = false;

  @HostBinding('tabIndex')
  @Input()
  set tabIndex(value: NumberInput) {
    this._tabIndex = numberAttribute(value, TAB_INDEX_DEFAULT);
  }

  get tabIndex(): number {
    return this.disabled ? TAB_INDEX_NOT_FOCUSABLE : this._tabIndex;
  }

  private _tabIndex = TAB_INDEX_DEFAULT;

  @Input() type: RdsLinkType = 'primary';
  @Input() size: RdsLinkSize = 'l';

  constructor() {
    this._preventDisabledLinkClick();
  }

  ngAfterViewInit(): void {
    this.focusMonitor.monitor(this.elementRef, true);
    this.contentObserver
      .observe(this.elementRef.nativeElement)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this.cdr?.markForCheck());
  }

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.elementRef);
  }

  focus(origin: FocusOrigin = 'program', options?: FocusOptions): void {
    this.focusMonitor.focusVia(this.elementRef.nativeElement, origin, options);
  }

  private _preventDisabledLinkClick(): void {
    const link = this.routerLink;

    if (link) {
      const onClick = link.onClick;

      link.onClick = (...args) => {
        if (this.disabled) {
          return !this.routerLink;
        } else {
          return onClick.apply(link, args);
        }
      };
    }
  }
}
