import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  HostBinding,
  inject,
  Input,
  isDevMode,
  OnChanges,
  SimpleChanges,
  ViewEncapsulation,
} from '@angular/core';
import {
  RDS_BADGE_COLORS,
  RDS_BADGE_DEFAULT_COLOR,
  RdsBadgeColor,
  RdsBadgeSize,
  RdsBadgeType,
} from './badge.types';

@Component({
  selector: 'rds-badge',
  templateUrl: './badge.component.html',
  styleUrls: ['./badge.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  standalone: true,
})
export class RdsBadgeComponent implements OnChanges {
  private readonly cdr = inject(ChangeDetectorRef);

  @HostBinding('class') class = 'rds-badge--container';

  /**
   * @desc Has effect only for standalone badges that has either `default` or `minimal` type
   */
  @Input() size: RdsBadgeSize = 's';
  @Input() label: string | null = null;

  /**
   * @desc Note: The availability of a given color depends on the type of the badge!
   * @see {@link RdsDefaultBadgeColor}
   * @see {@link RdsMinimalBadgeColor}
   * @see {@link RdsDotBadgeColor}
   */
  @Input() color: RdsBadgeColor;
  @Input() type: RdsBadgeType = 'default';
  @Input() ariaDescribedBy: string | null = null;
  _isAttached = false;

  ngOnChanges(changes: SimpleChanges): void {
    if (!this.color) {
      // for backwards compatibility when it comes to default colors
      this.color = RDS_BADGE_DEFAULT_COLOR[this.type];
    }

    if (isDevMode()) {
      if (
        (changes['color'] || changes['type']) &&
        this.type &&
        this.color &&
        !RDS_BADGE_COLORS[this.type].includes(this.color)
      ) {
        throw new Error(
          `The '${this.color}' color is not available for '${this.type}' badge type.`,
        );
      }
    }
  }

  detectChanges(): void {
    this.cdr.detectChanges();
  }
}
