import {
  Directive,
  ElementRef,
  EventEmitter,
  HostBinding,
  HostListener,
  inject,
  Input,
  OnInit,
  Output,
  Renderer2,
} from '@angular/core';
import { RdsUploadConfig } from './upload.types';

declare const ngDevMode: boolean;

@Directive({
  selector: 'input[rds-upload], input[rdsUpload]',
})
export class RdsUploadDirective implements OnInit {
  private _config: RdsUploadConfig;
  private readonly renderer = inject(Renderer2);
  private readonly nativeElement: HTMLInputElement =
    inject(ElementRef).nativeElement;

  @Input() set config(v: RdsUploadConfig) {
    this._config = v;
    this.onConfigChange();
  }

  @Output() fileSelected = new EventEmitter<File[]>();

  @HostBinding('style.display')
  private display = 'none';

  @HostBinding('type')
  private type = 'file';

  @HostListener('change')
  private onChange(): void {
    const files = Array.from(this.nativeElement.files ?? []);
    this.fileSelected.emit(files);
    this.nativeElement.value = '';
  }

  ngOnInit(): void {
    if (ngDevMode && !this._config) {
      throw new Error(
        `The "config" input is required, received "${JSON.stringify(
          this._config,
        )}"`,
      );
    }

    this.onConfigChange();
  }

  private onConfigChange(): void {
    this._config.multiple
      ? this.renderer.setAttribute(this.nativeElement, 'multiple', '')
      : this.renderer.removeAttribute(this.nativeElement, 'multiple');

    const accept = this._config.accept?.join(',');

    accept
      ? this.renderer.setAttribute(this.nativeElement, 'accept', accept)
      : this.renderer.removeAttribute(this.nativeElement, 'accept');
  }
}
