import { ActiveDescendantKeyManager, LiveAnnouncer } from '@angular/cdk/a11y';
import { BooleanInput } from '@angular/cdk/coercion';
import { SelectionModel } from '@angular/cdk/collections';
import {
  BlockScrollStrategy,
  CdkConnectedOverlay,
  CdkOverlayOrigin,
  ConnectedPosition,
  ScrollStrategy,
  ScrollStrategyOptions,
} from '@angular/cdk/overlay';
import { ViewportRuler } from '@angular/cdk/scrolling';
import {
  AfterContentInit,
  booleanAttribute,
  ChangeDetectorRef,
  ContentChild,
  ContentChildren,
  DestroyRef,
  Directive,
  DoCheck,
  ElementRef,
  EventEmitter,
  forwardRef,
  HostBinding,
  HostListener,
  inject,
  Input,
  NgZone,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  QueryList,
  Renderer2,
  SimpleChanges,
  ViewChild,
} from '@angular/core';
import { ControlValueAccessor, Validators } from '@angular/forms';
import { defer, merge, noop, Observable, Subject } from 'rxjs';
import {
  debounceTime,
  delay,
  distinctUntilChanged,
  startWith,
  switchMap,
  take,
  takeUntil,
} from 'rxjs/operators';
import { RdsListItemComponent } from '../../list-item';
import { updateErrorState } from '../../utils';
import { RdsAbstractFormFieldControl } from '../abstract-form-field-control';
import { RdsControlType } from '../control.type';
import {
  RdsFormFieldComponent,
  RdsFormFieldSize,
} from '../form-field.component';
import { AbstractFocusableOption } from './abstract-focusable-option';
import { RdsAbstractSelectOptionSelectionChangedEvent } from './select-option-selection-changed.event';
import { RdsSelectSearchInputComponent } from './select-search-input.component';
import {
  RdsAbstractSelectFilterByFunc,
  RdsSelectCompareWithFn,
  RdsSelectOption,
  SELECT_PANEL_MAX_HEIGHT,
} from './select-types';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

let nextUniqueId = 0;

@Directive()
export abstract class RdsAbstractSelectComponent<
    T,
    O extends RdsSelectOption<T>,
    ArrayOfFlatType extends T | T[],
    SelectedType = O | O[],
  >
  extends RdsAbstractFormFieldControl<ArrayOfFlatType>
  implements
    AfterContentInit,
    OnChanges,
    OnDestroy,
    OnInit,
    DoCheck,
    ControlValueAccessor
{
  protected readonly destroyRef = inject(DestroyRef);
  protected readonly viewportRuler = inject(ViewportRuler);
  protected readonly cdr = inject(ChangeDetectorRef);
  protected readonly ngZone = inject(NgZone);
  protected readonly scrollStrategyOptions = inject(ScrollStrategyOptions);
  protected readonly elementRef = inject(ElementRef);
  protected readonly liveAnnouncer = inject(LiveAnnouncer);
  protected readonly renderer = inject(Renderer2);
  protected readonly formField = inject<RdsFormFieldComponent<T>>(
    forwardRef(() => RdsFormFieldComponent),
  );

  protected _name = 'rds-abstract-select';
  private readonly _uid = `${this._name}-${nextUniqueId++}`;
  protected readonly selectAllDestroy$ = new Subject<void>();
  protected readonly destroyKeyManager$ = new Subject<void>();
  private readonly minPanelWidth = 170;
  private _filterValue: string | null = null;

  protected _panelOpen = false;

  /** The scroll position of the overlay panel, calculated to center the selected option. */
  private _scrollTop = 0;

  private _ariaLabel: string | null = '';

  private _ariaDescribedby: string;
  protected optionIds = '';

  private _hideValue = false;

  private _placeholder = '';

  protected selectionModel: SelectionModel<O>;

  protected keyManager: ActiveDescendantKeyManager<O>;

  readonly scrollStrategy: ScrollStrategy;

  readonly positions: ConnectedPosition[] = [
    {
      originX: 'start',
      originY: 'bottom',
      overlayX: 'start',
      overlayY: 'top',
    },
    {
      originX: 'start',
      originY: 'top',
      overlayX: 'start',
      overlayY: 'bottom',
    },
  ];

  readonly optionSelectionChanges = defer(() => {
    const options = this.options;

    if (options) {
      return options.changes.pipe(
        startWith(options),
        switchMap(() =>
          merge(...options.map((option) => option.selectionChange$)),
        ),
        // should emit one time if many selection changes at one time e.g.: select/deselect all
        debounceTime(1),
      );
    }

    return this.ngZone.onStable.asObservable().pipe(
      take(1),
      switchMap(() => this.optionSelectionChanges),
    );
  }) as Observable<RdsAbstractSelectOptionSelectionChangedEvent<T, O>>;

  override _id = this._uid;

  minWidth = this.minPanelWidth;
  triggerClientRect: ClientRect;
  transformOrigin = 'top';
  panelDoneAnimatingStream = new Subject<string>();

  @HostBinding('attr.role') hostRole = 'listbox';
  @HostBinding('class') hostClass = 'rds-form-field__control';

  @HostBinding('attr.aria-describedby') get hostAriaDescribedBy():
    | string
    | null {
    return this._ariaDescribedby || null;
  }

  @HostBinding('tabIndex') get hostTabindex(): string {
    return this.disabled ? '-1' : '0';
  }

  @HostBinding('attr.aria-owns') get hostAriaOwns(): string | null {
    return this.panelOpen ? this.optionIds : null;
  }

  @HostBinding('attr.aria-activedescendant')
  get hostActivedescendant(): string | null {
    if (this.panelOpen && this.keyManager && this.keyManager.activeItem) {
      return this.keyManager.activeItem.id;
    }

    return null;
  }

  @HostBinding('attr.aria-labelledby') get hostAriaLabelledBy(): string | null {
    return this.ariaLabelledby || null;
  }

  @HostBinding('attr.disabled')
  get attrDisabled(): boolean | null {
    if (this.ngControl && this.ngControl.disabled !== null) {
      return this.ngControl.disabled ? this.ngControl.disabled : null;
    }

    return this._disabled ? this._disabled : null;
  }

  @HostBinding('attr.required')
  get attrRequired(): boolean | null {
    return (
      this._required ??
      this.ngControl?.control?.hasValidator(Validators.required) ??
      null
    );
  }

  @Input()
  override set required(value: BooleanInput) {
    const newValue = booleanAttribute(value);

    if (newValue !== this._required) {
      this._required = newValue;
      this.stateChanges.next();
    }
  }

  override get required(): boolean {
    return this.attrRequired ?? false;
  }

  @HostBinding('attr.aria-invalid') get errorState(): boolean {
    return this._errorState;
  }

  @Input('aria-label')
  @HostBinding('attr.aria-label')
  get hostAriaLabel(): string | null {
    if (this.ariaLabelledby) {
      return null;
    }

    if (this._ariaLabel) {
      return this._ariaLabel;
    } else {
      return null;
    }
  }

  set hostAriaLabel(newValue: string | null) {
    if (newValue !== this._ariaLabel) {
      this._ariaLabel = newValue;
      this.stateChanges.next();
    }
  }

  @Input()
  @HostBinding('attr.id')
  override get id(): string {
    return this._id;
  }

  override set id(value: string) {
    this._id = value || this._uid;
    this.stateChanges.next();
  }

  @Input('aria-labelledby') ariaLabelledby: string;

  @Input() sortComparator: (a: O, b: O, options: O[]) => number;

  @Input() compareWith: RdsSelectCompareWithFn<T>;

  @Input()
  override set disabled(newValue: BooleanInput) {
    const disabled = booleanAttribute(newValue);

    if (disabled !== this._disabled) {
      if (!this.panelOpen && this.keyManager) {
        this.keyManager.updateActiveItem(-1);
      }

      this._disabled = disabled;
    }
  }

  override get disabled(): boolean {
    return this.attrDisabled || false;
  }

  @Input()
  set hideValue(value: BooleanInput) {
    const newValue = booleanAttribute(value);

    if (this._hideValue !== newValue) {
      this._hideValue = booleanAttribute(value);
      this.stateChanges.emit();
    }
  }

  get hideValue(): boolean {
    return this._hideValue;
  }

  @Input()
  get placeholder(): string {
    return this._placeholder;
  }

  set placeholder(value: string) {
    this._placeholder = value;
    this.stateChanges.emit();
  }

  @Input()
  override get value(): ArrayOfFlatType {
    return this._value;
  }

  override set value(newValue: ArrayOfFlatType) {
    if (newValue !== this._value) {
      this.writeValue(newValue);
      this._value = newValue;
    }
  }

  @Output() readonly valueChange = new EventEmitter<ArrayOfFlatType>();

  @Output() readonly openChange = new EventEmitter<boolean>();

  trigger: ElementRef<HTMLDivElement>;
  origin: CdkOverlayOrigin;

  @ViewChild('panel') panel: ElementRef;

  @ViewChild(CdkConnectedOverlay) overlayDir: CdkConnectedOverlay;

  @ContentChildren(RdsListItemComponent)
  options: QueryList<O>;

  @ContentChildren(AbstractFocusableOption)
  focusableOptions: QueryList<O>;

  @ContentChild(RdsSelectSearchInputComponent)
  selectSearch: RdsSelectSearchInputComponent;

  onChange: (value: ArrayOfFlatType) => void = noop;
  onTouched = noop;
  private _filterBy: RdsAbstractSelectFilterByFunc<T, O> = (t, o) =>
    this._defaultFilterBy(t, o);

  @Input()
  get filterBy(): RdsAbstractSelectFilterByFunc<T, O> {
    return this._filterBy;
  }

  set filterBy(fn: RdsAbstractSelectFilterByFunc<T, O>) {
    this._filterBy = fn;
  }

  @HostListener('keydown', ['$event']) handleKeydown(
    event: KeyboardEvent,
  ): void {
    if (!this.disabled) {
      this.panelOpen
        ? this.handleOpenKeydown(event)
        : this.handleClosedKeydown(event);
    }
  }

  @HostListener('focus') onFocus(): void {
    if (!this.disabled) {
      this._focused = true;
      this.stateChanges.next();
    }
  }

  @HostListener('blur') onBlur(): void {
    this._focused = false;

    if (!this.disabled) {
      this.onTouched();
      this.cdr.markForCheck();
      this.stateChanges.next();
    }
  }

  override get focused(): boolean {
    return this._focused || this._panelOpen;
  }

  setDescribedByIds(ids: string[]): void {
    this._ariaDescribedby = ids.join(' ');
  }

  onContainerClick(): void {
    this.focus();
    this.open();
  }

  get shouldLabelFloat(): boolean {
    return !this.hideValue && (this._panelOpen || !this.empty);
  }

  get empty(): boolean {
    return !this.selectionModel || this.selectionModel.isEmpty();
  }

  get controlType(): RdsControlType {
    return 'select';
  }

  get panelOpen(): boolean {
    return this._panelOpen;
  }

  abstract get selected(): SelectedType;

  abstract get triggerValues(): string[];

  constructor() {
    super();

    if (this.ngControl) {
      // Note: we provide the value accessor through here, instead of
      // the `providers` to avoid running into a circular import.
      this.ngControl.valueAccessor = this;
    }

    const scrollStrategy = (): BlockScrollStrategy =>
      this.scrollStrategyOptions.block();
    this.scrollStrategy = scrollStrategy();

    if (!this.compareWith) {
      this.compareWith = (o1: T, o2: T) => o1 === o2;
    }
  }

  ngOnInit(): void {
    this.stateChanges.next();
    this.stateChanges
      .pipe(delay(1), takeUntilDestroyed(this.destroyRef))
      .subscribe(() => {
        this._setSize(this.formField.size);
        this.cdr.markForCheck();
      });

    // We need `distinctUntilChanged` here, because some browsers will
    // fire the animation end event twice for the same animation. See:
    // https://github.com/angular/angular/issues/24084
    this.panelDoneAnimatingStream
      .pipe(distinctUntilChanged(), takeUntilDestroyed(this.destroyRef))
      .subscribe(() => {
        if (this.panelOpen) {
          this._scrollTop = 0;
        } else {
          this.overlayDir.offsetX = 0;
          this.cdr.markForCheck();
        }
      });

    this.viewportRuler
      .change()
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => {
        if (this._panelOpen) {
          this.triggerClientRect =
            this.trigger.nativeElement.getBoundingClientRect();
          this.minWidth = Math.max(
            this.minPanelWidth,
            this.triggerClientRect.width,
          );
          this.cdr.markForCheck();
        }
      });
  }

  ngAfterContentInit(): void {
    this.initKeyManager();

    this.selectionModel.changed
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((event) => {
        event.added.forEach((option) => (option.checked = true));
        event.removed.forEach((option) => (option.checked = false));
        this.cdr.markForCheck();
      });

    this.options.changes
      .pipe(startWith([null]), takeUntilDestroyed(this.destroyRef))
      .subscribe(() => {
        this.resetOptions();
        this.initializeSelection();
      });

    if (this.selectSearch) {
      this.selectSearch.changed
        .pipe(takeUntilDestroyed(this.destroyRef))
        .subscribe((filterValue) => {
          this._filterValue = filterValue;
          this.refreshOptionsVisibility();
          this.options.forEach((o) => o.setInactiveStyles());
          this.initKeyManager();
        });
    }
  }

  ngDoCheck(): void {
    if (this.ngControl) {
      const oldState = this._errorState;
      this._errorState = updateErrorState(
        this.errorState,
        this.parentFormGroup,
        this.parentForm,
        this.ngControl.control,
      );

      if (oldState !== this._errorState) {
        this.stateChanges.next();
      }
    }

    this.dirtyDisabledValue();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['disabled']) {
      this.stateChanges.next();
    }
  }

  ngOnDestroy(): void {
    this.selectAllDestroy$.complete();
    this.destroyKeyManager$.next();
    this.destroyKeyManager$.complete();
    this.stateChanges.complete();
    this.openChange.complete();
  }

  private dirtyDisabledValue(): void {
    if (this._disabled !== this.disabled) {
      this._disabled = this.disabled;
      this.stateChanges.next();
    }
  }

  protected removeValue(index: number): void {
    const values = Array.from(
      this.options.filter((item) => item.checked && !item.disabled),
    );
    values.splice(index, 1);
    this.selectionModel.clear();
    this.selectionModel.select(...values);
    this.propagateChanges();
  }

  toggle(): void {
    this.panelOpen ? this.close() : this.open();
  }

  resetFilterValue(): void {
    this.selectSearch?.reset();
  }

  open(): void {
    if (
      this.disabled ||
      !this.options ||
      !this.options.length ||
      this._panelOpen
    ) {
      return;
    }

    this.initKeyManager();
    this.triggerClientRect = this.trigger.nativeElement.getBoundingClientRect();
    this.minWidth = Math.max(this.minPanelWidth, this.triggerClientRect.width);
    this._panelOpen = true;
    this.keyManager.withHorizontalOrientation(null);
    this.cdr.markForCheck();
    this.openChange.emit(true);
  }

  close(): void {
    if (this._panelOpen) {
      this.focus();
      this.options.forEach((i) => i.setInactiveStyles());
      this._panelOpen = false;
      this.keyManager.withHorizontalOrientation('rtl');
      this.onTouched();
      this.cdr.markForCheck();
      this.openChange.emit(false);
    }
  }

  writeValue(value: ArrayOfFlatType): void {
    if (this.options) {
      this.setSelectionByValue(value);
    }
  }

  registerOnChange(fn: (value: ArrayOfFlatType) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
    this.cdr.markForCheck();
    this.stateChanges.next();
  }

  focus(options?: FocusOptions): void {
    this.elementRef.nativeElement.focus(options);
  }

  onAttached(): void {
    this.overlayDir.positionChange.pipe(take(1)).subscribe(() => {
      this.cdr.detectChanges();
      this.overlayDir.overlayRef.updatePosition();
      this.panel.nativeElement.scrollTop = this._scrollTop;
      this.selectSearch?.focus();
    });
  }

  refreshOptionsVisibility(): void {
    if (
      this.panel &&
      this.panel.nativeElement &&
      this.options &&
      this.options.length > 0
    ) {
      this.options.forEach((item, index, array) => {
        if (typeof this._filterBy === 'function') {
          item.toggleVisibility(
            this._filterBy(this._filterValue, item, index, array),
          );
        } else {
          item.toggleVisibility(this._defaultFilterBy(this._filterValue, item));
        }
      });

      if (this.options.filter((item) => item.isVisible()).length === 0) {
        this.renderer.setStyle(this.panel.nativeElement, 'overflow', 'hidden');
      } else {
        this.renderer.removeStyle(this.panel.nativeElement, 'overflow');
      }

      this.cdr.markForCheck();
    }
  }

  shouldShowPlaceholder(): boolean {
    return (
      this.hasPlaceholderValue() &&
      (this.formField._labelPosition !== 'floating' ||
        (this.formField._labelPosition === 'floating' &&
          this.selectionModel.isEmpty() &&
          this.shouldLabelFloat))
    );
  }

  shouldShowValue(): boolean {
    return (
      !this.hideValue &&
      (this.selectionModel.hasValue() || !this.hasPlaceholderValue())
    );
  }

  private hasPlaceholderValue(): boolean {
    return !!this.placeholder && String(this.placeholder).trim().length > 0;
  }

  private _defaultFilterBy(text: string | null, option: O): boolean {
    if (text === null || text === '') {
      return true;
    } else if (
      option &&
      typeof option.value === 'string' &&
      typeof text === 'string'
    ) {
      return option.value.includes(text);
    } else {
      return option.value === text;
    }
  }

  protected abstract handleClosedKeydown(event: KeyboardEvent): void;

  protected abstract handleOpenKeydown(event: KeyboardEvent): void;

  private initializeSelection(): void {
    Promise.resolve().then(() => {
      if (this.ngControl) {
        this._value = this.ngControl.value;
      }

      this.setSelectionByValue(this._value);
      this.stateChanges.next();
    });
  }

  protected abstract setSelectionByValue(value: ArrayOfFlatType): void;

  protected selectValue(value: T): O | undefined {
    const correspondingOption = this.options.find((option) => {
      try {
        // Treat null as a special reset value.
        return option.value != null && this.compareWith(option.value, value);
      } catch (error) {
        return false;
      }
    });

    if (correspondingOption) {
      this.selectionModel.select(correspondingOption);
    }

    return correspondingOption;
  }

  protected initKeyManager(): void {
    this.destroyKeyManager$.next();
    this.keyManager = new ActiveDescendantKeyManager<O>(
      this.focusableOptions.filter((o) => o.isVisible()),
    )
      .withVerticalOrientation()
      .withHorizontalOrientation('rtl')
      .withAllowedModifierKeys(['shiftKey'])
      .withHomeAndEnd();

    if (this.selectionModel.selected[0]) {
      const selectedIndex = this.focusableOptions
        .filter((o) => o.isVisible())
        .indexOf(this.selectionModel.selected[0]);
      this.keyManager.updateActiveItem(selectedIndex);
    }

    this.keyManager.tabOut
      .pipe(
        takeUntil(this.destroyKeyManager$),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe(() => {
        if (this.panelOpen) {
          this.close();
        }
      });
    this.keyManager.change
      .pipe(
        takeUntil(this.destroyKeyManager$),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe(() => {
        if (this._panelOpen && this.panel) {
          this.scrollActiveOptionIntoView();
        }
      });
  }

  protected resetOptions(): void {
    // Listen to changes in the internal state of the options and react accordingly.
    // Handles cases like the labels of the selected options changing.
    merge(...this.options.map((option) => option.checkedChanged))
      .pipe(
        // should emit one time if states changes for many options
        debounceTime(1),
        takeUntil(this.options.changes),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe(() => {
        this.stateChanges.next();
      });

    this.optionIds = this.options.map((option) => option.id).join(' ');
  }

  protected onSelect(option: O): void {
    const wasSelected = this.selectionModel.isSelected(option);

    if (wasSelected !== option.checked) {
      option.checked
        ? this.selectionModel.select(option)
        : this.selectionModel.deselect(option);
    }

    if (wasSelected !== this.selectionModel.isSelected(option)) {
      this.propagateChanges();
    }

    this.stateChanges.next();
  }

  protected abstract getValueToEmit(): ArrayOfFlatType;

  protected propagateChanges(): void {
    const valueToEmit = this.getValueToEmit();
    this._value = valueToEmit;
    this.valueChange.emit(valueToEmit);
    this.onChange(valueToEmit);
    this.cdr.markForCheck();
  }

  private scrollActiveOptionIntoView(): void {
    const itemHeight = this.keyManager.activeItem?.rect().height || 0;
    const scrollContainerHeight = this.panel.nativeElement.scrollHeight;
    const panelHeight = Math.min(
      scrollContainerHeight,
      SELECT_PANEL_MAX_HEIGHT,
    );
    const maxScroll = scrollContainerHeight - panelHeight;
    const scrollBuffer = panelHeight / 2;
    const optionOffsetFormScrollTop =
      (this.keyManager.activeItemIndex || 0) * itemHeight;
    const halfOptionHeight = itemHeight / 2;

    const optimalScrollPosition =
      optionOffsetFormScrollTop - scrollBuffer + halfOptionHeight;
    this._scrollTop = Math.min(Math.max(0, optimalScrollPosition), maxScroll);
    this.panel.nativeElement.scrollTop = this._scrollTop;
  }

  protected _setSize(size: RdsFormFieldSize): void {
    this.options?.forEach((option) => (option.size = size));

    if (this.selectSearch) {
      this.selectSearch.size = size;
    }
  }
}
