import {
  Directive,
  ElementRef,
  EventEmitter,
  HostBinding,
  HostListener,
  inject,
  OnDestroy,
  OnInit,
  Output,
} from '@angular/core';
import { ENTER, hasModifierKey, SPACE } from '@angular/cdk/keycodes';
import { FocusMonitor } from '@angular/cdk/a11y';
import { TAB_INDEX_DEFAULT, TAB_INDEX_NOT_FOCUSABLE } from '../utils';

@Directive({
  selector: 'rds-header-app-name',
})
export class RdsHeaderAppNameDirective implements OnDestroy, OnInit {
  private readonly elementRef = inject(ElementRef);
  private readonly focusMonitor = inject(FocusMonitor);

  @HostBinding('class') class = 'rds-header-app-name';

  @HostBinding('class.rds-header-app-name-triggerable')
  get isTriggerable(): boolean {
    return this.triggered.observed;
  }

  @HostBinding('tabIndex')
  get tabIndex(): number {
    return this.isTriggerable ? TAB_INDEX_DEFAULT : TAB_INDEX_NOT_FOCUSABLE;
  }

  @Output() triggered = new EventEmitter<MouseEvent | KeyboardEvent>();

  ngOnInit(): void {
    this.focusMonitor.monitor(this.elementRef.nativeElement);
  }

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.elementRef.nativeElement);
  }

  @HostListener('click', ['$event'])
  @HostListener('keydown', ['$event'])
  onLogoClick(event: MouseEvent | KeyboardEvent): void {
    if (
      (event instanceof KeyboardEvent &&
        !hasModifierKey(event, 'shiftKey', 'altKey', 'ctrlKey') &&
        (event.keyCode === SPACE || event.keyCode === ENTER)) ||
      event instanceof MouseEvent
    ) {
      if (event instanceof KeyboardEvent && event.keyCode === SPACE) {
        event.stopPropagation();
        event.preventDefault();
      }

      this.triggered.next(event);
    }
  }
}
