import {
  AfterViewInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  HostBinding,
  HostListener,
  inject,
  Input,
  OnDestroy,
  OnInit,
  ViewEncapsulation,
} from '@angular/core';
import { FocusMonitor } from '@angular/cdk/a11y';
import { ENTER, SPACE } from '@angular/cdk/keycodes';
import { merge, Subscription } from 'rxjs';
import { RdsSortable, RdsSortDirective, SortDirection } from './sort.directive';
import {
  animate,
  animateChild,
  AnimationTriggerMetadata,
  keyframes,
  query,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import { RDS_SORT_HEADER_COLUMN_DEF } from './cell';

export type ArrowViewState = SortDirection | 'hint' | 'active';

export interface ArrowViewStateTransition {
  fromState?: ArrowViewState;
  toState: ArrowViewState;
}

const SORT_ANIMATION_TRANSITION = '225ms cubic-bezier(0.4,0.0,0.2,1)';

const rdsSortAnimations: {
  readonly arrowOpacity: AnimationTriggerMetadata;
  readonly arrowPosition: AnimationTriggerMetadata;
  readonly allowChildren: AnimationTriggerMetadata;
  readonly arrow: AnimationTriggerMetadata;
} = {
  arrowOpacity: trigger('arrowOpacity', [
    state('desc-to-active, asc-to-active, active', style({ opacity: 1 })),
    state('desc-to-hint, asc-to-hint, hint', style({ opacity: 0.54 })),
    state(
      'hint-to-desc, active-to-desc, desc, hint-to-asc, active-to-asc, asc, void',
      style({ opacity: 0 }),
    ),
    transition(
      '* => asc, * => desc, * => active, * => hint, * => void',
      animate('0ms'),
    ),
    transition('* <=> *', animate(SORT_ANIMATION_TRANSITION)),
  ]),
  arrowPosition: trigger('arrowPosition', [
    transition(
      '* => desc-to-hint, * => desc-to-active',
      animate(
        SORT_ANIMATION_TRANSITION,
        keyframes([
          style({ transform: 'translateY(-25%)' }),
          style({ transform: 'translateY(0)' }),
        ]),
      ),
    ),
    transition(
      '* => hint-to-desc, * => active-to-desc',
      animate(
        SORT_ANIMATION_TRANSITION,
        keyframes([
          style({ transform: 'translateY(0)' }),
          style({ transform: 'translateY(25%)' }),
        ]),
      ),
    ),
    transition(
      '* => asc-to-hint, * => asc-to-active',
      animate(
        SORT_ANIMATION_TRANSITION,
        keyframes([
          style({ transform: 'translateY(25%)' }),
          style({ transform: 'translateY(0)' }),
        ]),
      ),
    ),
    transition(
      '* => hint-to-asc, * => active-to-asc',
      animate(
        SORT_ANIMATION_TRANSITION,
        keyframes([
          style({ transform: 'translateY(0)' }),
          style({ transform: 'translateY(-25%)' }),
        ]),
      ),
    ),
    state(
      'desc-to-hint, asc-to-hint, hint, desc-to-active, asc-to-active, active',
      style({ transform: 'translateY(0)' }),
    ),
    state(
      'hint-to-desc, active-to-desc, desc',
      style({ transform: 'translateY(-25%)' }),
    ),
    state(
      'hint-to-asc, active-to-asc, asc',
      style({ transform: 'translateY(25%)' }),
    ),
  ]),
  allowChildren: trigger('allowChildren', [
    transition('* <=> *', [query('@*', animateChild(), { optional: true })]),
  ]),
  arrow: trigger('arrow', [
    state('asc', style({ transform: '' })),
    state('desc', style({ transform: 'scaleY(-1)' })),
    transition('asc => desc', [animate(SORT_ANIMATION_TRANSITION)]),
    transition('desc => asc', [animate(SORT_ANIMATION_TRANSITION)]),
  ]),
};

@Component({
  selector: '[rds-sort-header], [rdsSortHeader]',
  exportAs: 'rdsSortHeader',
  templateUrl: './sort-header.html',
  styleUrls: ['./sort-header.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  animations: [
    rdsSortAnimations.arrowOpacity,
    rdsSortAnimations.arrowPosition,
    rdsSortAnimations.allowChildren,
    rdsSortAnimations.arrow,
  ],
})
export class RdsSortHeaderComponent
  implements RdsSortable, OnDestroy, OnInit, AfterViewInit
{
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly sort = inject(RdsSortDirective, {
    optional: true,
  }) as RdsSortDirective;
  private readonly columnDef = inject(RDS_SORT_HEADER_COLUMN_DEF, {
    optional: true,
  });
  private readonly focusMonitor = inject(FocusMonitor);
  private readonly elementRef = inject<ElementRef<HTMLElement>>(ElementRef);

  private _rerenderSubscription: Subscription | null = null;

  @HostBinding('class') class = 'rds-sort-header';

  @HostBinding('class.rds-sort-header-disabled')
  get _disabledClass(): boolean {
    return this._isDisabled();
  }

  @HostBinding('attr.aria-sort')
  get ariaSortAttribute(): string {
    if (!this._isSorted()) {
      return 'none';
    }

    return this.sort.direction == 'asc' ? 'ascending' : 'descending';
  }

  _showIndicatorHint = false;
  _viewState: ArrowViewStateTransition | null = null;
  _arrowDirection: SortDirection = '';
  _disableViewStateAnimation = false;

  // eslint-disable-next-line @angular-eslint/no-input-rename
  @Input('rds-sort-header') id = '';

  @Input() arrowPosition: 'left' | 'right' = 'right';
  @Input() start: SortDirection = '';
  @Input({ transform: booleanAttribute }) disableClear = false;
  @Input({ transform: booleanAttribute }) disabled = false;

  constructor() {
    if (!this.sort) {
      throw Error(
        `RdsSortHeader must be placed within a parent element with the RdsSort directive.`,
      );
    }

    this._handleStateChanges();
  }

  ngOnInit(): void {
    if (!this.id && this.columnDef) {
      this.id = this.columnDef.name;
    }

    this._updateArrowDirection();
    this._setAnimationTransitionState({
      toState: this._isSorted() ? 'active' : this._arrowDirection,
    });

    this.sort.register(this);
  }

  ngAfterViewInit(): void {
    this.focusMonitor.monitor(this.elementRef, true).subscribe((origin) => {
      const newState = !!origin;

      if (newState !== this._showIndicatorHint) {
        this._setIndicatorHintVisible(newState);
        this.cdr.markForCheck();
      }
    });
  }

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.elementRef);
    this.sort.deregister(this);
    this._rerenderSubscription?.unsubscribe();
  }

  @HostListener('mouseenter')
  onMouseenter(): void {
    this._setIndicatorHintVisible(true);
  }

  @HostListener('mouseleave')
  onMouseleave(): void {
    this._setIndicatorHintVisible(false);
  }

  @HostListener('click')
  _handleClick(): void {
    if (!this._isDisabled()) {
      this.sort.sort(this);
    }
  }

  @HostListener('keydown', ['$event'])
  _handleKeydown(event: KeyboardEvent): void {
    if (
      !this._isDisabled() &&
      (event.keyCode === SPACE || event.keyCode === ENTER)
    ) {
      event.preventDefault();
      this._toggleOnInteraction();
    }
  }

  _isSorted(): boolean {
    return (
      this.sort.active == this.id &&
      (this.sort.direction === 'asc' || this.sort.direction === 'desc')
    );
  }

  _isDisabled(): boolean {
    return this.sort.disabled || this.disabled;
  }

  _renderArrow(): boolean {
    return !this._isDisabled() || this._isSorted();
  }

  _getArrowDirectionState(): string {
    return `${this._isSorted() ? 'active-' : ''}${this._arrowDirection}`;
  }

  _getArrowViewState(): string {
    const fromState = this._viewState?.fromState;

    return (fromState ? `${fromState}-to-` : '') + this._viewState?.toState;
  }

  private _updateArrowDirection(): void {
    this._arrowDirection = this._isSorted()
      ? this.sort.direction
      : this.start || this.sort.start;
  }

  private _handleStateChanges(): void {
    this._rerenderSubscription = merge(
      this.sort.rdsSortChange,
      this.sort._stateChanges,
    ).subscribe(() => {
      if (this._isSorted()) {
        this._updateArrowDirection();

        if (
          this._viewState?.toState === 'hint' ||
          this._viewState?.toState === 'active'
        ) {
          this._disableViewStateAnimation = true;
        }

        this._setAnimationTransitionState({
          fromState: this._arrowDirection,
          toState: 'active',
        });
        this._showIndicatorHint = false;
      }

      if (
        !this._isSorted() &&
        this._viewState &&
        this._viewState.toState === 'active'
      ) {
        this._disableViewStateAnimation = false;
        this._setAnimationTransitionState({
          fromState: 'active',
          toState: this._arrowDirection,
        });
      }

      this.cdr.markForCheck();
    });
  }

  private _setIndicatorHintVisible(visible: boolean): void {
    if (this._isDisabled() && visible) {
      return;
    }

    this._showIndicatorHint = visible;

    if (!this._isSorted()) {
      this._updateArrowDirection();

      if (this._showIndicatorHint) {
        this._setAnimationTransitionState({
          fromState: this._arrowDirection,
          toState: 'hint',
        });
      } else {
        this._setAnimationTransitionState({
          fromState: 'hint',
          toState: this._arrowDirection,
        });
      }
    }
  }

  private _setAnimationTransitionState(
    viewState: ArrowViewStateTransition,
  ): void {
    this._viewState = viewState;

    if (this._disableViewStateAnimation) {
      this._viewState = { toState: viewState.toState };
    }
  }

  private _toggleOnInteraction(): void {
    this.sort.sort(this);

    if (
      this._viewState?.toState === 'hint' ||
      this._viewState?.toState === 'active'
    ) {
      this._disableViewStateAnimation = true;
    }
  }
}
