import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

export type RdsAutocompleteIntlToken =
  keyof typeof RdsAutocompleteIntl.translations;

@Injectable({ providedIn: 'root' })
export class RdsAutocompleteIntl {
  static readonly translations = {
    placeholder: 'Select an option',
    loadingText: 'Loading...',
    noDataText: 'No results found',
    newItemText: 'Create new',
  };

  private readonly _change$ = new BehaviorSubject(
    RdsAutocompleteIntl.translations,
  );
  readonly change$ = this._change$.asObservable();

  setTranslation(key: RdsAutocompleteIntlToken, value: string): void {
    RdsAutocompleteIntl.translations[key] = value;
    this._change$.next({ ...RdsAutocompleteIntl.translations });
  }
}
