import {
  animate,
  AnimationTriggerMetadata,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';

export const HEADER_SEARCH_EXPANSION_TIMING_VALUE = 300;
export const HEADER_SEARCH_EXPANSION_TIMING = `${HEADER_SEARCH_EXPANSION_TIMING_VALUE}ms ease-in`;

export const rdsHeaderSearchAnimations: {
  readonly searchExpansion: AnimationTriggerMetadata;
  readonly headerCollapse: AnimationTriggerMetadata;
} = {
  searchExpansion: trigger('searchExpansion', [
    state(
      'collapsed, void',
      style({ display: 'none', opacity: 0, transform: 'translateX(-100%)' }),
    ),
    state(
      'expanded',
      style({ display: 'flex', opacity: 1, transform: 'translateX(0)' }),
    ),
    transition(
      'expanded <=> collapsed, void => collapsed',
      animate(HEADER_SEARCH_EXPANSION_TIMING),
    ),
  ]),
  headerCollapse: trigger('headerCollapse', [
    state(
      'collapsed, void',
      style({ transform: 'translateX(-100%)', display: 'none', opacity: 0 }),
    ),
    state(
      'expanded',
      style({ transform: 'translateX(0)', display: 'flex', opacity: 1 }),
    ),
    transition(
      'expanded <=> collapsed, void => collapsed',
      animate(HEADER_SEARCH_EXPANSION_TIMING),
    ),
  ]),
};
