import { FocusMonitor } from '@angular/cdk/a11y';
import { ContentObserver } from '@angular/cdk/observers';
import {
  AfterContentInit,
  AfterViewInit,
  Attribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChild,
  DestroyRef,
  ElementRef,
  HostBinding,
  HostListener,
  inject,
  Input,
  numberAttribute,
  OnDestroy,
  ViewEncapsulation,
} from '@angular/core';
import { RdsCardImageDirective } from './card-image.directive';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { TAB_INDEX_DEFAULT } from '../utils';

let nextUniqueId = 0;

@Component({
  selector: 'rds-card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  standalone: true,
  exportAs: 'rdsCard',
  imports: [],
})
export class RdsCardComponent
  implements AfterViewInit, AfterContentInit, OnDestroy
{
  protected readonly cdr = inject(ChangeDetectorRef);
  private readonly destroyRef = inject(DestroyRef);
  private readonly focusMonitor = inject(FocusMonitor);
  private readonly elementRef = inject(ElementRef);
  private readonly contentObserver = inject(ContentObserver);
  private readonly _uniqueId = `rds-card-${++nextUniqueId}`;

  @ContentChild(RdsCardImageDirective) image: RdsCardImageDirective;

  @HostBinding('class') get class(): string {
    return 'rds-card rds-focus-indicator rds-card-default';
  }

  @HostBinding('tabIndex')
  @Input({ transform: (v: unknown) => numberAttribute(v, TAB_INDEX_DEFAULT) })
  tabIndex = TAB_INDEX_DEFAULT;

  @HostBinding('attr.id')
  get inputId(): string {
    return `${this.id || this._uniqueId}`;
  }

  @Input() id = this._uniqueId;

  constructor(@Attribute('tabindex') tabIndex?: string) {
    this.tabIndex = numberAttribute(tabIndex, TAB_INDEX_DEFAULT);
  }

  ngAfterViewInit(): void {
    this.focusMonitor.monitor(this.elementRef, false);
  }

  ngAfterContentInit(): void {
    this.contentObserver
      .observe(this.elementRef.nativeElement)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this.cdr.markForCheck());
  }

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.elementRef);
  }

  @HostListener('focus')
  onFocus(): void {
    this.elementRef.nativeElement.focus();
  }

  focus(options?: FocusOptions): void {
    this.elementRef.nativeElement.focus(options);
  }
}
