import { PointerEvent } from 'react';

export function isElement(target: EventTarget): target is Element {
  return 'closest' in target;
}

export function isInsideRect(event: PointerEvent, rect: DOMRect): boolean {
  return (
    event.clientX > rect.left &&
    event.clientX < rect.right &&
    event.clientY > rect.top &&
    event.clientY < rect.bottom
  );
}

export function cellsToRowsHTML(cells: Element[], maxRows?: number): string {
  if (typeof maxRows === 'number') {
    cells = cells.slice(0, maxRows);
  }

  return cells
    .map((cell) => {
      const height = cell.parentElement?.offsetHeight;

      return `<tr class="rds-row">
                <td class="rds-cell" style="height: ${
                  height ? height + 'px' : 'unset'
                }">
                  ${cell.innerHTML}
                </td>
              </tr>`;
    })
    .join('\n');
}
