import {
  FactoryProvider,
  Injectable,
  OnDestroy,
  Optional,
  SkipSelf,
} from '@angular/core';
import { DateAdapter } from './adapter';
import { Observable, Subject } from 'rxjs';

export class DateRange<D> {
  private _disableStructuralEquivalency: never;

  constructor(
    readonly start: D | null,
    readonly end: D | null,
  ) {}
}

export type ExtractDateTypeFromSelection<T> =
  T extends DateRange<infer D> ? D : NonNullable<T>;

export interface DateSelectionModelChange<S> {
  selection: S;
  source: unknown;
  oldValue?: S;
}

@Injectable()
export abstract class RdsDateSelectionModel<
  S,
  D = ExtractDateTypeFromSelection<S>,
> implements OnDestroy
{
  private _selectionChanged = new Subject<DateSelectionModelChange<S>>();
  public selection: S | null = null;

  selectionChanged: Observable<DateSelectionModelChange<S>> =
    this._selectionChanged;

  protected constructor(protected readonly _dateAdapter: DateAdapter<D>) {}

  updateSelection(value: S, source: unknown): void {
    const oldValue = this.selection;
    this.selection = value;
    this._selectionChanged.next({
      selection: value,
      source,
      oldValue: oldValue as S,
    });
  }

  ngOnDestroy(): void {
    this._selectionChanged.complete();
  }

  protected _isValidDateInstance(date: D): boolean {
    return (
      this._dateAdapter.isDateInstance(date) && this._dateAdapter.isValid(date)
    );
  }

  abstract add(date: D | null): void;

  abstract isValid(): boolean;

  abstract isComplete(): boolean;

  abstract clone(): RdsDateSelectionModel<S, D>;
}

@Injectable()
export class RdsSingleDateSelectionModel<D> extends RdsDateSelectionModel<
  D | null,
  D
> {
  constructor(adapter: DateAdapter<D>) {
    super(adapter);
    this.selection = null;
  }

  add(date: D | null): void {
    super.updateSelection(date, this);
  }

  isValid(): boolean {
    return this.selection != null && this._isValidDateInstance(this.selection);
  }

  isComplete(): boolean {
    return this.selection != null;
  }

  clone(): RdsDateSelectionModel<D | null, D> {
    const clone = new RdsSingleDateSelectionModel<D>(this._dateAdapter);
    clone.updateSelection(this.selection, this);

    return clone;
  }
}

@Injectable()
export class RdsRangeDateSelectionModel<D> extends RdsDateSelectionModel<
  DateRange<D>,
  D
> {
  public override selection: DateRange<D>;

  constructor(dateAdapter: DateAdapter<D>) {
    super(dateAdapter);
    this.selection = new DateRange<D>(null, null);
  }

  add(date: D | null): void {
    let { start, end } = this.selection;

    if (start == null) {
      start = date;
    } else if (end == null) {
      end = date;
    } else {
      start = date;
      end = null;
    }

    super.updateSelection(new DateRange<D>(start, end), this);
  }

  isValid(): boolean {
    const { start, end } = this.selection;

    // Empty ranges are valid.
    if (start == null && end == null) {
      return true;
    }

    // Complete ranges are only valid if both dates are valid and the start is before the end.
    if (start != null && end != null) {
      return (
        this._isValidDateInstance(start) &&
        this._isValidDateInstance(end) &&
        this._dateAdapter.compareDate(start, end) <= 0
      );
    }

    // Partial ranges are valid if the start/end is valid.
    return (
      (start == null || this._isValidDateInstance(start)) &&
      (end == null || this._isValidDateInstance(end))
    );
  }

  isComplete(): boolean {
    return this.selection.start != null && this.selection.end != null;
  }

  clone(): RdsRangeDateSelectionModel<D> {
    const clone = new RdsRangeDateSelectionModel<D>(this._dateAdapter);
    clone.updateSelection(this.selection, this);

    return clone;
  }
}

export function RDS_SINGLE_DATE_SELECTION_MODEL_FACTORY(
  parent: RdsSingleDateSelectionModel<unknown>,
  dateAdapter: DateAdapter<unknown>,
): RdsSingleDateSelectionModel<unknown> {
  return parent || new RdsSingleDateSelectionModel(dateAdapter);
}

export const RDS_SINGLE_DATE_SELECTION_MODEL_PROVIDER: FactoryProvider = {
  provide: RdsDateSelectionModel,
  deps: [[new Optional(), new SkipSelf(), RdsDateSelectionModel], DateAdapter],
  useFactory: RDS_SINGLE_DATE_SELECTION_MODEL_FACTORY,
};

export function RDS_RANGE_DATE_SELECTION_MODEL_FACTORY(
  parent: RdsSingleDateSelectionModel<unknown>,
  dateAdapter: DateAdapter<unknown>,
): RdsSingleDateSelectionModel<unknown> {
  return parent || new RdsRangeDateSelectionModel(dateAdapter);
}

export const RDS_RANGE_DATE_SELECTION_MODEL_PROVIDER: FactoryProvider = {
  provide: RdsDateSelectionModel,
  deps: [[new Optional(), new SkipSelf(), RdsDateSelectionModel], DateAdapter],
  useFactory: RDS_RANGE_DATE_SELECTION_MODEL_FACTORY,
};
