import {
  BooleanInput,
  coerceBooleanProperty,
  coerceStringArray,
} from '@angular/cdk/coercion';
import { ESCAPE, hasModifierKey, UP_ARROW } from '@angular/cdk/keycodes';
import {
  FlexibleConnectedPositionStrategy,
  Overlay,
  OverlayConfig,
  OverlayRef,
  ScrollStrategy,
} from '@angular/cdk/overlay';
import {
  ComponentPortal,
  ComponentType,
  TemplatePortal,
} from '@angular/cdk/portal';
import { DOCUMENT } from '@angular/common';
import {
  AfterViewInit,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ComponentRef,
  Directive,
  ElementRef,
  EventEmitter,
  forwardRef,
  HostBinding,
  HostListener,
  inject,
  InjectionToken,
  Input,
  isDevMode,
  NgZone,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild,
  ViewContainerRef,
  ViewEncapsulation,
} from '@angular/core';
import { DateAdapter } from './adapter';
import { merge, Observable, Subject, Subscription } from 'rxjs';
import { filter, map, startWith, take } from 'rxjs/operators';
import { RdsCalendarComponent } from './calendar.component';
import { RDS_CALENDAR_VIEW_ORDERED, RdsCalendarView } from './calendar.types';
import { createMissingDateImplError } from './datepicker-errors';
import {
  RdsCalendarCellClassFunction,
  RdsCalendarUserEvent,
} from './calendar-body.component';
import { DateFilterFn } from './datepicker-input.base';
import {
  DateRange,
  ExtractDateTypeFromSelection,
  RdsDateSelectionModel,
} from './date-selection.model';
import {
  RDS_DATE_RANGE_SELECTION_STRATEGY,
  RdsDateRangeSelectionStrategy,
} from './date-range-selection.strategy';
import { RdsDatepickerIntl } from './datepicker.intl';
import { RdsDialogRef, RdsDialogService } from '../dialog';
import {
  animate,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

let datepickerUid = 0;

export const RDS_DATEPICKER_SCROLL_STRATEGY = new InjectionToken<
  () => ScrollStrategy
>('RDS_DATEPICKER_SCROLL_STRATEGY');

export function RDS_DATEPICKER_SCROLL_STRATEGY_FACTORY(
  overlay: Overlay,
): () => ScrollStrategy {
  return () => overlay.scrollStrategies.reposition();
}

export type DatepickerDropdownPositionX = 'start' | 'end';
export type DatepickerDropdownPositionY = 'above' | 'below';

export const RDS_DATEPICKER_SCROLL_STRATEGY_FACTORY_PROVIDER = {
  provide: RDS_DATEPICKER_SCROLL_STRATEGY,
  deps: [Overlay],
  useFactory: RDS_DATEPICKER_SCROLL_STRATEGY_FACTORY,
};

class RdsDatepickerContentBase {
  public readonly _elementRef = inject(ElementRef);
}

@Component({
  selector: 'rds-datepicker-content',
  templateUrl: './datepicker-content.html',
  styleUrls: ['./datepicker-content.scss'],
  animations: [
    trigger('transformPanel', [
      state(
        'void',
        style({
          opacity: 0,
          transform: 'scale(1, 0.8)',
        }),
      ),
      transition(
        'void => enter',
        animate(
          '120ms cubic-bezier(0, 0, 0.2, 1)',
          style({
            opacity: 1,
            transform: 'scale(1, 1)',
          }),
        ),
      ),
      transition('* => void', animate('100ms linear', style({ opacity: 0 }))),
    ]),
    trigger('fadeInCalendar', [
      state('void', style({ opacity: 0 })),
      state('enter', style({ opacity: 1 })),
      transition(
        'void => *',
        animate('120ms 100ms cubic-bezier(0.55, 0, 0.55, 0.2)'),
      ),
    ]),
  ],
  exportAs: 'rdsDatepickerContent',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsDatepickerContentComponent<
    C extends RdsDatepickerControl<D>,
    S,
    D = ExtractDateTypeFromSelection<S>,
  >
  extends RdsDatepickerContentBase
  implements OnInit, AfterViewInit, OnDestroy
{
  public override readonly _elementRef = inject(ElementRef);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly globalModel = inject<RdsDateSelectionModel<S, D>>(
    RdsDateSelectionModel,
  );
  private readonly dateAdapter = inject<DateAdapter<D>>(DateAdapter);
  private readonly rangeSelectionStrategy = inject<
    RdsDateRangeSelectionStrategy<D>
  >(RDS_DATE_RANGE_SELECTION_STRATEGY, { optional: true });
  private readonly intl = inject(RdsDatepickerIntl);

  private _subscriptions = new Subscription();
  private _model: RdsDateSelectionModel<S, D> | null = null;

  @HostBinding('class') class = 'rds-datepicker-content';

  @HostBinding('class.rds-datepicker-content-touch') get isContentTouch():
    | boolean
    | null {
    return this.datepicker && this.datepicker.touchUi ? true : null;
  }

  @HostBinding('@transformPanel') get transformPanel(): 'enter' | 'void' {
    return this._animationState;
  }

  @ViewChild(forwardRef(() => RdsCalendarComponent))
  _calendar: RdsCalendarComponent<D> | null = null;

  datepicker: RdsDatepickerBaseDirective<C, S, D> | null = null;
  comparisonStart: D | null = null;
  comparisonEnd: D | null = null;
  _animationState: 'enter' | 'void' = 'enter';
  _animationDone = new Subject<void>();
  _closeButtonText: string | null = null;
  _closeButtonFocused: boolean | null = null;
  _actionsPortal: TemplatePortal | null = null;

  @HostListener('@transformPanel.done')
  _onAnimationDone(): void {
    this._animationDone.next();
  }

  constructor() {
    super();
    this.intl.changes
      .pipe(
        startWith(this.intl.closeCalendarLabel),
        map(() => this.intl.closeCalendarLabel || 'Close calendar'),
        takeUntilDestroyed(),
      )
      .subscribe((value) => {
        this._closeButtonText = value;
        this.cdr.markForCheck();
      });
  }

  ngOnInit(): void {
    this._model = this._actionsPortal
      ? this.globalModel.clone()
      : this.globalModel;
  }

  ngAfterViewInit(): void {
    this._subscriptions.add(
      this.datepicker?.stateChanges.subscribe(() => {
        this.cdr.markForCheck();
      }),
    );
    this._calendar?.focusActiveCell();
  }

  ngOnDestroy(): void {
    this._subscriptions.unsubscribe();
    this._animationDone.complete();
  }

  _handleUserSelection(event: RdsCalendarUserEvent<D | null>): void {
    const selection = this._model?.selection;
    const value = event.value;
    const isRange = selection instanceof DateRange;

    if (isRange && this.rangeSelectionStrategy) {
      const newSelection = this.rangeSelectionStrategy.selectionFinished(
        value,
        selection as unknown as DateRange<D>,
        event.event,
      );
      this._model?.updateSelection(newSelection as unknown as S, this);
    } else if (
      value &&
      (isRange || !this.dateAdapter.sameDate(value, selection as unknown as D))
    ) {
      this._model?.add(value);
    }

    if ((!this._model || this._model.isComplete()) && !this._actionsPortal) {
      this.datepicker?.close();
    }
  }

  _startExitAnimation(): void {
    this._animationState = 'void';
    this.cdr.markForCheck();
  }

  _getSelected(): D | DateRange<D> | null {
    return (this._model?.selection as D | DateRange<D> | null) || null;
  }

  _applyPendingSelection(): void {
    if (this._model !== this.globalModel) {
      this.globalModel.updateSelection(this._model?.selection as S, this);
    }
  }
}

export interface RdsDatepickerControl<D> {
  getStartValue(): D | null;

  min: D | null;
  max: D | null;
  disabled: boolean;
  dateFilter: DateFilterFn<D>;

  getConnectedOverlayOrigin(): ElementRef;

  stateChanges: Observable<void>;
}

export interface RdsDatepickerPanel<
  C extends RdsDatepickerControl<D>,
  S,
  D = ExtractDateTypeFromSelection<S>,
> {
  closedStream: EventEmitter<void>;
  datepickerInput: C;
  disabled: boolean;
  id: string;
  opened: boolean;
  openedStream: EventEmitter<void>;
  stateChanges: Subject<void>;
  availableViews: RdsCalendarView[];

  open(): void;

  registerInput(input: C): RdsDateSelectionModel<S, D>;
}

@Directive()
export abstract class RdsDatepickerBaseDirective<
  C extends RdsDatepickerControl<D>,
  S,
  D = ExtractDateTypeFromSelection<S>,
> implements RdsDatepickerPanel<C, S, D>, OnDestroy, OnChanges
{
  private readonly dialog = inject(RdsDialogService);
  private readonly overlay = inject(Overlay);
  private readonly ngZone = inject(NgZone);
  private readonly viewContainerRef = inject(ViewContainerRef);
  private readonly scrollStrategy = inject(RDS_DATEPICKER_SCROLL_STRATEGY);
  private readonly dateAdapter = inject<DateAdapter<D>>(DateAdapter, {
    optional: true,
  }) as DateAdapter<D>;
  private readonly document = inject(DOCUMENT, { optional: true });
  private readonly model = inject<RdsDateSelectionModel<S, D>>(
    RdsDateSelectionModel,
  );
  private _originalShowToggle: boolean | null = null;

  private _inputStateChanges = Subscription.EMPTY;

  @Input() calendarHeaderComponent: ComponentType<unknown> | null = null;

  @Input()
  get startAt(): D | null {
    return (
      this._startAt ||
      (this.datepickerInput ? this.datepickerInput.getStartValue() : null)
    );
  }

  set startAt(value: D | null) {
    this._startAt = this.dateAdapter.getValidDateOrNull(
      this.dateAdapter.deserialize(value),
    );
  }

  private _startAt: D | null = null;

  @Input() startView: RdsCalendarView = 'month';

  @Input()
  set touchUi(value: BooleanInput) {
    const isTouchUi = booleanAttribute(value);

    if (isTouchUi !== this._touchUi) {
      this._touchUi = isTouchUi;
      this.stateChanges.next();
    }
  }

  get touchUi(): boolean {
    return this._touchUi;
  }

  private _touchUi = false;

  @Input()
  set disabled(value: BooleanInput) {
    const newValue = booleanAttribute(value);

    if (this._disabled !== newValue) {
      this._disabled = newValue;
      this.stateChanges.next();
    }
  }

  get disabled(): boolean {
    return this._disabled === null && this.datepickerInput
      ? this.datepickerInput.disabled
      : !!this._disabled;
  }

  private _disabled: boolean | null = null;

  @Input()
  set availableViews(newViews: RdsCalendarView[]) {
    const views = RDS_CALENDAR_VIEW_ORDERED.filter((view) =>
      newViews.includes(view),
    );

    if (views.join('') !== this._availableViews.join('')) {
      this._availableViews = views;
      this._updateShowToggleIfNeeded();
      this.stateChanges.next();
    }
  }

  get availableViews(): RdsCalendarView[] {
    return this._availableViews;
  }

  private _availableViews: RdsCalendarView[] = RDS_CALENDAR_VIEW_ORDERED;

  @Input()
  set showToggle(value: BooleanInput | null) {
    const newValue = value === null ? value : coerceBooleanProperty(value);

    if (this._originalShowToggle !== newValue) {
      this._originalShowToggle = newValue;
      this._updateShowToggleIfNeeded();
      this.stateChanges.next();
    }
  }

  get showToggle(): boolean | null {
    return this._showToggle;
  }

  private _showToggle: boolean | null = true;

  @Input()
  xPosition: DatepickerDropdownPositionX = 'start';

  @Input()
  yPosition: DatepickerDropdownPositionY = 'below';

  @Input({ transform: booleanAttribute }) restoreFocus = true;

  @Output() readonly yearSelected = new EventEmitter<D>();
  @Output() readonly monthSelected = new EventEmitter<D>();
  @Output() readonly viewChanged = new EventEmitter<RdsCalendarView>(true);

  @Input() dateClass: RdsCalendarCellClassFunction<D> | null = null;
  @Output() openedStream = new EventEmitter<void>();
  @Output() closedStream = new EventEmitter<void>();

  @Input({ transform: coerceStringArray }) panelClass: string[] | string = '';

  @Input()
  set opened(value: BooleanInput) {
    booleanAttribute(value) ? this.open() : this.close();
  }

  get opened(): boolean {
    return this._opened;
  }

  private _opened = false;

  id = `rds-datepicker-${datepickerUid++}`;

  private _popupRef: OverlayRef | null = null;
  private _dialogRef: RdsDialogRef<
    RdsDatepickerContentComponent<C, S, D>,
    D
  > | null = null;
  private _popupComponentRef: ComponentRef<
    RdsDatepickerContentComponent<C, S, D>
  > | null = null;
  private _focusedElementBeforeOpen: HTMLElement | null = null;
  private _backdropHarnessClass = `${this.id}-backdrop`;
  private _actionsPortal: TemplatePortal | null = null;
  datepickerInput: C;

  readonly stateChanges = new Subject<void>();

  _getStartView(): RdsCalendarView {
    return this.availableViews.includes(this.startView)
      ? this.startView
      : this.availableViews[0];
  }

  _getMinDate(): D | null {
    return this.datepickerInput && this.datepickerInput.min;
  }

  _getMaxDate(): D | null {
    return this.datepickerInput && this.datepickerInput.max;
  }

  _getDateFilter(): DateFilterFn<D> {
    return this.datepickerInput && this.datepickerInput.dateFilter;
  }

  constructor() {
    if (!this.dateAdapter && isDevMode()) {
      throw createMissingDateImplError('DateAdapter');
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    const positionChange = changes['xPosition'] || changes['yPosition'];

    if (positionChange && !positionChange.firstChange && this._popupRef) {
      this._setConnectedPositions(
        this._popupRef.getConfig()
          .positionStrategy as FlexibleConnectedPositionStrategy,
      );

      if (this.opened) {
        this._popupRef.updatePosition();
      }
    }

    this.stateChanges.next();
  }

  ngOnDestroy(): void {
    this._destroyPopup();
    this.close();
    this._inputStateChanges.unsubscribe();
    this.stateChanges.complete();
  }

  select(date: D): void {
    this.model.add(date);
  }

  _selectYear(normalizedYear: D): void {
    this.yearSelected.emit(normalizedYear);
  }

  _selectMonth(normalizedMonth: D): void {
    this.monthSelected.emit(normalizedMonth);
  }

  _viewChanged(view: RdsCalendarView): void {
    this.viewChanged.emit(view);
  }

  registerInput(input: C): RdsDateSelectionModel<S, D> {
    if (this.datepickerInput) {
      throw Error(
        'A RdsDatepicker can only be associated with a single input.',
      );
    }

    this._inputStateChanges.unsubscribe();
    this.datepickerInput = input;
    this._inputStateChanges = input.stateChanges.subscribe(() =>
      this.stateChanges.next(undefined),
    );

    return this.model;
  }

  registerActions(portal: TemplatePortal): void {
    if (this._actionsPortal) {
      throw Error(
        'A RdsDatepicker can only be associated with a single actions row.',
      );
    }

    this._actionsPortal = portal;
  }

  removeActions(portal: TemplatePortal): void {
    if (portal === this._actionsPortal) {
      this._actionsPortal = null;
    }
  }

  open(): void {
    if (this._opened || this.disabled) {
      return;
    }

    if (!this.datepickerInput) {
      throw Error(
        'Attempted to open an RdsDatepicker with no associated input.',
      );
    }

    if (this.document) {
      this._focusedElementBeforeOpen = this.document
        .activeElement as HTMLElement | null;
    }

    this.touchUi ? this._openAsDialog() : this._openAsPopup();
    this._opened = true;
    this.openedStream.emit();
  }

  close(): void {
    if (!this._opened) {
      return;
    }

    if (this._popupComponentRef && this._popupRef) {
      const instance = this._popupComponentRef.instance;
      instance._startExitAnimation();
      instance._animationDone
        .pipe(take(1))
        .subscribe(() => this._destroyPopup());
    }

    if (this._dialogRef) {
      this._dialogRef.close();
      this._dialogRef = null;
    }

    const completeClose = (): void => {
      if (this._opened) {
        this._opened = false;
        this.closedStream.emit();
        this._focusedElementBeforeOpen = null;
      }
    };

    if (
      this.restoreFocus &&
      this._focusedElementBeforeOpen &&
      typeof this._focusedElementBeforeOpen.focus === 'function'
    ) {
      this._focusedElementBeforeOpen.focus();
      setTimeout(completeClose);
    } else {
      completeClose();
    }
  }

  _applyPendingSelection(): void {
    const instance =
      this._popupComponentRef?.instance || this._dialogRef?.componentInstance;
    instance?._applyPendingSelection();
  }

  private _updateShowToggleIfNeeded(): void {
    const shouldShowToggle =
      this._originalShowToggle === null
        ? this._availableViews.length > 1
        : coerceBooleanProperty(this._originalShowToggle);

    if (this._showToggle !== shouldShowToggle) {
      this._showToggle = shouldShowToggle;
    }
  }

  private _openAsDialog(): void {
    if (this._dialogRef) {
      this._dialogRef.close();
    }

    this._dialogRef = this.dialog.open<
      RdsDatepickerContentComponent<C, S, D>,
      D
    >(RdsDatepickerContentComponent, {
      viewContainerRef: this.viewContainerRef,
      panelClass: 'rds-datepicker-dialog',
      hasBackdrop: true,
      disableClose: false,
      size: 'xl',
      height: '',
      minHeight: '',
      maxHeight: '',
    });

    this._dialogRef.afterClosed().subscribe(() => this.close());

    if (this._dialogRef.componentInstance) {
      this._forwardContentValues(this._dialogRef.componentInstance);
    }
  }

  private _openAsPopup(): void {
    const portal = new ComponentPortal<RdsDatepickerContentComponent<C, S, D>>(
      RdsDatepickerContentComponent,
      this.viewContainerRef,
    );

    this._destroyPopup();
    this._createPopup();
    this._popupComponentRef = (this._popupRef as OverlayRef).attach(portal);
    this._forwardContentValues(this._popupComponentRef?.instance);

    this.ngZone.onStable.pipe(take(1)).subscribe(() => {
      this._popupRef?.updatePosition();
    });
  }

  protected _forwardContentValues(
    instance: RdsDatepickerContentComponent<C, S, D>,
  ): void {
    instance.datepicker = this;
    instance._actionsPortal = this._actionsPortal;
  }

  private _createPopup(): void {
    const positionStrategy = this.overlay
      .position()
      .flexibleConnectedTo(this.datepickerInput.getConnectedOverlayOrigin())
      .withTransformOriginOn('.rds-datepicker-content')
      .withFlexibleDimensions(false)
      .withViewportMargin(8)
      .withLockedPosition();

    const overlayConfig = new OverlayConfig({
      positionStrategy: this._setConnectedPositions(positionStrategy),
      hasBackdrop: true,
      backdropClass: [
        'rds-overlay-transparent-backdrop',
        this._backdropHarnessClass,
      ],
      scrollStrategy: this.scrollStrategy(),
      panelClass: 'rds-datepicker-popup',
    });

    this._popupRef = this.overlay.create(overlayConfig);
    this._popupRef.overlayElement.setAttribute('role', 'dialog');

    merge(
      this._popupRef.backdropClick(),
      this._popupRef.detachments(),
      this._popupRef.keydownEvents().pipe(
        filter((event) => {
          return (
            (event.keyCode === ESCAPE && !hasModifierKey(event)) ||
            (this.datepickerInput &&
              hasModifierKey(event, 'altKey') &&
              event.keyCode === UP_ARROW)
          );
        }),
      ),
    ).subscribe((event) => {
      if (event) {
        event.preventDefault();
      }

      this.close();
    });
  }

  private _destroyPopup(): void {
    if (this._popupRef) {
      this._popupRef.dispose();
      this._popupRef = this._popupComponentRef = null;
    }
  }

  private _setConnectedPositions(
    strategy: FlexibleConnectedPositionStrategy,
  ): FlexibleConnectedPositionStrategy {
    const primaryX = this.xPosition === 'end' ? 'end' : 'start';
    const secondaryX = primaryX === 'start' ? 'end' : 'start';
    const primaryY = this.yPosition === 'above' ? 'bottom' : 'top';
    const secondaryY = primaryY === 'top' ? 'bottom' : 'top';

    return strategy.withPositions([
      {
        originX: primaryX,
        originY: secondaryY,
        overlayX: primaryX,
        overlayY: primaryY,
      },
      {
        originX: primaryX,
        originY: primaryY,
        overlayX: primaryX,
        overlayY: secondaryY,
      },
      {
        originX: secondaryX,
        originY: secondaryY,
        overlayX: secondaryX,
        overlayY: primaryY,
      },
      {
        originX: secondaryX,
        originY: primaryY,
        overlayX: secondaryX,
        overlayY: secondaryY,
      },
    ]);
  }
}
