import { RdsFileItem } from './file-item.class';
import {
  RdsFileItemProgressChange,
  RdsFileItemStatusChange,
  RdsUploadConfig,
} from './upload.types';
import {
  HttpClient,
  HttpErrorResponse,
  HttpEvent,
  HttpEventType,
  HttpProgressEvent,
  HttpRequest,
  HttpResponse,
} from '@angular/common/http';
import { Observable, Subscription } from 'rxjs';

export class RdsHttpClientWrapper<T = unknown> {
  private readonly form = new FormData();
  private readonly request: HttpRequest<FormData>;
  private readonly response$: Observable<HttpEvent<T>>;
  private responseSubscription?: Subscription;

  progress?: (change: RdsFileItemProgressChange) => void;
  success?: (change: RdsFileItemStatusChange<T>) => void;
  error?: (change: RdsFileItemStatusChange<T, HttpErrorResponse>) => void;
  complete?: (
    change: RdsFileItemStatusChange<T, HttpResponse<T> | HttpErrorResponse>,
  ) => void;
  cancel?: (change: RdsFileItemStatusChange<T>) => void;

  constructor(
    private readonly item: RdsFileItem,
    readonly config: RdsUploadConfig,
    private readonly client: HttpClient,
  ) {
    this.form.append('file', item.file, item.file.name);

    const method = config.method ?? 'POST';
    this.request = new HttpRequest(method, config.url, this.form, {
      headers: config.headers,
      reportProgress: true,
      withCredentials: !!config.authToken,
      responseType: config.responseType,
    });

    if (config.authToken) {
      this.request = this.request.clone({
        headers: this.request.headers.set('Authorization', config.authToken),
      });
    }

    this.response$ = this.client.request(this.request);
  }

  abort(): void {
    this.responseSubscription?.unsubscribe();
    this.responseSubscription = undefined;
    this.onAbort();
  }

  send(): void {
    this.responseSubscription = this.response$.subscribe({
      error: (e) => this.onError(e),
      next: (e) => {
        if (e.type === HttpEventType.UploadProgress) {
          this.onProgress(e);
        } else if (e.type === HttpEventType.Response) {
          this.onSuccess(e);
        }
      },
    });
  }

  private onSuccess(event: HttpResponse<T>): void {
    const change: RdsFileItemStatusChange<T> = {
      item: this.item,
      event,
    };

    this.success && this.success(change);
    this.complete && this.complete(change);
  }

  private onError(event: HttpErrorResponse): void {
    const change: RdsFileItemStatusChange<T, HttpErrorResponse> = {
      item: this.item,
      event,
    };

    this.error && this.error(change);
    this.complete && this.complete(change);
  }

  private onProgress(event: HttpProgressEvent): void {
    const progress =
      event.total != undefined
        ? Math.round((event.loaded * 100) / event.total)
        : null;

    this.progress && this.progress({ item: this.item, progress });
  }

  private onAbort(): void {
    const change: RdsFileItemStatusChange<T> = {
      item: this.item,
    };

    this.cancel && this.cancel(change);
    this.complete && this.complete(change);
  }
}
