import { BehaviorSubject, Subject } from 'rxjs';
import { RdsFileItem } from './file-item.class';
import { RdsUploadFilters } from './upload.filters';
import {
  RdsFileItemProgressChange,
  RdsFileItemStatusChange,
  RdsUploadConfig,
  RdsUploadFileAddError,
  RdsUploadFilter,
} from './upload.types';
import {
  HttpClient,
  HttpErrorResponse,
  HttpResponse,
} from '@angular/common/http';
import { RdsHttpClientWrapper } from './http-client-wrapper';

export class RdsFileUploader<T = unknown> {
  _config: RdsUploadConfig & Required<Pick<RdsUploadConfig, 'filters'>> = {
    autoUpload: true,
    filters: [],
    url: '',
  };

  readonly queue: RdsFileItem[] = [];
  readonly itemIdToRequest = new Map<number, RdsHttpClientWrapper<T>>();

  get config(): Readonly<RdsUploadConfig> {
    return this._config;
  }

  private readonly events = {
    fileInvalid: new Subject<RdsUploadFileAddError>(),
    fileItemSuccess: new Subject<RdsFileItemStatusChange<T>>(),
    fileItemError: new Subject<RdsFileItemStatusChange<T, HttpErrorResponse>>(),
    fileItemCanceled: new Subject<RdsFileItemStatusChange<T>>(),
    fileItemCompleted: new Subject<
      RdsFileItemStatusChange<T, HttpResponse<T> | HttpErrorResponse>
    >(),
    allComplete: new Subject<void>(),
    isUploading: new BehaviorSubject(false),
    config: new BehaviorSubject<Readonly<RdsUploadConfig>>(this._config),
    queue: new BehaviorSubject(this.queue),
  };

  readonly fileInvalid$ = this.events.fileInvalid.asObservable();
  readonly fileItemSuccess$ = this.events.fileItemSuccess.asObservable();
  readonly fileItemError$ = this.events.fileItemError.asObservable();
  readonly fileItemCanceled$ = this.events.fileItemCanceled.asObservable();
  readonly fileItemCompleted$ = this.events.fileItemCompleted.asObservable();
  readonly allComplete$ = this.events.allComplete.asObservable();
  readonly isUploading$ = this.events.isUploading.asObservable();
  readonly config$ = this.events.config.asObservable();
  readonly queue$ = this.events.queue.asObservable();

  get isUploading(): boolean {
    return this.events.isUploading.value;
  }

  private set isUploading(v: boolean) {
    this.events.isUploading.next(v);
  }

  constructor(
    config: RdsUploadConfig,
    private client?: HttpClient,
  ) {
    this.setConfig(config);
  }

  setConfig(config: RdsUploadConfig): void {
    this._config = {
      ...config,
      filters: RdsUploadFilters.compose(config.filters ?? [], config),
    };

    if (this._config.queueLimit == undefined) {
      this._config.queueLimit = this._config.multiple ? undefined : 1;
    }

    this.events.config.next(this.config);
  }

  updateConfig(update: Partial<RdsUploadConfig>): void {
    this._config = Object.assign(this._config, update);

    this._config.filters = RdsUploadFilters.compose(
      this._config.filters,
      this._config,
    );

    this._config = { ...this._config };
    this.events.config.next(this.config);
  }

  addToQueue(
    files: File[],
    filters?: RdsUploadFilter[] | string,
    bypassAutoUpload = false,
  ): RdsFileItem[] {
    const list = [...files];
    const selectedFilters = this.getFilters(filters);
    const addedItems: RdsFileItem[] = [];

    list.forEach((file) => {
      const fileItem = this.addFileToQueue(file, selectedFilters);

      if (!fileItem) {
        return;
      }

      addedItems.push(fileItem);

      if (!bypassAutoUpload && this._config.autoUpload) {
        this.uploadItem(fileItem);
      }
    });

    this.events.queue.next(this.queue);

    return addedItems;
  }

  removeFromQueue(value: RdsFileItem | number): boolean {
    const index = this.getIndexOfItem(value);

    if (index == null) {
      return false;
    }

    this.removeItem(index);

    return true;
  }

  clearQueue(): void {
    for (let i = this.queue.length; i >= 0; i--) {
      this.removeItem(i, false);
    }

    this.events.queue.next(this.queue);
  }

  uploadItem(value: RdsFileItem): void {
    const index = this.getIndexOfItem(value);

    if (index == null) {
      return;
    }

    const item = this.queue[index];

    if (
      (this.isUploading && this._config.uploadSequentially) ||
      item.hasFailedFilters
    ) {
      return;
    }

    if (!this.client) {
      throw new Error(
        'Missing HttpClient! Pass it as a RdsUploader\'s constructor param or use "registerHttpClient" method to provide one.',
      );
    }

    this.isUploading = true;

    const request = new RdsHttpClientWrapper<T>(
      item,
      this._config,
      this.client,
    );
    request.progress = (change) => this.onProgressItem(change);
    request.success = (change) => this.onSuccessItem(change);
    request.cancel = (change) => this.onCancelItem(change);
    request.error = (change) => this.onErrorItem(change);
    request.complete = (change) => this.onCompleteItem(change);

    item.onBeforeUpload();

    request.send();

    this.itemIdToRequest.set(item.id, request);
  }

  cancelItem(value: RdsFileItem | number): void {
    const index = this.getIndexOfItem(value);

    if (index == null) {
      return;
    }

    const item = this.queue[index];

    if (item && item.isUploading) {
      const request = this.itemIdToRequest.get(item.id);
      request?.abort();
    }
  }

  uploadAll(): void {
    const items = this.queue.filter(
      (item) =>
        item.state !== 'success' && !item.isUploading && !item.hasFailedFilters,
    );

    if (!items.length) {
      return;
    }

    items.forEach((item) => {
      this.uploadItem(item);
    });
  }

  cancelAll(): void {
    this.queue
      .filter((item) => item.isUploading)
      .forEach((item) => this.cancelItem(item));
  }

  destroy(): void {
    this.cancelAll();
    Object.values(this.events).forEach((subject) => subject.complete());
  }

  registerHttpClient(client: HttpClient): void {
    this.client = client;
  }

  isHttpClientRegistered(): boolean {
    return !!this.client;
  }

  private removeItem(index: number, emitEvent = true): void {
    const fileItem = this.queue[index];

    if (fileItem.isUploading) {
      this.cancelItem(fileItem);
    }

    this.queue.splice(index, 1);
    this.itemIdToRequest.delete(fileItem.id);
    emitEvent && this.events.queue.next(this.queue);
  }

  private getIndexOfItem(value: RdsFileItem | number): number | null {
    if (typeof value === 'number') {
      return this.isValidIndex(value) ? value : null;
    } else {
      return this.queue.indexOf(value);
    }
  }

  private isValidIndex(index: number): boolean {
    return 0 <= index && index < this.queue.length;
  }

  private getReadyItems(): RdsFileItem[] {
    return this.queue.filter((item) => item.state === 'ready');
  }

  private onErrorItem(
    event: RdsFileItemStatusChange<T, HttpErrorResponse>,
  ): void {
    event.item.onError();
    this.events.fileItemError.next(event);
  }

  private onCompleteItem(
    change: RdsFileItemStatusChange<T, HttpResponse<T> | HttpErrorResponse>,
  ): void {
    if (this._config.removeAfterUpload && change.item.state === 'success') {
      this.removeFromQueue(change.item);
    }

    this.events.fileItemCompleted.next(change);

    const nextItem = this.getReadyItems()[0];
    this.isUploading = false;

    if (nextItem) {
      this.uploadItem(nextItem);

      return;
    }

    this.events.allComplete.next();
  }

  private addFileToQueue(
    file: File,
    filters: RdsUploadFilter[],
  ): RdsFileItem | null {
    const { isValid, failedFilters } = this.isValidFile(file, filters);

    if (!isValid) {
      this.events.fileInvalid.next({
        file,
        failedFilters,
        config: this.config,
      });
    }

    if (failedFilters.some((f) => f.preventsAddingToQueue)) {
      return null;
    }

    const fileItem = new RdsFileItem(file, failedFilters);
    this.queue.push(fileItem);

    return fileItem;
  }

  private getFilters(filters?: RdsUploadFilter[] | string): RdsUploadFilter[] {
    if (!filters) {
      return this._config.filters;
    }

    if (Array.isArray(filters)) {
      return filters;
    }

    const names = filters.match(/[^\s,]+/g);

    if (!names) {
      return this._config.filters;
    }

    return this._config.filters.filter((filter) => names.includes(filter.name));
  }

  private isValidFile(
    file: File,
    filters: RdsUploadFilter[],
  ): { isValid: boolean; failedFilters: RdsUploadFilter[] } {
    const failedFilters: RdsUploadFilter[] = [];

    filters.forEach((filter) => {
      if (!filter.fn(file, this.config, this.queue)) {
        failedFilters.push(filter);
      }
    });

    return { isValid: failedFilters.length === 0, failedFilters };
  }

  private onProgressItem(change: RdsFileItemProgressChange): void {
    change.item.onProgress(change.progress);
  }

  private onSuccessItem(change: RdsFileItemStatusChange<T>): void {
    change.item.onSuccess();
    this.events.fileItemSuccess.next(change);
  }

  private onCancelItem(change: RdsFileItemStatusChange<T>): void {
    change.item.onAbort();
    this.events.fileItemCanceled.next(change);
  }
}
