import { AbstractControl, FormGroupDirective, NgForm } from '@angular/forms';
import { ErrorStateMatcher } from '../core';

export const updateErrorState = (
  errorState: boolean,
  parentFormGroup: FormGroupDirective | null,
  parentForm: NgForm | null,
  control: AbstractControl | null,
  matcher?: ErrorStateMatcher,
): boolean => {
  const oldState = errorState;
  const parent = parentFormGroup || parentForm;
  const newState =
    matcher instanceof ErrorStateMatcher
      ? matcher.isErrorState(control, parent)
      : isErrorState(control, parent);

  if (newState !== oldState) {
    return newState;
  }

  return oldState;
};

export const isErrorState = (
  control: AbstractControl | null,
  form: FormGroupDirective | NgForm | null,
): boolean => {
  return !!(
    control &&
    control.invalid &&
    (control.touched || (form && form.submitted))
  );
};
