import {
  AfterViewInit,
  ContentChildren,
  DestroyRef,
  Directive,
  ElementRef,
  EventEmitter,
  inject,
  Input,
  NgZone,
  numberAttribute,
  Output,
  QueryList,
  Renderer2,
} from '@angular/core';
import { fromEvent, Observable } from 'rxjs';
import { RdsTableDragHandleDirective } from './table-drag-handle.directive';
import { filter, map, take, takeUntil } from 'rxjs/operators';
import { PointerEvent } from 'react';
import { DOCUMENT } from '@angular/common';
import {
  cellsToRowsHTML,
  isElement,
  isInsideRect,
} from './table-draggable.helpers';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

const HEADER_ROW_SELECTOR = '.rds-header-row';
const HEADER_CELL_SELECTOR = '.rds-header-cell';

export type RdsTableDragEndEvent = {
  oldIndex: number;
  newIndex: number;
};

type RdsTableDragStartEvent = {
  nativeEvent: PointerEvent<Element>;
  draggedHeaderCell: Element;
};

@Directive({
  selector: '[rdsTableDraggable], [rds-table-draggable]',
})
export class RdsTableDraggableDirective implements AfterViewInit {
  private readonly destroyRef = inject(DestroyRef);
  private readonly document = inject(DOCUMENT);
  private readonly renderer = inject(Renderer2);
  private readonly originalTable =
    inject<ElementRef<HTMLElement>>(ElementRef).nativeElement;
  private readonly zone = inject(NgZone);

  private header: HTMLElement | null = null;
  private container: HTMLElement;
  private boundingRect: DOMRect;
  private columnPreview: HTMLElement | null = null;
  private currentIndex: number;
  private originalIndex: number;
  private clientX: number;
  private clientY: number;
  private columnRects: DOMRect[] = [];
  private moveColumnAnimationFrame: number;

  @ContentChildren(RdsTableDragHandleDirective, { descendants: true })
  dragHandlesQuery: QueryList<RdsTableDragHandleDirective>;

  @Input({ transform: numberAttribute }) maxPreviewRows = 10;

  @Output()
  rdsTableDragEnd = new EventEmitter<RdsTableDragEndEvent>();

  ngAfterViewInit(): void {
    this.zone.runOutsideAngular(() => {
      this.createDragStartObservable().subscribe((e) => this.onDragStart(e));
    });
  }

  private onDragStart({
    draggedHeaderCell,
    nativeEvent,
  }: RdsTableDragStartEvent): void {
    this.columnRects = [];
    this.createAndInsertContainerElement();
    this.saveCoords(nativeEvent);
    this.getHeaderCells().forEach((headerCell, index) =>
      this.createColumnAndHandleDrag(draggedHeaderCell, headerCell, index),
    );
    this.createDragMoveObservable().subscribe((e) => this.onDragMove(e));
    this.createDragEndObservable().subscribe(() => this.onDragEnd());
  }

  private createColumnAndHandleDrag(
    draggedHeaderCell: Element,
    headerCell: Element,
    index: number,
  ): void {
    headerCell === draggedHeaderCell
      ? this.addDraggedColumn(headerCell, index)
      : this.addColumn(headerCell, index);
  }

  private addColumn(headerCell: Element, index: number): void {
    const { width } = headerCell.getBoundingClientRect();
    const rowsHTML = cellsToRowsHTML(this.getColumnCells(index));
    this.container.innerHTML += this.createColumnHTML(
      rowsHTML,
      width,
      headerCell,
    );

    this.saveLastColumnRect(index);
  }

  private addDraggedColumn(headerCell: Element, index: number): void {
    this.currentIndex = index;
    this.originalIndex = index;

    const { width, x, y } = headerCell.getBoundingClientRect();
    const rowsHTML = cellsToRowsHTML(
      this.getColumnCells(index),
      this.maxPreviewRows,
    );

    this.container.innerHTML += this.createColumnPlaceholderHTML(
      width,
      this.boundingRect.height,
    );

    this.saveLastColumnRect(index);

    this.columnPreview = this.createColumnPreview(headerCell, width, rowsHTML);
    this.renderer.insertBefore(this.document.body, this.columnPreview, null);

    this.moveColumnAnimationFrame = requestAnimationFrame(
      this.followPointerFactory(x, y),
    );
  }

  private saveLastColumnRect(index: number): void {
    this.columnRects[index] =
      // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
      this.container.lastElementChild!.getBoundingClientRect();
  }

  private createDragStartObservable(): Observable<RdsTableDragStartEvent> {
    return fromEvent<PointerEvent>(this.originalTable, 'pointerdown').pipe(
      map((event) => this.addHeaderCellData(event)),
      filter(Boolean),
      takeUntilDestroyed(this.destroyRef),
    );
  }

  private createDragMoveObservable(): Observable<PointerEvent<Element>> {
    return fromEvent<PointerEvent>(this.document.body, 'pointermove').pipe(
      takeUntil(this.rdsTableDragEnd),
      takeUntilDestroyed(this.destroyRef),
    );
  }

  private addHeaderCellData(
    event: PointerEvent<Element>,
  ): RdsTableDragStartEvent | null {
    if (!isElement(event.target)) {
      return null;
    }

    if (
      this.dragHandlesQuery.length &&
      !this.dragHandlesQuery.some(
        (handle) => handle.nativeElement === event.target,
      )
    ) {
      return null;
    }

    // header is rebuilt after each drag end, so we need to query it on each drag start
    this.header = event.target.closest(HEADER_ROW_SELECTOR);

    if (!this.header) {
      return null;
    }

    const draggedHeaderCell = event.target.closest(HEADER_CELL_SELECTOR);

    if (!draggedHeaderCell) {
      return null;
    }

    return { draggedHeaderCell, nativeEvent: event };
  }

  private createAndInsertContainerElement(): void {
    this.boundingRect = this.originalTable.getBoundingClientRect();

    this.container = this.renderer.createElement('div') as HTMLElement;
    this.renderer.addClass(this.container, 'rds-table-draggable-preview');
    this.renderer.setStyle(
      this.container,
      'width',
      this.boundingRect.width + 'px',
    );
    this.renderer.setStyle(
      this.container,
      'height',
      this.boundingRect.height + 'px',
    );

    this.renderer.insertBefore(
      this.originalTable.parentElement,
      this.container,
      this.originalTable,
    );
  }

  private getColumnCells(columnIndex: number): Element[] {
    return Array.from(
      this.originalTable.querySelectorAll(
        `.rds-cell:nth-child(${columnIndex + 1})`,
      ),
    );
  }

  private createColumnPreview(
    headerCell: Element,
    width: number,
    rowsHTML: string,
  ): HTMLElement {
    const columnPreview = this.renderer.createElement('div') as HTMLDivElement;
    this.renderer.setStyle(columnPreview, 'position', 'fixed');
    this.renderer.setStyle(columnPreview, 'top', '0');
    this.renderer.setStyle(columnPreview, 'left', '0');
    this.renderer.setStyle(columnPreview, 'z-index', '99999999');

    const height = headerCell.parentElement?.offsetHeight;

    columnPreview.innerHTML = `
      <table class="${
        this.originalTable.classList.value
      }" style="width: ${width}px; ">
          <thead>
            <tr class="rds-row">
              <th class="rds-header-cell" style="height: ${
                height ? height + 'px' : 'unset'
              }">${headerCell.innerHTML}</th>
            </tr>
          </thead>
          <tbody>
            ${rowsHTML}
          </tbody>
      </table>
        `;

    return columnPreview;
  }

  private createColumnHTML(
    rowsHTML: string,
    width: number,
    headerCell: Element,
  ): string {
    const height = headerCell.parentElement?.offsetHeight;

    return `
          <div style="flex-shrink: 0;">
            <table class="${
              this.originalTable.classList.value
            }" style="width: ${width}px;">
                <thead>
                  <tr class="rds-row">
                    <th class="rds-header-cell" style="height: ${
                      height ? height + 'px' : 'unset'
                    }">${headerCell.innerHTML}</th>
                  </tr>
                </thead>
                <tbody>
                  ${rowsHTML}
                </tbody>
            </table>
          </div>
        `;
  }

  private createColumnPlaceholderHTML(width: number, height: number): string {
    return `
      <div class="rds-table-draggable-column-placeholder" style="width: ${width}px; height: ${height}px"></div>
    `;
  }

  private getHeaderCells(): Element[] {
    return Array.from(
      this.header?.querySelectorAll(HEADER_CELL_SELECTOR) ?? [],
    );
  }

  private swapColumns(aIndex: number, bIndex: number): void {
    const aColumn = this.container.children.item(aIndex);
    const bColumn = this.container.children.item(bIndex);

    if (!aColumn || !bColumn) {
      return;
    }

    if (bIndex > aIndex) {
      this.renderer.insertBefore(this.container, bColumn, aColumn);
    } else {
      this.renderer.insertBefore(this.container, aColumn, bColumn);
    }
  }

  private createDragEndObservable(): Observable<PointerEvent> {
    return fromEvent<PointerEvent>(window, 'pointerup', { once: true }).pipe(
      take(1),
      takeUntilDestroyed(this.destroyRef),
    );
  }

  private onDragMove(event: PointerEvent<Element>): void {
    this.saveCoords(event);

    for (let i = 0; i < this.columnRects.length; i++) {
      if (i === this.currentIndex) {
        continue;
      }

      const rect = this.columnRects[i];

      if (isInsideRect(event, rect)) {
        this.swapColumns(this.currentIndex, i);
        this.currentIndex = i;

        break;
      }
    }
  }

  private saveCoords({ clientX, clientY }: PointerEvent<Element>): void {
    this.clientX = clientX;
    this.clientY = clientY;
  }

  private onDragEnd(): void {
    cancelAnimationFrame(this.moveColumnAnimationFrame);
    this.removeContainerAndPreview();
    this.emitChange();
  }

  private emitChange(): void {
    this.zone.run(() => {
      this.rdsTableDragEnd.emit({
        oldIndex: this.originalIndex,
        newIndex: this.currentIndex,
      });
    });
  }

  private removeContainerAndPreview(): void {
    this.renderer.removeChild(this.container.parentElement, this.container);

    if (this.columnPreview) {
      this.renderer.removeChild(this.document.body, this.columnPreview);
    }
  }

  private followPointerFactory(
    columnOriginX: number,
    columnOriginY: number,
  ): () => void {
    const offsetX = this.clientX - columnOriginX;
    const offsetY = this.clientY - columnOriginY;

    const followPointer = (): void => {
      this.renderer.setStyle(
        this.columnPreview,
        'transform',
        `translate(${this.clientX - offsetX}px, ${this.clientY - offsetY}px)`,
      );

      this.moveColumnAnimationFrame = requestAnimationFrame(followPointer);
    };

    return followPointer;
  }
}
