import {
  booleanAttribute,
  ChangeDetectionStrategy,
  Component,
  HostBinding,
  Input,
  ViewEncapsulation,
} from '@angular/core';
import { RdsListItemComponent } from './list-item.component';
import { RDS_NESTABLE_LIST_ITEM_TEMPLATE } from './list-item.templates';

@Component({
  template: RDS_NESTABLE_LIST_ITEM_TEMPLATE,
  selector: 'button[rds-nestable-list-item], button[rdsNestableListItem]',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  providers: [
    {
      provide: RdsListItemComponent,
      useExisting: RdsNestableListItemComponent,
    },
  ],
})
export class RdsNestableListItemComponent extends RdsListItemComponent {
  @HostBinding('class')
  protected override get cssClasses(): string[] {
    const classes = [...super.cssClasses, 'rds-nestable-list-item'];

    if (!this.disabled && this.activated) {
      classes.push('rds-nestable-list-item-activated');
    }

    return classes;
  }

  @Input({ transform: booleanAttribute }) hasChildItems = false;
  @Input({ transform: booleanAttribute }) activated = false;
}
