import {
  booleanAttribute,
  Component,
  EventEmitter,
  HostBinding,
  HostListener,
  inject,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { RdsChipComponent, RdsChipEditedEvent } from '../../chips';
import { RdsChipEditInputDirective } from '../../chips/chip-edit-input';
import { DOCUMENT } from '@angular/common';
import { BACKSPACE, DELETE, ENTER } from '@angular/cdk/keycodes';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

@Component({
  selector: 'rds-input-chip',
  templateUrl: './input-chip.component.html',
  styleUrls: ['../../chips/chips.common.scss', './input-chip.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [
    { provide: RdsChipComponent, useExisting: RdsInputChipComponent },
  ],
})
export class RdsInputChipComponent<T>
  extends RdsChipComponent<T>
  implements OnInit, OnDestroy
{
  private readonly document = inject(DOCUMENT);

  /**
   * The editing action has to be triggered in a timeout. While we're waiting on it, a blur
   * event might occur which will interrupt the editing. This flag is used to avoid interruptions
   * while the editing action is being initialized.
   */
  private editStartPending = false;

  editReady = false;
  isEditing = false;

  @Input({ transform: booleanAttribute }) editable = false;

  @Input()
  ariaDescription: string | null = null;

  @Output() readonly edited = new EventEmitter<RdsChipEditedEvent<T>>();

  @ViewChild(RdsChipEditInputDirective) editInput: RdsChipEditInputDirective;

  @HostBinding('attr.role')
  override role = 'row';

  ngOnInit(): void {
    this.blur$.pipe(takeUntilDestroyed(this.destroyRef)).subscribe(() => {
      if (this.isEditing && !this.editStartPending) {
        this.onEditFinish();
      }
    });
  }

  @HostBinding('class')
  get classes(): Record<string, boolean> {
    return {
      'rds-chips__chip': true,
      'rds-input-chip': true,
      'rds-input-chip-editing': this.isEditing,
      'rds-chips__chip--disabled': this.disabled,
    };
  }

  @HostListener('focus')
  handleFocus(): void {
    if (!this.isEditing && !this.disabled) {
      this.focus();
    }
  }

  @HostListener('dblclick')
  handleDoubleclick(): void {
    if (!this.disabled && this.editable) {
      this.startEditing();
    }
  }

  override handleKeydown(event: KeyboardEvent): void {
    if (event.keyCode === ENTER && !this.disabled) {
      if (this.isEditing) {
        event.preventDefault();
        this.onEditFinish();
      } else if (this.editable) {
        this.startEditing();
      }
    } else if (this.isEditing) {
      event.stopPropagation();
    } else {
      if (event.keyCode === BACKSPACE || event.keyCode === DELETE) {
        event.preventDefault();
        this.closeChip.emit();
      }
    }
  }

  private startEditing(): void {
    const value = this.value;
    this.isEditing = true;
    this.editStartPending = true;

    setTimeout((): void => {
      this.getEditInput().initialize(
        typeof value === 'string' ? value : this.innerText,
      );
      this.editStartPending = false;
      this.editReady = true;
    });
  }

  private onEditFinish(): void {
    this.isEditing = false;
    this.editStartPending = false;
    this.editReady = false;
    this.edited.emit({ source: this, value: this.getEditInput().getValue() });
    this.getEditInput().setValue('');

    if (
      this.document.activeElement === this.getEditInput().getNativeElement() ||
      this.document.activeElement === this.document.body
    ) {
      this.focus();
    }
  }

  private getEditInput(): RdsChipEditInputDirective {
    return this.editInput;
  }
}
