import {
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  HostBinding,
  Input,
  OnChanges,
  SimpleChanges,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';

export type RdsTreeNodeTextAlignment = 'left' | 'center' | 'right';

@Component({
  selector: 'rds-tree-node-text',
  exportAs: 'rdsTreeText',
  template: `
    <span class="rds-tree-text-node" #rdsTreeNodeElement>
      <ng-content />
    </span>
  `,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsTreeNodeTextComponent implements OnChanges {
  @HostBinding('class.rds-tree-text-node') truncateClass = true;
  @HostBinding('class.rds-tree-text-node__container') containerClass = true;
  @HostBinding('class.rds-tree-node--content-left') contentLeftClass = true;
  @HostBinding('class.rds-tree-text-node__container-align-right')
  alignRightClass = false;
  @HostBinding('class.rds-tree-text-node__container-align-center')
  alignCenterClass = false;

  @ViewChild('rdsTreeNodeElement', { static: true })
  rdsTreeNodeElement: ElementRef;

  @Input()
  textAlignment: RdsTreeNodeTextAlignment = 'left';

  ngOnChanges(changes: SimpleChanges): void {
    if (
      changes['textAlignment'] &&
      changes['textAlignment'].previousValue !==
        changes['textAlignment'].currentValue
    ) {
      if (this.textAlignment === 'right') {
        this.alignRightClass = true;
        this.alignCenterClass = false;
      } else if (this.textAlignment === 'center') {
        this.alignRightClass = false;
        this.alignCenterClass = true;
      } else {
        this.alignRightClass = false;
        this.alignCenterClass = false;
      }
    }
  }
}
