import { inject, Injectable } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { fromEvent, Subject } from 'rxjs';
import { distinctUntilChanged, filter, map, takeUntil } from 'rxjs/operators';
import { RdsThemeDark, RdsThemeLight, RdsThemeName } from '@rds/core';
import { RDS_THEME_CONFIG } from './theme.config';

@Injectable({
  providedIn: 'root',
})
export class RdsThemeService<T extends string = RdsThemeName | 'auto'> {
  private static readonly ATTRIBUTE_NAME = 'rds-theme';
  private readonly documentElement = inject(DOCUMENT).documentElement;
  private readonly config = inject(RDS_THEME_CONFIG, { optional: true });
  private readonly _killAutoTheme$ = new Subject<void>();
  private readonly _globalThemeChange$ = new Subject<T | null>();
  readonly globalThemeChange$ = this._globalThemeChange$.pipe(
    distinctUntilChanged(),
  );

  constructor() {
    if (!this.config || this.config.setAutoTheme) {
      this.setAuto();
    }

    new MutationObserver(() => this.emitValue()).observe(this.documentElement, {
      attributes: true,
      attributeFilter: [RdsThemeService.ATTRIBUTE_NAME],
    });
  }

  setAuto(): void {
    this._killAutoTheme$.next();

    const savedThemeChoice = localStorage.getItem(
      RdsThemeService.ATTRIBUTE_NAME,
    );

    if (savedThemeChoice) {
      this.set(savedThemeChoice as T);

      return;
    }

    if (window.matchMedia) {
      if (
        window.matchMedia('(prefers-color-scheme: dark)').matches &&
        !this.isDark()
      ) {
        this.setDark();
      }

      fromEvent<MediaQueryListEvent>(
        window.matchMedia('(prefers-color-scheme: dark)'),
        'change',
      )
        .pipe(
          map((e) => (e.matches ? RdsThemeDark : RdsThemeLight)),
          filter((t) => t !== this.get()),
          takeUntil(this._killAutoTheme$),
        )
        .subscribe((t) => this.set(t as T));
    }
  }

  setLight(element: Element = this.documentElement): void {
    this.set(RdsThemeLight as T, element);
  }

  setDark(element: Element = this.documentElement): void {
    this.set(RdsThemeDark as T, element);
  }

  isDark(element: Element = this.documentElement): boolean {
    return this.get(element) === RdsThemeDark;
  }

  isLight(element: Element = this.documentElement): boolean {
    return ([RdsThemeLight, null] as (T | null)[]).includes(this.get(element));
  }

  set(theme: T, element: Element = this.documentElement): void {
    element.setAttribute(RdsThemeService.ATTRIBUTE_NAME, theme as string);
    localStorage.setItem(RdsThemeService.ATTRIBUTE_NAME, theme);

    if (element === this.documentElement) {
      this.emitValue();
    }
  }

  clear(element: Element = this.documentElement): void {
    element.removeAttribute(RdsThemeService.ATTRIBUTE_NAME);
    localStorage.setItem(RdsThemeService.ATTRIBUTE_NAME, RdsThemeLight);
    this.emitValue();
  }

  get(element: Element = this.documentElement): T | null {
    return element.getAttribute(RdsThemeService.ATTRIBUTE_NAME) as T;
  }

  has(element: Element = this.documentElement): boolean {
    return element.hasAttribute(RdsThemeService.ATTRIBUTE_NAME);
  }

  private emitValue(): void {
    this._globalThemeChange$.next(
      this.documentElement.getAttribute(
        RdsThemeService.ATTRIBUTE_NAME,
      ) as T | null,
    );
  }
}
