import { FocusOrigin } from '@angular/cdk/a11y';
import { ContentObserver } from '@angular/cdk/observers';
import {
  AfterViewInit,
  booleanAttribute,
  ChangeDetectorRef,
  Component,
  ContentChild,
  DestroyRef,
  EventEmitter,
  HostBinding,
  HostListener,
  inject,
  Input,
  OnChanges,
  Output,
  SimpleChanges,
} from '@angular/core';
import { RdsAvatarComponent, RdsAvatarSize } from '../avatar';
import { RdsListItemComponent, RdsListItemSize } from './list-item.component';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

const RDS_LIST_ITEM_SIZE_TO_AVATAR_SIZE_MAP: Record<
  RdsListItemSize,
  RdsAvatarSize
> = {
  s: 'xs',
  m: 's',
  l: 'm',
};

@Component({ template: '' })
export abstract class RdsAbstractSelectableListItemComponent
  extends RdsListItemComponent
  implements AfterViewInit, OnChanges
{
  private readonly destroyRef = inject(DestroyRef);
  protected readonly contentObserver = inject(ContentObserver);
  protected readonly cdr = inject(ChangeDetectorRef);

  @ContentChild(RdsAvatarComponent) protected avatar: RdsAvatarComponent;

  @Output() readonly checkedChanged = new EventEmitter<boolean>();

  @Input()
  @HostBinding('attr.role')
  override role: 'menuitem' | 'option' = 'option';

  @HostBinding('class')
  protected override get cssClasses(): string[] {
    return [...super.cssClasses, this.getSelectTypeClass()];
  }

  @HostBinding('class.rds-list-item--checked')
  @Input({ transform: booleanAttribute })
  checked = false;

  ngAfterViewInit(): void {
    this.contentObserver
      .observe(this.elementRef.nativeElement)
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => {
        if (this.avatar) {
          this.avatar.disableInteractions = true;
          this.adjustAvatarSize();
        }

        this.cdr.markForCheck();
      });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['size']) {
      this.adjustAvatarSize();
    }
  }

  @HostListener('click')
  toggle(): void {
    if (!this.disabled) {
      this.checked = !this.checked;
      this.checkedChanged.emit(this.checked);
      this.cdr.markForCheck();
    }
  }

  protected abstract getSelectTypeClass(): string;

  private adjustAvatarSize(): void {
    if (
      this.avatar &&
      this.avatar.size !== RDS_LIST_ITEM_SIZE_TO_AVATAR_SIZE_MAP[this.size]
    ) {
      this.avatar.size = RDS_LIST_ITEM_SIZE_TO_AVATAR_SIZE_MAP[this.size];
    }
  }

  override focus(origin?: FocusOrigin): void {
    this.renderer.addClass(this.elementRef.nativeElement, 'cdk-focused');

    if (origin === 'keyboard') {
      this.renderer.addClass(
        this.elementRef.nativeElement,
        'cdk-keyboard-focused',
      );
    }

    this.cdr.markForCheck();
    this._focused.next(this);
  }
}
