import { InjectionToken } from '@angular/core';
import { Overlay, ScrollStrategy } from '@angular/cdk/overlay';
import { RdsPopoverDefaultOptions } from './popover.types';

const SCROLL_THROTTLE_MS = 20;

export const RDS_POPOVER_SCROLL_STRATEGY = new InjectionToken<
  () => ScrollStrategy
>('RDS_POPOVER_SCROLL_STRATEGY');

/** @docs-private */
export function RDS_POPOVER_SCROLL_STRATEGY_FACTORY(
  overlay: Overlay,
): () => ScrollStrategy {
  return () =>
    overlay.scrollStrategies.reposition({ scrollThrottle: SCROLL_THROTTLE_MS });
}

/** @docs-private */
export const RDS_POPOVER_SCROLL_STRATEGY_FACTORY_PROVIDER = {
  provide: RDS_POPOVER_SCROLL_STRATEGY,
  deps: [Overlay],
  useFactory: RDS_POPOVER_SCROLL_STRATEGY_FACTORY,
};

export const RDS_POPOVER_DEFAULT_OPTIONS =
  new InjectionToken<RdsPopoverDefaultOptions>('RDS_POPOVER_DEFAULT_OPTIONS', {
    providedIn: 'root',
    factory: RDS_POPOVER_DEFAULT_OPTIONS_FACTORY,
  });

/** @docs-private */
export function RDS_POPOVER_DEFAULT_OPTIONS_FACTORY(): RdsPopoverDefaultOptions {
  return {
    hideDelay: 0,
    showDelay: 0,
    touchendHideDelay: 1500,
    hideArrow: false,
  };
}
