import {
  AfterViewChecked,
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  DestroyRef,
  ElementRef,
  HostBinding,
  inject,
  Input,
  OnChanges,
  OnDestroy,
  QueryList,
  SimpleChanges,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { fromEvent, merge, Subject } from 'rxjs';
import { delay, filter, map, takeUntil } from 'rxjs/operators';
import { RdsTimelineEventComponent } from './timeline-event/timeline-event.component';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

export type RdsTimelineAlign = 'start' | 'end' | 'alternate';
export type RdsTimelineOrientation = 'vertical' | 'horizontal';
export type RdsTimelinePresentationMode = 'slider' | 'pagination' | 'scroll';

@Component({
  selector: 'rds-timeline',
  templateUrl: './timeline.component.html',
  styleUrls: ['./timeline.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class RdsTimelineComponent
  implements AfterViewInit, AfterViewChecked, OnChanges, OnDestroy
{
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly destroyRef = inject(DestroyRef);
  private readonly removeOldHandlers$ = new Subject<void>();

  @Input() align: RdsTimelineAlign = 'start';
  @Input() presentationMode: RdsTimelinePresentationMode = 'scroll';
  @Input() orientation: RdsTimelineOrientation = 'vertical';

  protected activePage = 1;
  protected numberOfPages = 0;
  private readonly triggerCalculation$ = new Subject<void>();
  private readonly scroll$ = new Subject<'start' | 'end'>();
  private isScrolling = false;
  private isScrollable: boolean;
  private pageOffsetFactor = 0.3;

  @HostBinding('class') protected class = 'rds-timeline';
  @ViewChild('widgetsContent')
  protected widgetsContent!: ElementRef<HTMLElement>;

  @ContentChildren(RdsTimelineEventComponent) protected events: QueryList<
    RdsTimelineEventComponent<unknown>
  >;

  protected get showGradientRight(): boolean {
    return (
      this.presentationMode !== 'scroll' &&
      this.orientation === 'horizontal' &&
      !this.isStartButtonActive &&
      this.isEndButtonActive
    );
  }

  protected get showGradientLeft(): boolean {
    return (
      this.presentationMode !== 'scroll' &&
      this.orientation === 'horizontal' &&
      this.isStartButtonActive &&
      !this.isEndButtonActive
    );
  }

  protected get showCircleGradientHorizontal(): boolean {
    return (
      this.presentationMode !== 'scroll' &&
      this.orientation === 'horizontal' &&
      this.isStartButtonActive &&
      this.isEndButtonActive
    );
  }

  protected get showCircleGradientVertical(): boolean {
    return (
      this.presentationMode !== 'scroll' &&
      this.orientation === 'vertical' &&
      this.isStartButtonActive &&
      this.isEndButtonActive
    );
  }

  protected get showGradientTop(): boolean {
    return (
      this.presentationMode !== 'scroll' &&
      this.orientation === 'vertical' &&
      !this.isStartButtonActive &&
      this.isEndButtonActive
    );
  }

  protected get showGradientBottom(): boolean {
    return (
      this.presentationMode !== 'scroll' &&
      this.orientation === 'vertical' &&
      this.isStartButtonActive &&
      !this.isEndButtonActive
    );
  }

  protected get isStartButtonActive(): boolean {
    return this.numberOfPages > 0 && this.activePage > 1;
  }

  protected get isEndButtonActive(): boolean {
    return this.numberOfPages > 0 && this.activePage < this.numberOfPages;
  }

  ngAfterViewInit(): void {
    this.triggerCalculation$
      .pipe(delay(1), takeUntilDestroyed(this.destroyRef))
      .subscribe(() => this.calculateNumberOfPages());

    this.setupScrollListeners();

    this.setupEventHandlers(this.events);

    this.events.changes
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe((events) => {
        this.removeOldHandlers$.next();
        this.setupEventHandlers(events);
        this.triggerCalculation$.next();
      });

    this.triggerCalculation$.next();
  }

  ngAfterViewChecked(): void {
    if (this.orientation === 'horizontal') {
      const newValue =
        this.widgetsContent.nativeElement.scrollWidth >
        this.widgetsContent.nativeElement.clientWidth;
      this.contentScroll(newValue);
    } else {
      const newValue =
        this.widgetsContent.nativeElement.scrollHeight >
        this.widgetsContent.nativeElement.clientHeight;
      this.contentScroll(newValue);
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (
      'orientation' in changes ||
      'align' in changes ||
      'presentationMode' in changes
    ) {
      this.activePage = 1;
      this.triggerCalculation$.next();
    }
  }

  ngOnDestroy(): void {
    this.triggerCalculation$.complete();
  }

  hasPrevPage(): boolean {
    return 1 < this.activePage && this.activePage <= this.numberOfPages;
  }

  hasNextPage(): boolean {
    return this.activePage < this.numberOfPages;
  }

  scrollStart(): void {
    this.scroll$.next('start');
  }

  scrollEnd(): void {
    this.scroll$.next('end');
  }

  protected contentScroll(newValue?: boolean): void {
    if (this.isScrollable !== newValue) {
      this.isScrollable = !!newValue;
      this.cdr.markForCheck();
    }
  }

  private setupEventHandlers<T>(
    events: QueryList<RdsTimelineEventComponent<T>>,
  ): void {
    merge(...events.map((e) => e.selected.pipe(map(() => e))))
      .pipe(
        takeUntil(this.removeOldHandlers$),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe((e) => this.updateEventStates(e));
  }

  private setupScrollListeners(): void {
    fromEvent(this.widgetsContent.nativeElement, 'scroll')
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => (this.isScrolling = true));
    fromEvent(this.widgetsContent.nativeElement, 'scrollend')
      .pipe(takeUntilDestroyed(this.destroyRef))
      .subscribe(() => (this.isScrolling = false));

    this.scroll$
      .pipe(
        filter(() => !this.isScrolling),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe((dir) => this.scroll(dir));
  }

  private updateEventStates<T>(selected: RdsTimelineEventComponent<T>): void {
    if (selected.event.status !== 'disabled') {
      this.events.forEach((eventComponent) => {
        eventComponent.isActive = selected === eventComponent;
      });
    }
  }

  private scroll(direction: 'start' | 'end'): void {
    const hasPage =
      direction === 'end' ? this.hasNextPage() : this.hasPrevPage();

    if (hasPage) {
      const directionSign = direction === 'end' ? 1 : -1;
      this.activePage = this.activePage + directionSign;
      const scrollOptions: ScrollToOptions = { behavior: 'smooth' };

      if (this.orientation === 'horizontal') {
        scrollOptions.left =
          this.widgetsContent.nativeElement.scrollLeft +
          this.getScrollOffset() * directionSign;
      } else {
        scrollOptions.top =
          this.widgetsContent.nativeElement.scrollTop +
          this.getScrollOffset() * directionSign;
      }

      this.widgetsContent.nativeElement.scrollTo(scrollOptions);
      this.cdr.markForCheck();
    }
  }

  private calculateNumberOfPages(): void {
    const beforeNumberOfPages = this.numberOfPages;
    const segments =
      this.orientation === 'horizontal'
        ? (this.widgetsContent.nativeElement.scrollWidth -
            this.widgetsContent.nativeElement.clientWidth) /
          this.getScrollOffset()
        : (this.widgetsContent.nativeElement.scrollHeight -
            this.widgetsContent.nativeElement.clientHeight) /
          this.getScrollOffset();
    this.numberOfPages = Math.ceil(segments) + 1;

    if (beforeNumberOfPages !== this.numberOfPages) {
      this.cdr.markForCheck();
    }

    setTimeout(() => {
      this.widgetsContent.nativeElement.scrollTo({
        top: 0,
        left: 0,
        behavior: 'instant',
      });

      // when changing orientation something triggers scroll event, that's why the line below is needed
      this.isScrolling = false;
    }, 25);
  }

  private getScrollOffset(): number {
    const pageWidth =
      this.orientation === 'horizontal'
        ? this.widgetsContent.nativeElement.offsetWidth
        : this.widgetsContent.nativeElement.offsetHeight;

    return pageWidth - pageWidth * this.pageOffsetFactor;
  }
}
