import { RdsMultiSelectOptionComponent } from './multi-select-option.component';
import { RdsSingleSelectOptionComponent } from './single-select-option.component';

export const SELECT_PANEL_MAX_HEIGHT = 256;

export type RdsAbstractSelectFilterByFunc<T, O extends RdsSelectOption<T>> = (
  text: string | null,
  option: O,
  optionIndex?: number,
  options?: O[],
) => boolean;

export type RdsSingleSelectFilterByFunc<T> = RdsAbstractSelectFilterByFunc<
  T,
  RdsSingleSelectOptionComponent<T>
>;
export type RdsMultiSelectFilterByFunc<T> = RdsAbstractSelectFilterByFunc<
  T,
  RdsMultiSelectOptionComponent<T>
>;

export type RdsSelectOption<T> =
  | RdsSingleSelectOptionComponent<T>
  | RdsMultiSelectOptionComponent<T>;

export type RdsSelectCompareWithFn<T> = (o1: T, o2: T) => boolean;
