import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable()
export class RdsSidenavService {
  private _isMinimal = new BehaviorSubject(false);
  isMinimal$ = this._isMinimal.asObservable();

  set isMinimal(v: boolean) {
    this._isMinimal.next(v);
  }

  get isMinimal(): boolean {
    return this._isMinimal.getValue();
  }

  toggleIsMinimal(): void {
    this.isMinimal = !this.isMinimal;
  }
}
