import { ComponentPortal, ComponentType, Portal } from '@angular/cdk/portal';
import {
  AfterContentInit,
  AfterViewChecked,
  booleanAttribute,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  EventEmitter,
  forwardRef,
  HostBinding,
  inject,
  Input,
  isDevMode,
  OnChanges,
  OnDestroy,
  Output,
  SimpleChanges,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { DateAdapter, RDS_DATE_FORMATS, RdsDateFormats } from './adapter';
import { Subject, Subscription } from 'rxjs';
import {
  RdsCalendarCellClassFunction,
  RdsCalendarUserEvent,
} from './calendar-body.component';
import { createMissingDateImplError } from './datepicker-errors';
import { RdsDatepickerIntl } from './datepicker.intl';
import { RdsMonthViewComponent } from './month-view.component';
import {
  getActiveOffset,
  isSameMultiYearView,
  RdsMultiYearViewComponent,
  yearsPerPage,
} from './multi-year-view.component';
import { RdsYearViewComponent } from './year-view.component';
import {
  DateRange,
  RDS_SINGLE_DATE_SELECTION_MODEL_PROVIDER,
} from './date-selection.model';
import { RdsCalendarView } from './calendar.types';
import { BooleanInput } from '@angular/cdk/coercion';

let uniqueId = 0;

@Component({
  selector: 'rds-calendar',
  templateUrl: './calendar.component.html',
  styleUrls: ['./calendar.component.scss'],
  exportAs: 'rdsCalendar',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [RDS_SINGLE_DATE_SELECTION_MODEL_PROVIDER],
})
export class RdsCalendarComponent<D>
  implements AfterContentInit, AfterViewChecked, OnDestroy, OnChanges
{
  private readonly intl = inject(RdsDatepickerIntl);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly dateAdapter = inject(DateAdapter, {
    optional: true,
  }) as DateAdapter<D>;
  private readonly dateFormats = inject(RDS_DATE_FORMATS, {
    optional: true,
  }) as RdsDateFormats;

  @HostBinding('class') class = 'rds-calendar';

  @Input() headerComponent: ComponentType<unknown> | null = null;

  _calendarHeaderPortal: Portal<unknown> | null = null;

  private _intlChanges: Subscription;

  private _moveFocusOnNextTick = false;

  @Input()
  get startAt(): D | null {
    return this._startAt;
  }

  set startAt(value: D | null) {
    this._startAt = this.dateAdapter.getValidDateOrNull(
      this.dateAdapter.deserialize(value),
    );
  }

  private _startAt: D | null = null;

  @Input() startView: RdsCalendarView = 'month';

  @Input()
  get selected(): DateRange<D> | D | null {
    return this._selected;
  }

  set selected(value: DateRange<D> | D | null) {
    if (value instanceof DateRange) {
      this._selected = value;
    } else {
      this._selected = this.dateAdapter.getValidDateOrNull(
        this.dateAdapter.deserialize(value),
      );
    }
  }

  private _selected: DateRange<D> | D | null = null;

  @Input()
  get minDate(): D | null {
    return this._minDate;
  }

  set minDate(value: D | null) {
    this._minDate = this.dateAdapter.getValidDateOrNull(
      this.dateAdapter.deserialize(value),
    );
  }

  private _minDate: D | null = null;

  @Input()
  get maxDate(): D | null {
    return this._maxDate;
  }

  set maxDate(value: D | null) {
    this._maxDate = this.dateAdapter.getValidDateOrNull(
      this.dateAdapter.deserialize(value),
    );
  }

  private _maxDate: D | null = null;

  @Input() dateFilter: ((date: D) => boolean) | null = null;

  @Input() dateClass: RdsCalendarCellClassFunction<D> | null = null;

  @Input() comparisonStart: D | null = null;

  @Input() comparisonEnd: D | null = null;

  @Input({ transform: booleanAttribute }) isMonthDisabled = false;

  @Input({ transform: booleanAttribute }) isMultiYearDisabled = false;

  @Input({ transform: booleanAttribute }) isYearDisabled = false;

  @Input()
  set showToggle(value: BooleanInput | null) {
    const newValue = booleanAttribute(value);

    if (this._showToggle !== newValue) {
      this._showToggle = newValue;
      this.stateChanges.next();
      this.cdr.markForCheck();
    }
  }

  get showToggle(): boolean | null {
    return this._showToggle;
  }

  @Output() readonly selectedChange = new EventEmitter<D | null>();

  @Output() readonly yearSelected = new EventEmitter<D>();

  @Output() readonly monthSelected = new EventEmitter<D>();

  @Output() readonly viewChanged = new EventEmitter<RdsCalendarView>(true);

  @Output() readonly _userSelection = new EventEmitter<
    RdsCalendarUserEvent<D | null>
  >();

  @ViewChild(RdsMonthViewComponent) monthView: RdsMonthViewComponent<D> | null =
    null;

  @ViewChild(RdsYearViewComponent) yearView: RdsYearViewComponent<D> | null =
    null;

  @ViewChild(RdsMultiYearViewComponent)
  multiYearView: RdsMultiYearViewComponent<D> | null = null;

  get activeDate(): D {
    return this._clampedActiveDate as D;
  }

  set activeDate(value: D) {
    this._clampedActiveDate = this.dateAdapter.clampDate(
      value,
      this.minDate,
      this.maxDate,
    );
    this.stateChanges.next();
    this.cdr.markForCheck();
  }

  private _clampedActiveDate: D | null = null;

  get currentView(): RdsCalendarView {
    return this._currentView as RdsCalendarView;
  }

  set currentView(value: RdsCalendarView) {
    const viewChangedResult = this._currentView !== value ? value : null;
    this._currentView = value;
    this._moveFocusOnNextTick = true;
    this.cdr.markForCheck();

    if (viewChangedResult) {
      this.viewChanged.emit(viewChangedResult);
    }
  }

  @Input()
  get availableViews(): RdsCalendarView[] {
    return this._availableViews;
  }

  set availableViews(value: RdsCalendarView[]) {
    this._availableViews = value;
    this._moveFocusOnNextTick = true;
    this.cdr.markForCheck();
  }

  private _currentView: RdsCalendarView | null = null;
  private _availableViews: RdsCalendarView[] = ['month', 'year', 'multi-year'];
  private _showToggle: boolean | null = null;

  stateChanges = new Subject<void>();

  constructor() {
    if (!this.dateAdapter && isDevMode()) {
      throw createMissingDateImplError('DateAdapter');
    }

    if (!this.dateFormats && isDevMode()) {
      throw createMissingDateImplError('RDS_DATE_FORMATS');
    }

    this._intlChanges = this.intl.changes.subscribe(() => {
      this.cdr.markForCheck();
      this.stateChanges.next();
    });
  }

  ngAfterContentInit(): void {
    this._calendarHeaderPortal = new ComponentPortal(
      this.headerComponent || RdsCalendarHeaderComponent,
    );
    this.activeDate = this.startAt || this.dateAdapter.today();
    this._currentView = this.startView;
  }

  ngAfterViewChecked(): void {
    if (this._moveFocusOnNextTick) {
      this._moveFocusOnNextTick = false;
      this.focusActiveCell();
    }
  }

  ngOnDestroy(): void {
    this._intlChanges.unsubscribe();
    this.stateChanges.complete();
  }

  ngOnChanges(changes: SimpleChanges): void {
    const change =
      changes['minDate'] || changes['maxDate'] || changes['dateFilter'];

    if (change && !change.firstChange) {
      const view = this._getCurrentViewComponent();

      if (view) {
        this.cdr.detectChanges();
        view._init();
      }
    }

    this.stateChanges.next();
  }

  focusActiveCell(): void {
    this._getCurrentViewComponent()?._focusActiveCell(false);
  }

  updateTodaysDate(): void {
    const currentView = this.currentView;
    let view:
      | RdsMonthViewComponent<D>
      | RdsYearViewComponent<D>
      | RdsMultiYearViewComponent<D>
      | null;

    if (currentView === 'month') {
      view = this.monthView;
    } else if (currentView === 'year') {
      view = this.yearView;
    } else {
      view = this.multiYearView;
    }

    view?._init();
  }

  _dateSelected(event: RdsCalendarUserEvent<D | null>): void {
    const date = event.value;

    if (
      date &&
      !this.dateAdapter.sameDate(this.activeDate, date, 'month') &&
      !(this.selected instanceof DateRange)
    ) {
      this.activeDate = date;
    }

    if (
      this.selected instanceof DateRange ||
      (date && !this.dateAdapter.sameDate(date, this.selected))
    ) {
      this.selectedChange.emit(date);
    }

    this._userSelection.emit(event);
  }

  _yearSelectedInMultiYearView(event: RdsCalendarUserEvent<D>): void {
    const normalizedYear = this.dateAdapter.createDate(
      this.dateAdapter.getYear(event.value),
      0,
      1,
    );
    this.yearSelected.emit(normalizedYear);
    this._goToDateInViewIfAvailable(event, normalizedYear, 'year');
  }

  _monthSelectedInYearView(event: RdsCalendarUserEvent<D>): void {
    const normalizedMonth = this.dateAdapter.createDate(
      this.dateAdapter.getYear(event.value),
      this.dateAdapter.getMonth(event.value),
      1,
    );
    this.monthSelected.emit(normalizedMonth);
    this._goToDateInViewIfAvailable(event, normalizedMonth, 'month');
  }

  private _goToDateInViewIfAvailable(
    event: RdsCalendarUserEvent<D>,
    normalizedDate: D,
    view?: RdsCalendarView,
  ): void {
    this.activeDate = event.value;

    if (view && this.availableViews.includes(view)) {
      this.currentView = view;
    } else {
      this._dateSelected({
        value: this.dateAdapter.clone(normalizedDate),
        event: event.event,
      });
    }
  }

  private _getCurrentViewComponent():
    | RdsMonthViewComponent<D>
    | RdsYearViewComponent<D>
    | RdsMultiYearViewComponent<D>
    | null {
    return this.monthView || this.yearView || this.multiYearView;
  }
}

@Component({
  selector: 'rds-calendar-header',
  templateUrl: './calendar-header.component.html',
  exportAs: 'rdsCalendarHeader',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RdsCalendarHeaderComponent<D> {
  private readonly cdr = inject(ChangeDetectorRef);
  public readonly intl = inject(RdsDatepickerIntl);
  public readonly calendar = inject<RdsCalendarComponent<D>>(
    forwardRef(() => RdsCalendarComponent),
  );
  private readonly dateAdapter = inject<DateAdapter<D>>(DateAdapter);
  private readonly dateFormats = inject(RDS_DATE_FORMATS);

  _buttonDescriptionId = `rds-calendar-button-${uniqueId++}`;

  constructor() {
    this.calendar.stateChanges.subscribe(() => this.cdr.markForCheck());
  }

  get periodButtonText(): string {
    if (this.calendar.currentView === 'month') {
      return this.dateAdapter.format(
        this.calendar.activeDate,
        this.dateFormats.display.monthYearLabel,
      );
    }

    if (this.calendar.currentView === 'year') {
      return this.dateAdapter.getYearName(this.calendar.activeDate);
    }

    const activeYear = this.dateAdapter.getYear(this.calendar.activeDate);
    const minYearOfPage =
      activeYear -
      getActiveOffset(
        this.dateAdapter,
        this.calendar.activeDate,
        this.calendar.minDate,
        this.calendar.maxDate,
      );
    const maxYearOfPage = minYearOfPage + yearsPerPage - 1;
    const minYearName = this.dateAdapter.getYearName(
      this.dateAdapter.createDate(minYearOfPage, 0, 1),
    );
    const maxYearName = this.dateAdapter.getYearName(
      this.dateAdapter.createDate(maxYearOfPage, 0, 1),
    );

    return this.intl.formatYearRange(minYearName, maxYearName);
  }

  get periodButtonLabel(): string {
    return this.calendar.currentView === 'month'
      ? this.intl.switchToMultiYearViewLabel
      : this.intl.switchToMonthViewLabel;
  }

  get prevButtonLabel(): string {
    return {
      month: this.intl.prevMonthLabel,
      year: this.intl.prevYearLabel,
      'multi-year': this.intl.prevMultiYearLabel,
    }[this.calendar.currentView];
  }

  get nextButtonLabel(): string {
    return {
      month: this.intl.nextMonthLabel,
      year: this.intl.nextYearLabel,
      'multi-year': this.intl.nextMultiYearLabel,
    }[this.calendar.currentView];
  }

  currentPeriodClicked(): void {
    this.calendar.currentView =
      this.calendar.currentView == 'month' ? 'multi-year' : 'month';
  }

  previousClicked(): void {
    this.calendar.activeDate =
      this.calendar.currentView === 'month'
        ? this.dateAdapter.addCalendarMonths(this.calendar.activeDate, -1)
        : this.dateAdapter.addCalendarYears(
            this.calendar.activeDate,
            this.calendar.currentView === 'year' ? -1 : -yearsPerPage,
          );
  }

  nextClicked(): void {
    this.calendar.activeDate =
      this.calendar.currentView === 'month'
        ? this.dateAdapter.addCalendarMonths(this.calendar.activeDate, 1)
        : this.dateAdapter.addCalendarYears(
            this.calendar.activeDate,
            this.calendar.currentView === 'year' ? 1 : yearsPerPage,
          );
  }

  previousEnabled(): boolean {
    if (!this.calendar.minDate) {
      return true;
    }

    return (
      !this.calendar.minDate ||
      !this._isSameView(this.calendar.activeDate, this.calendar.minDate)
    );
  }

  nextEnabled(): boolean {
    return (
      !this.calendar.maxDate ||
      !this._isSameView(this.calendar.activeDate, this.calendar.maxDate)
    );
  }

  private _isSameView(date1: D, date2: D): boolean {
    if (this.calendar.currentView === 'month') {
      return (
        this.dateAdapter.getYear(date1) === this.dateAdapter.getYear(date2) &&
        this.dateAdapter.getMonth(date1) === this.dateAdapter.getMonth(date2)
      );
    }

    if (this.calendar.currentView === 'year') {
      return (
        this.dateAdapter.getYear(date1) === this.dateAdapter.getYear(date2)
      );
    }

    return isSameMultiYearView(
      this.dateAdapter,
      date1,
      date2,
      this.calendar.minDate,
      this.calendar.maxDate,
    );
  }
}
