import {
  booleanAttribute,
  ChangeDetectionStrategy,
  Component,
  forwardRef,
  Input,
  ViewChild,
} from '@angular/core';
import { RdsAbstractSearchComponent } from './abstract-search.component';
import { RdsFormFieldComponent } from '../form-field';
import { NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'rds-global-search',
  templateUrl: './global-search.component.html',
  styleUrls: ['./global-search.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => RdsGlobalSearchComponent),
      multi: true,
    },
  ],
})
export class RdsGlobalSearchComponent extends RdsAbstractSearchComponent {
  @ViewChild(RdsFormFieldComponent, { static: true })
  formField: RdsFormFieldComponent<string>;

  @Input({ transform: booleanAttribute }) override includeButton = true;

  private btnFocused: false | 'reset' | 'search' = false;

  get active(): boolean {
    return this.formField?.control?.focused || this.btnFocused !== false;
  }

  setBtnFocused(
    btnName: 'reset' | 'search',
    eventType: 'focus' | 'blur',
  ): void {
    if (eventType === 'focus') {
      this.btnFocused = btnName;
    } else if (eventType === 'blur' && this.btnFocused === btnName) {
      this.btnFocused = false;
    }
  }
}
