import {
  ComponentRef,
  inject,
  Injectable,
  Injector,
  NgZone,
  SecurityContext,
} from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { from, noop, Observable } from 'rxjs';
import { RdsComponentPortal } from './portal/rds-component-portal';
import { RdsToastOverlayService } from './overlay/rds-toast-overlay.service';
import { RdsToastInjector } from './rds-toast.injector';
import { RdsToastRef } from './rds-toast.ref';
import { RdsToastContainerDirective } from './rds-toast-container.directive';
import {
  DEFAULT_GLOBAL_CONFIG,
  RDS_TOAST_GLOBAL_CONFIG_TOKEN,
  RdsToastGlobalConfig,
  RdsToastIndividualConfig,
} from './rds-toast.config';
import { RdsToastPackage } from './rds-toast.package';
import { LiveAnnouncer } from '@angular/cdk/a11y';
import { RdsToastComponent } from './rds-toast.component';
import { takeUntil } from 'rxjs/operators';
import { RdsToastModule } from './rds-toast.module';

export interface RdsActiveToast<C = RdsToastComponent> {
  toastId: number;
  title: string;
  message: string;
  portal: ComponentRef<C>;
  toastRef: RdsToastRef<C>;
  onShown: Observable<void>;
  onHidden: Observable<void>;
  onTap: Observable<void>;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  onAction: Observable<any>;
}

@Injectable({
  providedIn: RdsToastModule,
})
export class RdsToastService {
  private readonly overlayService = inject(RdsToastOverlayService);
  private readonly injector = inject(Injector);
  private readonly domSanitizer = inject(DomSanitizer);
  private readonly ngZone = inject(NgZone);
  private readonly liveAnnouncer = inject(LiveAnnouncer);
  private readonly toastGlobalConfig = inject(RDS_TOAST_GLOBAL_CONFIG_TOKEN, {
    optional: true,
  });

  globalConfig: RdsToastGlobalConfig;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  private readonly toasts: RdsActiveToast<any>[] = [];
  private currentlyActive = 0;
  private overlayContainer?: RdsToastContainerDirective;
  private previousToastMessage: string | undefined;
  private _index = 0;

  constructor() {
    if (this.toastGlobalConfig) {
      this.globalConfig = { ...this.toastGlobalConfig };
    } else {
      this.globalConfig = { ...DEFAULT_GLOBAL_CONFIG };
    }
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  show<T = RdsToastComponent, A = any>(
    message?: string,
    title?: string,
    override: Partial<RdsToastIndividualConfig> = {},
  ): RdsActiveToast<T> | null {
    if (
      override.type === 'info' ||
      override.type === 'warning' ||
      override.type === 'success' ||
      override.type === 'error'
    ) {
      return this[override.type](message, title, override);
    } else {
      return this._preBuildNotification<T, A>(
        message,
        title,
        this._applyConfig(override),
      );
    }
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  success<T = RdsToastComponent, A = any>(
    message?: string,
    title?: string,
    override: Partial<RdsToastIndividualConfig> = {},
  ): RdsActiveToast<T> | null {
    override.type = 'success';
    override.icon = 'checkmark_circle';
    override.iconNamespace = 'outlined';

    return this._preBuildNotification<T, A>(
      message,
      title,
      this._applyConfig(override),
    );
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  error<T = RdsToastComponent, A = any>(
    message?: string,
    title?: string,
    override: Partial<RdsToastIndividualConfig> = {},
  ): RdsActiveToast<T> | null {
    override.type = 'error';
    override.icon = 'close_circle';
    override.iconNamespace = 'outlined';

    return this._preBuildNotification<T, A>(
      message,
      title,
      this._applyConfig(override),
    );
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  info<T = RdsToastComponent, A = any>(
    message?: string,
    title?: string,
    override: Partial<RdsToastIndividualConfig> = {},
  ): RdsActiveToast<T> | null {
    override.type = 'info';
    override.icon = 'info';
    override.iconNamespace = 'outlined';

    return this._preBuildNotification<T, A>(
      message,
      title,
      this._applyConfig(override),
    );
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  warning<T = RdsToastComponent, A = any>(
    message?: string,
    title?: string,
    override: Partial<RdsToastIndividualConfig> = {},
  ): RdsActiveToast<T> | null {
    override.type = 'warning';
    override.icon = 'warning_circle';
    override.iconNamespace = 'outlined';

    return this._preBuildNotification<T, A>(
      message,
      title,
      this._applyConfig(override),
    );
  }

  clear<T>(toastId?: number | RdsActiveToast<T>): void {
    const toast =
      typeof toastId === 'number'
        ? this.toasts.find((t) => t.toastId === toastId)
        : this.toasts.find((t) => t === toastId);

    if (!toast) {
      this.toasts.forEach((t) => t.toastRef.manualClose());
    } else {
      toast.toastRef.manualClose();
    }
  }

  private _remove(toastId: number): boolean {
    const toastIndex = this.toasts.findIndex((t) => t.toastId === toastId);

    if (toastIndex === -1) {
      return false;
    }

    if (!this.toasts[toastIndex].toastRef.isClosed()) {
      this.toasts[toastIndex].toastRef.close();
    }

    this.toasts.splice(toastIndex, 1);
    this.currentlyActive = this.currentlyActive - 1;

    if (!this.globalConfig.maxOpened || !this.toasts.length) {
      return false;
    }

    if (
      this.currentlyActive < this.globalConfig.maxOpened &&
      this.toasts[this.currentlyActive]
    ) {
      const p = this.toasts[this.currentlyActive].toastRef;

      if (!p.isInactive()) {
        this.currentlyActive = this.currentlyActive + 1;
        p.activate();
      }
    }

    return true;
  }

  private _applyConfig(
    override: Partial<RdsToastIndividualConfig> = {},
  ): RdsToastGlobalConfig {
    return {
      ...this.globalConfig,
      ...override,
      // remove this line when library should allow to pass custom component
      ...{ toastComponent: RdsToastComponent },
    };
  }

  private _preBuildNotification<T, A>(
    message: string | undefined,
    title: string | undefined,
    config: RdsToastGlobalConfig,
  ): RdsActiveToast<T> | null {
    if (config.onActivateTick) {
      return this.ngZone.run(() =>
        this._buildNotification<T, A>(message, title, config),
      );
    }

    return this._buildNotification<T, A>(message, title, config);
  }

  private _buildNotification<T, A>(
    message: string | undefined,
    title: string | undefined,
    config: RdsToastGlobalConfig,
  ): RdsActiveToast<T> | null {
    if (!config.toastComponent) {
      throw new Error('toastComponent required');
    }

    if (typeof title !== 'string' || title.trim().length === 0) {
      title = '';
    }

    if (typeof message !== 'string' || message.trim().length === 0) {
      message = title;
      title = '';
    }

    this.previousToastMessage = message;
    let keepInactive = false;

    if (
      this.globalConfig.maxOpened &&
      this.currentlyActive >= this.globalConfig.maxOpened
    ) {
      keepInactive = true;

      if (this.globalConfig.autoDismiss) {
        this.clear(this.toasts[0]);
      }
    }

    const overlayRef = this.overlayService.create<T>(
      config.position,
      this.overlayContainer,
    );
    this._index = this._index + 1;
    let sanitizedMessage: string | undefined | null = message;

    if (message && config.enableHtml) {
      sanitizedMessage = this.domSanitizer.sanitize(
        SecurityContext.HTML,
        message,
      );
    }

    const toastRef = new RdsToastRef<T>(overlayRef);
    const toastPackage = new RdsToastPackage<T, A>(
      this._index,
      config,
      sanitizedMessage,
      title,
      toastRef,
    );
    const toastInjector = new RdsToastInjector<T, A>(
      toastPackage,
      this.injector,
    );
    const component = new RdsComponentPortal(
      config.toastComponent,
      toastInjector,
    );
    const portal = overlayRef.attach(component, config.newestOnTop);
    toastRef.componentInstance = portal.instance;
    const ins: RdsActiveToast<T> = {
      toastId: this._index,
      title: title,
      message: message,
      toastRef,
      onShown: toastRef.afterActivate(),
      onHidden: toastRef.afterClosed(),
      onTap: toastPackage.onTap(),
      onAction: toastPackage.onAction(),
      portal,
    };

    if (!keepInactive) {
      this.currentlyActive = this.currentlyActive + 1;
      from(Promise.resolve())
        .pipe(takeUntil(ins.toastRef.afterClosed()))
        .subscribe(() => ins.toastRef.activate());
    }

    toastRef.afterClosed().subscribe(() => {
      this.liveAnnouncer.clear();
      this._remove(ins.toastId);
    });
    toastRef
      .afterActivate()
      .pipe(takeUntil(toastRef.afterClosed()))
      .subscribe(() => {
        if (config.announcementMessage) {
          from(
            this.liveAnnouncer.announce(
              config.announcementMessage,
              config.politeness,
            ),
          ).subscribe(noop);
        } else {
          from(
            this.liveAnnouncer.announce(
              `${config.type} Toast: Title: ${ins.title}: Message: ${ins.message}`,
              config.politeness,
            ),
          ).subscribe(noop);
        }
      });

    this.toasts.push(ins);

    return ins;
  }
}
