import {
  AfterContentInit,
  DestroyRef,
  Directive,
  ElementRef,
  HostBinding,
  HostListener,
  inject,
  Input,
  OnChanges,
  OnDestroy,
  SimpleChanges,
} from '@angular/core';
import { fromEvent, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { RdsDropdownToggleTrigger } from './dropdown.types';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

@Directive({
  selector: '[rdsDropdownToggle], [rds-dropdown-toggle]',
  exportAs: 'rdsDropdownToggle',
})
export class RdsDropdownToggleDirective
  implements AfterContentInit, OnChanges, OnDestroy
{
  private static readonly TRIGGER_SPLITTER = ', ';

  public readonly elementRef = inject<ElementRef<HTMLElement>>(ElementRef);
  protected readonly destroyRef = inject(DestroyRef);

  readonly toggle = new Subject<MouseEvent | KeyboardEvent>();
  protected _isOpen = false;
  private readonly _eventsDestroyer$ = new Subject<void>();

  @Input()
  trigger: RdsDropdownToggleTrigger = 'click';

  @HostBinding('class')
  class = 'rds-dropdown-toggle';

  @HostBinding('disabled')
  disabled = false;

  @Input()
  @HostBinding('attr.aria-expanded')
  get isOpen(): boolean {
    return this._isOpen;
  }

  set isOpen(isOpen: boolean) {
    this._isOpen = isOpen;
  }

  @HostListener('keydown.enter', ['$event'])
  onEnter($event: KeyboardEvent): void {
    this.toggle.next($event);
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['trigger'] && !changes['trigger'].isFirstChange()) {
      this._refreshEvents();
    }
  }

  ngAfterContentInit(): void {
    this._refreshEvents();
  }

  ngOnDestroy(): void {
    this._eventsDestroyer$.next();
    this._eventsDestroyer$.complete();
  }

  private _refreshEvents(): void {
    this._eventsDestroyer$.next();

    if (this.trigger === null || this.trigger === undefined) {
      this.trigger = 'click';
    }

    this.trigger
      .split(RdsDropdownToggleDirective.TRIGGER_SPLITTER)
      .forEach((trigger) =>
        fromEvent<MouseEvent>(this.elementRef.nativeElement, trigger)
          .pipe(
            takeUntil(this._eventsDestroyer$),
            takeUntilDestroyed(this.destroyRef),
          )
          .subscribe((event) => this.toggle.next(event)),
      );
  }
}
