import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { RdsIconComponent } from '../icons';
import { RdsChipAvatarDirective } from './chip-avatar.directive';
import { RdsChipLeadingIconDirective } from './chip-leading-icon.directive';
import { RdsChipListComponent } from './chip-list/chip-list.component';
import { RdsChipComponent } from './chip/chip.component';
import { RDS_AVATAR_DIRECTIVES } from '../avatar';
import { RdsButtonModule } from '../button';
import { RdsChipSelectableComponent } from './chip-selectable/chip-selectable.component';
import { RdsChipSetComponent } from './chip-set';
import { RdsChipActionDirective } from './chip-action';
import { RdsChipEditInputDirective } from './chip-edit-input';
import { RDS_CHIPS_DEFAULT_OPTIONS } from './chip.tokens';
import { RdsChipsDefaultOptions } from './chip.types';

const CHIP_DECLARATIONS = [
  RdsChipListComponent,
  RdsChipComponent,
  RdsChipLeadingIconDirective,
  RdsChipAvatarDirective,
  RdsChipSelectableComponent,
  RdsChipSelectableComponent,
  RdsChipActionDirective,
];

const PRIVATE_DECLARATIONS = [RdsChipEditInputDirective, RdsChipSetComponent];

@NgModule({
  imports: [
    CommonModule,
    RdsIconComponent,
    RdsButtonModule,
    ...RDS_AVATAR_DIRECTIVES,
  ],
  declarations: [...CHIP_DECLARATIONS, ...PRIVATE_DECLARATIONS],
  exports: [...CHIP_DECLARATIONS],
  providers: [
    {
      provide: RDS_CHIPS_DEFAULT_OPTIONS,
      useValue: {
        separatorKeys: ['Enter', ','],
      } as RdsChipsDefaultOptions,
    },
  ],
})
export class RdsChipsModule {}
